    "Fishery Management": "<ul>\n<li><a href=\"https://www.fisheries.noaa.gov/region/new-england-mid-atlantic\">NOAA Fisheries</a> and the <a href=\"https://www.nefmc.org/index.html\">New England Fishery Management Council </a>manage the white hake fishery.</li>\n<li>White hake is managed as a single stock&nbsp;in U.S. waters.</li>\n<li>White hake, along with other groundfish in New England waters, is managed under the <a href=\"https://www.nefmc.org/management-plans/small-mesh-multispecies\">Northeast Multispecies Fishery Management Plan</a>, which includes:\n<ul>\n<li>Permitting requirements for commercial vessels.</li>\n<li>Separate management measures for recreational vessels.</li>\n<li>Time/area closures to protect spawning fish and habitat.</li>\n<li>Annual catch limits&nbsp;based on best available science.</li>\n</ul>\n</li>\n<li>According to the 2019&nbsp;stock assessment, white hake&nbsp;is overfished.&nbsp; A rebuilding plan is in place until the stock rebuilds to the target population level.</li>\n<li>An optional sector (<a href=\"https://www.fisheries.noaa.gov/new-england-mid-atlantic/commercial-fishing/sector-management-northeast-multispecies-fishery\">catch share</a>) program can be used for white hake&nbsp;and other groundfish species. The sector program allows fishermen to form harvesting cooperatives and work together to decide when, where, and how they harvest fish.</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>White hake are groundfish, meaning they live near the ocean bottom.</li>\n<li>White hake prefer areas with sandy or muddy bottoms.</li>\n<li>White hake live in relatively deep waters, typically at least 80 meters below sea level.</li>\n</ul>\n",
