    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of white hake totaled 4.2&nbsp;million pounds&nbsp;and were valued at over $3.8&nbsp;million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>White hake are commonly harvested using trawl nets, gillnets, bottom longlines, and rod and reel.</li>\n<li>Gillnets, longlines, and rod and reel used to harvest white hake have little to no impact on habitat.&nbsp;</li>\n<li>Closed areas and gear restrictions reduce habitat impacts from trawl nets.&nbsp;</li>\n<li>Fishermen follow management measures designed to reduce interactions with marine mammals, including gear modifications, seasonsal closures, and use of marine mammal deterrents.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>White hake are encountered by recreational fishermen fishing with hook-and-line. Fishing occurs year round.&nbsp;</li>\n<li>There is no minimum fish size limit for white hake, and the recreational bag limit is unlimited.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
