    "Human Health": null,
    "Physical Description": "<ul>\n<li>White hake have a large mouth that extends back below their eyes.</li>\n<li>White hake have one anal fin and two dorsal fins. The first dorsal fin is tall and triangular, and the second is lower, rounded, and extends almost to the fishs tail.</li>\n<li>The third fin ray on the first (triangular) dorsal fin is elongated and extends much higher than the rest of the fin.</li>\n<li>White hake are a member of the cod order and have a barbel (whisker) on their chin.</li>\n<li>White hake vary in color; most adults range from purplish-brown to golden-brown on their back and sides, and they have a yellowish-white belly speckled with small black spots.</li>\n</ul>\n",
