    "Fishery Management": "<ul>\n<li>\n<p><a href=\"https://archive.fisheries.noaa.gov/garfo/sustainable/species/msb/index.html\">NOAA Fisheries</a> and the <a href=\"https://www.mafmc.org/\">Mid-Atlantic Fishery Management Council</a> manage the Atlantic chub mackerel fishery.</p>\n</li>\n<li>\n<p>Managed under the <a href=\"https://www.mafmc.org/msb/\">Mackerel, Squid, Butterfish Fishery Management Plan</a>:</p>\n<ul>\n<li>\n<p>Atlantic chub mackerel was added to the Mackerel, Squid, and Butterfish Fishery Management Plan in&nbsp;2020.</p>\n</li>\n<li>\n<p>Atlantic chub mackerel was added to the FMP after&nbsp;a directed commercial fishery developed in federal waters. Due to that, and other considerations, the Council concluded that this species is in need of specific conservation and management measures of its own.&nbsp;</p>\n</li>\n<li>\n<p>Atlantic chub mackerel are managed in federal waters. There are no state management measures for mackerel.</p>\n</li>\n<li>\n<p>Managed using annual catch limits allocated between the commercial and recreational fisheries.</p>\n</li>\n<li>\n<p>Managers monitor commercial catch on a weekly basis and will close the fisheries if the limits are reached before the fishing season is over.</p>\n</li>\n<li>\n<p>Fishermen must have a Mackerel, Squid, and Butterfish permit to harvest Atlantic chub mackerel.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>Atlantic chub mackerel are a schooling pelagic species.&nbsp;</li>\n<li>They are found on the continental shelf to depths of about 250300 meters throughout much of the western and eastern Atlantic Ocean. They can be found throughout U.S. Atlantic waters.&nbsp;</li>\n<li>Most Atlantic chub mackerel catches in offshore surveys occur south of the Hudson Shelf Valley in warm waters.&nbsp;</li>\n<li>State trawl surveys and recreational catch data suggest that Atlantic chub mackerel are also found in inshore waters.</li>\n</ul>\n",
