    "Harvest": "<ul>\n<li>Commercial Fishery\n<ul>\n<li>In 2019, commercial landings of Atlantic chub mackerel totaled 58,000 pounds and were valued at $38,000, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>.</li>\n<li>Most commercial vessels fishing for Atlantic chub mackerel are participants in the shortfin squid fishery.</li>\n<li>Bottom trawl gear is used to catch Atlantic chub mackerel.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>\n<p>Many habitat areas in the Atlantic chub mackerel fishery have been impacted by fishing activities over many years.</p>\n</li>\n<li>\n<p>There are gear restricted areas in place for shortfin squid vessels, designed to protect essential fish habitat.&nbsp;</p>\n</li>\n<li>\n<p>There are mesh size and other restrictions for shortfin squid vessels but no specific gear restrictions for Atlantic chub mackerel.</p>\n</li>\n</ul>\n</li>\n<li>\n<p>Recreational fishery:</p>\n<ul>\n<li>\n<p>In 2019, recreational anglers landed more than 74,000 pounds of Atlantic chub mackerel, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">recreational fishing landings database</a>.&nbsp;&nbsp;&nbsp;</p>\n</li>\n<li>\n<p>Recreational catch is monitored and included in annual catch levels.&nbsp;</p>\n</li>\n<li>\n<p>Commercial vessels must possess a charter party permit in order to possess Atlantic chub mackerel.&nbsp;</p>\n</li>\n<li>\n<p>There are no size or gear limits for the recreational fleet.&nbsp;</p>\n</li>\n</ul>\n</li>\n</ul>\n",
