    "Species Name": "Shortfin Squid",
    "Animal Health": null,
    "Availability": "<p>Summer and fall.</p>\n",
    "Biology": "<ul>\n<li>Shortfin squid live for less than one year. They have a high natural mortality rate, and a long spawning season.</li>\n<li>Females can release multiple egg masses during a single spawning season, but die after they spawn. Spawning can occur year round with seasonal peaks from October to June.</li>\n<li>Shortfin squid have extremely variable birth, growth, and maturity rates. This makes them extremely sensitive to climate-driven changes.&nbsp;</li>\n<li>They grow about one millimeter a day.</li>\n<li>Shortfin squid are visual predators that eat crustaceans, fish, and other squid, including their own species.</li>\n<li>They are food for many fish, including <a href=\"https://www.fishwatch.gov/profiles/western-atlantic-bluefin-tuna\">bluefin tuna</a>, <a href=\"https://www.fishwatch.gov/profiles/silver-hake\">silver hake</a>, <a href=\"https://www.fishwatch.gov/profiles/red-hake\">red hake</a>, <a href=\"https://www.fishwatch.gov/profiles/bluefish\">bluefish</a>, <a href=\"https://www.fishwatch.gov/profiles/monkfish\">goosefish</a>, fourspot flounder, <a href=\"https://www.fishwatch.gov/profiles/atlantic-cod\">Atlantic cod</a>, sea raven, <a href=\"https://www.fishwatch.gov/profiles/atlantic-spiny-dogfish\">spiny dogfish</a>, and <a href=\"https://www.fishwatch.gov/profiles/north-atlantic-swordfish\">swordfish</a>. Seabird predators include shearwaters, gannets, and fulmars.</li>\n<li>Shortfin squid undergo daily vertical migrations between cooler deep water and warmer surface water. They are nearest the seabed during the day, and higher in the water column during the night.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
