    "Harvest": "<ul>\n<li>Commercial fishery\n<ul>\n<li>In 2019, commercial landings of shortfin squid totaled approximately 58 million pounds, and were valued at approximately $27.3 million according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>. These figures may not match other agency sources of data due to confidential information.</li>\n<li>Fisheries for shortfin squid reflect the species seasonal migrations.</li>\n<li>The majority of landings come from Rhode Island and New Jersey.</li>\n<li>Harvested for bait domestically, and exported for bait and food.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>The majority of shortfin squid is harvested June 1 through October 31 using small-mesh bottom trawls. The fishery is open year round, but the squid arent available in commercial quantities year round.</li>\n<li>Sandy or muddy habitat, where squid are fished, is less sensitive to the impacts of trawling.</li>\n<li>Small-mesh bottom trawls can incidentally catch marine mammals and large pelagic species, including pilot whales, common dolphin, swordfish, and a variety of sharks, ray, and tuna species. Finfish such as butterfish, hakes, longfin squid, summer flounder, herring, spiny dogfish, and Atlantic mackerel are also incidentally caught in this fishery.</li>\n<li>Measures to prevent or minimize bycatch include:\n<ul>\n<li>Fishing must occur seaward of the 50-fathom depth line to reduce finfish and longfin inshore squid bycatch.</li>\n<li>Outreach to fishermen to educate them on actions to take in the event of a marine mammal interaction.</li>\n<li>Real-time communication to vessels regarding hotspots of marine mammal interactions.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Squid are an excellent source of selenium, riboflavin, and vitamin B12.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Female shortfin squid range from 7 to 12 inches in mantle length, while males are 7 to 10.6 inches in mantle length.</li>\n<li>They can regulate their body color, but are primarily orange-colored with a brown stripe that extends along the top side of the mantle.</li>\n</ul>\n",
