    "Fishery Management": "<ul>\n<li>The states and <a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/species/lobster/\">NOAA Fisheries</a> cooperatively manage the American lobster resource and fishery under the framework of the <a href=\"http://www.asmfc.org/\">Atlantic States Marine Fisheries Commission</a> (ASMFC).</li>\n</ul>\n<ul>\n<li>Each lobster harvesting state has three members on the ASMFC lobster management board, and NOAA Fisheries has one representative on the board. Each state, and NOAA Fisheries, has one vote when deliberating management measures for American lobster. The management board looks to industry advisors to provide recommendations for managing the fishery to meet management objectives.</li>\n</ul>\n<ul>\n<li>States have jurisdiction for implementing measures in state waters (within 3 nautical miles of shore), while <a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/species/lobster/\">NOAA Fisheries</a> implements complementary regulations for the American lobster fishery in offshore federal waters (3 to 200 nautical miles from shore).</li>\n</ul>\n<ul>\n<li>The American lobsters range is divided into&nbsp;<a href=\"http://www.asmfc.org/uploads/file/58f8cd9aLobsterManagement_StockArea_Map_Nov2016.JPG\">two stock areas and seven management areas</a>. There are seven Lobster Conservation Management Teams, one for each management area. These teams, made up of industry representatives, recommend measures to address the specific needs in their respective management areas. Federal waters contain portions of six of the seven management areas. Only Area 6 is totally within state waters (Long Island Sound, which consists of New York and Connecticut state waters).</li>\n</ul>\n<ul>\n<li>Managed in state waters (within 3 nautical miles of shore) under the <a href=\"http://www.asmfc.org/species/american-lobster\">Interstate Fishery Management Plan for American Lobster</a>. Each management area has unique regulations that include:\n<ul>\n<li>Limits on the minimum and maximum size of lobster than can be harvested.</li>\n<li>Trap limits control fishing effort. Each lobster vessel is limited to either a vessel-based trap allocation based on its historical fishing practices, or an area-wide trap cap (the maximum number of traps a vessel may fish in a specific area).&nbsp;</li>\n<li>Measures to protect egg-bearing femalesfishermen may not harvest them and, in most areas, if one is caught in their trap, they must notch its tail fin in a v shape before returning it to the water.</li>\n<li>Prohibition on possession of lobster meat and lobster parts (lobsters must be landed live and whole to ensure they are of legal size).</li>\n<li>Gear restrictions, trap configuration requirements, and prohibition on using spears to fish for lobsters.</li>\n<li>Restrictions on the amount of lobster that can be harvested with non-trap gear.</li>\n<li>Monitoring and reporting requirements.</li>\n<li>A series of annual trap reductions are underway in lobster management areas 2 and 3 to reduce both latent and active effort and scale the Southern New England lobster fishery to the size of the stock.</li>\n<li>A <a href=\"https://www.greateratlantic.fisheries.noaa.gov/nr/2017/July/17lobttpgphl.pdf\">trap transfer program</a> was initiated in 2015 that allows Federal permit holders to mitigate the impacts of the annual trap reductions by purchasing partial trap allocation from other authorized permit holders.&nbsp; Others may sell allocation to other Federal lobster permit holders to downsize their own fishing operations and allow other permit holders to gain access to the trap fishery in certain management areas.</li>\n</ul>\n</li>\n</ul>\n<ul>\n<li>Managed in federal waters (3 to 200 nautical miles offshore) under regulations implemented through the <a href=\"http://www.asmfc.org/uploads/file/ACFCMA.pdf\">Atlantic Coastal Fisheries Cooperative Management Act</a>, including:\n<ul>\n<li>Fishermen must have a permit to harvest lobster. A temporary moratorium on the issuance of federal lobster permits, which limits the amount of available permits to control the number of fishermen harvesting lobster, was extended indefinitely in 1999.</li>\n<li>Limits on the minimum and maximum size of lobsters that can be harvested, which varies by management area.</li>\n<li>Prohibition on possession of lobster meat and lobster parts (lobsters must be landed live and whole to ensure they are of legal size).</li>\n<li>Measures to protect egg-bearing femalesfishermen may not harvest them and, in most areas, if one is caught in their trap, they must notch its tail fin in a v shape before returning it to the water.</li>\n<li>Gear restrictions (trap size, gear marking requirements, escape vents, and ghost panels).</li>\n<li>Trap limits, which vary among management areas.</li>\n<li>To improve data collection in the fishery, all federal lobster dealers must submit weekly electronic reports for all lobsters they purchase from fishermen with federal permits. Federal lobster permit holders are not required to report landings unless they have another Federal fishery permit, in addition to their Federal lobster permit, that &nbsp;requires landings reports (e.g., Northeast multispecies permit).</li>\n<li>Area-specific measures have been approved to reduce fishing exploitation on the Southern New England stock, including biological and effort control management measures.</li>\n<li>Regulations require biodegradable escape panels or hinges on traps to prevent ghost fishing (when lost gear continues to capture lobster and other species). Escape panels must be large enough to reduce bycatch of undersized lobsters.</li>\n</ul>\n</li>\n</ul>\n<ul>\n<li><a href=\"http://www.dfo-mpo.gc.ca/fm-gp/sustainable-durable/fisheries-peches/lobster-homard-eng.htm\">Fisheries and Oceans Canada</a> manages the American lobster resource in Canadian territorial waters of the northwest Atlantic Ocean.</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>American lobsters live on the ocean floor.</li>\n</ul>\n<ul>\n<li>They live alone and are very territorial.</li>\n</ul>\n<ul>\n<li>They can live in a variety of habitats as long as there is a burrow or crevice for cover.</li>\n</ul>\n<ul>\n<li>Coastal lobsters like rocky areas where they can readily find shelter, although theyre sometimes found in mud bottoms where they can burrow.</li>\n</ul>\n<ul>\n<li>Offshore populations are most abundant along the edge of the continental shelf near underwater canyons.</li>\n</ul>\n<ul>\n<li>Near the coast, small lobsters do not travel much, but larger ones may travel extensively. Offshore lobsters migrate during the spring anywhere from 50 to 190 miles.</li>\n</ul>\n<ul>\n<li>Scientists, managers, and fishermen are concerned about the habitat conditions for American lobster in inshore Southern New England waters, particularly in Long Island Sound. Scientists believe that a combination of warmer water temperatures, hypoxia (low dissolved oxygen levels), and other stress factors resulted in lobster die-offs in western Long Island Sound in late 1999 and in 2002. If these conditions continue, future die-offs are possible. Researchers also believe that increased water temperatures in Southern New England may be driving lobsters to cooler offshore waters and disrupting the settlement of larvae in traditional coastal areas.</li>\n</ul>\n",
    "Habitat Impacts": "Fishing gears used to harvest American lobster have minimal impacts on habitat.",
