    "Location": "<ul>\n<li>American lobsters are found in the northwest Atlantic Ocean from Labrador to Cape Hatteras. Theyre most abundant in coastal waters from Maine through New Jersey, and are also common offshore to depths of 2,300 feet from Maine through North Carolina.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "Above target population levels in the Gulf of Maine and Georges Bank. Significantly below target levels in Southern New England.",
    "Population Status": "<ul>\n<li>According to the 2020&nbsp;<a href=\"http://www.asmfc.org/uploads/file/55d61d73AmLobsterStockAssmt_PeerReviewReport_Aug2015_red2.pdf\">stock assessment</a><a href=\"https://www.asmfc.org/uploads/file/5fb2c4a82020AmLobsterBenchmarkStockAssmt_PeerReviewReport.pdf\">&nbsp;</a>conducted by the <a href=\"http://www.asmfc.org/\">Atlantic States Marine Fisheries Commission</a> (ASMFC), there is record high stock abundance&nbsp;in the Gulf of Maine and Georges Bank, and record low abundance&nbsp;and continued recruitment failures&nbsp;in Southern New England. The Gulf of Maine and Georges Bank stock is not overfished. However, the ASMFC considers the Southern New England stock severely depleted due to environmental factors and fishing pressure. Neither stock is subject to overfishing.</li>\n<li>Since 2012, <a href=\"http://umaine.edu/wahlelab/american-lobster-settlement-index-alsi/american-lobster-settlement-index/\">Young of Year surveys</a> in the Gulf of Maine and Georges Bank stock have shown consistent declines, which could indicate future declines in recruitment and landings.</li>\n</ul>\n",
    "Scientific Name": "Homarus americanus",
    "Species Aliases": "<a href=\"/species-aliases/lobster\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Lobster</a>",
