    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of American lobster totaled approximately 120 million pounds and were valued at more than $524.5 million, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:29869960868046::NO:::\">commercial fishing landings database</a>.</li>\n<li>The two stocks of American lobsterGulf of Maine and Georges Bank, and Southern New Englandsupport both inshore and offshore fisheries. The Gulf of Maine and Southern New England areas are predominantly inshore fisheries, while the Georges Bank area is predominantly an offshore fishery. Most U.S. harvest is caught in inshore waters.</li>\n<li>Theres a reason we associate Maine with lobstersthe state has led American lobster landings for over 3 decades. Massachusetts is the second leading producer. Together, these two states produce 93&nbsp;percent of the total U.S. American lobster harvest, and 93&nbsp;percent of the coast-wide landings come from the Gulf of Maine lobster stock.</li>\n</ul>\n</li>\n</ul>\n<ul>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Most fishermen use traps to harvest lobster. They bait rectangular, wire-mesh traps then lower them to the ocean floor in water 15 to 1,000 feet deep. A buoy that marks the traps location is attached to the trap line. Fishermen haul the traps back to the surface every few days to check their catch, although the frequency varies depending on the season and the location.</li>\n<li>The Northeast/Mid-Atlantic American lobster trap/pot fishery can incidentally entangle large whales. To reduce injuries and deaths of large whales due to fisheries interactions, the <a href=\"https://www.greateratlantic.fisheries.noaa.gov/Protected/whaletrp/\">Atlantic Large Whale Take Reduction Plan</a> was implemented in 1997. Lobstermen must follow a number of regulations to protect large whales from fishing gear. For example, lobstermen must use sinking groundlines between traps to reduce the amount of line in the water column, which reduces the potential for whales and other protected species to become entangled. In addition, lobster permit holders are required to haul their active traps at least once every 30 days.</li>\n<li>Traps can incidentally catch finfish and invertebrates (such as crabs and conch). Regulations require traps to be configured with biodegradable escape panels or hinges on traps to prevent ghost fishing (lost gear that continues to capture lobster and other species and may pose a hazard to other marine species). Escape panels must be large enough to reduce bycatch of undersized lobsters.</li>\n</ul>\n</li>\n</ul>\n<ul>\n<li>Recreational fishery:\n<ul>\n<li>Recreational fishermen catch lobsters in coastal waters with pots and by hand while scuba diving. Recreational fishermen with a federal lobster permit may harvest lobster in federal waters, but the lobster cannot be sold.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Lobster is low in saturated fat and is a very good source of protein and selenium. The FDA <a href=\"https://www.fda.gov/downloads/food/guidanceregulation/ucm252395.pdf\">advises </a>consumers to not eat the tomalley, the light-green substance found in the lobster.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>American lobster is a crustacean with a large shrimp-like body and 10 legs, two of which are large, strong claws.</li>\n</ul>\n<ul>\n<li>One claw is a big-toothed crusher claw for pulverizing shells, and the other is a finer-edged ripper claw, resembling a steak knife, for tearing soft flesh.</li>\n</ul>\n<ul>\n<li><a href=\"https://www.greateratlantic.fisheries.noaa.gov/regs/infodocs/lobster_sex_id.pdf\">Male and female lobsters</a> can be distinguished by the first pair of swimmerets (pleopods) on the upper portion of the underside of the tail. The male swimmerets are larger and more rigid. The female swimmerets are softer, smaller, and have rounded edges.</li>\n</ul>\n<ul>\n<li>Live lobsters are not red like those you see in a restaurant or grocery store, after they have been cooked. Most are either olive-green or greenish-brown. Some have orange, reddish, dark green, or black speckles and bluish colors in the joints of their appendages.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.80 g",
    "Quote": "U.S. wild-caught American lobster is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#996633",
    "Research": "<ul>\n<li>State scientists, in cooperation with the lobster industry, are conducting projects to assist with the effective management of the lobster resource. Many states have established <a href=\"http://www.asmfc.org/fisheries-science/surveys\">ventless trap survey</a>s to quantify the abundance of juvenile lobsters. By removing escape vents from the lobster traps and randomly placing those traps within certain depth categories and geographic areas, researchers can assess the abundance of juvenile lobsters and the potential for young lobsters to reach a size or life stage that can be caught by the fishing gear (recruitment) in the future. These surveys complement longstanding fishery-independent bottom trawl surveys conducted by NOAA Fisheries and the states. Because trawl gear cannot effectively sample rocky or shallow coastal bottom types, the ventless trap surveys&nbsp;attempt to fill this data gap by using fixed lobster gear without escape vents.</li>\n</ul>\n",
