    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Yellowtail rockfish mature between three and five years old.</li>\n<li>They can live up to 50 years.</li>\n<li>They grow more than two feet in length.</li>\n<li>Females can produce between 50,000 and 600,000 eggs, depending on the size of the female.</li>\n<li>Yellowtail rockfish have internal fertilization and the females give birth to live young.</li>\n<li>Adults feed on shellfish, such as shrimp, and small forage fish, such as anchovies.</li>\n<li>Yellowtail rockfish are unique in that they can rapidly release gas from their swim bladders.</li>\n<li>When caught at depth the yellowtail rockfish can avoid barotrauma that kills most other species.</li>\n</ul>\n",
