    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial landings of yellowtail rockfish totaled more than 7.7 million pounds and were valued at &nbsp;$2.3 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:13037956116849::NO:::\">commercial fishing landings database</a>.&nbsp;The majority of landings are in Oregon and Washington.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Primarily harvested with midwater trawl gear, which has minimal impacts on ocean bottom habitats. To a lesser extent, harvested with bottom trawl gear.</li>\n<li>Midwater and bottom trawls may sometimes catch other species of fish, including overfished and protected species.\n<ul>\n<li>Gear restrictions, closed areas, and catch share programs limit when, where, and how much trawl fishermen can harvest to reduce bycatch of other species.</li>\n<li><a href=\"https://www.westcoast.fisheries.noaa.gov/fisheries/management/groundfish_closures/rockfish_areas.html\">Rockfish conservation areas</a> eliminate fishing in areas on the West Coast where overfished rockfish species co-occur with target stocks, like <a href=\"https://www.fishwatch.gov/profiles/canary-rockfish\">canary rockfish</a>. These closed areas help prevent bycatch of overfished rockfish.</li>\n</ul>\n</li>\n<li>Yellowtail rockfish are often caught incidentally in the <a href=\"https://www.fishwatch.gov/profiles/pacific-whiting\">Pacific whiting</a> fishery.\n<ul>\n<li>Managers are working to reduce incidental catch through the use of annual catch limits and catch shares.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Rockfish are high in selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Yellowtail rockfish are greyish brown on top and fade to white on the belly.</li>\n<li>Their body has trace yellow spotting.</li>\n<li>Yellowtail tail fins are yellowish green.</li>\n<li>Their other fins have a darker yellowish green coloration.</li>\n</ul>\n",
