    "Quote": "U.S. wild-caught Yellowtail rockfish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#7a622d",
    "Research": "<ul>\n<li>NOAAs <a href=\"https://www.nwfsc.noaa.gov/\">Northwest</a> and <a href=\"https://www.fisheries.noaa.gov/about/alaska-fisheries-science-center\">Alaska</a> Fisheries Science Centers survey the abundance of yellowtail rockfish off the West Coast and Alaska.</li>\n<li>Yellowtail rockfish is not typically assessed as part of a single-species abundance survey. It is more commonly assessed along with other groundfish.</li>\n</ul>\n",
