    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Bocaccio is a species of rockfish. Rockfishes are unusual among the bony fishes in that fertilization and embryo development is internal and female rockfish give birth to live larval young.</li>\n<li>Like most other species of rockfish, bocaccio are long-lived. Bocaccio mature and begin to reproduce between ages 4 and 7 years old, and they can live to be 50 years old.</li>\n<li>Bocaccio larvae are opportunistic feeders. Early on, larvae mostly eat copepod nauplii and eat some invertebrate eggs. As they grow, larvae start eating copepodites, adult copepods, and euphausiids.</li>\n<li>Within the first year of their lives, bocaccio begin foraging on other young fishes.</li>\n<li>Adult bocaccio mostly eat fish. Their preferred meal is other rockfishes, but they will also eat <a href=\"https://www.fishwatch.gov/profiles/sablefish\">sablefish</a>, anchovies, lantern fish and squid.</li>\n<li>Female bocaccio may spawn one to three times per season. They are highly fecund for rockfish and have been recorded with anywhere from 290 thousand up to 1.9 million eggs in the ovaries at one time.</li>\n<li>In the Southern California Bight, Bocaccio spawn from October to July, peaking in January. Off central and northern California, Bocaccio spawn from January to May and peak in February.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
