    "Harvest": "<ul>\n<li>Commercial fishery\n<ul>\n<li>In 2019, commercial landings of bocaccio totaled approximately 1 million pounds and were valued at approximately $478,000, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:1186819784242::NO:::\">commercial fishing landings database</a>.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts and bycatch\n<ul>\n<li>Bottom trawl gear is the predominant fishing gear used to catch bocaccio.</li>\n<li>Bottom trawls may sometimes catch other species of fish, including overfished and protected species.</li>\n<li>Gear restrictions, closed areas, and catch share programs limit when, where, and how much trawl fishermen can harvest to reduce bycatch of other species.</li>\n<li><a href=\"https://www.westcoast.fisheries.noaa.gov/fisheries/management/groundfish_closures/rockfish_areas.html\">Rockfish conservation areas</a> eliminate fishing in areas on the West Coast where overfished rockfish species co-occur with target stocks, like <a href=\"https://www.fishwatch.gov/profiles/canary-rockfish\">canary rockfish</a>. These closed areas help prevent bycatch of overfished rockfish.</li>\n</ul>\n</li>\n<li>Recreational fishery\n<ul>\n<li>Bocaccio is an important recreational fish in state waters.</li>\n<li>In 2019, recreational anglers landed 330,000 pounds of bocaccio, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:1186819784242::NO:::\">recreational fishing landings database</a>.&nbsp;&nbsp;</li>\n<li>State agencies encourage anglers to avoid catching rockfish intentionally, to deep-water release all released rockfish, and to relocate if they unintentionally catch rockfish.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Low in saturated fat and very high in selenium, phosphorus, and potassium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Bocaccio can grow up to three feet long and weigh up to 21 pounds.</li>\n<li>They are identifiable based on their long jaw, which extends to or past the eye socket.</li>\n<li>Young bocaccio are light bronze with small brown spots along their sides. As they grow older, they lose their spots and darken.</li>\n<li>Adult bocaccio have backs that are olive, burnt-orange or brown as adults. They have pink and red stomachs.</li>\n</ul>\n",
