    "Animal Health": "<ul>\n<li>Management &amp; Remedies:\n<ul>\n<li>Fish diseases occur naturally in the wild, but their effects go unnoticed because dead fish quickly become prey.</li>\n<li>Vaccines, probiotics, limiting farming density, high-quality diets, and controlled use of antibiotics prevent&nbsp;bacterial diseases in fish.</li>\n<li>Management of viral infections occurs through thorough monitoring, healthy culture conditions, low stress environments, and good nutrition and genetics.</li>\n<li>Parasites are controlled on farms using therapeutants, fallowing farm sites, and pest management such as the use of cleaner wrasses.</li>\n<li>Most states have comprehensive aquatic animal health regulations, such as routine health exams by veterinarians.</li>\n</ul>\n</li>\n<li>Antibiotic Use:\n<ul>\n<li>In the United States, antibiotics may only be used to treat bacterial infections in marine fish under direction of a veterinarian on a case-by-case basis.</li>\n<li>Antibiotics are considered a method of last resort and cannot be preemptively fed to fish.</li>\n<li>Special permits obtained from the Food &amp; Drug Administration may be required.</li>\n<li>Vaccines have been effective in reducing, and in some cases eliminating, the need for antibiotics.</li>\n</ul>\n</li>\n</ul>\n",
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Females can grow more than 3 feet in length.</li>\n<li>Females are able to reproduce at 6  years old and more than 2 feet in length.</li>\n<li>Males are able to reproduce at age 5 and 1.9 feet in length.</li>\n<li>Female sablefish usually produce between 60,000 and 200,000 eggs.</li>\n<li>Sablefish can live to be more than 90 years old.</li>\n</ul>\n",
    "Bycatch": null,
    "Calories": "195",
