    "Taste": "<p>Sablefish have high oil content, making them exceptionally flavorful. They are often called butterfish because of their melt-in-your-mouth, oil-rich meat.</p>\n",
    "Texture": "<p>Soft, velvety texture. Their meat has large, white flakes.</p>\n",
