    "Location": "<ul>\n<li>Atlantic halibut are found from Labrador and Greenland to Iceland, and from the Barents Sea south to the Bay of Biscay and Virginia.</li>\n<li>In U.S. waters, halibut is most common in the Gulf of Maine.</li>\n</ul>\n",
