    "Population": "Significantly below target population levels.",
    "Population Status": "<ul>\n<li>The Atlantic halibut stock is at a very low level. Fishing is still allowed, but at reduced levels.</li>\n<li>According to the 2012 stock assessment, the&nbsp;Atlantic halibut stock is overfished, but is not subject to overfishing. The estimated biomass is only 3 percent of its target level. It will remain in a rebuilding plan until it is rebuilt.&nbsp;Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10056\">Stock SMART</a>.</li>\n</ul>\n",
    "Scientific Name": "Hippoglossus hippoglossus",
    "Species Aliases": "<a href=\"/species-aliases/atlantic-halibut\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Atlantic halibut</a>, <a href=\"/species-aliases/halibut\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Halibut</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/atlantic-halibut-illustration.png",
    "alt": "Illustration of Atlantic Halibut.",
    "title": "Illustration of Atlantic Halibut."
    },
    "Species Name": "Atlantic Halibut",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Atlantic halibut is the largest species of flatfish in the world.</li>\n<li>Atlantic halibut can reach up to 15 feet in length</li>\n<li>The largest Atlantic halibut recorded was taken off Cape Ann, Massachusetts, and weighed 615 pounds (eviscerated with the head still attached). It likely weighed 700 pounds when it was alive.</li>\n<li>It is a long-lived, late-maturing species that can live up to 50 years.</li>\n<li>Average age at maturity is about 10 years.</li>\n<li>Full grown females average 100 to 150 pounds, while males tend to be smaller.</li>\n<li>Females are batch spawners, producing several batches of eggs each year.</li>\n<li>In Canadian waters, Atlantic halibut spawn from late winter to early spring, while spawning can last through September for fish from Georges Bank to the Grand Banks.</li>\n<li>Halibut food preferences vary by fish size: smaller fish (up to 12 inches in length) feed almost exclusively on invertebrates. The proportion of fish in the diet increases as the fish grow in size until they feed almost exclusively on fishes when they reach approximately 31 inches.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "110",
    "Carbohydrate": "0",
    "Cholesterol": "32 mg",
    "Color": "<p>Uncooked, white and almost translucent. It should not look dull, yellowish or dried out. When cooked, the meat is white.</p>\n",
