    "Fishing Rate": "Not subject to overfishing.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of Atlantic halibut totaled more than 95,000 pounds and were valued at approximately $490,000, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:2110496234333::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>Regulations include a minimum fish size and a limit of one fish per vessel per trip in federal waters.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Halibut are commonly harvested using trawl nets, bottom longlines, and rod and reel.</li>\n<li>Trawl gear used to harvest halibut can impact habitat, depending on where they are used. Trawl gear can incidentally catch other fish and marine mammals.</li>\n<li>Longlines, and rod and reel used to harvest halibut have little to no impact on habitat.</li>\n<li>Regulations close key areas to fishing year-round or seasonally to protect habitat and spawning aggregations.</li>\n<li>Restrictions on the size of gear that makes contact with the bottom in certain areas also help reduce habitat impacts.</li>\n<li>Fishermen follow a number of strict regulations and use modified fishing gear to reduce bycatch of other species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Halibut are sometimes targeted by recreational anglers, but are most often encountered when targeting other groundfish species like cod and haddock.</li>\n<li><a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/recfishing/regs/index.html\">Regulations</a> include a minimum fish size and a limit of one fish per vessel per trip in federal waters.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Halibut is low in saturated fat and sodium, and is a very good source of protein, niacin, phosphorus, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Atlantic halibut can be distinguished from other right-eyed flounders by their large size, concave caudal fin, large, gaping mouth, and arched lateral line.</li>\n<li>One of the largest fish found in the Gulf of Maine.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.81 g",
    "Quote": "Although populations are well below target levels, U.S. wild-caught Atlantic halibut is still a smart seafood choice because it is sustainably managed under a rebuilding plan that allows limited harvest by U.S. fishermen.",
    "Quote Background Color": "#7a622d",
    "Research": "<ul>\n<li>Scientists at NOAAs <a href=\"https://www.nefsc.noaa.gov/\">Northeast Fisheries Science Center</a> conduct research bottom trawl surveys throughout the Northeast continental shelf every year during the fall and spring. These surveys collect data on the environment as well as biological samples from fish caught during research trawling. The data from these and other sources are used by scientists in stock assessments to estimate population size and fishing pressure.</li>\n</ul>\n",
