    "NOAA Fisheries Region": "West Coast",
    "Population": "Above target population level on the Pacific Coast.",
    "Population Status": "<ul>\n<li>\n<p>There are three stocks of shortspine thornyhead: Pacific coast, one stock contained in a stock complex in the Gulf of Alaska, and one stock contained in a stock complex in the Bering Sea/Aleutian Islands.&nbsp; According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Pacific Coast stock is not overfished (2013 stock assessment) and not subject to overfishing based on the 2018 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Shortspine%20thornyhead%20-%20Pacific%20Coast&amp;stockid=10458\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The population status of the Gulf of Alaska Thornyhead Rockfish Complex, which includes shortspine thornyhead, is unknown.&nbsp; The complex has not been assessed, but according to 2020 catch data, the complex is not subject to overfishing.</p>\n</li>\n<li>\n<p>The population status of Bering Sea/Aleutian Islands Other Rockfish Complex, which includes shortspine thornyhead, has been assessed (2020 stock assessment), but there is not enough information to determine the population size so the population status is unknown.&nbsp;This complex is not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Bering%20Sea%20/%20Aleutian%20Islands%20Other%20Rockfish%20Complex&amp;stockid=10974\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Sebastolobus alascanus",
    "Species Aliases": "<a href=\"/species-aliases/thornyhead\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Thornyhead</a>, <a href=\"/species-aliases/idiot-fish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Idiot fish</a>, <a href=\"/species-aliases/idiot-cod\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Idiot cod</a>, <a href=\"/species-aliases/rockfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Rockfish</a>",
