    "Species Name": "Shortspine Thornyhead",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Shortspine thornyheads grow slowly and may reach lengths up to 80 centimeters.</li>\n<li>They may live 80 to 100 years.</li>\n<li>Females grow larger than males, and reach maturity around 18 centimeters, or 8 to 10 years of age.</li>\n<li>Shortspine thornyhead spawn pelagic, gelatinous masses.</li>\n<li>They spawn between December and May along the West Coast and between April and May in the Gulf of Alaska.</li>\n<li>Shortspine feed on shrimp, crabs, zooplankton, amphipods, and other benthic invertebrates.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch of overfished and protected species.",
