    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of shortspine thornyhead totaled 930,000 pounds and were valued at more than $1.5 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:13791159990321::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>The trawl, longline, and pot gear used to harvest shortspine thornyhead have minimal or temporary effects on habitat. Area closures and gear restrictions protect sensitive rocky, cold-water coral and sponge habitats from bottom trawl gear.</li>\n<li>In the Gulf of Alaska, they are mainly caught with trawl and longline gear as bycatch in the rockfish and sablefish fisheries. On the Pacific Coast, they are targeted in the rockfish fishery with bottom trawl, longline, and pot gear.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Thornyheads are occasionally caught in recreational fisheries but are not often targeted, due to the complications of deep-sea fishing.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Rockfish are high in selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Shortspine thornyhead have elongated bodies and large heads.</li>\n<li>They have a strong spiny ridge and 16 dorsal spines.</li>\n<li>They are bright red or orange red in color, and often have white patches on their sides, backs, cheeks and spiny dorsal fin tips when seen underwater.</li>\n<li>Their fins can have black patches and there may be dark spots or speckles on their sides.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.36 g",
    "Quote": "U.S. wild-caught shortspine thornyhead is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#ab3539",
    "Research": "<p><a href=\"http://agris.fao.org/agris-search/search.do?recordID=US201700202112\" rel=\"external\">Tagging study of shortspine thornyhead in Alaska</a> confirms that the management range is appropriate.</p>\n",
