    "Species Name": "Canary Rockfish",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Canary rockfish are large rockfish that reach up to 2.5 feet in length and 10 pounds.</li>\n<li>They can live up to 75 years.</li>\n<li>Approximately 50 percent of adult canary rockfish are mature at 14 inches total length (about 5 to 6 years of age).</li>\n<li>Rockfishes are unusual among the bony fishes in that fertilization and embryo development is internal and female rockfish give birth to live larval young.</li>\n<li>Female can have 260,000 to 1.9 million eggs, considerably more than many other rockfish species.</li>\n<li>Larval rockfish feed on diatoms, dinoflagellates, tintinnids, and cladocerans.</li>\n<li>Juveniles consume copepods and euphausiids of all life stages.</li>\n<li>Adults eat demersal invertebrates and small fishes, including other species of rockfish.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch of overfished and protected species.",
