    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial landings of canary rockfish totaled more than 1 million pounds and were valued at $522,000, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:1044144346001::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Primarily harvested with midwater trawl gear, which has minimal impacts on ocean bottom habitats. To a lesser extent, harvested with bottom trawl gear.</li>\n<li>Midwater and bottom trawls may sometimes catch other species of fish, including overfished and protected species.\n<ul>\n<li>Gear restrictions, closed areas, and catch share programs limit when, where, and how much trawl fishermen can harvest to reduce bycatch of other species.</li>\n<li><a href=\"http://www.westcoast.fisheries.noaa.gov/fisheries/management/groundfish_closures/rockfish_areas.html\">Rockfish conservation areas</a> eliminate fishing in areas on the West Coast where overfished rockfish species co-occur with target stocks, like canary rockfish. These closed areas help prevent bycatch of overfished rockfish.</li>\n</ul>\n</li>\n<li>Canary rockfish are often caught incidentally in the Pacific whiting fishery.\n<ul>\n<li>Managers are working to reduce incidental catch through the use of annual catch limits and catch shares.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational anglers fish for canary rockfish, but they comprise only a minor part of recreational groundfish fisheries.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Rockfish are high in selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Adult canary rockfish are bright yellow/orange mottling above and gray underneath, three orange stripes across the head, and orange fins.</li>\n<li>Animals less than 14 inches long have dark markings on the posterior part of the spiny dorsal fin.</li>\n<li>Adults have gray along the lateral line.</li>\n<li>The genus name Sebastes is Greek for \"magnificent\" and the species name pinniger is Latin for \"large-finned.\"</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.36 g",
    "Quote": "U.S. wild-caught canary rockfish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#d99d57",
    "Research": "<ul>\n<li>Between 2014-2016, charter boat captains and volunteer anglers collected 100 rare rockfish in the Puget Sound region. Three species had been listed as threatened or endangered, and NOAA looked to local ecological knowledge for the scientific sampling required for recovery. Genetic analyses from these fishing trips provided new information, contributed to the <a href=\"https://www.fisheries.noaa.gov/action/removal-puget-sound-georgia-basin-canary-rockfish-dps-federal-list-threatened-and-endangered\">delisting of canary rockfish</a>, and expanded protective boundaries for yelloweye rockfish.</li>\n</ul>\n",
