    "Location": "<ul>\n<li>Black grouper are found in the western Atlantic from Massachusetts to Brazil.</li>\n<li>They are particularly associated with the southern Gulf of Mexico, Florida Keys, Cuba, the Bahamas, and throughout the Caribbean.</li>\n<li>The US stock primarily occurs in the Florida Keys.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Southeast",
    "Population": "Above target population level in the Gulf of Mexico/South Atlantic.",
    "Population Status": "<ul>\n<li>There are two stocks of black grouper: Gulf of Mexico/South Atlantic and Caribbean. According to the most recent stock assessments:\n<ul>\n<li>The Gulf of Mexico/South Atlantic stock is not overfished (2010 stock assessment), and is not subject to overfishing based on 2019 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=14148\">Stock SMART</a>.</li>\n<li>In the Caribbean, black grouper is part of the Caribbean groupers complex and is not assessed so the overfished status is unknown. The groupers complex is not subject to overfishing based on 2019 catch data.</li>\n</ul>\n</li>\n</ul>\n",
