    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial landings of black grouper totaled approximately 98,000 pounds and were valued at more than $496,000,&nbsp;according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:5157410082980::NO:::\">commercial fishing landings database</a>.&nbsp;There are limited landings in the U.S. Caribbean.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen mainly use hook-and-line gear, including longlines and handlines, to harvest black grouper.</li>\n<li>Trawl gear, fish traps, and bottom longlines are prohibited in some areas to reduce bycatch. Several areas are closed to all fishing to protect snappers and groupers, including black grouper.</li>\n<li>Sea turtles and other reef fishes, such as snappers and groupers, can be incidentally caught while fishing for black grouper.</li>\n<li>In certain areas, fishermen are required to use circle hooks to improve the chance of survival of any unintentionally caught fish and to reduce turtle hookings.</li>\n<li>Commercial and charterboat/headboat reef fish fishermen must use appropriate release gear and follow handling protocols to increase the chance of survival for any incidentally caught <a href=\"http://sero.nmfs.noaa.gov/sustainable_fisheries/gulf_sa/turtle_sawfish_release/index.html\">sea turtles</a>.</li>\n<li>Fishermen are encouraged to use <a href=\"https://www.flseagrant.org/fisheries/venting/\" rel=\"external\">venting tools</a> or <a href=\"https://www.flseagrant.org/fisheries/catch-and-release-fishing/descending-and-venting/\" rel=\"external\">fish descenders</a> when fish are caught showing signs of barotrauma. Barotrauma occurs when reef fish are quickly brought to the surface by hook-and-line and the gas in their swim bladders expands. <a href=\"https://www.flseagrant.org/fisheries/venting/\" rel=\"external\">Venting tools</a> help deflate the expanded abdominal cavity, potentially reduce injury to the fish, and make it easier to return to deep water.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Black grouper is a popular fish among recreational fishermen in the South Atlantic, Gulf of Mexico, and Caribbean.</li>\n<li>\n<p>In 2019, recreational anglers landed more than 69,000 pounds of black grouper, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:5157410082980::NO:::\">recreational fishing landings database</a>.&nbsp;</p>\n</li>\n<li>In the South Atlantic:\n<ul>\n<li>Annual catch limits and accountability measures.</li>\n<li>Bag and size limits.</li>\n<li>The fishery is closed during the spawning season (from January through April).</li>\n</ul>\n</li>\n<li>In the Gulf of Mexico:\n<ul>\n<li>Annual catch limits and accountability measures.</li>\n<li>Bag and size limits.</li>\n<li>The fishery is closed during the spawning season (from February 1 through March 31) in deep water (greater than 120 feet) to protect spawning aggregations.</li>\n</ul>\n</li>\n<li>In the U.S. Caribbean:\n<ul>\n<li>Annual catch limits</li>\n<li>Seasonal closure for black, red, tiger, yellowfin, and yellowedge groupers from February 1 through April 30.</li>\n<li>Bag and vessel limits.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Grouper is low in saturated fat. It is a good source of vitamins B6 and B12, phosphorus, potassium, protein and selenium. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Black grouper have an olive or gray body, with black blotches and brassy spots.</li>\n<li>Their cheeks are gently rounded.</li>\n</ul>\n",
    "Production": null,
    "Protein": "19.38 g",
    "Quote": "U.S. wild-caught black grouper is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#8c7f63",
    "Research": "<ul>\n<li><a href=\"https://mote.org/news/article/mote-scientist-identifies-first-u.s.-black-grouper-spawning-site\" rel=\"external\">First U.S. black grouper spawning site identified</a>.</li>\n<li><a href=\"https://www.st.nmfs.noaa.gov/spo/FishBull/1141/locascio.pdf\">A passive acoustic survey of fish sound production at Rileys Hump within Tortugas South Ecological Reserve: implications regarding spawning and habitat use</a>.</li>\n</ul>\n",
