    "Sodium": "53 mg",
    "Source": "<p>U.S. wild-caught from South Carolina to Florida, and in the Caribbean.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Grouper has a mild but distinct flavor, somewhere between bass and halibut. Gag is sometimes mistakenly referred to as black grouper in the market. The two species are very similar in taste and texture.</p>\n",
    "Texture": "<p>Firm.</p>\n",
