    "Location": "<p>Striped marlin live throughout tropical and sub-tropical waters of the Pacific and Indian Oceans.</p>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Pacific Islands",
    "Population": "Above target population level in the eastern Pacific. Significantly below target population level in the western and central Pacific due to international fishing. Measures to rebuild the stock are in place in U.S. and international waters.",
    "Population Status": "<ul>\n<li>\n<p>There are two stocks of striped marlin: The Eastern Pacific and Western and Central North Pacific stocks. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Eastern Pacific stock is not overfished and not subject to overfishing (2010 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=11073\">Stock SMART</a>. The Inter-American Tropical Tuna Commission (IATTC) conducted this assessment.</p>\n</li>\n<li>\n<p>The Western and Central North Pacific stock is overfished and subject to overfishing (2019 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=11692\">Stock SMART</a>. Because the overfished status of this stock is due to international fishing pressure, it is not being managed under a rebuilding plan that is typically required for solely domestic fish stocks.</p>\n</li>\n</ul>\n</li>\n<li>The Western and Central Pacific Fisheries Commission (WCPFC), of which the United States is a member, has agreed to international conservation and management measures for the Western and Central North Pacific stock. While current measures may not be sufficient in ending overfishing or rebuilding the stock, NOAA Fisheries continues to work with the Western Pacific and Pacific Fishery Management Councils and the U.S. Department of State to recommend or propose effective management measures to be adopted by the WCPFC.</li>\n<li>Population assessments for the Western and Central North Pacific striped marlin stock are conducted by the <a href=\"http://isc.fra.go.jp/working_groups/billfish.html\" rel=\"external\">Billfish Working Group</a> of the <a href=\"http://isc.fra.go.jp/\" rel=\"external\">International Scientific Committee for Tuna and Tuna-Like Species in the North Pacific Ocean (ISC)</a>. NOAA Fisheries scientists participate in the ISC assessments and contribute relevant U.S. fishery data.</li>\n</ul>\n",
    "Scientific Name": "Kajikia audax",
    "Species Aliases": "<a href=\"/species-aliases/nairagi\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Nairagi</a>, <a href=\"/species-aliases/%E2%80%98u\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Au</a>, <a href=\"/species-aliases/makijki\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Makijki</a>, <a href=\"/species-aliases/barred-marlin\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Barred marlin</a>, <a href=\"/species-aliases/spikefish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Spikefish</a>, <a href=\"/species-aliases/striped-swordfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Striped swordfish</a>",
