    "Species Name": "Striped Marlin",
    "Animal Health": null,
    "Availability": "<p>Striped marlin is caught year-round, but the majority is caught between November and June.</p>\n",
    "Biology": "<ul>\n<li>Striped marlin are smaller than other marlin species, but can reach a length of 12 feet and weigh more than 450 pounds.</li>\n<li>Spawning occurs in the central Pacific and off central Mexico.</li>\n<li>Juvenile fish move east toward the coast of Mexico, where they are found in high abundance around the tip of the Baja Peninsula.</li>\n<li>Striped marlin are opportunistic feeders of fish including mackerel, sardine, and anchovy. They will also eat invertebrates, including squid.</li>\n<li>Off the coast of southern California, they often feed at the surface on small coastal fish and squid.</li>\n<li>Large pelagic sharks or toothed whales prey on adult marlin.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch. Striped marlin are caught incidentally in some fisheries that target tunas and swordfish.",
    "Calories": "120",
    "Carbohydrate": "0",
    "Cholesterol": "40 mg",
    "Color": "<p>Varies from light pink to orange-red. Orange-red meat is desired by the sashimi market.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
