    "Fishing Rate": "At recommended level in the eastern Pacific. Reduced to end overfishing in the western and central North Pacific.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial landings of Pacific striped marlin totaled more than 1 million pounds and were valued at more than $1.6 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:12064433848579::NO:::\">commercial fishing landings database</a>. These figures may not match other agency sources of data due to confidential information.</li>\n<li>The <a href=\"https://www.govtrack.us/congress/bills/112/hr2706/text\" rel=\"external\">Billfish Conservation Act</a>, along with existing billfish regulations, prohibits the sale and commercial possession of billfish and billfish products. However, those that are caught in Hawaii and the Pacific Insular Areas (which includes American Samoa, Guam, and the Northern Mariana Islands) are exempt and can be sold.</li>\n<li>Prohibitions on the sale of striped marlin on the U.S. West Coast provide a strong disincentive for commercial fishermen to catch striped marlin.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Marlin are primarily caught incidentally in pelagic longline commercial fisheries for tuna and swordfish.</li>\n<li>U.S. pelagic longline fishermen, who target tuna and swordfish and may incidentally catch striped marlin, are required to use specific tools and handling techniques to mitigate bycatch of sea turtles, seabirds, and marine mammals. The Hawaii longline regulation summary can be found <a href=\"https://www.fisheries.noaa.gov/resource/document/hawaii-pelagic-longline-regulation-summary\">here</a>.</li>\n<li>If too many interactions with sea turtles occur, NOAA Fisheries closes the fishery for the remainder of the year.</li>\n<li>To prevent seabird interactions, deep and shallow-set longline vessels must either deploy longline gear from the side of the vessel (as opposed to the back of the vessel) or use blue-dyed bait (which is less visible to seabirds).</li>\n<li>In the deep-set longline sector, vessels must use circle hooks and certain gear setups to allow whales to bend the hooks and escape an incidental hooking.</li>\n<li>Time-area closures also limit and prevent interactions between pelagic longline gear and non-target species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Striped marlin are a favorite target for recreational fishermen and one of the most sought-after billfish in the region because the fish are acrobatic and tend to put up an incredible fight when hooked.</li>\n<li>Hawaii hosts one of the largest billfish tournaments in the United States. Most fish caught in recreational tournaments are tagged and released. Tournament proceeds, tackle, and trip-related expenditures contribute significantly to local economies.</li>\n<li>There is little bycatch associated with the recreational fishery.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Striped marlin is an excellent source of extra-lean protein. It is low in saturated fat and sodium. It is rich in niacin, vitamins B6 and B12, phosphorus, selenium, and omega-3 fatty acids.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Striped marlin are large, oceanic fish with long, round bills, small teeth, and a tall dorsal fin.</li>\n<li>Their bodies are dark blue-black on the top and fade to a silvery white on the bottom.</li>\n<li>They have rows of blue colored stripes made up of smaller round dots or narrow bands.</li>\n</ul>\n",
    "Production": null,
    "Protein": "24 g",
    "Quote": "U.S. wild-caught striped marlin is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#204ab7",
    "Research": "<ul>\n<li>Researchers at Stanford University and the International Game Fish Association are investigating the movements of marlin through the <a href=\"http://igmr.igfa.org/Conserve/IGMR.aspx\" rel=\"external\">Great Marlin Race</a>.</li>\n<li>The <a href=\"http://www.gtopp.org/\" rel=\"external\">Global Tagging of Pelagic Predators</a> program is an international collaborative effort that tracks the location of predators (such as marlin and sharks) in the ocean to better understand how to protect these pelagic animals.</li>\n<li><a href=\"https://www.umass.edu/archivenewsoffice/article/pacific-wide-study-reveals-striped-marlins\" rel=\"external\">University of Massachusetts Amhersts Large Pelagics Research Center</a> collaborated with the University of Southern California and the Conservation Science Institute of Waikoloa, Hawaii, to investigate the vertical habitat of striped marlin.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "1",
    "Selenium": "110 mcg",
    "Serving Weight": "113 g",
    "Servings": "1",
    "Sodium": "45 mg",
    "Source": "<p>U.S. wild-caught from waters around Hawaii, other U.S. Pacific Islands, and the high seas.</p>\n",
    "Sugars, Total": "0",
    "Taste": "<p>Distinctive flavor similar to but more pronounced than swordfish. It is considered the finest eating of all marlin species.</p>\n",
    "Texture": "<p>Firm.</p>\n",
