    "Fishing Rate": "In the Mid-Atlantic, South Atlantic, and Gulf of Mexico, at recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In response to the&nbsp;stock assessment in 2013, more restrictive regulations were implemented in the South Atlantics Snapper-Grouper FMP and commercial landings declined by half.</li>\n<li>In 2017, management measures were developed by the Mid-Atlantic Council.</li>\n<li>In 2019, commercial landings of blueline tilefish totaled 261,000 pounds and were valued at $635,000, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:31009455552533::NO:::\">commercial fishing landings database</a>. These figures may not match other agency sources of data due to confidential information.</li>\n<li>2019 landings occurred in Connecticut, Rhode Island, New York, New Jersey, Virginia, North Carolina, South Carolina, Florida, Louisiana, and Texas. The highest landings were in North Carolina and Florida.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen mainly use longline and vertical hook-and-line gear to harvest blueline tilefish.</li>\n<li>Sea turtles, marine mammals, smalltooth sawfish, and reef fishes can be incidentally caught while fishing for blueline tilefish.</li>\n<li>Commercial reef fish fishermen in the South Atlantic and Gulf of Mexico must use release gear and follow safe-handling protocols to increase the chance of survival for incidentally caught protected species, like <a href=\"http://sero.nmfs.noaa.gov/sustainable_fisheries/gulf_sa/turtle_sawfish_release/index.html\">sea turtles and smalltooth sawfish</a>.</li>\n<li>To further reduce bycatch, the use of trawl gear, fish traps, and bottom longlines are prohibited in certain areas of the South Atlantic, and several areas are also closed to all fishing to protect snapper and grouper species.</li>\n<li>To protect reef fish, sea turtles, and bottom habitat in the Gulf of Mexico, there are restrictions on the areas and depths where longlines can be used.</li>\n<li>In the Gulf of Mexico and certain areas of the South Atlantic, fishermen are required to use <a href=\"https://teacheratsea.files.wordpress.com/2013/05/screen-shot-2013-04-18-at-7-15-32-am.jpg\">circle hooks</a> to reduce injury to any unintentional catch.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>In 2019, recreational anglers caught 159,000 pounds of blueline tilefish.</li>\n<li>Charterboat/headboat reef fish fishermen in the South Atlantic and Gulf of Mexico must use de-hooking devices and follow safe-handling protocols to increase the chance of survival for incidentally caught protected species, like <a href=\"http://sero.nmfs.noaa.gov/sustainable_fisheries/gulf_sa/turtle_sawfish_release/index.html\">sea turtles and smalltooth sawfish</a>.</li>\n<li>In the Mid-Atlantic, for-hire vessels are required to have a permit and report catch, and recreational anglers&nbsp;can keep a limited number of blueline tilefish per fishing trip.</li>\n<li>In the South Atlantic, blueline tilefish are included in the daily aggregate grouper bag limit for recreational fishermen.</li>\n<li>In the Gulf of Mexico, there is a limit on the number of reef fish, including blueline tilefish, that recreational fishermen can keep per day.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Blueline tilefish is low in sodium. It is a good source of niacin, phosphorus, protein, vitamin B12, and selenium. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Blueline tilefish get their name from a narrow gold stripe underlined in blue that runs from their snout to the tip of their eye.</li>\n<li>They have a long snout and are a dull olive-gray on the top of their body and white on the bottom.</li>\n<li>They have long, continuous dorsal and anal fins that are more than half the length of their body.</li>\n<li>Unlike <a href=\"https://www.fishwatch.gov/profiles/tilefish\">golden tilefish</a>, they do not have a large adipose flap (crest) on their head.</li>\n</ul>\n",
    "Production": null,
    "Protein": "17.5 g",
    "Quote": "U.S. wild-caught blueline tilefish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#79b5dc",
    "Research": "<ul>\n<li>Blueline tilefish are monitored and assessed by scientists from NOAAs <a href=\"https://www.sefsc.noaa.gov/\">Southeast Fisheries Science Center</a> through the <a href=\"http://sedarweb.org/\">Southeast Data, Assessment, and Review (SEDAR) process</a>.</li>\n</ul>\n",
