    "Servings": "1",
    "Sodium": "53 mg",
    "Source": "<p>U.S. wild-caught from Rhode Island to the west coast of Florida.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Blueline tilefish has a mild sweet flavor, similar to lobster or crab. It is similar in taste to <a href=\"https://www.fishwatch.gov/profiles/tilefish\">golden tilefish</a>. Almost all tilefish is sold fresh.</p>\n",
    "Texture": "<p>Firm but tender.</p>\n",
