    "Fishery Management": "<ul>\n<li><a href=\"https://www.greateratlantic.fisheries.noaa.gov/\">NOAA Fisheries</a> and the <a href=\"https://www.nefmc.org/index.html\">New England Fishery Management Council</a> manage the red hake fishery.</li>\n<li>Managed under the <a href=\"https://www.nefmc.org/management-plans/small-mesh-multispecies\">Northeast Multispecies Fishery Management Plan for Small Mesh Multispecies</a>:\n<ul>\n<li>Permitting requirements.</li>\n<li>A cap on the amount of groundfish bycatch that fishing vessels can take.</li>\n<li>Seasonal and spatial limitations on fishing with small mesh nets throughout the Gulf of Maine and Georges Bank.</li>\n<li>Trip limits by mesh size and area.</li>\n<li>In-season trip limit reductions and catch monitoring help prevent excessive catches.</li>\n</ul>\n</li>\n<li>The small-mesh multispecies fishery is managed primarily through a series of <a href=\"https://www.greateratlantic.fisheries.noaa.gov/regs/infodocs/small_mesh_exemption.pdf\">exemptions</a> from the Northeast Multispecies Fishery Management Plan. Exempted fisheries allow vessels to fish for specific species, such as red hake, in designated areas using mesh sizes smaller than the minimum mesh size allowed under the Regulated Mesh Area regulations.</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>The northern stock of red hake inhabits the waters of the Gulf of Maine and Northern Georges Bank, and the southern stock inhabits the waters of Southern Georges Bank and the Mid-Atlantic Bight.</li>\n<li>As nocturnal, semi-pelagic predators, red hake move up in the water column to feed at night, primarily between dusk and midnight. They return to rest on the ocean floor during the day, preferring sandy, muddy, or pebbly ocean bottoms.</li>\n<li>Red hake migrate in response to seasonal changes in water temperatures, moving toward shallow, warmer waters in the spring.</li>\n<li>They spawn in these shallow waters during late spring and early summer and then return to deeper waters in the autumn.</li>\n<li>Juveniles seek shelter from predators in scallop beds and commonly hide underneath <a href=\"https://www.fishwatch.gov/profiles/atlantic-sea-scallop\">sea scallops</a>.</li>\n<li>Older, larger red hake prefer deeper waters.</li>\n<li>During the summer, portions of both stocks can be found on Georges Bank.</li>\n<li>During the winter, fish in the northern stock move to deep basins in the Gulf of Maine, while fish in the southern stock move to outer continental shelf and slope waters.</li>\n<li>Red hake are widely distributed, and have been observed at temperature ranges of 2 to 17 C (36 to 63 F) and depth ranges of 11 to 500 meters (36 to 1,640 feet). However, they are most commonly found in temperatures between 7 and 10 C (45 to 50 F).</li>\n</ul>\n",
    "Habitat Impacts": "Fishing gears used to harvest red hake have minimal impacts on habitat.",
