    "Location": "<ul>\n<li>Red hake are found in the Northwest Atlantic Ocean and range primarily from Newfoundland to North Carolina.</li>\n<li>They are most abundant from the western Gulf of Maine through Southern New England waters.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "The northern stock is above its target population level. The southern stock is significantly below its target level and fishing rate promotes population growth. A rebuilding plan is being developed for the southern stock.",
    "Population Status": "<ul>\n<li>\n<p>There are two stocks of red hake: Gulf of Maine/Northern Georges Bank and Southern Georges Bank/Mid Atlantic. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Gulf of Maine/Northern Georges Bank (northern) stock of red hake is not overfished and is not subject to overfishing (2017 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10516\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Southern Georges Bank/Mid Atlantic (southern) stock of red hake is overfished and subject to overfishing (2017 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10517\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Urophycis chuss",
    "Species Aliases": "<a href=\"/species-aliases/ling\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Ling</a>, <a href=\"/species-aliases/squirrel-hake\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Squirrel hake</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/red-hake-illustration-formatted.png",
    "alt": "Red Hake",
    "title": "Red Hake"
    },
    "Species Name": "Red Hake",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Red hake are a member of the cod family.</li>\n<li>They do not grow as large as white hake and normally reach a maximum size of 50 centimeters (20 inches) and 2 kilograms (4.4 pounds).</li>\n<li>Females are generally larger than males of the same age, and reach a maximum length of 63 centimeters (25 inches) and a weight of 3.6 kilograms (7.9 pounds).</li>\n<li>Although they generally do not live longer than 8 years, red hake have been recorded up to 14 years old.</li>\n<li>Red hake feed primarily on crustaceans such as decapods and rock crabs as well as fish such as <a href=\"https://www.fishwatch.gov/profiles/haddock\">haddock</a>, <a href=\"https://www.fishwatch.gov/profiles/silver-hake\">silver hake</a>, sea robins, sand lance, <a href=\"https://www.fishwatch.gov/profiles/atlantic-mackerel\">mackerel</a>, and small red hake.</li>\n<li>Primary predators of red hake include <a href=\"https://www.fishwatch.gov/profiles/atlantic-spiny-dogfish\">spiny dogfish</a>, <a href=\"https://www.fishwatch.gov/profiles/atlantic-cod\">cod</a>, <a href=\"https://www.fishwatch.gov/profiles/monkfish\">goosefish</a>, and <a href=\"https://www.fishwatch.gov/profiles/silver-hake\">silver hake</a>.</li>\n</ul>\n",
    "Bycatch": "Raised-footrope trawls are required in designated areas when targeting hake to minimize bycatch of other species.",
    "Calories": "90",
    "Carbohydrate": "0 g",
    "Cholesterol": "67 mg",
    "Color": "<p>Raw and cooked hake is white to off-white.</p>\n",
