    "Fishing Rate": "At recommended levels for the northern red hake stock. At levels to reduce overfishing for the southern red hake stock.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>The 2020 landings of red hake totaled 690,000 pounds, and were valued at $315,000, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:12965743518037::NO:::\">commercial fishing landings database</a>. &nbsp;</li>\n<li>Red hake is part of the small-mesh multispecies management unit, along with <a href=\"https://www.fishwatch.gov/profiles/silver-hake\">silver hake</a> and offshore hake. This three-species complex is sometimes marketed together as whiting.</li>\n<li>Red hake are usually a bycatch species in the small-mesh fisheries that target whiting (<a href=\"https://www.fishwatch.gov/profiles/silver-hake\">silver hake</a>) and squid.</li>\n<li>Low value and limited domestic market for red hake result in high percentages of discard which account for about 60% of the annual catch.</li>\n<li>Red hake are frequently used as bait in the lobster and tuna fisheries. Some are also sold as fillets.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Fishermen using small-mesh trawl gear to catch red hake must comply with a number of specific requirements to reduce bycatch of larger groundfish species.</li>\n<li>In designated areas, fishermen are required to use raised-footrope trawls, which are designed to keep the net off the bottom to reduce the bycatch of flatfish and other species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational fishermen do not target red hake but sometimes catch them incidentally while fishing for other groundfish.</li>\n<li>There are currently no restrictions on possession, size, or gear-type for red hake caught recreationally in federal waters.</li>\n<li>There are no specific areas for the recreational fishery.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Red hake is a good source of selenium, vitamin B, magnesium, and protein.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Red hake vary in color depending on their environment. Most tend to be a reddish brown to olive-brown color with pale tan spots on their sides and shades of white on their undersides.</li>\n<li>They are a member of the cod family and have a barbel (whisker) on their chin.</li>\n<li>Their dorsal fin is triangular, but their second dorsal and anal fins are long, continuous, and do not attach to the tail fin, much like an eel.</li>\n<li>They have a small head, but a large mouth with many small teeth.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.31 g",
    "Quote": "U.S. wild-caught red hake is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
