    "Location": "<ul>\n<li>Atlantic sharpnose sharks are commonly found in the western Atlantic from New Brunswick, Canada, through the Gulf of Mexico, and are commonly caught in U.S. waters from Virginia to Texas.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic, Southeast",
    "Population": "Above target population levels.",
    "Population Status": "<ul>\n<li>There are two stocks of Atlantic sharpnose shark: Atlantic and Gulf of Mexico. &nbsp;According to the most recent stock assessments:\n<ul>\n<li>The Atlantic stock is not overfished and not subject to overfishing (2013). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Atlantic%20sharpnose%20shark%20-%20Atlantic&amp;stockid=14141\">Stock SMART</a>.</li>\n<li>The Gulf of Mexico stock is not overfished and not subject to overfishing (2013). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Atlantic%20sharpnose%20shark%20-%20Gulf%20of%20Mexico&amp;stockid=14142\">Stock SMART</a>.</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Rhizoprionodon terraenovae",
    "Species Aliases": "<a href=\"/species-aliases/newfoundland-shark\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Newfoundland shark</a>, <a href=\"/species-aliases/sharp-nosed-shark\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sharp-nosed shark</a>, <a href=\"/species-aliases/white-shark\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">White shark</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/Atlantic_Sharpnosed_Shark_NB_W_smaller_0.png",
    "alt": "Atlantic Sharpnose Shark",
    "title": "Atlantic Sharpnose Shark"
    },
    "Species Name": "Atlantic Sharpnose Shark",
    "Animal Health": null,
    "Availability": "<p>Fresh year-round.</p>\n",
    "Biology": "<ul>\n<li>Atlantic sharpnose sharks can grow to up to 32 inches in length.</li>\n<li>They grow and mature at different rates in the Atlantic and Gulf of Mexico.</li>\n<li>Females mature at around 2 years old in the Atlantic when they reach approximately 24 inches in length, and at around 1.3 years old in the Gulf of Mexico when they are approximately 25 inches in length.</li>\n<li>Atlantic sharpnose sharks have been observed to live up to 18 years.</li>\n<li>In both the Atlantic and Gulf of Mexico, Atlantic sharpnose sharks mate annually between mid-May and mid-July in inshore waters.</li>\n<li>After mating they migrate offshore to deeper waters.</li>\n<li>The mother feeds the pups through a placental sac and after a gestation period of 10 to 11 months the females return to nearshore areas to give birth in June.</li>\n<li>Litters average approximately four pups in both the Gulf of Mexico and Atlantic.</li>\n<li>Atlantic sharpnose sharks eat small fish, including menhaden, eels, silversides, wrasses, jacks, toadfish, and filefish. They also eat worms, shrimp, crabs, and mollusks.</li>\n<li>Large carnivorous fish, including large sharks, eat Atlantic sharpnose sharks.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
