    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of Atlantic sharpnose shark in the Atlantic and Gulf of Mexico totaled 294,000 pounds, and were valued at $244,000 according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:21826065749539::NO:::\">commercial fishing landings database</a>.</li>\n<li>To commercially harvest Atlantic sharks, vessel owners must obtain a valid Atlantic shark directed or incidental limited access permit or a smoothhound shark open access permit. &nbsp;More information regarding limited access permits can be found in the <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-highly-migratory-species-fishery-compliance-guides\">Atlantic HMS commercial compliance guide</a>.</li>\n<li>Atlantic sharpnose shark belong to the small coastal shark (SCS) complex. For SCS sharks, there is no retention limit per vessel per trip for commercial fishermen with a directed permit.</li>\n<li>They are harvested primarily off the east coast of Florida and North Carolina.</li>\n<li>Their meat is sold as seafood and also used by fishermen as bait for other larger species of shark.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen primarily use gillnet and bottom longline gear, which have minimal impacts on habitat due to how and where they are deployed.</li>\n<li>These gear types sometimes interact with sea turtles, marine mammals, and the endangered smalltooth sawfish.</li>\n<li>Several measures are in place to protect species that may be caught unintentionally in Atlantic sharpnose shark fisheries.</li>\n<li>Fishermen using bottom longline or gillnet gear must complete a protected species safe handling, release, and identification&nbsp;<a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-protected-species-safe-handling-release-and\">workshop</a>, and all bottom longline vessels must carry <a href=\"http://sero.nmfs.noaa.gov/sustainable_fisheries/gulf_sa/turtle_sawfish_release/index.html\">sea turtle handling and release gear</a> on board.</li>\n<li>To reduce bycatch of finfish, including Atlantic sharpnose shark, shrimp trawlers are required to use <a href=\"https://www.sefsc.noaa.gov/labs/mississippi/brd/\">bycatch reduction devices</a>, which are designed to retain shrimp but allow fish to exit the net.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>In 2020, approximately 196,000 pounds of Atlantic sharpnose shark were caught recreationally in the United States, according to the NOAA Fisheries&nbsp;<a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:21826065749539::NO:::\">recreational fishing landings database</a>.</li>\n<li>Recreational fishermen primarily use rod and reel gear, which have minimal impacts on habitat and bycatch.</li>\n<li>Recreational fishermen must have an Atlantic <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-highly-migratory-species-permits\">HMS permit</a> to harvest Atlantic sharpnose sharks in federal waters. As of January 1, 2018, all HMS recreational permit holders will need a shark endorsement to fish for, retain, possess, or land sharks.</li>\n<li>Fishermen fishing recreationally for sharks will be required to use circle hook in most places.&nbsp; For more information regarding these requirements, please refer to HMS regulations and the <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-highly-migratory-species-fishery-compliance-guides\">Amendment 5b compliance guide</a>.</li>\n<li>There is no minimum size for Atlantic sharpnose sharks.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Shark is a low-fat source of protein that is high in selenium and vitamins B6 and B12. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Atlantic sharpnose sharks are small for sharks and have a streamlined body.</li>\n<li>They get their name from their long, pointy snout.</li>\n<li>They are several different shades of gray and have a white underside.</li>\n<li>Adults have white spots on their sides and white along the edges of their pectoral fins.</li>\n<li>Young sharks have black on their dorsal (back) and caudal (tail) fin edges.</li>\n<li>The lower and upper jaws of an Atlantic sharpnose shark have 24 or 25 rows of triangular teeth.</li>\n</ul>\n",
    "Production": null,
    "Protein": "21 g",
    "Quote": "U.S. wild-caught Atlantic sharpnose shark is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#8d7170",
    "Research": "<ul>\n<li><a href=\"https://www.sefsc.noaa.gov/\">NOAA Southeast Fisheries Science Center</a> staff assesses the status of Atlantic sharpnose sharks through the <a href=\"http://sedarweb.org\">Southeast Data, Assessment, and Review</a> (SEDAR) process.</li>\n<li>Ongoing research priorities for Atlantic sharpnose sharks include enhancing life history information, identifying additional bycatch minimization strategies, and improving the identification of essential fish habitat. Scientists have made great strides collecting data on Atlantic sharpnose shark catch in other fisheries. For instance, records for sharks caught in shrimp trawls are now more detailed and include species-level information, which helps managers effectively understand and minimize bycatch of sharpnose sharks in the trawl fisheries.</li>\n</ul>\n",
