    "Species Name": "Pacific Sardine",
    "Animal Health": null,
    "Availability": "<p>Usually year-round, but currently unavailable because the fishery is closed</p>\n",
    "Biology": "<ul>\n<li>Pacific sardines are fast growing and can grow to more than 12 inches long.</li>\n<li>They can live up to 13 years, but usually not past 5.</li>\n<li>They reproduce at age 1 or 2, depending on conditions.</li>\n<li>Pacific sardines spawn multiple times per season.</li>\n<li>Females release eggs that are fertilized externally and hatch in about 3 days.</li>\n<li>Pacific sardines feed on plankton (tiny floating plants and animals).</li>\n<li>They are prey for many fish, marine mammals, and seabirds.</li>\n</ul>\n",
    "Bycatch": "Bycatch is low because gear used is selective.",
