    "Fishing Rate": "The fishery is closed because the population is estimated to be below a precautionary level set by managers.",
    "Harvest": "<ul>\n<li>The fishery was closed in 2015, and has not reopened.</li>\n<li>In 2014, more than 23,000 metric tons were harvested and were valued at $8.85 million.</li>\n<li>Round haul nets are used to catch Pacific sardines.</li>\n<li>Habitat and bycatch impacts are minimal because the gear is used at the surface around dense schools of fish, which usually contain only one species.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Sardines are a very good source of selenium and vitamin B12 and a good source of calcium, niacin, phosphorus, and omega-3 fatty acids.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Pacific sardines are small.</li>\n<li>They are blue-green on the back and have white flanks with one to three sets of dark spots along the middle.</li>\n</ul>\n",
    "Production": null,
    "Protein": "24.58 g",
    "Quote": "Commercial fishing for Pacific sardines is prohibited because the population is estimated to be below a precautionary level set by managers.",
    "Quote Background Color": "#8ea38b",
    "Research": "<ul>\n<li>Scientists from <a href=\"https://swfsc.noaa.gov/textblock.aspx?Division=FRD&amp;ParentMenuId=187&amp;id=1120\">NOAAs Southwest Fisheries Science Center</a> assess the abundance of the Pacific sardine resource every year. Managers use these abundance estimates to set catch limits for the following year.</li>\n</ul>\n",
