    "Taste": "<p>When fresh, small sardines have a delicate flavor. Larger sardines have a fuller, oilier flavor, similar to anchovies but milder. If buying fresh, look for sardines with bright eyes, shiny skin, and a mildly fishy aroma. Plan to cook them within a day of purchase.</p>\n",
    "Texture": "<p>Firm when canned; soft when fresh.</p>\n",
