    "Selenium": null,
    "Serving Weight": "n/a",
    "Servings": "1",
    "Sodium": "106 mg",
    "Source": "<p>Estuaries, mostly in the Pacific Northwest.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Ranges from sweet to briny.</p>\n",
