    "Scientific Name": "Panopea generosa",
    "Species Aliases": "<a href=\"/species-aliases/king-clam\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">King Clam</a>, <a href=\"/species-aliases/elephant-clam\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Elephant Clam</a>, <a href=\"/species-aliases/gweduck\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Gweduck</a>, <a href=\"/species-aliases/goeduck\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Goeduck</a>, <a href=\"/species-aliases/goiduck\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Goiduck</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/geoduck.png",
    "alt": "Illustration of a Geoduck",
    "title": "Geoduck"
    },
    "Species Name": "Geoduck ",
    "Animal Health": null,
    "Availability": "<p>Available year-round.</p>\n",
    "Biology": "<ul>\n<li>Geoducks are long-lived; the oldest ever recorded was 168 years old.</li>\n<li>Geoducks burrow into the sediment about 1 foot per year to a depth of 3 feet.</li>\n<li>In the first 3-5 years, geoducks grow to 1.5 pounds, though they make not reach maximum size, about 7 pounds, for 15 years.</li>\n<li>Females release between 1 and 2 million eggs per spawn.</li>\n</ul>\n",
    "Bycatch": null,
    "Calories": "74",
    "Carbohydrate": "0 g",
    "Cholesterol": null,
    "Color": null,
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": "<ul>\n<li>Water quality improvements:\n<ul>\n<li>Geoducks are filter-feeders, removing algae, organic matter and excess nutrients from the water column as they grow and improving water quality.</li>\n<li>When geoducks are harvested, excess nutrients, such as nitrogen and phosphorus, are removed from the ecosystem.</li>\n</ul>\n</li>\n</ul>\n",
    "Environmental Considerations": "<ul>\n<li>Habitat:\n<ul>\n<li>Geoduck farming can have minimal impacts within the first 1-2 years of grow-out when PVC pipe and net are present, but these effects disappear after removal.</li>\n<li>Geoduck harvesting causes minimal impacts on the benthic communities.</li>\n<li>PVC pipe and net can affect eelgrass growth, so geoduck farms are not planted where eelgrass is present.</li>\n</ul>\n</li>\n<li>Feeds:\n<ul>\n<li>Once past the larval stage, geoducks do not need to be fed because they filter their food from the water column.</li>\n</ul>\n</li>\n</ul>\n",
    "Environmental Effects": "Geoducks provide net environmental benefits by removing excess nutrients and improving water quality.",
    "Farming Methods": "Geoducks are planted in PVC pipes on intertidal beaches until they are large enough to burrow into the sediment.",
    "Farming Methods_": "<ul>\n<li>Juvenile geoduck (seed) production:\n<ul>\n<li>Geoduck larvae are bred in on-land hatcheries and fed a diet of algae.</li>\n<li>Larvae set within three weeks and are grown in tanks with shallow sand until they reach about 4-6 mm.</li>\n</ul>\n</li>\n<li>Grow-out:\n<ul>\n<li>Geoduck seed are planted in PVC pipes in the sediment of intertidal beaches.</li>\n<li>The PVC pipes, often covered by nets, protect the juvenile geoducks until they grow large enough to burrow into the sediment, safe from predators.</li>\n<li>Grow-out can take 4-7 years for a geoduck to reach harvest size of 2 pounds.</li>\n</ul>\n</li>\n</ul>\n",
