    "Harvest Type": "Farmed",
    "Health Benefits": "<p>Geoducks are excellent sources of protein and iron.</p>\n",
    "Human_Health_": "Shellfish toxins and bacteria occur naturally in the environment and can cause foodborne illnesses. State and federal regulations require monitoring of farmed geoducks to ensure they are safe to eat. ",
    "Human Health": "<ul>\n<li>Shellfish toxins:\n<ul>\n<li>Shellfish poisoning is an illness that can occur from eating contaminated shellfish.</li>\n<li>Geoducks can assimilate the toxins that cause&nbsp;shellfish poisoning from the algae on which they feed.</li>\n<li>Early warning systems exist to detect harmful algal blooms that produce toxins.</li>\n<li>New technologies, such as the <a href=\"https://www.nwfsc.noaa.gov/research/datatech/tech/esp.cfm\">Environmental Sample Processor</a>, provide near real-time detection of harmful algal species.</li>\n<li>For more information on identification, prevention and monitoring of harmful algal blooms, read about the <a href=\"https://oceanservice.noaa.gov/hazards/hab/welcome.html\">NOAA Ocean Service Harmful Algal Bloom programs</a>.</li>\n</ul>\n</li>\n<li>Public health officials monitor shellfish from growing areas to ensure they are safe to eat.</li>\n</ul>\n",
    "Physical Description": "<ul>\n<li>Geoducks are characterized by a small shell and very long siphon, or \"neck.\"</li>\n<li>The long neck has two openings at the end  one for taking in oxygen and phytoplankton and one for releasing excess water.</li>\n</ul>\n",
    "Production": "<ul>\n<li>In 2016&nbsp;the United States produced 9.7&nbsp;million pounds of clams, including geoducks, valued at $138&nbsp;million.</li>\n</ul>\n",
