    "Research": "<ul>\n<li>Farming geoduck\n<ul>\n<li><a href=\"https://wsg.washington.edu/\" rel=\"external\">Washington Sea Grant</a> has developed <a href=\"https://wsg.washington.edu/wordpress/wp-content/uploads/publications/Geoduck-Final-Report-Dec-2013.pdf\" rel=\"external\">insights and monitoring recommendations</a> for Washington State geoduck farms.</li>\n</ul>\n</li>\n<li>Ocean acidification\n<ul>\n<li>Acidification causes a number of changes in water chemistry that may be stressful to estuarine organisms.</li>\n<li>Ocean acidification and its impacts on shellfish are being investigated by NOAA and other labs. For more information, visit <a href=\"http://www.oceanacidification.noaa.gov/\">NOAAs Ocean Acidification Program</a>.</li>\n</ul>\n</li>\n</ul>\n",
