    "Management": "<ul>\n<li>Permitting for shellfish aquaculture is governed by federal, state and local governments.</li>\n<li>The federal agencies involved are NOAA, the Army Corps of Engineers, Fish &amp; Wildlife Service, US Department of Agriculture, the Environmental Protection Agency, the Food and Drug Administration, the Bureau of Ocean Energy Management and the Coast Guard.</li>\n<li>Shellfish farms must adhere to federal regulations including those in the Magnuson-Stevens Fishery Conservation &amp; Management Act, the Endangered Species Act, the National Environmental Policy Act and the Clean Water Act.</li>\n<li>Information on shellfish aquaculture permitting can be found in the <a href=\"https://media.fisheries.noaa.gov/dam-migration/growers_guide_17feb2016_as.pdf\">Shellfish Growers Guide</a>.</li>\n<li>A variety of <a href=\"https://coastalscience.noaa.gov/research/marine-spatial-ecology/coastal-aquaculture-planning-portal-capp/#?tx_category=shellfish-algae-planning-and-siting\">shellfish aquaculture tools</a>, including maps and models, are available to coastal managers. //</li>\n</ul>\n",
