    "Biology": "<ul>\n<li>Are of the shellfish family. Like oysters, clams and scallops they are bivalve mollusks, and have a hinged shell.</li>\n<li>Adults are sessile  they stay in one place  and inhabit both intertidal and subtidal areas.</li>\n<li>Have fast growth rates and high reproduction rates.</li>\n<li>First mature as males, then later develop female reproductive capabilities.</li>\n<li>Each female can produce between 50 and 200 million eggs during a spawning event.</li>\n</ul>\n",
