    "Ecosystem Services": "<ul>\n<li>Water quality improvements:\n<ul>\n<li>Mussels are filter-feeders, removing algae, organic matter and excess nutrients from the water column as they grow and improving water quality.</li>\n<li>When mussels are harvested, excess nutrients, such as nitrogen and phosphorus, are removed from the ecosystem.</li>\n</ul>\n</li>\n<li>Providing habitat:\n<ul>\n<li>Mussels and the gear used to farm them provide habitat for marine organisms.</li>\n<li>Mussel beds stabilize coastal sediments and help minimize impacts from storm surges.</li>\n</ul>\n</li>\n</ul>\n",
    "Environmental Considerations": "<ul>\n<li>Habitat:\n<ul>\n<li>Mussel farming has a benign ecological footprint, with little disturbance of sediments or aquatic vegetation during grow-out.</li>\n<li>Some mussel harvesting methods involve dredging, but long-term effects on the environment are rare.</li>\n</ul>\n</li>\n<li>Feeds:\n<ul>\n<li>Mussels do not need to be fed because they filter their food from the water column.</li>\n</ul>\n</li>\n</ul>\n",
    "Environmental Effects": "Mussels provide net environmental benefits by removing excess nutrients and improving water quality.",
    "Farming Methods": "Mussels can be grown in tidal areas or the open ocean. They can be grown directly on the beach bottom or suspended in the water column.",
    "Farming Methods_": "<ul>\n<li>Farmers collect larval mussels (spat) from wild populations.</li>\n<li>Mature mussel grow-out:\n<ul>\n<li>On-bottom  mussels are seeded on sea bottom to form beds.</li>\n<li>Off-bottom  mussels are grown in the water column using longline (rope), raft or bouchot methods&nbsp;(on ropes wrapped around marine pilings or poles).</li>\n</ul>\n</li>\n</ul>\n",
