    "Human Health": "<ul>\n<li>Shellfish toxins:\n<ul>\n<li>Shellfish poisoning is an illness that can occur from eating contaminated shellfish.</li>\n<li>Shellfish&nbsp;can assimilate the toxins that cause&nbsp;shellfish poisoning from the algae on which they feed.</li>\n<li>Early warning systems exist to detect harmful algal blooms that produce toxins.</li>\n<li>New technologies, such as the <a href=\"https://www.nwfsc.noaa.gov/research/datatech/tech/esp.cfm\">Environmental Sample Processor</a>, provide near real-time detection of harmful algal species.</li>\n<li>For more information on identification, prevention and monitoring of harmful algal blooms, read about the <a href=\"https://oceanservice.noaa.gov/hazards/hab/welcome.html\">NOAA Ocean Service Harmful Algal Bloom</a> programs.</li>\n</ul>\n</li>\n<li>Pathogenic bacteria:\n<ul>\n<li>The bacteria <em>Vibrio parahaemolyticus</em> (Vp) and <em>Vibrio vulnificus</em> (Vv) occur naturally in the environment and can cause food-borne illness from consuming raw shellfish.</li>\n<li>Ingestion of undercooked or raw shellfish with Vp or Vv can lead to gastrointestinal illness.</li>\n</ul>\n</li>\n<li>Public health officials monitor shellfish from growing areas to ensure they are safe to eat.</li>\n</ul>\n",
    "Physical Description": "<ul>\n<li>Range from 2-4 inches at maturity, though can grow up to 8 inches.</li>\n<li>The shell is black, blue-black or brown, tear-drop shaped and has concentric lines marking the outside; the inner shell is white.</li>\n<li>The beard is the&nbsp;byssal threads allowing the mussel to attach to substrate.</li>\n</ul>\n",
    "Production": "<ul>\n<li>In 2016&nbsp;the United States produced 894,000 pounds of mussels, valued at $10.48 million.</li>\n</ul>\n",
    "Protein": "11.9 g",
    "Quote": "U.S. farmed Blue mussels are a smart seafood choice because they are sustainably grown and harvested under U.S. state and federal regulations.",
    "Quote Background Color": "#283350",
    "Research": "<ul>\n<li>Growth &amp; reproduction\n<ul>\n<li>The <a href=\"https://www.nefsc.noaa.gov/nefsc/Milford/\">Northeast Fisheries Science Centers Milford Lab</a> is conducting research on the ability of mussels to improve coastal water quality.</li>\n<li>NOAA, New Hampshire Sea Grant, and the University of New Hampshire worked with the Portsmouth Commercial Fishermans Association to show that growing mussels alongside steelhead trout and sugar kelp <a href=\"https://seagrant.unh.edu/blog/2021/12/aquafort-revolutionizing-local-aquaculture-new-hampshire-0\" rel=\"external\">results in net water quality improvement</a>.</li>\n</ul>\n</li>\n<li>Ocean acidification\n<ul>\n<li>Acidification causes a number of changes in water chemistry that may be stressful to shellfish.</li>\n<li>Ocean acidification and its impacts on shellfish are being investigated by NOAA and other labs. For more information, visit <a href=\"https://oceanacidification.noaa.gov/\">NOAAs Ocean Acidification Program</a>.</li>\n</ul>\n</li>\n</ul>\n",
