    "Fishery Management": "<ul>\n<li><a href=\"https://archive.fisheries.noaa.gov/garfo/sustainable/species/multispecies/\">NOAA Fisheries</a> and the <a href=\"http://www.mafmc.org/\">Mid-Atlantic Fishery Management Council</a>&nbsp;manage the butterfish fishery.</li>\n<li>Managed under the <a href=\"http://www.mafmc.org/msb/\">Atlantic Mackerel, Squid, and Butterfish Fishery Management Plan</a>:\n<ul>\n<li>Fishermen must have a permit to harvest butterfish. Managers limit the amount of available permits to control harvests.</li>\n<li>Annual catch limits are in place to prevent overfishing.</li>\n<li>Managers monitor commercial catch weekly.</li>\n<li>Vessel trip limits are based on gear mesh sizes, and are adjusted based on landings.</li>\n</ul>\n</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>Butterfish shift their distribution in response to changing bottom water temperatures.</li>\n<li>During summer, they move northward and inshore to feed and spawn.</li>\n<li>During winter, they move southward and offshore to avoid cold waters.</li>\n</ul>\n",
    "Habitat Impacts": "Fishing gears used to harvest butterfish have minimal impacts on habitat.",
