    "Location": "<ul>\n<li>Butterfish are found from Florida to Newfoundland, but they are primarily found from Cape Hatteras to the Gulf of Maine.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic, Southeast",
    "Population": "Below target level, but fishing rate promotes population growth.",
    "Population Status": "<ul>\n<li>According to the 2020&nbsp;stock assessment, butterfish are not overfished&nbsp;and not subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10187\">Stock SMART.</a></li>\n<li>Scientists at <a href=\"https://www.nefsc.noaa.gov/\">NOAAs Northeast Fisheries Science Center</a> survey the abundance of butterfish off the East Coast.</li>\n<li>They added temperature and habitat information into the stock assessment model, which helps them more accurately estimate fish abundance. Using this approach, the model indicated that the population was more abundant than previously estimated.</li>\n<li>Incorporating this type of environmental information into stock assessments will become increasingly important as the climate changes and the oceans warm.</li>\n</ul>\n",
    "Scientific Name": "Peprilus triacanthus",
    "Species Aliases": "<a href=\"/species-aliases/american-butterfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">American butterfish</a>, <a href=\"/species-aliases/atlantic-butterfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Atlantic butterfish</a>, <a href=\"/species-aliases/dollarfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Dollarfish</a>, <a href=\"/species-aliases/shiner\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Shiner</a>, <a href=\"/species-aliases/skipjack\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Skipjack</a>, <a href=\"/species-aliases/sheepshead\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sheepshead</a>, <a href=\"/species-aliases/harvestfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Harvestfish</a>",
