    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Butterfish are short-lived and grow rapidly.</li>\n<li>Few live to more than 3 years of age, and most are sexually mature at age 1.</li>\n<li>Spawning occurs during June and July.</li>\n<li>They are semi-pelagic, and form loose schools that feed upon small invertebrates.</li>\n<li>They have a high natural mortality rate and are preyed upon by many species of fish, marine mammals, and seabirds.</li>\n</ul>\n",
    "Bycatch": "Regulations limit possession of bycatch species and require modified fishing gear to reduce bycatch.",
