    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>In 2019, commercial landings of butterfish totaled more than 7.6 million pounds and were valued at more than $5.9 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:15681617080903::NO:::\">commercial fishing landings database.</a>&nbsp;</li>\n<li>Butterfish are primarily landed in Point Judith and North Kingstown, Rhode Island; Montauk, New York; and New Bedford, Massachusetts. They are generally exported to Japan, where they are a popular menu item.</li>\n<li>Otter trawls are used to catch butterfish.</li>\n<li>Otter trawls can incidentally catch other fish and marine mammals as bycatch, and can impact habitat, depending on where they are used.</li>\n<li>Gear restrictions (minimum mesh size) when fishing with otter trawls are used to reduce bycatch.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Butterfish is a good source of niacin, vitamin B6, phosphorus, protein, vitamin B12, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Butterfish are dull blue on the top, with pale sides and a silvery belly. Numerous irregular dark spots fade after they are harvested.</li>\n<li>Very thin and deep-bodied, like a flounder set upright, and somewhat circular or rounded.</li>\n<li>Small mouths with weak teeth and blunt noses.</li>\n<li>6 to 9 inches in length, though some individuals can reach 12 inches.</li>\n<li>Up to 1.25 pounds in weight.</li>\n</ul>\n",
    "Production": null,
    "Protein": "17 g",
    "Quote": "U.S. wild-caught butterfish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
