    "Location": "<ul>\n<li>Winter skates range from the Gulf of Saint Lawrence in Canada to Cape Hatteras, North Carolina.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "Above target population level.",
    "Population Status": "<ul>\n<li>According to the 2020 stock assessment, winter skate is not overfished and not subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Winter%20skate%20-%20Georges%20Bank%20/%20Southern%20New%20England&amp;stockid=11080\">Stock SMART</a>.</li>\n<li>Seven skate species, including winter skate, are managed as one northeast skate complex. There is a lack of data for this complex. New requirements to report skate landings by species should improve the data-poor nature of this complex.</li>\n<li>Estimates of winter skate abundance, or biomass, peaked in the mid-1980s, declined through the early 1990s, and increased again in recent years to moderately high levels.&nbsp;</li>\n</ul>\n",
    "Scientific Name": "Leucoraja ocellata",
    "Species Aliases": "<a href=\"/species-aliases/skate\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Skate</a>, <a href=\"/species-aliases/big-skate\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Big skate</a>, <a href=\"/species-aliases/spotted-skate\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Spotted skate</a>, <a href=\"/species-aliases/eyed-skate\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Eyed skate</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/winter_skate.png",
    "alt": "Illustration of a Winter Skate",
    "title": "Winter Skate"
    },
    "Species Name": "Winter Skate",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Winter skates have large bodies and can grow up to 5 feet in length.</li>\n<li>They can live for about 20 years.</li>\n<li>They reproduce at a late age, when theyre approximately 11 years old and 2.5 feet long.</li>\n<li>Skates lay eggs year-round but have few offspring. Their eggs are enclosed in a hard leathery case called a mermaids purse. The eggs incubate for 6 to 12 months, and young skates have the adult form when they hatch.</li>\n<li>Skates feed on a variety of organisms such as crustaceans, mollusks, worms, squids, and fish.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "90",
    "Carbohydrate": "0 g",
    "Cholesterol": "0 mg",
    "Color": "<p>The meat is off-white, sometimes pinkish, when raw and is off-white when cooked.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
    "Farming Methods": null,
    "Farming Methods_": null,
    "Fat, Total": "1 g",
    "Feeds_": null,
    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings totaled approximately 22 million pounds&nbsp;and were valued at $5.6 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:1107541287724::NO:RP::\">commercial fishing landings database</a>.</li>\n<li>More than half of skate landings come from Massachusetts and Rhode Island.</li>\n<li>Skates are harvested in two different fisheries, one for wings for human consumption and one for lobster bait.\n<ul>\n<li>In the bait fishery, vessels from southern New England target mostly little skates (more than 90 percent) and, to a much lesser extent, juvenile winter skates (less than 10 percent). Juvenile winter skates are difficult to differentiate from little skates because they look nearly identical.</li>\n<li>The wing fishery is labor-intensive because the wings need to be cut into fillets, but participation has grown recently due to increasing restrictions on other, more profitable groundfish species.</li>\n<li>In the wing fishery, mainly trawlers and gillnetters harvest winter skates when targeting other species such as groundfish, monkfish, and scallops. Fishermen keep the skates if the price is profitable.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n<ul>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Most skates are caught incidentally in otter trawl fisheries targeting other more valuable species, such as groundfish, monkfish, and scallops. Otter trawls can impact habitat depending on where they are used. They can also interact with marine mammals (e.g., whales, harbor porpoises, and seals) and sea turtles. Management measures including closed areas, restrictions on gear and fishing effort, and modifying fishing gear to reduce contact with habitat help reduce these impacts and interactions.</li>\n<li>Skates are also caught in bottom gillnet fisheries, which have less of an impact on habitat.</li>\n<li>Skates are caught and discarded as bycatch in numerous fisheries, but the rate of discards has decreased in recent years as the value of skate products has increased.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": null,
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Skates are a relative of sharks and rays and have a kite-like shape.</li>\n<li>Winter skates are light brown and covered with small dark spots.</li>\n<li>Small spines cover most of their back.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "20 g",
    "Quote": "U.S. wild-caught winter skate is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#734623",
    "Research": "<ul>\n<li>Scientists and managers have limited information about the skate species. They have identified a number of research needs for the species including additional studies on life history (age, growth, and reproduction), the survival rates of discarded skates (i.e., caught but thrown back), species identification, habitat preferences, and the structure of the stock.</li>\n</ul>\n",
