    "Fishery Management": "<ul>\n<li><a href=\"https://www.westcoast.fisheries.noaa.gov/\">NOAA Fisheries</a> and the <a href=\"https://www.pcouncil.org/\">Pacific Fishery Management Council</a> manage pink salmon on the West Coast.</li>\n<li>Managed under the <a href=\"https://www.pcouncil.org/fishery-management-plan-and-amendments-3/\">Pacific Coast Salmon Plan</a>:\n<ul>\n<li>Every year, the council reviews reports of the previous fishing season and current estimates of salmon abundance. Using this information, they make recommendations for management of the upcoming fishing season.</li>\n<li>Their general goal is to allow fishermen to harvest the maximum amount of salmon that will support the fishery while preventing overharvest of the resource and ensuring that salmon populations with low abundance can rebuild.</li>\n<li>Specific management measures vary year to year depending on current salmon abundance, and include size limits, season length, quotas, and gear restrictions.</li>\n<li>The council usually increases harvest limits for pink salmon in odd years when more adults are returning to spawn.</li>\n<li>Management of pink salmon must also comply with laws such as the <a href=\"https://www.fisheries.noaa.gov/topic/laws-policies#endangered-species-act\">Endangered Species Act</a>.</li>\n<li>Final recommendations are implemented by NOAA Fisheries. <a href=\"https://www.pcouncil.org/salmon/current-season-management/\">Check here</a> for the current seasons management. State and tribal managers use council management recommendations to shape their policies for inland fisheries, to ensure that conservation objectives are met.</li>\n</ul>\n</li>\n<li><a href=\"https://www.fisheries.noaa.gov/region/alaska\">NOAA Fisheries</a> and the <a href=\"https://www.npfmc.org/\">North Pacific Fishery Management Council</a> manage pink salmon in Alaska.</li>\n<li>Managed under the <a href=\"https://www.fisheries.noaa.gov/management-plan/alaska-salmon-fisheries-management-plan\">Fishery Management Plan for Salmon Fisheries in the EEZ off the Coast of Alaska</a>:\n<ul>\n<li>All management of the salmon fisheries in federal waters is delegated to the <a href=\"http://www.adfg.alaska.gov/index.cfm?adfg=CommercialByFisherySalmon.main\" rel=\"external\">State of Alaska</a>, which is also responsible for managing the commercial, recreational, and subsistence fisheries for salmon in state waters. This ensures that management is consistent throughout salmons range.\n<ul>\n<li>Managers regulate the fishery based on <a href=\"http://www.adfg.alaska.gov/index.cfm?adfg=sonar.escapementgoals\" rel=\"external\">escapement goals</a>&nbsp;to ensure harvests are sustainable. They want enough salmon to be able to escape the fishery and return to fresh water to spawn and replenish the population.</li>\n<li>Salmon fishery management largely relies on in-season assessment of how many salmon return to fresh water to spawn.</li>\n<li>Managers set harvest levels based on these returns. When abundance is high and the number of fish returning is much higher than that needed to meet escapement goals, harvest levels are set higher.</li>\n<li>In years of low abundance, harvest levels are lowered.</li>\n</ul>\n</li>\n<li>During the fishing season, scientists monitor catch and escapement, comparing current returns with those from previous years, to keep an eye on abundance and actively manage the fishery.</li>\n</ul>\n</li>\n<li>Off the West Coast and in Alaska, the <a href=\"https://www.psc.org/about_treaty.htm\" rel=\"external\">Pacific Salmon Treaty</a> and the <a href=\"https://www.psc.org/index.htm\" rel=\"external\">Pacific Salmon Commission</a> help coordinate management, research, and enhancement of shared U.S. and international salmon stocks, including pink salmon.</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>Freshwater streams, estuaries, and associated wetlands provide vital nursery grounds for pink salmon.</li>\n<li>Adult salmon leave the ocean, enter fresh water, and migrate upstream to spawn, usually in the stream of their birth.</li>\n<li>Pink salmon spawn in rivers closer to tidewater than most other Pacific salmon species, generally within 30 miles of a river mouth.</li>\n</ul>\n",
    "Habitat Impacts": "Fishing gear used to catch pink salmon rarely contacts the ocean floor and has little impact on habitat.",
