    "Species Name": "Pink Salmon",
    "Animal Health": null,
    "Availability": "<p>Fresh from late summer to fall, and frozen and canned year-round.</p>\n",
    "Biology": "<ul>\n<li>Pink salmon are anadromous  they hatch in freshwater streams and rivers then migrate out to the saltwater environment of the ocean to feed and grow.</li>\n<li>Unlike <a href=\"https://www.fishwatch.gov/profiles/coho-salmon\">coho</a>, <a href=\"https://www.fishwatch.gov/profiles/chinook-salmon\">Chinook</a>, or <a href=\"https://www.fishwatch.gov/profiles/sockeye-salmon\">sockeye</a> salmon, pink salmon do not reside in fresh water for an extended period.</li>\n<li>Young pink salmon (fry) typically migrate directly to estuarine and marine waters soon after they are born.</li>\n<li>Once they reach the ocean, they feed voraciously and grow rapidly. In fact, theyre among the fastest growing of the Pacific salmon species.</li>\n<li>After about 1 years of feeding and growing in the ocean, maturing pink salmon return to fresh water to spawn, usually from August to October.</li>\n<li>Females construct nests (redds) in the riverbed by turning on their sides and vigorously flexing their bodies and tails, digging a shallow hole.</li>\n<li>Females deposit between 1,200 and 1,900 eggs in the redds, which the males then fertilize.</li>\n<li>The female stays and defends her redd from other females until she dies, usually within two weeks.</li>\n<li>All pink salmon die after they spawn.</li>\n<li>They typically spawn at the age of 2.</li>\n<li>Because the pink salmon life cycle is so regular, independent populations spawn in even and odd years. For example, in the southern part of their range, they usually spawn in odd years&nbsp;in most river systems. However, pink salmon do spawn in even years in some Puget Sound rivers.</li>\n<li>Throughout most of Alaska, there is no dominant year, except in the northwestern part of Alaska where even-year runs predominate.</li>\n<li>Pink salmon feed on small crustaceans, zooplankton (tiny floating animals), squid, and small fish.</li>\n<li>In fresh water, aquatic invertebrates, other fishes, birds, and small mammals prey on pink salmon eggs, alevins, and fry.</li>\n<li>In the ocean, other fishes (including other Pacific salmon) and coastal seabirds prey on pink salmon fry and juveniles.</li>\n<li>Marine mammals, sharks, other fishes (such as <a href=\"https://www.fishwatch.gov/profiles/pacific-halibut\">Pacific halibut</a>), and humpback whales feed on adult pink salmon.</li>\n<li>In freshwater spawning habitats, bears are predators of adult pink salmon. Wolves, river otters, and bald eagles will also occasionally eat pre-spawning adult pinks.</li>\n<li>After salmon spawn and die, <a href=\"http://www.adfg.alaska.gov/index.cfm?adfg=wildlifenews.view_article&amp;articles_id=97&amp;issue_id=21\" rel=\"external\">salmon carcasses</a> are a valuable source of energy and nutrients to the river ecosystem. Carcasses have been shown to improve newly hatched salmon growth and survival by contributing nitrogen and phosphorous compounds to streams.</li>\n</ul>\n",
