    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial harvest of pink salmon totaled approximately 396 million pounds and were valued at more than $106 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:12702819011307::NO:::\">commercial fishing landings database</a>.</li>\n<li>Almost all the pink salmon harvested in the United States comes from Alaska fisheries.</li>\n<li>Small numbers of pink salmon are also harvested off the West Coast, mainly Washington, especially in odd-numbered years (see the Science section for more about pink salmons unique two-year life cycle).</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Purse seines, gillnets, and reef nets are used to catch pink salmon.</li>\n<li>Purse seiners catch salmon by encircling them with a long net and drawing the bottom closed to capture the fish.</li>\n<li>Gillnetters catch salmon by setting curtain-like nets perpendicular to the pink salmon trajectory as they migrate along the coast toward fresh water.</li>\n<li>Fishing gear used to catch pink salmon rarely contacts the ocean floor and has little impact on habitat.</li>\n<li>Bycatch is low and usually consists of other salmon species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Salmon are a favorite catch of recreational fishermen.</li>\n<li>Recreational fishermen use a variety of fishing gear to harvest pink salmon.</li>\n<li>While their relatively small size makes them less popular with sport anglers than other salmon species, pink salmon are excellent fish to catch because theyre aggressive and make a nice, mild-tasting meal.</li>\n<li>To ensure recreational fisheries are sustainable, West Coast anglers are only allowed to keep a certain number of salmon per fishing trip.</li>\n<li>In Alaska, regulations vary by area and individual fisheries.</li>\n<li>Recreational fisheries in high-use areas (Cook Inlet, Southeast Alaska, Copper River) are regulated through management plans that allocate fish between competing commercial and recreational fishermen.</li>\n</ul>\n</li>\n<li>Subsistence fishery:\n<ul>\n<li>Salmon is an important source of spiritual and physical sustenance for Western Indian tribes and Alaska natives, and salmon are culturally important to many other residents of these areas.</li>\n<li>Subsistence fishermen use a variety of fishing gear to harvest pink salmon.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Pink salmon is low in sodium and is a good source of omega-3 fatty acids, protein, niacin, vitamin B12, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Pink salmon are the smallest of the Pacific salmon found in North America, weighing between 3.5 and 5 pounds, with an average length of 20 to 25 inches.</li>\n<li>Pink salmon can be distinguished from other Pacific salmon by the large dark oval spots on their back and entire tail fin as well as their general coloring and form.</li>\n<li>In the sea, pink salmon are steel blue to blue-green on the back, silver on the sides, and white on the belly.</li>\n<li>Breeding males become dark on the back and red with brownish green blotches on the sides.</li>\n<li>Males also develop a hump on their back, which is why they are often called humpback salmon.</li>\n<li>Breeding females are similar but lack a dorsal hump and are less distinctly colored.</li>\n</ul>\n",
    "Production": null,
    "Protein": "19.94 g",
    "Quote": "U.S. wild-caught pink salmon is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#ddb4b4",
    "Research": "<ul>\n<li><a href=\"https://www.afsc.noaa.gov/ABL/SOE/soe_default.php\">NOAA's Alaska Fisheries Science Center Salmon Research</a></li>\n<li><a href=\"https://www.nwfsc.noaa.gov/research/themes/species/index.cfm#salmon\">NOAAs Northwest Fisheries Science Center Salmon Research</a></li>\n<li><a href=\"https://swfsc.noaa.gov/textblock.aspx?id=1040&amp;ParentMenuId=110\">NOAAs Southwest Fisheries Science Center Salmon Research</a></li>\n</ul>\n",
