    "Species Name": "Atlantic Northern Shrimp",
    "Animal Health": null,
    "Availability": "<p>Usually available fresh from winter to spring and frozen year-round, but currently unavailable because the fishery is closed.</p>\n",
    "Biology": "<ul>\n<li>Northern shrimp are protandrous hermaphrodites  they begin life as males and sexually mature at roughly 2 years old. They transform to females at about 3 years old.</li>\n<li>They start spawning in late July in offshore waters, mainly in deep mud basins in the southwestern Gulf of Maine. By early fall, most adult females have pushed their eggs out onto their abdomen.</li>\n<li>In late fall through winter, egg-bearing females move inshore where the eggs hatch. Juveniles remain in coastal waters for a year or more before migrating to deeper offshore waters, where they mature as males.</li>\n<li>Their reproductive success, growth, and development can be affected by temperature. They can grow up to 3 or 4 inches long, and most do not live past age 5.</li>\n<li>They prey on plankton (tiny floating plants and animals) and bottom-dwelling invertebrates.</li>\n<li>They are eaten by many important fish species such as cod, redfish, and silver and white hake.</li>\n<li>Northern shrimp are an important part of the marine food chain, and maintaining a healthy population is important to supporting a balanced Gulf of Maine ecosystem.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize non-shrimp bycatch and minimize the catch of small (male) shrimp with low market value.",
