    "Fishing Rate": "Fishing for northern shrimp is prohibited.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>The fishery has been closed by moratorium since 2014 because the stock has collapsed. Due to continued low population levels the moratorium has been extended each year.</li>\n<li>Following high landings in the late 1960s and early 1970s, the fishery was closed in 1978 due to a stock collapse. It was reopened the following year, and landings increased throughout the 1980s and 1990s before dropping again. For several seasons prior to the 2014 fishing moratorium, the commercial season was closed early due to landings exceeding the catch limit.</li>\n<li>The fishery is seasonal in nature, peaking in late winter when egg-bearing females move into inshore waters where the eggs hatch.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Fishermen mainly use otter trawls to harvest northern shrimp, although some Maine fishermen use traps.</li>\n<li>Northern shrimp are harvested over soft mud bottoms, which are more resilient to the impacts of trawling compared to structured habitats such as corals. Also, the small mesh in the shrimp otter trawls creates more drag than a groundfish net and cant be towed as fast for the same size net, reducing the potential impact on shrimp habitat.</li>\n<li>Minimum mesh size in trawls to prevent bycatch of small male shrimp.</li>\n<li>Finfish excluder devices and size-sorting grates in the gear to prevent bycatch of groundfish and small male shrimp.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Northern shrimp are low in saturated fat and high in iron, phosphorus, and zinc.</p>\n",
