    "Human Health": null,
    "Physical Description": "<ul>\n<li>Northern shrimp are crustaceans, like lobsters and crabs.</li>\n<li>They are much smaller than warm-water shrimp, averaging 2 to 4 inches in length.</li>\n<li>When alive, the tails and bodies of northern shrimp are more red than pink, and the shells are translucent.</li>\n<li>They have appendages called <a href=\"https://www.nefsc.noaa.gov/femad/pbb/fwdp/training/img35.jpg\">pleopods</a> on their tail that act like paddles and enable them to move with remarkable agility over considerable distances.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.31 g",
    "Quote": "Commercial fishing for northern shrimp is prohibited due to its extremely depleted state.",
    "Quote Background Color": "#de563a",
    "Research": "<ul>\n<li>Northern shrimp are sensitive to temperature. <a href=\"https://www.nefsc.noaa.gov/press_release/pr2017/features/gulf-of-maine-shrimp-habitat/\">Thermal habitat is key</a>.</li>\n<li><a href=\"https://apps-nefsc.fisheries.noaa.gov/coopresearch/pdfs/Nordmore_Bycatch_Assessment_Final_Report_Nov09.pdf\" rel=\"external\">A Contemporary Assessment of the Bycatch of Regulated Species and the Nordmore Grate in the Northern Shrimp Fishery</a></li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.328 g",
    "Selenium": "38 mcg",
    "Serving Weight": "100 g (raw)",
    "Servings": "1",
    "Sodium": " 148 mg",
    "Source": "<p>U.S. wild-caught from Maine to Massachusetts.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Sweet and more flavorful than warm-water shrimp.</p>\n",
    "Texture": "<p>Firm and moist.</p>\n",
