    "Location": "<ul>\n<li>Swordfish are found around the world in tropical, temperate, and sometimes cold waters of the Atlantic, Indian, and Pacific Oceans, including the waters around the U.S. Pacific Islands and off the U.S. West Coast.&nbsp;&nbsp;</li>\n</ul>\n",
