    "NOAA Fisheries Region": "West Coast, Pacific Islands",
    "Population": "Above target population levels.",
    "Population Status": "<ul>\n<li>\n<p>There are two stocks of North Pacific swordfish: the Eastern Pacific Ocean stock and the Western and Central North Pacific Ocean stock. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Eastern Pacific Ocean stock is not overfished but is subject to overfishing (2014 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=11643\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Western and Central North Pacific Ocean stock is not overfished and is not subject to overfishing (2018 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=11642\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
