    "Availability": "<p>Year-round.&nbsp;&nbsp;</p>\n",
    "Biology": "<ul>\n<li>Swordfish are one of the fastest and largest predators in the ocean. Their streamlined body allows them to swim at high speeds, up to 50 mph.</li>\n<li>They grow rapidly, reaching a maximum length of 14 feet and almost 1,200 pounds (although the average size caught in the fishery is 50200 pounds).</li>\n<li>They are able to reproduce when they reach 5 to 6 years old.</li>\n<li>Swordfish spawn numerous times throughout the year near the surface of warm tropical and sub-tropical waters.</li>\n<li>In cooler waters, they spawn several times during the spring and summer.</li>\n<li>Swordfish are productive, and their eggs are fertilized externally and float at the sea surface where they incubate for about 2 1/2 days. Swordfish live for about 9 years.</li>\n<li>Swordfish feed on a variety of fish and invertebrates such as squid.</li>\n<li>They capture their prey by slashing their bills back and forth, stunning or injuring the prey in the process.</li>\n<li>They have developed unique characteristics, such as special eye muscles and a heat exchange system that allow them to swim in deep cold water in search of prey.</li>\n<li>Swordfish feed at the top of the food chain and are rarely preyed upon by other animals, but juvenile swordfish are sometimes eaten by sharks and larger predatory fish.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
