    "Fishing Rate": "At recommended level in the Western and Central North Pacific. Reduced to end overfishing in the Eastern Pacific.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>\n<p>In 2020, commercial landings of swordfish in Hawaii totaled 800,000 pounds and were valued at &nbsp;$2.9 million,&nbsp;according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:17585728475351::NO:::\">commercial fishing landings database</a>.&nbsp;</p>\n</li>\n<li>\n<p>In 2020, commercial landings of swordfish in California totaled one million pounds and were valued at more than $2.7 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:17585728475351::NO:::\">commercial fishing landings database</a>.&nbsp;</p>\n</li>\n<li>\n<p>The majority of North Pacific swordfish landed in California&nbsp;is landed by Hawaii-based vessels.</p>\n</li>\n<li>Hawaii-based longliners accounted for the majority&nbsp;of U.S. catch of North Pacific swordfish.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen use longline gear to harvest swordfish in federal waters off of Hawaii and on the high seas.</li>\n<li>In federal waters off the West Coast, commercial fishermen harvest swordfish using drift gillnets and harpoons, and using deep-set buoy gear authorized under exempted fishing permits in recent years.</li>\n<li>Fishing gear used to catch swordfish rarely contacts the seafloor so habitat impacts are minimal.</li>\n<li>Longlines and gillnets can incidentally catch sea turtles, marine mammals, seabirds, sharks, and other finfish species. Gear restrictions and operational requirements are in place to minimize bycatch.</li>\n<li>U.S. longline&nbsp;and drift gillnet vessels operate under regulations to minimize impacts on other species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Off the U.S. West Coast, recreational fishermen fish for swordfish with rod-and-reel gear. They are prohibited from selling their catch.</li>\n<li>Fishermen must have a fishing license to catch and land swordfish and are allowed to keep two swordfish per day.</li>\n<li>Commercial passenger fishing vessels must have a permit and keep a monthly log of their fishing activity.</li>\n<li>There are no federal regulations for recreational fishing off Hawaii and U.S. Pacific Island territories, but local rules may apply.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Swordfish is an excellent source of selenium, niacin, vitamin B12, and zinc. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Swordfish have a long, flattened bill that looks like a sword, as their name implies.</li>\n<li>They have a stout, rounded body and large eyes.</li>\n<li>Their first dorsal fin is tall and crescent-shaped. The second is much smaller.</li>\n<li>Their anal fins are similar in shape to the dorsal fins but smaller.</li>\n<li>They have a broad, crescent-shaped tail.</li>\n<li>Their color is darkest on top, generally black or brown.</li>\n<li>Adult swordfish have no teeth or scales.</li>\n</ul>\n",
    "Production": null,
    "Protein": "19.8 g",
    "Quote": "U.S. wild-caught North Pacific swordfish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#2d2e2a",
    "Research": "<ul>\n<li>NOAAs <a href=\"https://www.pifsc.noaa.gov/\">Pacific Islands Fisheries Science Center</a> maintains <a href=\"https://www.pifsc.noaa.gov/eod/turtlewatch.php\">TurtleWatch</a>a map providing up-to-date information about the thermal habitat of loggerhead sea turtles in the Pacific Ocean north of the Hawaiian Islands. These maps are used by swordfish fishermen to avoid fishing in waters preferred by loggerhead sea turtles.</li>\n<li>NOAAs <a href=\"https://www.pifsc.noaa.gov/\">Pacific Islands</a> and <a href=\"https://swfsc.noaa.gov/textblock.aspx?Division=FRD&amp;ParentMenuId=141&amp;id=1127\">Southwest Fisheries Science Centers</a> conduct additional research on swordfish to inform management of the species.</li>\n<li><a href=\"http://www.westcoast.fisheries.noaa.gov/stories/2017/06_02062017_swordfish_efp_tagging.html\">Testing of deep-set buoy gear</a> as a means to selectively target swordfish off of the U.S. West Coast.</li>\n</ul>\n",
