    "Population Status": "<ul>\n<li>According to the 2021 stock assessment, Atlantic mackerel is&nbsp;overfished and subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Atlantic%20mackerel%20-%20Gulf%20of%20Maine%20/%20Cape%20Hatteras&amp;stockid=10164\">Stock SMART</a>.</li>\n</ul>\n",
    "Scientific Name": "Scomber scombrus",
    "Species Aliases": "<a href=\"/species-aliases/mackerel\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Mackerel</a>, <a href=\"/species-aliases/common-mackerel\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Common mackerel</a>, <a href=\"/species-aliases/boston-mackerel\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Boston mackerel</a>, <a href=\"/species-aliases/caballa\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Caballa</a>",
