    "Species Name": "Atlantic Mackerel",
    "Animal Health": null,
    "Availability": "<p>Year-round. Some is sold fresh in domestic markets, but most is frozen and exported to markets around the world.</p>\n",
    "Biology": "<ul>\n<li>Atlantic mackerel grow fast, up to 16  inches and 2.2 pounds.</li>\n<li>They can live up to 20 years and are able to reproduce by the time they reach age 2 to 3.</li>\n<li>There are two major spawning groups of Atlantic mackerel in the western Atlantic:\n<ul>\n<li>The southern group spawns primarily in the Mid-Atlantic Bight from April to May.</li>\n<li>The northern group spawns in the Gulf of St. Lawrence in June and July.</li>\n<li>Both groups typically spawn 10 to 30 miles off shore.</li>\n</ul>\n</li>\n<li>Depending on their size, females can have between 285,000 and almost 2 million eggs. They release their eggs in batches, between five and seven times throughout the spawning season.</li>\n<li>Eggs generally float in the surface water and hatch in 4 to 7  days, depending on water temperature.</li>\n<li>Atlantic mackerel feed heavily on crustaceans such as copepods, krill, and shrimp. They also eat squid, as well as some fish and ascidians (sac-like marine invertebrate filter feeders).</li>\n<li>Several species of fish and marine mammals eat Atlantic mackerel.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
