    "Fishing Rate": "Reduced to end overfishing.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of Atlantic mackerel totaled more than 17&nbsp;million pounds and were valued at approximately $5 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:6228678179267::NO:::\">commercial fishing landings database</a>. Massachusetts, New Jersey, and Rhode Island harvested the majority.</li>\n<li>Commercial landings from 2004 to 2021&nbsp;ranged from a high of 125 million pounds in 2006 to a low of 1.1 million pounds in 2011.</li>\n<li>The U.S. commercial fishery for Atlantic mackerel operates primarily between January and May in southern New England and Mid-Atlantic coastal waters and between May and December in the Gulf of Maine.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Fishermen harvest mackerel in large volumes using mid-water trawls.</li>\n<li>Although mid-water trawls have minimal impact on habitat, they can incidentally catch marine mammals such as short- and long-finned pilot whales, Atlantic white-sided dolphins, and common dolphins.</li>\n<li>The <a href=\"https://www.greateratlantic.fisheries.noaa.gov/prot_res/atgtrp/meeting/ATGTRT%20Draft%20Take%20Reduction%20Strategy%20FINAL.pdf\">Atlantic Trawl Gear Take Reduction Strategy</a> provides measures to reduce potential impacts of bycatch. These include outreach to educate fishermen about actions to take in the event of a marine mammal interaction and efforts to communicate interaction hotspots to fishermen in real time. It also contains voluntary measures concerning fishing practices, such as reducing tow times and limiting turns while fishing.</li>\n<li>The mackerel fishery has some incidental catch of river herring and shad. A cap was established in 2014 to limit river herring and shad catch in the mackerel fishery.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Mackerel are important to recreational fisheries in the Mid-Atlantic and New England regions, with the highest landings occurring from Massachusetts and&nbsp;Maine.</li>\n<li>In 2020, recreational harvest of Atlantic mackerel totaled 4 million pounds, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:6228678179267::NO:::\">recreational fishing landings database</a>.&nbsp;</li>\n<li>The recreational mackerel fishery is open all year, with a catch limit set annually at the same time as commercial limits.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Mackerel is high in omega-3 fatty acids and is an excellent source of selenium, niacin, and vitamins B6 and B12.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Atlantic mackerel are iridescent blue green on the back with a silvery white underbelly.</li>\n<li>They have 20 to 30 wavy black bars that run across the top half of their body, and a narrow dark streak that runs below these bars along each side.</li>\n<li>Their body is spindle-shaped, tapering at both ends.</li>\n<li>Their two large dorsal fins are gray or dusky. The pectoral fins are black or dusky at the base, and the tail fin is gray or dusky.</li>\n<li>Their distinctive coloring fades quickly after they die.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.6 g",
    "Quote": "U.S. wild-caught Atlantic mackerel is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#c3c792",
    "Research": "<ul>\n<li>Scientists at NOAAs <a href=\"https://www.nefsc.noaa.gov/\">Northeast Fisheries Science Center</a> collect information on Atlantic mackerel through their bottom trawl surveys. They research the abundance, biology, and distribution of Atlantic mackerel and other marine resources in the Northwest Atlantic. They use this information to assess the status of the Atlantic mackerel stock and provide recommendations to managers.</li>\n<li>Atlantic mackerel are sensitive to changes in water temperature and migrate long distances on a seasonal basis to feed and spawn. NOAA Fisheries scientists have found that changing environmental factors have altered the distribution of Atlantic mackerel, shifting the stock northeastward and into shallower waters.&nbsp;These <a href=\"https://www.nefsc.noaa.gov/press_release/2011/SciSpot/SS1104/\">findings</a> could have significant implications for U.S. commercial and recreational mackerel fisheries, which mostly occur during late winter and early spring.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "3.257 g",
    "Selenium": "44.1 mcg",
    "Serving Weight": "100 g (raw)",
    "Servings": "1",
    "Sodium": "90 mg",
    "Source": "<p>U.S. wild-caught from Maine to North Carolina.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Mackerel has a rich, pronounced flavor. For a milder flavor, cut out the outer bands of dark meat along the midline.</p>\n",
    "Texture": "<p>Soft, flaky, and moist.</p>\n",
