    "Species Name": "Atlantic Bigeye Tuna",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Bigeye tuna grow fast and can reach about 5.5 feet in length.</li>\n<li>They can live up to 9 years and are able to reproduce when they are 3.5 years old.</li>\n<li>Bigeye tuna spawn throughout the year but most often in the summer.</li>\n<li>They usually spawn two or more times&nbsp;a year.</li>\n<li>Females release between 3 million and 6 million eggs each time they spawn.</li>\n<li>Bigeye tuna feed near the top of the food chain, preying on fish, crustaceans, and squid.</li>\n<li>They are prey for many top predators, including sharks, billfish, larger tunas, and toothed whales.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "130",
    "Carbohydrate": "0 g",
    "Cholesterol": "45 mg",
    "Color": "<p>Bigeye tuna meat has a reddish-pink color.</p>\n",
