    "Fishing Rate": "The United States is working with other nations to reduce harvest rates and end overfishing.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>U.S. harvest of Atlantic bigeye tuna is usually only a small fraction (1 percent or less) of the global Atlantic bigeye harvest.</li>\n<li>In 2020, U.S. commercial landings of Atlantic bigeye tuna totaled one million pounds, and were valued at more than $4.8 million, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:1801229959954::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>U.S. commercial fishermen mainly harvest bigeye tuna using pelagic longlines. They sometimes use rod-and-reel gear.</li>\n<li>Fishing gear used to catch bigeye tuna rarely contacts the seafloor so habitat impacts are minimal.</li>\n<li>Commercial fishing gear can catch protected species such as marine mammals and sea turtles.</li>\n<li>NOAA Fisheries has taken the following additional measures to reduce&nbsp;bycatch, bycatch mortality, and sustainably manage the pelagic longline&nbsp;fishery. Fishermen are:\n<ul>\n<li>Required to use large circle hooks and certain types of bait that limit gear interactions with sea turtles. Circle hooks are specifically designed to minimize the damage caused by hooking, giving animals that are captured and released a better chance at survival.</li>\n<li>Trained to use special techniques to safely dehook and release any incidentally caught turtles.</li>\n<li>Required to stop fishing and move 1 nautical mile if they encounter a protected species.</li>\n<li>Required to protect pilot whales and Risso's dolphins when fishing in the Mid-Atlantic Bight by limiting the length of their lines to 20 nautical miles and posting <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/safe-handling-release-and-identification-workshops\">marine mammal handling/release guidelines</a> on their vessels. In addition, if fishing in the Cape Hatteras Special Research Area, pelagic longliners must contact NOAA Fisheries at least 48 hours prior to a trip and carry observers if requested.</li>\n<li>Required to use weak hooks in the Gulf of Mexico to reduce incidental catch of bluefin tuna and prohibited from using live bait to reduce bycatch of billfish.</li>\n<li>Restricted from certain areas of the Gulf of Mexico to reduce bycatch of multiple&nbsp;species.</li>\n<li>Required to carry <a href=\"https://www.fisheries.noaa.gov/topic/enforcement/vessel-monitoring\">vessel monitoring systems</a> onboard their boats to ensure compliance with these closures.</li>\n<li>Required to carry at-sea fisheries observers upon request. NOAA Fisheries reviews observer data to monitor protected species interactions and takes appropriate action as necessary.</li>\n<li>Required to install electronic monitoring (cameras) and submit camera hard drives to NOAA Fisheries to monitor bluefin tuna catch and discards.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational fishermen must have a permit to fish for bigeye tuna.</li>\n<li>Minimum size limit.</li>\n<li>Recreational fishing for highly migratory species such as bigeye tuna provides significant economic benefits to coastal communities through individual angler expenditures, recreational charters, tournaments, and the shoreside businesses that support those activities.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Bigeye tuna is low in saturated fat and sodium and is rich in niacin, vitamins B6 and B12, selenium, and phosphorous.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Bigeye tuna are dark metallic blue on the back and upper sides and white on the lower sides and belly.</li>\n<li>The first fin on their back is deep yellow, the second dorsal and anal fins are pale yellow, and the finlets are bright yellow with black edges.</li>\n<li>Bigeye and <a href=\"https://www.fishwatch.gov/profiles/atlantic-yellowfin-tuna\">yellowfin</a> tuna look fairly similar. In fact, its hard to distinguish the two species without experience.</li>\n<li>Among other characteristics, the bigeyes eyes are larger than the yellowfins and their finlets have black edges.</li>\n</ul>\n",
    "Production": null,
    "Protein": "27 g",
    "Quote": "U.S. wild-caught Atlantic bigeye tunawhile only a small percent (about 1%) of the total Atlantic-wide harvestis a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#102b50",
    "Research": "<ul>\n<li>Skipjack often school around floating objects, including <a href=\"https://www.fisheries.noaa.gov/national/bycatch/fishing-gear-fish-aggregating-devices\">fish aggregating devices</a> (FADs) intentionally placed in the water to attract fish. Foreign purse seiners and baitboats have used FADs extensively in the Atlantic since the early 1990s to catch skipjack, primarily off the coast of West Africa. Researchers have found that the increasing use of FADs has changed the species composition of tuna catches, resulting in a higher capture rate of juvenile <a href=\"https://www.fishwatch.gov/profiles/atlantic-yellowfin-tuna\">yellowfin</a> and bigeye tuna for vessels targeting <a href=\"https://www.fishwatch.gov/profiles/atlantic-skipjack-tuna\">skipjack</a>. The higher capture rate of juvenile bigeye tuna in purse seine fisheries associated with FADs has been demonstrated by ICCAT to reduce the overall maximum sustainable yield of the stock. The increased presence of FADs may also have an impact on the biology and ecology of skipjack, bigeye, and yellowfin tuna. Also, sea turtles and marine mammals can become entangled in nets, ropes, and lines that are used in the FADs.&nbsp;This research influenced ICAATs requirements related to the use of FADs in the bigeye tuna fishery (see management section).</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.5 g",
    "Selenium": "160% daily value",
    "Serving Weight": "113 g",
    "Servings": "1",
    "Sodium": "70 mg",
    "Source": "<p>U.S. wild-caught from Massachusetts to Florida.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Bigeye tuna has a mild, meaty flavor, with a higher fat content than <a href=\"https://www.fishwatch.gov/profiles/atlantic-yellowfin-tuna\">yellowfin&nbsp;tuna</a>. Sashimi lovers prefer it.</p>\n",
    "Texture": "<p>Firm and moist with large flakes.</p>\n",
