    "Location": "<ul>\n<li>Albacore tuna live in tropical and warm temperate waters in the Atlantic, Pacific, and Indian Oceans. In the Atlantic, theyre found from Nova Scotia to northern Argentina, and from Ireland to South Africa.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic, Southeast",
    "Population": "Above target population level.",
    "Population Status": "<ul>\n<li>According to the 2020&nbsp;stock assessment, North Atlantic albacore tuna are not overfished, have rebuilt to target population levels, and are not subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10950\">Stock SMART</a>.</li>\n<li>The <a href=\"http://www.iccat.int/en/\" rel=\"external\">International Commission for the Conservation of Atlantic Tunas</a>&nbsp;(ICCAT) assesses the abundance of North Atlantic albacore tuna and evaluates the sustainability of current and proposed harvest practices. They use the scientific information from these assessments to make management recommendations.</li>\n<li>An international rebuilding plan was put in place in 2010. The stock was declared rebuilt in 2016, and since then has been managed under a conservation and management program.</li>\n<li>In 2017, ICCAT adopted an interim Harvest Control Rule (HCR) for North Atlantic&nbsp;albacore for 2018-2020, consistent with scientific advice, with the goal of adopting a long-term HCR following further Management Strategy Evaluation testing over the next few years.</li>\n</ul>\n",
    "Scientific Name": "Thunnus alalunga ",
    "Species Aliases": "<a href=\"/species-aliases/northern-albacore\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Northern albacore</a>, <a href=\"/species-aliases/longfin-tuna\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Longfin tuna</a>, <a href=\"/species-aliases/germon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Germon</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/atlantic_albacore_tuna_0.png",
    "alt": "Atlantic albacore tuna",
    "title": ""
    },
    "Species Name": "North Atlantic Albacore Tuna",
    "Animal Health": null,
    "Availability": "<p>Fresh year-round, with peaks in the fall, and frozen year-round.</p>\n",
    "Biology": "<ul>\n<li>Albacore grow relatively fast, up to more than 4 feet and 88 pounds.</li>\n<li>In the Atlantic, they live up to 13 years and are able to reproduce by age 5.</li>\n<li>In the spring and summer, albacore spawn in subtropical waters of the Atlantic and throughout the Mediterranean Sea. Depending on their size, females have between 2 million and 3 million eggs per spawning season.</li>\n<li>Similarly sized albacore travel together in schools that can be up to 19 miles wide. Schools of albacore also sometimes include other tuna species such as skipjack, yellowfin, and bluefin.</li>\n<li>Albacore constantly swim with their mouths open in order to breathe because they lack the structures needed to pump oxygen-rich water over their gills.</li>\n<li>They have unique biological characteristics that enable them to swim at speeds over 50 miles per hour and cover vast areas during annual migrations:\n<ul>\n<li>Torpedo-shaped bodies, smooth skin, and streamlined fins.</li>\n<li>Highly evolved circulatory system that regulates body temperature and increases muscle efficiency.</li>\n<li>High metabolism.</li>\n<li>High blood pressure, volume, and hemoglobin, all of which increase oxygen absorption.</li>\n</ul>\n</li>\n<li>Albacore tuna feed near the top of the food chain, preying upon a variety of fish, crustaceans, and squid.</li>\n<li>They are prey for many top predators, including sharks, rays, larger tunas, and billfishes.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "150",
    "Carbohydrate": "0 g",
    "Cholesterol": "45 mg",
    "Color": "<p>Lightest-colored meat of all the tunas  it can range from light beige to almost brown when raw, but becomes off-white when cooked. When canned, its the only tuna meat allowed to be labeled white meat.</p>\n",
