    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>U.S. fishermen catch approximately&nbsp;1&nbsp;percent of the total international catch of North Atlantic albacore.</li>\n<li>In 2020, U.S. commercial landings of North Atlantic albacore totaled approximately 614,000 pounds and were valued at $770,000, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:17024088696573::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Albacore in the North Atlantic are most often harvested incidentally in the commercial pelagic longline fishery for swordfish and other tunas. U.S. commercial fishermen also use rod-and-reel gear to catch albacore tuna.</li>\n<li>Fishing gear used to catch albacore has no impact on habitat because its used in the water column and doesnt come into contact with the ocean floor.</li>\n<li>Rod-and-reel gear is very selective and bycatch is minimal. U.S. commercial fishermen fishing with pelagic longline gear follow a number of strict regulations to prevent incidentally catching marine mammals, sea turtles, and birds.</li>\n<li>NOAA Fisheries has taken the following additional measures to prevent bycatch and sustainably manage this fishery. Fishermen are:\n<ul>\n<li>Required to use large circle hooks and certain types of bait that limit gear interactions with sea turtles. Circle hooks are specifically designed to minimize the damage caused by hooking, giving animals that are captured and released a better chance at survival.</li>\n<li>Trained to use special techniques to safely dehook and release any incidentally caught turtles.</li>\n<li>Required to stop fishing and move 1 nautical mile if they encounter a protected species.</li>\n<li>Required to protect pilot whales and Risso's dolphins when fishing in the Mid-Atlantic Bight by limiting the length of their lines to 20 nautical miles and posting <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/safe-handling-release-and-identification-workshops\">marine mammal handling/release guidelines</a>&nbsp;on their vessels. In addition, if fishing in the Cape Hatteras Special Research Area, pelagic longliners must contact NOAA Fisheries at least 48 hours prior to a trip and carry observers if requested.</li>\n<li>Required to use <a href=\"https://www.fisheries.noaa.gov/action/pelagic-longline-bluefin-tuna-area-based-and-weak-hook-management-measures\">weak hooks</a>&nbsp;from January through June in the Gulf of Mexico to reduce incidental catch of bluefin tuna and prohibited from using live bait to reduce bycatch of billfish.</li>\n<li>Restricted from areas of the Gulf of Mexico to reduce bycatch of all species.</li>\n<li>Required to carry <a href=\"https://www.fisheries.noaa.gov/topic/enforcement#vessel-monitoring\">vessel monitoring systems</a> onboard their boats to ensure compliance with these closures.</li>\n<li>Required to carry at-sea fisheries observers upon request. NOAA Fisheries reviews observer data to monitor protected species interactions and takes appropriate action as necessary.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational fishing for highly migratory species such as albacore tuna provides significant economic benefits to coastal communities through individual angler expenditures, recreational charters, tournaments, and the shoreside businesses that support those activities. Federal permits are required.</li>\n<li>In 2020, recreational anglers landed approximately 872,000 pounds of North Atlantic albacore, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:3750059992269::NO:RP::\">recreational fishing landings database</a>.&nbsp;&nbsp;</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Low in fat and cholesterol with the most omega-3 fatty acids of any of the tuna species.&nbsp;More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Albacore tuna are metallic, dark blue on the back with dusky to silvery white coloration along the sides of the belly.</li>\n<li>They have exceptionally long pectoral fins, which are nearly half the length of their bodies.</li>\n<li>The edge of the tail fin is white.</li>\n</ul>\n",
    "Production": null,
    "Protein": "31 g ",
    "Quote": "U.S. wild-caught North Atlantic albacore tuna is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#79b5dc",
    "Research": "<ul>\n<li>NOAA Fisheries scientists continue to research improvements in how they model changes in the Atlantic albacore tuna population and the impact of fishing on the resource.</li>\n</ul>\n",
