    "Fishery Management": "<ul>\n<li>We manage a single stock of Acadian redfish in U.S. waters.</li>\n<li><a href=\"http://www.greateratlantic.fisheries.noaa.gov/sustainable/species/multispecies/\">NOAA Fisheries</a> and the <a href=\"https://www.nefmc.org/\">New England Fishery Management Council</a> manage the fishery.</li>\n<li>Redfish, along with other groundfish in New England waters, are managed under the <a href=\"https://www.nefmc.org/management-plans/northeast-multispecies\">Northeast Multispecies Fishery Management Plan</a>, which includes:\n<ul>\n<li>Permitting requirements for commercial vessels.</li>\n<li>Separate management measures for recreational vessels.</li>\n<li>Time/Area Closures to protect spawning fish and habitat.</li>\n<li>Minimum fish sizes to prevent harvest of juvenile fish.</li>\n<li>Annual catch limits, based on best available science.</li>\n<li>An optional sector (<a href=\"https://www.fisheries.noaa.gov/national/laws-and-policies/catch-shares\">catch share</a>) program can be used for cod and other groundfish species. The sector program allows fishermen to form harvesting cooperatives and work together to decide when, where, and how they harvest fish.</li>\n</ul>\n</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>Acadian redfish are found over rocky, mud, or clay ocean bottoms.</li>\n<li>Off New England they are most common in the deep waters of the Gulf of Maine (to depths of 975 feet).</li>\n<li>They tend to move off the bottom at night to feed and move closer to shore in the winter.</li>\n</ul>\n",
    "Habitat Impacts": "Area closures and gear restrictions protect habitat that are affected by some kinds of trawl gear.",
