    "Population Status": "<ul>\n<li>According to the 2020&nbsp;stock assessment, Acadian redfish are not overfished and&nbsp;not subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10455\">Stock SMART</a>.</li>\n</ul>\n",
    "Scientific Name": "Sebastes fasciatus",
    "Species Aliases": "<a href=\"/species-aliases/redfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Redfish</a>, <a href=\"/species-aliases/ocean-perch\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Ocean perch</a>, <a href=\"/species-aliases/labrador-redfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Labrador redfish</a>",
