    "Species Name": "Acadian Redfish",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Acadian redfish are slow-growing, long-lived fish.</li>\n<li>Redfish can grow up to 18 to 20 inches long and live 50 years or more.</li>\n<li>They mature at a late age (5 to 6 years) and have low reproductive rates.</li>\n<li>They mate in late autumn and early winter.</li>\n<li>Redfish give birth to live young (an unusual feature for fish), and fertilization, incubation, and hatching of eggs all occur within the females body.</li>\n<li>Eggs are not fertilized until spring and then incubate for 45 to 60 days. Females release their hatched larvae from late spring through July and August.</li>\n<li>Females generally produce between 15,000 and 20,000 larvae per spawning cycle.</li>\n<li>Newly hatched redfish can swim well at birth and are soon able to forage for plankton (tiny floating plants and animals).</li>\n<li>Their survival rate is relatively high compared with that of egg-laying fish.</li>\n<li>Young redfish stay in the upper waters feeding on small crustaceans until they are about 2 inches long.</li>\n<li>In the fall, the young settle to the ocean bottom.</li>\n<li>Older redfish feed on larger invertebrates and small fish.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
