    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial landings of Acadian redfish totaled more than 11.7 million pounds and were valued at approximately $6.2 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>NOAA Fisheries has been working with fishermen to increase opportunities to harvest redfish.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Redfish are most commonly harvested using trawl nets, although they are sometimes also caught using gillnets, bottom longline, and rod and reel.</li>\n<li>Area closures and gear restrictions reduce habitat impacts from trawl nets.</li>\n<li>Fishermen follow management measures to designed to reduce interactions with marine mammals, including gear modifications, seasonal closures, and use of marine mammal deterrents.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Acadian redfish are not a common target of anglers but may be encountered when targeting other groundfish species like cod and haddock.\n<ul>\n<li><a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/recfishing/regs/index.html\">Regulations</a> are limited to a minimum fish size in federal waters.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Redfish is low in saturated fat. Its a good source of niacin, vitamins B6 and B12, calcium, protein, phosphorus, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Acadian redfish are orange to flame red, with paler underbellies.</li>\n<li>They have a flattened body that is longer than it is deep.</li>\n<li>They have large eyes and a large mouth lined with many small teeth.</li>\n<li>They have one continuous dorsal fin that runs from the nape of their neck to their caudal peduncle (where the body meets the tail) and a small tail fin.</li>\n<li>Young redfish are marked with patches of black and green pigment. They dont develop their red pigment until after they move to the ocean bottom.</li>\n</ul>\n",
    "Production": null,
    "Protein": "15.3 g",
    "Quote": "U.S. wild-caught Acadian redfish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#c43b22",
    "Research": "<ul>\n<li>Scientists at NOAAs <a href=\"http://www.nefsc.noaa.gov/\">Northeast Fisheries Science Center</a> conduct bottom trawl surveys every year during the fall and spring in inshore and offshore areas off the northeast coast to assess and monitor the abundance of redfish and other species. Managers use these data along with information from Canadian, state agency, and university-run surveys to determine the status of the redfish stock.</li>\n<li>Funded by the Northeast Fisheries Science Center's Cooperative Research Program, researchers, regulators, and industry members are conducting a project called <a href=\"http://www.nefmc.org/research/cte_mtg_docs/120625/Rednet%20Report/FInalComponent2.pdf\">REDNET: A Network to Redevelop a Sustainable Redfish (<em>Sebastes fasciatus</em>) Trawl Fishery in the Gulf of Maine</a>. The primary objective of this project is to devise strategies and means to efficiently harvest the redfish resource in the Gulf of Maine while avoiding non-target catch.</li>\n</ul>\n",
