    "Location": "<ul>\n<li>Skipjack tuna are found in tropical, subtropical, and warm temperate waters of all oceans. In the western Atlantic, skipjack is found from Massachusetts to Brazil, including in the Gulf of Mexico and the Caribbean.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic, Southeast",
    "Population": "Above target population level.",
    "Population Status": "<ul>\n<li>According to the 2014 stock assessment, Atlantic skipjack tuna is&nbsp;not overfished and&nbsp;not subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10158\">Stock SMART</a>.</li>\n<li>The <a href=\"http://www.iccat.int/en/\" rel=\"external\">International Commission for the Conservation of Atlantic Tunas</a> (ICCAT) assesses the abundance of Atlantic skipjack tuna and evaluates the sustainability of current and proposed harvest practices. They use the scientific information from these assessments to make management recommendations.</li>\n<li>Its difficult to assess the abundance of skipjack tuna due to its unique biological and fishery characteristics. Various assessment methods are used for skipjack tuna to overcome these difficulties, and several fishery indicators are analyzed for evidence of changes in the state of the stock over time.</li>\n</ul>\n",
    "Scientific Name": "Katsuwonus pelamis",
    "Species Aliases": "<a href=\"/species-aliases/tuna\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Tuna</a>, <a href=\"/species-aliases/ocean-bonito\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Ocean bonito</a>, <a href=\"/species-aliases/lesser-tuna\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Lesser tuna</a>, <a href=\"/species-aliases/aku\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Aku</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/atlantic_skipjack_tuna_0.png",
    "alt": "Illustration of an Atlantic Skipjack Tuna",
    "title": "Atlantic Skipjack Tuna"
    },
    "Species Name": "Atlantic Skipjack Tuna",
    "Animal Health": null,
    "Availability": "<p>Fresh in late summer through early fall, and frozen and canned year-round.&nbsp;</p>\n",
    "Biology": "<ul>\n<li>Like other tropical tunas, skipjack tuna grow fast, up to over 3 feet and 40 pounds, and have a relatively short life span, around 7 years.</li>\n<li>In the eastern Atlantic, skipjack are able to reproduce when theyre 1 year old. They spawn throughout the year in tropical waters and seasonally (spring to early fall) in subtropical areas.</li>\n<li>Depending on their size, females can produce between 100,000 and 2 million eggs per year.</li>\n<li>Skipjack spawn more than once a season, as often as once per day.</li>\n<li>Once fertilized, the eggs hatch in about 1 day (depending on the temperature).</li>\n<li>Skipjack tuna are opportunistic feeders, preying on a variety of fish (e.g., herrings), crustaceans, cephalopods, mollusks, and sometimes other skipjack tunas.</li>\n<li>Large pelagic fishes such as billfish, sharks, and other large tunas prey on skipjack tuna.</li>\n</ul>\n",
    "Bycatch": "Bycatch is minimal because rod-and-reel gear and handlines are very selective.",
    "Calories": "103",
    "Carbohydrate": "0 g",
    "Cholesterol": "47 mg",
    "Color": "<p>When raw, good-quality skipjack tuna meat is deep red. Smaller fish are lighter red. Cooked skipjack becomes light gray.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
    "Farming Methods": null,
    "Farming Methods_": null,
    "Fat, Total": "1.01 g",
