    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>Skipjack tuna are caught by U.S. vessels in the western North Atlantic, but are only a minor component of total U.S. tuna landings and a very small percentage of the total international landings of western Atlantic skipjack tuna.</li>\n<li>In 2020, the United States contributed less than one percent of the total western Atlantic skipjack landings.</li>\n<li>In 2020, commercial landings of Atlantic skipjack tuna totaled 1,300 pounds and were valued at approximately $1,300, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:28806357993474::NO:::\">commercial fishing landings database</a>.</li>\n<li>Almost all of the U.S. commercial harvest of skipjack tuna comes from the <a href=\"https://www.fishwatch.gov/profiles/pacific-skipjack-tuna\">Pacific</a>. Much of it is exported to foreign markets.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>In the Atlantic, U.S. commercial fishermen mainly use handgear to harvest skipjack tuna.</li>\n<li>Handgear includes rod-and-reel and handline gear, which consists of a mainline that is retrieved by hand with no more than two hooks attached.</li>\n<li>Handgear is highly selective and does not contact the ocean floor.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>U.S. recreational fishermen account for more of the total catch of skipjack tuna in the western Atlantic than commercial fishermen.</li>\n<li>In 2020, recreational landings of skipjack tuna totaled approximately 93,000 pounds, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:10548459952340::NO:RP::\">recreational fishing landings database</a>.</li>\n<li>Recreational fishermen must have a permit to fish for skipjack tuna.</li>\n<li>Recreational fishing for highly migratory species such as skipjack tuna provides significant economic benefits to coastal communities through individual angler expenditures, recreational charters, tournaments, and the shoreside businesses that support those activities.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Skipjack is an excellent source of low-fat protein.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Skipjack tuna do not have scales except on the corselet (a band of large, thick scales behind the head) and the faint lateral line running lengthwise down each side of the fish.</li>\n<li>Their back is dark purplish blue, and their lower sides and belly are silvery with four to six conspicuous longitudinal dark bands, which may look like continuous lines of dark blotches.</li>\n</ul>\n",
    "Production": null,
    "Protein": "22.00 g",
    "Quote": "U.S. wild-caught Atlantic skipjack tuna is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#414551",
    "Research": "<ul>\n<li>Skipjack often school around floating objects, including <a href=\"https://www.fisheries.noaa.gov/national/bycatch/fishing-gear-fish-aggregating-devices\">fish aggregating devices</a> (FADs) intentionally placed in the water to attract fish. Foreign purse seiners and baitboats have used FADs extensively in the Atlantic since the early 1990s to catch skipjack, primarily off the coast of West Africa. Researchers have found that the increasing use of FADs has changed the species composition of tuna catches, resulting in a higher capture rate of juvenile <a href=\"https://www.fishwatch.gov/profiles/atlantic-yellowfin-tuna\">yellowfin</a> and <a href=\"https://www.fishwatch.gov/profiles/atlantic-bigeye-tuna\">bigeye</a>&nbsp;tuna for vessels targeting skipjack. The increased presence of FADs may also have an impact on the biology and ecology of skipjack, bigeye, and yellowfin tuna. Also, sea turtles and marine mammals can become entangled in nets, ropes, and lines that are used in the FADs.</li>\n</ul>\n",
