    "Fishery Management": "<ul>\n<li><a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/species/scallop/\">NOAA Fisheries</a> and the <a href=\"https://www.nefmc.org/\">New England Fishery Management Council</a>&nbsp;manage the Atlantic sea scallop fishery.</li>\n<li>Managed under the <a href=\"https://www.nefmc.org/management-plans/scallops\">Atlantic Sea Scallop Fishery Management Plan</a>:\n<ul>\n<li>Managers determine a total allowable catch for the scallop fishery based on estimates of the scallop population. They allocate this catch amount to different groups of the fishery, depending on their permit type and historical catch, through days-at-sea and number of trips to special access areas.</li>\n<li>Other management measures include:\n<ul>\n<li>Limits on crew size.</li>\n<li>Areas closed to scallop dredging to allow young scallops to grow large and reproduce, and to reduce bycatch of non-targeted species.</li>\n<li>Vessels harvesting scallops must use <a href=\"https://www.fisheries.noaa.gov/topic/enforcement/vessel-monitoring\">vessel monitoring systems</a> (a satellite communications system used to monitor fishing activities).</li>\n<li>Individual Fishing Quotas (IFQs), a type of <a href=\"https://www.fisheries.noaa.gov/national/laws-and-policies/catch-shares\">catch share</a> program, for Limited Access General Category permit holders.</li>\n</ul>\n</li>\n<li>There is a sea scallop fishery in the Gulf of Maine that operates in both federal and state waters. This fishery primarily occurs in Maine state waters and is managed by the state through gear and seasonal restrictions and rotational closures. The federal component of the fishery is managed through daily catch limits and gear restrictions.</li>\n</ul>\n</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>Adult scallops live close together in groups called beds, on sandy or gravelly parts of the ocean floor.</li>\n<li>They are generally found at depths of about 100 to 300 feet on Georges Bank and in the Mid-Atlantic.</li>\n<li>Sea scallops can be found in shallower waters in Maine and Canada.</li>\n</ul>\n",
    "Habitat Impacts": "Area closures and gear restrictions protect habitat that are affected by some kinds of trawl and dredge gear.",
