    "Population Status": "<ul>\n<li>According to the 2020&nbsp;stock assessment, Atlantic sea scallops are not overfished and are not subject to overfishing.&nbsp;Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10786\">Stock SMART</a>.</li>\n</ul>\n",
