    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Sea scallops can live up to 20 years. They grow quickly for the first few years of their life.</li>\n<li>The largest scallop ever reported was about 9 inches in shell height, but they typically dont grow larger than 6 inches.</li>\n<li>Sea scallops can reproduce by age 2, but dont produce many eggs or sperm until they are about 4 years old.</li>\n<li>They are very fertilea female sea scallop can produce hundreds of millions of eggs per year. For this reason, scallops may respond more rapidly to management actions than species that reproduce slowly and in small numbers.</li>\n<li>Sea scallops usually spawn in late summer or early fall. They also may spawn in the spring, especially in the Mid-Atlantic Bight.</li>\n<li>After hatching, scallop larvae remain in the water column for 4 to 6 weeks before settling on the ocean floor.</li>\n<li>Sea scallops feed by filtering phytoplankton or other small organisms out of the water column, which can actually help to improve water quality by removing suspended materials.</li>\n<li>Many kinds of pelagic fish and invertebrates eat scallop larvae.</li>\n<li>Cod, wolffish, eel pout, flounder, crabs, lobster, sea turtles, and sea stars feed on juvenile and adult scallops.</li>\n<li>Using its adductor muscle to snap its top and bottom shells open and shut, a sea scallop can propel itself through the water. This helps them escape predators, such as sea stars, that other bivalves like mussels, clams, and oysters cant avoid.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "88",
