    "Cholesterol": "33 mg",
    "Color": "<p>Raw scallops are shiny and creamy white, sometimes with an orange or pinkish tint (a natural variation that does not affect taste or quality). High-quality scallops have an ivory translucence and should keep their shape.</p>\n",
