    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of Atlantic sea scallop totaled almost&nbsp;49 million pounds of sea scallop meats and were valued at approximately&nbsp;$486 million,&nbsp;according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>.</li>\n<li>Scallop vessels from Massachusetts, Virginia, and New Jersey are responsible for the majority of the U.S. harvest.</li>\n<li>The principal U.S. commercial fisheries for sea scallop are in the Mid-Atlantic (from Virginia to Long Island, New York) and on Georges Bank and neighboring areas, such as the Great South Channel and Nantucket Shoals. There is also a small, primarily inshore fishery for sea scallops in the Gulf of Maine.</li>\n<li>The U.S. sea scallop fishery is extremely important to the U.S. economy and is the largest wild scallop fishery in the world.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>U.S. commercial fishermen harvest sea scallops year-round, primarily using turtle deflectorstyle scallop dredges that catch scallops much like rakes collect leaves.</li>\n<li>A small number of fishermen use otter trawls, mostly in the Mid-Atlantic.</li>\n<li>Divers and Digby dredges are sometimes used in near-shore areas in the Gulf of Maine.</li>\n<li>The bottom fishing gears used to harvest scallops, such as dredges and trawls, can remove some bottom habitatforming organisms, including tubeworms and sponges.</li>\n<li>Managers have implemented a variety of measures to protect habitat from potential impacts of <a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/species/scallop/\">fishing gear</a>:\n<ul>\n<li>Several areas are closed year-round to harvesting scallops to protect sensitive habitat.</li>\n<li>Managers have implemented a rotational access area program, which restricts where and when scallop vessels can fish, benefitting both habitat and scallop populations.</li>\n<li>Fishermen use 4-inch rings in their scallop dredges that increase the dredges efficiency, catching larger scallops and allowing smaller scallops and other small marine life to return to the sea floor by passing through the dredge rings. The 4-inch rings reduce the amount of time dredges contact the bottom.</li>\n</ul>\n</li>\n<li>Sea turtles, finfish (such as yellowtail flounder, skates, and monkfish), and undersized scallops can be incidentally caught in the scallop fishery.</li>\n<li>Managers seasonally prohibit fishing in areas where finfish species congregate, reducing catch of these untargeted species.</li>\n<li>Scallop fishermen receive annual allocations of windowpane and yellowtail flounder that they can catch. If the catches are exceeded, managers will implement measures to minimize catch of these species in a future fishing year (e.g., gear restrictions and seasonal closures).</li>\n<li>Scallop vessels in the Mid-Atlantic must use a <a href=\"https://www.fisheries.noaa.gov/action/atlantic-sea-scallop-turtle-deflector-dredge-and-chain-mat-regulated-areas\">turtle deflector dredge&nbsp;and chain mat</a>&nbsp;when and where sea turtles occur on scallop grounds. The turtle deflector dredge excludes sea turtles from being caught in the dredge and prevents serious injuries to the turtles.</li>\n<li>Scallop dredges using twine-tops must adhere to gear modifications designed to allow fish to escape.</li>\n<li>Research and experimentation with different techniques and gears, as well as education efforts, have helped reduce bycatch.</li>\n<li>Managers and the fishing industry continue to collaborate to redesign scallop dredges to reduce flounder bycatch and to minimize injuries to and capture of sea turtles.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Scallops are a good low-fat source of protein and are high in selenium and B vitamins.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Scallops are bivalves (having two shells), like clams and oysters.</li>\n<li>The shells are held together by the adductor muscle (the part of the scallop Americans typically eat).</li>\n<li>Sea scallops have a saucer-shaped shell with scalloped or fluted edges.</li>\n<li>The upper shell is usually reddish-pink or brown in color. The lower shell is white or cream.</li>\n<li>A small percentage (5-10 percent) of sea scallops are albinos, with white upper and lower shells.</li>\n<li>Sea scallop shells are smooth and lack the prominent ribbing that is characteristic of most other scallop shells. It is thought that the sea scallops smooth shell is an adaptation to allow it to propel itself faster and farther.</li>\n</ul>\n",
    "Production": null,
    "Protein": "16.7 g",
    "Quote": "U.S. wild-caught Atlantic sea scallop is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#a06840",
    "Research": "<ul>\n<li>Scientists from NOAAs <a href=\"https://www.fisheries.noaa.gov/about/northeast-fisheries-science-center\">Northeast Fisheries Science Center</a> have surveyed the Atlantic sea scallop population off the northeastern U.S. coast between North Carolina and Massachusetts every year since 1979. They divide the survey area into zones of various depths and habitat, then tow a dredge and camera to randomly sample and document the marine life and other conditions in these zones. After each tow, they sort, count, and measure their catch. Dredge catches provide relative indices of the average density of animals, along with some information about bottom type and habitat.</li>\n<li>Scientists have recently started using a new undersea camera called the <a href=\"http://habcam.whoi.edu/\" rel=\"external\">HabCam</a>&nbsp;in scallop surveys to supplement the dredge data. The HabCam (short for Habitat Mapping Camera System) was developed by scientists at Woods Hole Oceanographic Institution working with Cape Cod scallop fishermen. Images from the HabCam supply absolute densities of scallops and other species, are less labor intensive in terms of crew, and provide much more information about the bottom habitat, all in near-real time. Using both the annual dredge surveys and the HabCam provides a more complete picture of the scallop population and its habitat.</li>\n<li>Scientists use information from these surveys along with data from other surveys and the commercial fishery to estimate abundance (biomass) and assess the health of the population and the sustainability of the fishery.</li>\n<li>Under the <a href=\"https://www.fisheries.noaa.gov/new-england-mid-atlantic/funding-and-financial-services/research-set-aside-programs\">Scallop Research Set-Aside Program</a>, 1.25 million pounds (567 metric tons) of the allowed scallop harvest is set aside each year to fund scallop and habitat research and surveys to provide better information for future management decisions. This is not a federally-funded program. Participating scallop vessels fund research through the sale of the scallops they harvest. Past projects have focused on gear research to minimize bycatch of finfish, sea turtles, and small scallops; surveys of rotational access areas and other areas; studies of scallop biology;&nbsp;and development of survey technologies.</li>\n</ul>\n",
