    "Location": "<ul>\n<li>Atlantic salmon are the only salmon native to the Atlantic Ocean.</li>\n<li>There are three groups of wild Atlantic salmon: North American, European, and Baltic. The North American group, including the Canadian and U.S. populations, was historically found from northern Quebec southeast to Newfoundland and southwest to Long Island Sound.</li>\n<li>In the United States, Atlantic salmon were once native to almost every river north of the Hudson River. Due to the effects of industrial and agricultural development (including habitat destruction, dams, and historic overfishing), most populations native to New England were eradicated. Now, the only native populations of Atlantic salmon in the United States are found in Maine.</li>\n</ul>\n<p><img alt=\"A map showing the geographic range of Altantic Salmon.\" height=\"495\" width=\"640\" class=\"media-element file-default\" data-delta=\"1\" typeof=\"foaf:Image\" src=\"https://www.fishwatch.gov/sites/default/files/atlantic_salmon_migration_route_large.jpg\" /></p>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "Significantly below target population levels. Rebuilding plan is in place.",
    "Population Status": "<ul>\n<li>The U.S. Atlantic Salmon Assessment Committee, a team of state and federal biologists, collects data on Atlantic salmon throughout New England and assesses the species population status. According to the 2020&nbsp;stock assessment Atlantic salmon are overfished and returns remain at historically low levels. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10498\">Stock SMART</a>.</li>\n<li>Scientists determine the population status by counting the number of adults that return to spawn, either directly at traps and weirs or indirectly using nest surveys and modeling.</li>\n<li>Atlantic salmon stocks began to decline in the mid-1800s due to a number of factors including habitat destruction and historic overfishing.</li>\n</ul>\n",
    "Scientific Name": "Salmo salar",
    "Species Aliases": "<a href=\"/species-aliases/sea-run-salmon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sea run salmon</a>, <a href=\"/species-aliases/kelts\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Kelts</a>, <a href=\"/species-aliases/black-salmon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Black salmon</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/atlantic_salmon.png",
    "alt": "Illustration of an Atlantic Salmon.",
    "title": "Atlantic Salmon"
    },
    "Species Name": "Atlantic Salmon",
    "Animal Health": null,
    "Availability": "<p>Wild-caught is not available. <a href=\"https://www.fishwatch.gov/profiles/atlantic-salmon-farmed\">Farm-raised</a> is&nbsp;available year-round.</p>\n",
    "Biology": "<ul>\n<li>Atlantic salmon are anadromous  they leave the ocean to return to freshwater streams and rivers to breed.</li>\n<li>Females lay an average of 7,500 eggs in gravel nests, called redds.</li>\n<li>Eggs incubate slowly due to cold winter water temperatures. About 9 to 20 percent of the eggs survive to the fry stage.</li>\n<li>Fry remain buried in the gravel for about 6 weeks and emerge in mid-May.</li>\n<li>They quickly disperse from the redds and develop camouflaging stripes along their sides, entering the parr stage.</li>\n<li>Parr eventually undergo a physiological transformation called smoltification that prepares them for life in a marine habitat. During smoltification, fish imprint on the chemical nature of the stream or river to enable them to find their way back to where they were born. After smoltfication is complete in the spring, smolts migrate to the ocean to grow, feed, and mature.</li>\n<li>Atlantic salmon growth rates are variable and depend on several factors including season, habitat quality, age, sex, and population density.</li>\n<li>They grow much faster in saltwater than in freshwater. After 2 years at sea, adult salmon can grow to an average length of 28 to 30 inches and weight of 8 to 12 pounds.</li>\n<li>Unlike the Pacific salmon species, Atlantic salmon do not die after spawning, and adults can repeat the breeding cycle. They live for 4 to 6 years.</li>\n<li>Juvenile Atlantic salmon mostly prey on invertebrates and terrestrial insects while in freshwater and on amphipods (small, shrimp-like crustaceans), krill, and fishes while at sea.</li>\n<li>Larger adult Atlantic salmon mainly prey on fish such as Atlantic herring, alewife, rainbow smelt, capelin, mummichogs, sand lances, flatfish, and small Atlantic mackerel.</li>\n<li>Birds, marine mammals, and fish prey on Atlantic salmon.</li>\n</ul>\n",
    "Bycatch": "Not applicable  there is no commercial fishery for Atlantic salmon in the United States.",
