    "Fishing Rate": "Commercial and recreational fishing for Atlantic salmon is prohibited.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>There is no commercial catch of wild Atlantic salmon in the United States.</li>\n<li>Atlantic salmon were a highly prized game and food fish.</li>\n<li>They were caught by Native Americans before the first settlers arrived, and commercial fisheries for Atlantic salmon started in Maine during the 1600s.</li>\n<li>Around the time of the American Revolution, weirs (an enclosure of stakes set in a stream as a trap for fish) became the gear of choice in U.S. Atlantic salmon commercial fisheries and were modified as more effective materials and designs became available.</li>\n<li>Catches in Maine exceeded 90 metric tons in the late 1800s and 45 metric tons in some years during the early 1900s.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational fishermen have reportedly been angling for Atlantic salmon since 1832, when the first Atlantic salmon caught on rod-and-reel gear was captured in the Dennys River in Maine.</li>\n<li>Recreational fisheries are closed in the United States, with the exception of some landlocked fisheries for Atlantic salmon in New Hampshire, where fish retired from hatchery broodstock are released for angling.</li>\n</ul>\n</li>\n</ul>\n",
