    "Physical Description": "<ul>\n<li>Atlantic salmon have a spindle-like body shape  rounded, broad in the middle, and tapered at each end. The shape is somewhat flattened toward the sides, which is typical of salmon species.</li>\n<li>The head is relatively small, about one-fifth of the body length. The underside paired fins are prominent, especially on juveniles.</li>\n<li>Spawning adults darken to a bronze color after entering freshwater and darken further after they spawn. When spawning has been completed, they are often referred to as kelts or black salmon.&nbsp; Their silver color returns after they re-enter the sea.</li>\n</ul>\n",
    "Production": null,
    "Protein": "19.84 g",
    "Quote": "Commercial and recreational fishing for Atlantic salmon in the United States is prohibited. Atlantic salmon found in the market is farm-raised.",
    "Quote Background Color": "#807e92",
    "Research": "<ul>\n<li>For salmon in Maine, most current research focuses on two priorities  the impact of dams on populations and survival of salmon in the ocean. See <a href=\"https://www.nefsc.noaa.gov/salmon/\">NOAA Fisheries Northeast Salmon Team</a>.</li>\n<li>Atlantic salmon may be facing new challenges in the Gulf of Maine due to changing environmental conditions. Increasing numbers of hatchery-raised Atlantic salmon smolts are entering the ocean via the Gulf of Maine, but fewer are returning to spawn, raising questions as to what is happening to these fish at sea. A <a href=\"https://onlinelibrary.wiley.com/doi/full/10.1111/j.1365-2400.2011.00814.x\">2011 study</a> co-authored by a NOAA Northeast Fisheries Science Center researcher suggests that changing spring wind patterns, warming sea surface temperatures, and new predators along altered migration routes are affecting the survival of Atlantic salmon.</li>\n<li><a href=\"https://www.fisheries.noaa.gov/feature-story/learning-about-atlantic-salmon-greenland-satellite-tags\">Learning about Atlantic Salmon in Greenland via Satellite Tags</a></li>\n</ul>\n",
