    "Species Name": "Atlantic Pollock",
    "Animal Health": null,
    "Availability": "<p>Year-round. Peak landings are from November through January.</p>\n",
    "Biology": "<ul>\n<li>Atlantic pollock grow fast at first until they sexually mature between the ages of 3 and 6.</li>\n<li>They grow to more than 3  feet long and 35 pounds and can live a long time, up to 23 years.</li>\n<li>Atlantic pollock spawn from November through February over hard, stony, or rocky ocean bottoms in areas throughout the Gulf of Maine and Georges Bank.</li>\n<li>They spawn multiple times per season.</li>\n<li>Pollock eggs rise into the water column after they are released and fertilized.</li>\n<li>Smaller pollock in inshore waters feed on small crustaceans and small fish. Larger pollock mainly prey on fish.</li>\n<li>A variety of fish eat juvenile pollock. Spiny dogfish, monkfish, and other pollock prey on adults.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
