    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of Atlantic pollock totaled approximately 7.8 million pounds and were valued at $6.5 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:20028360240338::NO:RP::\">commercial fishing landings database</a>.</li>\n</ul>\n<p>\t\tGear types, habitat impacts, and bycatch:</p>\n<ul>\n<li>Pollock are commonly harvested using trawl nets, gillnets, bottom longlines, and rod and reel.</li>\n<li>Gillnets, longlines, and rod and reel used to harvest pollock have little to no impact on habitat.</li>\n<li>Areas closures and gear restrictions reduce habitat impacts from trawl nets.</li>\n<li>Fishermen follow management measures to designed to reduce interactions with marine mammals, including gear modifications, seasonal closures, and use of marine mammal deterrents.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Pollock is growing in popularity with anglers that have traditionally targeted other groundfish like cod and haddock. Anglers target pollock from boats and shore using both lures and bait. Successful anglers find that the fish puts up a spirited fight. Fishing occurs year-round.</li>\n<li><a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/recfishing/regs/index.html\">Regulations</a> are limited to a minimum fish size in federal waters.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Atlantic pollock is very low in saturated fat and is a very good source of protein, vitamin B12, phosphorus, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Atlantic pollock are brownish-green on the back and slightly pale on the belly.</li>\n<li>They have a small chin barbel, like the whiskers on a catfish.</li>\n<li>They are a member of the cod family but can be distinguished by their greenish hue and darker flesh.</li>\n</ul>\n",
    "Production": null,
    "Protein": "19.44 g",
    "Quote": "U.S. wild-caught Atlantic pollock is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#4b5657",
    "Research": "<ul>\n<li>Scientists at NOAAs <a href=\"https://www.nefsc.noaa.gov/\">Northeast Fisheries Science Center</a> conduct bottom trawl surveys every year during the fall and spring in inshore and offshore areas off the northeast coast to monitor the abundance of pollock and other species. They use these data, along with data from surveys conducted by the state fisheries management agencies and fishery statistics, to determine the status of the pollock stocks.</li>\n<li><a href=\"https://www.nefsc.noaa.gov/saw/sasi/sasi_report_options.php\">2019&nbsp;Northeast Groundfish Operational Assessments website</a></li>\n</ul>\n",
