    "Species Name": "Atlantic Yellowfin Tuna",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Yellowfin tuna grow fairly fast, up to 400 pounds, and have a somewhat short life span of about 7 years.</li>\n<li>Most yellowfin tuna are able to reproduce when they reach age 2 or 3.</li>\n<li>In the western Atlantic, they spawn from May to August in the Gulf of Mexico and from July to November in the southeastern Caribbean. In the eastern Atlantic, they spawn from October to March in the Gulf of Guinea and from April to June off Senegal.</li>\n<li>Females spawn about once every 3 days during the spawning season. They produce an average of 1 million to 4 million eggs each time they spawn.</li>\n<li>Yellowfin tuna feed near the top of the food chain on fish, squid, and crustaceans.</li>\n<li>They are prey for top predators such as sharks and large fish.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "109",
    "Carbohydrate": "0 g",
    "Cholesterol": "39 mg",
    "Color": "<p>Meat is bright red when raw and turns brown to grayish-tan when cooked.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
