    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>U.S. landings of Atlantic yellowfin tuna generally represent a small percentage (less than 5 percent)&nbsp;of the total international landings of Atlantic yellowfin tuna, although in 2020 they represented 13 percent.</li>\n<li>In 2020, commercial landings of Atlantic yellowfin tuna totaled more than 1.5 million pounds and were valued at $4.6 million, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:28806357993474::NO:::\">commercial fishing landings database</a>.</li>\n<li>Yellowfin tuna is the principal species of tropical tuna landed by U.S. fisheries in the western North Atlantic.</li>\n<li>The majority of U.S. commercial landings come from the longline fleet operating in the northwest Atlantic, Gulf of Mexico, and Caribbean.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>U.S. commercial fishermen primarily use longline, rod-and-reel, and handline gear to harvest Atlantic yellowfin tuna.</li>\n<li>Pelagic longline gear and handgear (handline and rod-and-reel) used to catch yellowfin tuna have no impact on habitat because theyre used in the water column and dont come into contact with the ocean floor.</li>\n<li>Handgear is very selective so bycatch is minimal, but pelagic longlines can incidentally catch other fish and protected species such as marine mammals and sea turtles.</li>\n<li>NOAA Fisheries has taken the following additional measures to prevent bycatch and sustainably manage this fishery. Fishermen are:\n<ul>\n<li>Required to use large circle hooks and certain types of bait that limit gear interactions with sea turtles. Circle hooks are specifically designed to minimize the damage caused by hooking, giving animals that are captured and released a better chance at survival.</li>\n<li>Trained to use special techniques to safely dehook and release any incidentally caught turtles.</li>\n<li>Required to stop fishing and move 1 nautical mile if they encounter a protected species.</li>\n<li>Required to protect pilot whales and Risso's dolphins when fishing in the Mid-Atlantic Bight by limiting the length of their lines to 20 nautical miles and posting <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/safe-handling-release-and-identification-workshops?_ga=2.56502568.2022413580.1600088945-1749182424.1566822859\">marine mammal handling/release guidelines</a> on their vessels. In addition, if fishing in the Cape Hatteras Special Research Area, pelagic longliners must contact NOAA Fisheries at least 48 hours prior to a trip and carry observers if requested.</li>\n<li>Required to use <a href=\"https://www.fisheries.noaa.gov/action/pelagic-longline-bluefin-tuna-area-based-and-weak-hook-management-measures?_ga=2.99541149.2022413580.1600088945-1749182424.1566822859\">weak hooks</a> in the Gulf of Mexico to reduce incidental catch of bluefin tuna and prohibited from using live bait to reduce bycatch of billfish.</li>\n<li>Restricted from areas of the Gulf of Mexico to reduce bycatch of all species.</li>\n<li>Required to carry <a href=\"https://www.fisheries.noaa.gov/topic/enforcement/vessel-monitoring\">vessel monitoring systems</a> onboard their boats to ensure compliance with these closures.</li>\n<li>Required to carry at-sea fisheries observers upon request. NOAA Fisheries reviews observer data to monitor protected species interactions and takes appropriate action as necessary.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>A high proportion of landings are rod-and-reel catches by recreational anglers.</li>\n<li>In 2020, recreational fishermen landed 12.8 million pounds of Atlantic yellowfin tuna, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:15392611375578::NO:RP::\">recreational fishing landings database</a>.</li>\n<li>Recreational fishermen enjoy catching yellowfin tuna because theyre large (up to 400 pounds) and they fight hard.</li>\n<li>Recreational fishermen must have a permit to catch yellowfin tuna.</li>\n<li>They have daily bag limits. However, catch, tag, and release is allowed.</li>\n<li>Recreational fishing for highly migratory species such as yellowfin tuna provides significant economic benefits to coastal communities through individual angler expenditures, recreational charters, tournaments, and the shoreside businesses that support those activities.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Yellowfin tuna is low in saturated fat and sodium and is a very good source of protein, thiamin, selenium, vitamin B6, and omega-3s.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Yellowfin tuna are torpedo-shaped.</li>\n<li>They are metallic dark blue on the back and upper sides, and change from yellow to silver on the belly.</li>\n<li>True to their name, their dorsal and anal fins and finlets are bright yellow.</li>\n<li>Yellowfin tuna can be distinguished from other tunas by their long, bright yellow dorsal fin and a yellow strip down the side. They are also more slender than bluefin tuna.</li>\n</ul>\n",
    "Production": null,
    "Protein": "24.4 g",
    "Quote": "U.S. wild-caught Atlantic yellowfin tuna is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#dfbd29",
    "Research": "<ul>\n<li>NOAA Fisheries scientists continue to research ways to improve the methods used to assess the yellowfin population.</li>\n<li>They will work with ICCAT in its large-scale tagging project to learn more about the lifecycle and migration patterns of Atlantic yellowfin and other Atlantic tropical tunas.</li>\n<li>Scientists from Texas A&amp;M University continue to analyze yellowfin tuna samples to help determine nursery areas within the Gulf of Mexico.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.172 g",
    "Selenium": "36.5 mcg",
    "Serving Weight": "100 g (raw)",
    "Servings": "1",
    "Sodium": "45 mg",
    "Source": "<p>U.S. wild-caught from Massachusetts to Texas.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Yellowfin tuna has a mild, meaty flavor. Its more flavorful than albacore, but leaner than <a href=\"https://www.fishwatch.gov/profiles/western-atlantic-bluefin-tuna\">bluefin tuna</a>.</p>\n",
    "Texture": "<p>The meat is firm and moist with large flakes.</p>\n",
