    "Location": "<ul>\n<li>Longfin squid are found from Newfoundland to the Gulf of Venezuela.</li>\n<li>In the northwest Atlantic Ocean, longfin squid are most abundant between Georges Bank and Cape Hatteras, North Carolina.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "Above target population level.",
    "Population Status": "<ul>\n<li>The species has a lifespan of less than one year. According to the 2020&nbsp;stock assessment, longfin squid are not overfished. There is currently not enough information to determine whether the stock is subject to overfishing.&nbsp;Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10792\">Stock SMART</a>.</li>\n</ul>\n",
    "Scientific Name": "Doryteuthis (Amerigo) pealeii",
    "Species Aliases": "<a href=\"/species-aliases/longfin-inshore-squid\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Longfin inshore squid</a>, <a href=\"/species-aliases/loligo\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Loligo</a>, <a href=\"/species-aliases/winter-squid\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Winter squid</a>, <a href=\"/species-aliases/boston-squid\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Boston squid</a>",
