    "Species Name": "Longfin Inshore Squid",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Longfin squid grow fast, up to 1.6 feet mantle length (large part of the squid in front of the head), but usually less than 1 foot.</li>\n<li>They have a short life span, reproducing right before they die at around six to eight months old.</li>\n<li>Their growth and development is highly sensitive to environmental conditions. Squid hatched in the summer grow faster than those hatched in the winter.&nbsp;</li>\n<li>They spawn year-round, with peak production in winter and summer. The male cements bundles of spermatophores into the mantle cavity of the female and/or deposits them in a pouch located near her mouth. The spermatophores penetrate the ova, or sperm is stored for later use.&nbsp;</li>\n<li>The female lays fertilized egg capsules that contain about 150 to 200 eggs each in clusters attached to the ocean bottom, with a typical female laying a total of 3,000 to 6,000 eggs. Eggs hatch between 11 and 26 days later, depending on water temperature.</li>\n<li>Small immature longfin squid feed on plankton, and larger squid feed on crustaceans and small fish.</li>\n<li>They are aggressive hunters, can consume fish larger than themselves, and do eat their own species.</li>\n<li>They are a key prey species for a variety of marine mammals, diving birds, and finfish species.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
