    "Fishing Rate": "Harvest quotas are trimester-based to ensure that it is fished at the recommended level.",
    "Harvest": "<ul>\n<li>In 2019, commercial landings totaled more than 27 million pounds, and were valued at approximately $43 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>Fisheries for longfin squid reflect the species seasonal migrations.</li>\n<li>The majority of landings come from Rhode Island, New York, New Jersey, and Massachusetts.</li>\n<li>Harvested for bait since the late 1800s, longfin squid have been harvested since the mid-1960s for their mild, sweet meat.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>The majority of longfin squid is harvested year-round using small-mesh bottom trawls. There are now only a few fishermen that use pound nets and fish traps during the spring and early summer when squid migrate inshore.</li>\n<li>Sandy or muddy habitat, where squid are fished, is less sensitive to the impacts of trawling.</li>\n<li>Small-mesh bottom trawls can incidentally catch marine mammals and large pelagic species, including pilot whales, common dolphin, swordfish, and a variety of shark, ray, and tuna species. Finfish such as butterfish, hakes, Illex squid, fluke, herring, spiny dogfish, and Atlantic mackerel are also incidentally caught in this fishery.</li>\n<li>Measures to prevent or minimize bycatch include:\n<ul>\n<li>Bycatch cap for <a href=\"https://www.fishwatch.gov/profiles/butterfish\">butterfish</a> caught in the longfin fishery.</li>\n<li>Minimum mesh size requirements for bottom trawl nets, but bycatch escapement is very limited because the codend mesh sizes are very small (1 7/8 in. during Trimester 2 and 2 1/8 in. during Trimesters 1 and 3) and are covered by an additional layer of mesh called a strengthener.</li>\n<li>Outreach to fishermen to educate them on actions to take in the event of a marine mammal interaction.</li>\n<li>Real-time communication to vessels&nbsp;regarding hotspots of marine mammal interactions.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Squid are an excellent source of selenium, riboflavin, and vitamin B12.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Longfin squid have an internal shell called a pen.</li>\n<li>Their fins are long, at least half the length of the mantle (large part of the squid in front of the head).</li>\n<li>The head has large eyes that are covered by a cornea.</li>\n<li>They are pink or orange and mottled with brown or purple.</li>\n<li>They are likely color blind, but are able to use special pigment cells in their skin (called chromatophores) to change their color and patterns to escape predators or disguise themselves from prey.&nbsp;&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "15.58 g",
    "Quote": "U.S. wild-caught longfin squid is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#6c1e16",
    "Research": "<ul>\n<li><a href=\"https://www.fisheries.noaa.gov/about/northeast-fisheries-science-center\">NOAA Northeast Fisheries Science Center</a> bottom trawl surveys assess the abundance of longfin squid and other species.</li>\n<li>A <a href=\"https://www.fisheries.noaa.gov/new-england-mid-atlantic/funding-and-financial-services/research-set-aside-programs\">research set-aside program</a> encourages cooperative research between commercial fishing vessels and researchers.</li>\n<li><a href=\"https://www.nationalgeographic.com/animals/article/110211-squid-pheromones-fighting-mating-woods-hole-animals-science\" rel=\"external\">Squid Get Violent After Touching Eggs, Study Says</a></li>\n</ul>\n",
