    "Location": "<ul>\n<li>Scup are found in the Northwest Atlantic Ocean, primarily between Cape Cod, Massachusetts, and Cape Hatteras, North Carolina.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic, Southeast",
    "Population": "Above target population level in the Mid-Atlantic/New England.",
    "Population Status": "<ul>\n<li>\n<p>There are two stocks of scup: Mid-Atlantic/New England and South Atlantic. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Mid-Atlantic/New England stock is not overfished and not subject to overfishing (2021&nbsp;stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Scup%20-%20Atlantic%20Coast&amp;stockid=10286\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>In the South Atlantic, scup is contained in the South Atlantic Porgy Complex. Scientists have not assessed the Porgy Complex, so the population status is unknown.</p>\n</li>\n</ul>\n</li>\n<li>With greatly improved reproduction and survival rates and low fishing rates since 1998, Mid-Atlantic spawning stock biomass (a measure of the amount of scup able to reproduce) has steadily increased since the mid-1990s.</li>\n</ul>\n",
    "Scientific Name": "Stenotomus chrysops",
    "Species Aliases": "<a href=\"/species-aliases/porgy\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Porgy</a>, <a href=\"/species-aliases/maiden\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Maiden</a>, <a href=\"/species-aliases/fair-maid\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Fair maid</a>, <a href=\"/species-aliases/ironsides\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Ironsides</a>, <a href=\"/species-aliases/northern-porgy\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Northern porgy</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/porgy_scup.png",
    "alt": "Scup illustration",
    "title": ""
    },
    "Species Name": "Scup",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Scup grow slowly, up to about 20 inches long and 4 pounds.</li>\n<li>They can live a relatively long time, up to about 20 years.</li>\n<li>Scup are able to reproduce when they reach age 2, when theyre about 8 inches long.</li>\n<li>They spawn over weedy or sandy areas in southern New England from Massachusetts Bay south to the New York Bight from May through August, with peak activity in June.</li>\n<li>Individual scup spawn once a year.</li>\n<li>Most fish spawn at night, but scientists believe scup spawn in the morning.</li>\n<li>Females release an average of 7,000 eggs, which are fertilized externally.</li>\n<li>Scup are browsers  they nibble on invertebrates that live on the ocean bottom.</li>\n<li>They are able to grasp food with their incisors and crush hard-shelled animals with their strong molars.</li>\n<li>A variety of plankton-eaterssuch as medusae, crustaceans, and fishprey on scup larvae.</li>\n<li>A number of fish and shorebirds prey on juvenile and adult scup.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
