    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of scup totaled 13.6 million pounds and were valued at over $9.3 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:16588225090891::NO:::\">commercial fishing landings database</a>.</li>\n<li>The top five states for commercial scup landings are New York, Rhode Island, New Jersey, Connecticut, and Massachusetts.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>About 75 percent of commercial scup landings are caught with otter trawls.</li>\n<li>Otter trawls can impact bottom habitat. Scup are mainly harvested over sand and mud habitats, which appear to be more resilient to the effects of trawling than more structured habitats, such as gravel or coral.</li>\n<li>Otter trawls can incidentally catch undersized scup. Minimum mesh size regulations and time and area restrictions for trawl nets help to reduce incidental catch of undersized scup.</li>\n<li>The rest of the commercial harvest is mainly caught with floating traps and hand lines.</li>\n<li>Floating traps are anchored to the bottom in inshore waters in the migratory path of scup. Fishermen check on the traps daily to sort their catch.</li>\n<li>Paired trawl nets, pound nets, and pots catch a small percentage of the commercial harvest.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>In 2020, recreational anglers landed more than 12.9 million pounds of scup, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:16588225090891::NO:::\">commercial fishing landings database</a>.</li>\n<li>The top three states for recreational scup landings are New York, Connecticut, and Rhode Island.</li>\n<li>In the Mid-Atlantic and southern New England, the recreational scup fishery is managed under separate regulations for federal and state waters.</li>\n<li>Managers set regulations for the recreational fishery annually. They include a combination of an annual catch quota, minimum size limits, bag limits, and fishing seasons.</li>\n<li>For-hire recreational vessels must have a permit to fish in federal waters.</li>\n<li>In the South Atlantic, there is a limit on the amount of scup recreational fishermen can keep and restrictions on the type of gear they can use.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Scup is a low-sodium, low-fat source of protein. It is high in niacin, phosphorus, vitamins B6 and B12, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Scup are deep-bodied (deeper from back to belly than they are wide).</li>\n<li>They are dusky brown with bright silvery reflections below and spiny fins.</li>\n<li>Adult fins are mottled with dark brown, and young scup fins may be faintly barred.</li>\n<li>Scups front teeth are very narrow, almost conical, and they have two rows of molars in the upper jaw.</li>\n<li>Longspine porgy look similar to scup, but can be easily identified by the elongated spines on their backs.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.88 g",
    "Quote": "U.S. wild-caught scup is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#81a2a5",
    "Research": "<ul>\n<li>Scientists from NOAAs <a href=\"https://www.fisheries.noaa.gov/feature-story/2019-fall-bottom-trawl-survey-completed-northeast\">Northeast Fisheries Science Center</a> conduct annual spring and fall bottom trawl surveys in waters from just south of Cape Hatteras to Canadian waters. These survey results, along with data from surveys run by states and universities and catch data from the commercial and recreational fisheries, help scientists estimate the abundance of scup and other species in the Mid-Atlantic and New England.</li>\n<li>The Northeast Fisheries Science Centers <a href=\"https://www.nefsc.noaa.gov/fsb/program.html\">Fishery Observer Program</a> has collected information on landings and discards in the commercial scup fishery for more than 20 years.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.64 g",
    "Selenium": "36.5 mcg",
    "Serving Weight": "100 g (raw)",
    "Servings": "1",
    "Sodium": "42 mg",
    "Source": "<p>U.S. wild-caught from Massachusetts to North Carolina.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Mild.</p>\n",
    "Texture": "<p>Scup have lean and flaky flesh, but also contain many bones, which makes them difficult to fillet. As a result, scup are generally sold and cooked whole, after theyve been scaled and dressed.</p>\n",
