    "Species Name": "Pink Shrimp",
    "Animal Health": null,
    "Availability": "<p>Year-round, with peaks in the winter.</p>\n",
    "Biology": "<ul>\n<li>Pink shrimp grow fairly fast, depending on factors such as water temperature and salinity, and can reach over 8 inches in length.&nbsp;</li>\n<li>They have a short life span, usually less than 2 years, and are often referred to as an annual crop.</li>\n<li>Pink shrimp are able to reproduce when they reach about 3.3 inches long.</li>\n<li>Off North Carolina, they spawn in May through July. In Florida they spawn multiple times, peaking from April through July when the water is warmest.</li>\n<li>Males mate with females and anchor their sperm to the females. Females release about 500,000 to 1 million eggs near the ocean floor, and the eggs are fertilized as they are released.</li>\n<li>Newly hatched shrimp travel to their estuarine nursery habitats in late spring and early summer, propelled by shoreward currents.</li>\n<li>Shrimp that survive the winter grow rapidly in late winter and early spring before returning to the ocean.</li>\n<li>Pink shrimp larvae feed on plankton (tiny floating plants and animals). Juvenile and adult shrimp are omnivorous, feeding on copepods, small mollusks, diatoms, algae, plant detritus, bacterial films, slime molds, and yeast.</li>\n<li>Sheepshead minnows, water boatmen, and insect larvae eat postlarval shrimp, and grass shrimp, killifishes, and blue crabs prey on young shrimp.</li>\n<li>A wide variety of finfish feed heavily on juvenile and adult shrimp.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "106",
    "Carbohydrate": "0.91 g",
    "Cholesterol": "152 mg",
    "Color": "<p>Raw shrimp meat is translucent pink to gray. When cooked, their shells are pinkish-red and their meat is pearly white with pink and red shadings.</p>\n",
    "Disease Treatment and Prevention": null,
