    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, landings of pink shrimp totaled 12 million pounds and were valued at more than $30 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:4612992745201::NO:::\">commercial fishing landings database</a>.&nbsp;&nbsp;</li>\n<li>The three species of penaeid shrimp (<a href=\"https://www.fishwatch.gov/profiles/white-shrimp\">white</a>, pink, and <a href=\"https://www.fishwatch.gov/profiles/brown-shrimp\">brown</a>) make up the vast majority&nbsp;of the shrimp harvested in the southeast. This fishery is one of the most valuable fisheries in the southeastern United States.</li>\n<li>Over 75 percent&nbsp;of the pink shrimp harvested in the United States comes from the west coast of Florida.</li>\n<li>Annual harvests of pink shrimp vary considerably from year to year, primarily due to environmental conditions. Harvests are much lower in years following severe winter weather.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen harvest shrimp with trawls towed near the ocean floor. The nets are wide in the front and taper toward the back.</li>\n<li>Shrimpers using otter trawl gear in the South Atlantic and Gulf of Mexico are required to use sea <a href=\"https://www.fisheries.noaa.gov/southeast/bycatch/turtle-excluder-device-regulations\">turtle excluder devices (TEDs)</a><a href=\"https://www.fisheries.noaa.gov/southeast/bycatch/turtle-excluder-device-regulations?_ga=2.124538186.1328632185.1589316919-1749182424.1566822859\">.</a></li>\n<li>Some shrimp trawlers must also install <a href=\"https://www.sefsc.noaa.gov/labs/mississippi/brd/\">bycatch reduction devices</a>&nbsp;behind the TED, to reduce finfish bycatch.</li>\n<li>Area closures if&nbsp;fishing effort exceeds certain thresholds.</li>\n<li>Trawlers in the Gulf of Mexico must have a weak-link in the tickler chain, which hangs in front of the net and drags along the ocean floor to stir up shrimp from the bottom into the net. This weak-link allows the tickler chain to drop away if it gets hung up on natural bottom structures.</li>\n<li>Fishermen do not trawl in areas with coral reefs and other known areas of high-relief to avoid damage to their nets.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Under federal management, there is no recognized recreational fishery. Fishing in federal waters requires a permit.</li>\n<li>Recreational fishermen catch pink shrimp seasonally and almost always in state waters. State regulations vary from state to state.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Pink shrimp is low in saturated fat and is a very good source of protein, selenium, and vitamin B12.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Pink shrimp are crustaceans with 10 slender, relatively long walking legs and five pairs of swimming legs, located on the front surface of the abdomen.</li>\n<li>They typically have a dark-colored spot on each side between their third and fourth abdominal segments. Their tail usually has a dark blue band (rather than the purplish band found on brown shrimp).</li>\n<li>Their carapace is grooved.</li>\n<li>Part of their shell is a well-developed, toothed rostrum that extends to or beyond the outer edge of the eyes.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.31 g",
    "Quote": "U.S. wild-caught pink shrimp is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#ebada3",
    "Research": "<ul>\n<li><a href=\"http://news.wgcu.org/post/pink-shrimp-may-indicate-whether-or-not-everglades-are-healthy\" rel=\"external\">Pink shrimp may indicate whether or not the Everglades are healthy</a></li>\n<li><a href=\"https://www.sefsc.noaa.gov/species/shrimp/\">NOAA Southeast Fisheries Science Center Shrimp Research</a></li>\n</ul>\n",
