    "Fishery Management": "<ul>\n<li><a href=\"http://sero.nmfs.noaa.gov/index.html\">NOAA Fisheries</a> and the <a href=\"http://www.safmc.net/\">South Atlantic </a>and <a href=\"http://gulfcouncil.org/index.php\">Gulf of Mexico</a> Fishery Management Councils manage the white shrimp fishery.</li>\n<li>In the South Atlantic, managed under the <a href=\"http://safmc.net/fishery-management-plans-amendments/shrimp-2/\">Shrimp Fishery Management Plan&nbsp;for the South Atlantic Region</a>:\n<ul>\n<li>Permits are needed to harvest shrimp in federal waters.</li>\n<li>Fishing trip reports must be submitted for each fishing trip.</li>\n<li>Observers must be carried aboard vessels if selected, to collect data on catch, bycatch, fishing effort, and fishing gear.</li>\n<li>Managers set catch levels based on historic harvest amounts and fishing rates, rather than abundance because white shrimp are short-lived and heavily influenced by environmental factors.</li>\n<li>The white shrimp population can be periodically decimated by severe winter cold in the South Atlantic, especially offshore of Georgia and South Carolina. Fishery closures may be implemented to help protect the remaining adult population so they can spawn.</li>\n<li>Federal waters close if cold weather reduces the shrimp population by 80 percent or more, or if water temperatures fall below a critical level.</li>\n</ul>\n</li>\n<li>In the Gulf of Mexico, managed under the <a href=\"http://gulfcouncil.org/fishery-management/implemented-plans/shrimp/\">Gulf of Mexico Shrimp Fishery Management Plan</a>:\n<ul>\n<li>Permits are needed to harvest shrimp in federal waters. Currently no new permits are being issued to prevent an increase in the number of boats participating in the fishery.</li>\n<li>Electronic logbooks must be installed and selected fishermen must submit trip reports for each fishing trip.</li>\n<li>Observers must be carried aboard vessels if selected, to collect data on catch, bycatch, fishing effort, and fishing gear.</li>\n<li>Each year all shrimping in federal waters off Texas is closed from approximately mid-May to mid-July to protect <a href=\"https://www.fishwatch.gov/profiles/brown-shrimp\">brown shrimp</a> populations.</li>\n</ul>\n</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>White shrimp commonly inhabit estuaries and coastal areas out to about 100 feet offshore.</li>\n<li>Young shrimp live and grow in nursery areas with muddy ocean bottoms and low to moderate salinity.</li>\n<li>White shrimp are often found in association with other shrimp species, specifically <a href=\"https://www.fishwatch.gov/profiles/brown-shrimp\">brown</a> shrimp.</li>\n</ul>\n",
    "Habitat Impacts": "Gear restrictions, such as a weak-link in the tickler chain, are in place to protect bottom habitat from trawl gear. ",
