    "Location": "<ul>\n<li>White shrimp are found from Fire Island, New York, to St. Lucie Inlet on the Atlantic Coast of Florida. In the Gulf of Mexico, they are found from the Ochlochonee River, Florida, to Campeche, Mexico.&nbsp;</li>\n</ul>\n",
