    "Population Status": "<ul>\n<li>\n<p>There are two stocks of white shrimp: Gulf of Mexico and South Atlantic. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Gulf of Mexico stock is not overfished and not subject to overfishing (2019). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=White%20shrimp%20-%20Gulf%20of%20Mexico&amp;stockid=10083\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The South Atlantic stock is not overfished and not subject to overfishing (2018). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=White%20shrimp%20-%20Southern%20Atlantic%20Coast&amp;stockid=10084\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
