    "Availability": "<p>Year-round, with peaks in the fall.</p>\n",
    "Biology": "<ul>\n<li>White shrimp grow fairly fast, depending on factors such as water temperature and salinity, and can reach up to 7 or 8 inches in length.</li>\n<li>They have a short life span, usually less than 2 years, and are often referred to as an annual crop.</li>\n<li>They are able to reproduce when they reach about 5  inches long.</li>\n<li>White shrimp spawn when offshore ocean bottom water temperatures increase, generally from May through September in the Carolinas, and from March through September in the Gulf of Mexico.</li>\n<li>Males mate with females and anchor their sperm to the females. Females release about 500,000 to 1 million eggs near the ocean floor, and the eggs are fertilized as they are released.</li>\n<li>Newly hatched shrimp travel to their estuarine nursery habitats in April and early May.</li>\n<li>Shrimp that survive the winter grow rapidly in late winter and early spring before returning to the ocean.</li>\n<li>White shrimp larvae feed on plankton (tiny floating plants and animals).</li>\n<li>Juvenile and adult shrimp are omnivorous and feed on the bottom on detritus, plants, microorganisms, macroinvertebrates, and small fish. Cannibalism is also common among adult white shrimp.</li>\n<li>Sheepshead minnows, water boatmen, and insect larvae eat postlarval shrimp, and grass shrimp, killifishes, and blue crabs prey on young shrimp.</li>\n<li>A wide variety of finfish feed heavily on juvenile and adult shrimp.</li>\n</ul>\n",
