    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, landings of white shrimp totaled 110 million pounds and were valued at &nbsp;$254 million, according to the NOAA Fisheries&nbsp;<a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:4411414098274::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>The three species of penaeid shrimp (white, <a href=\"https://www.fishwatch.gov/profiles/pink-shrimp\">pink</a>, and <a href=\"https://www.fishwatch.gov/profiles/brown-shrimp\">brown</a>) make up the vast majority&nbsp;of the shrimp harvested in the southeast. This fishery is one of the most valuable fisheries in the southeastern United States.</li>\n<li>Almost all&nbsp;of the white shrimp harvested in the United States comes from the Gulf of Mexico, mainly from Louisiana and Texas. Landings in the South Atlantic are generally spread evenly among the states.</li>\n<li>Annual harvests of white shrimp vary considerably from year to year, primarily due to environmental conditions. Harvests are much lower in years following severe winter weather.</li>\n<li>White shrimp were the first commercially important shrimp species in the United States, dating back to 1709.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen harvest shrimp with trawls towed near the ocean floor. The nets are wide in the front and taper toward the back.</li>\n<li>Shrimpers using otter trawl gear in the South Atlantic and Gulf of Mexico are required to use sea <a href=\"https://www.fisheries.noaa.gov/southeast/bycatch/turtle-excluder-device-regulations?_ga=2.62803436.1328632185.1589316919-1749182424.1566822859\">turtle excluder devices (TEDs)</a>.</li>\n<li>Some shrimp trawlers must also install <a href=\"http://www.sefsc.noaa.gov/labs/mississippi/brd/\">bycatch reduction devices</a><a href=\"https://www.sefsc.noaa.gov/labs/mississippi/brd/\">&nbsp;</a>behind the TED, to reduce finfish bycatch.</li>\n<li>Area closures if fishing effort exceeds certain thresholds.</li>\n<li>Trawlers in the Gulf of Mexico must have a weak-link in the tickler chain, which hangs in front of the net and drags along the ocean floor to stir up shrimp from the bottom into the net. This weak-link allows the tickler chain to drop away if it gets hung up on natural bottom structures.</li>\n<li>Fishermen do not trawl in areas with coral reefs and other known areas of high relief to avoid damage to their nets.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Under federal management, there is no recognized recreational fishery. Fishing in federal waters requires a permit.</li>\n<li>Recreational fishermen catch white shrimp seasonally and almost always in state waters. State regulations vary from state to state.</li>\n</ul>\n</li>\n</ul>\n",
