    "Human Health": null,
    "Physical Description": "<ul>\n<li>White shrimp are crustaceans with 10 slender, relatively long walking legs and five pairs of swimming legs located on the front surface of the abdomen.</li>\n<li>Their bodies are light gray, with green coloration on the tail and a yellow band on part of the abdomen.</li>\n<li>Their carapace is not grooved.</li>\n<li>Part of their shell is a well-developed, toothed rostrum that extends to or beyond the outer edge of the eyes.</li>\n<li>They have longer antennae than other shrimp (2.5 to 3 times longer than their body length).</li>\n</ul>\n",
