    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "Above target population levels for the northern stock and the southern stock.",
    "Population Status": "<ul>\n<li>\n<p>There are two stocks of silver hake: the Gulf of Maine/Northern Georges Bank stock and the Southern Georges Bank/Mid-Atlantic stock. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Gulf of Maine/Northern Georges Bank stock is not overfished and not subject to overfishing (2020 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st-dev.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10521\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Southern Georges Bank/Mid-Atlantic stock is not overfished and not subject to overfishing (2020 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st-dev.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10522\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Merluccius bilinearis",
    "Species Aliases": "<a href=\"/species-aliases/whiting\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Whiting</a>, <a href=\"/species-aliases/atlantic-hake\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Atlantic hake</a>, <a href=\"/species-aliases/new-england-hake\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">New England hake</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/silver_hake.png",
    "alt": "Silver Hake illustration",
    "title": "Silver Hake"
    },
    "Species Name": "Silver Hake",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Female silver hake are serial spawners, producing and releasing up to three batches of eggs in a single spawning season.</li>\n<li>Spawning areas include the coastal region of the Gulf of Maine from Cape Cod to Grand Manan Island, southern and southeastern Georges Bank, and the southern New England area south of Marthas Vineyard.</li>\n<li>Their peak spawning occurs earlier in the south (May to June) than in the north (July to August).</li>\n<li>More than half of 2-year-old silver hake and virtually all 3-year-olds are sexually mature.</li>\n<li>Silver hake can live as long as 14 years, although few fish older than age 6 have been observed in U.S. waters in recent years.</li>\n<li>They can grow to be more than 28 inches long and up to 5 pounds.</li>\n<li>Silver hake are important to the northwest Atlantic ecosystem both as prey and predators.</li>\n<li>They are voracious predators and feed on fish, crustaceans, and squid.</li>\n</ul>\n",
    "Bycatch": "Raised-footrope trawls are required in designated areas when targeting hake to minimize bycatch of other species.",
    "Calories": "90 ",
    "Carbohydrate": "0 g",
    "Cholesterol": "67 mg",
    "Color": "<p>Raw and cooked hake is white to off-white.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
    "Farming Methods": null,
    "Farming Methods_": null,
    "Fat, Total": "1.31 g",
    "Feeds_": null,
    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>\n<p>In 2019, commercial landings of silver hake totaled 11.5 million pounds and were valued at $8.6 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:12024094975784::NO:::\">commercial fishing landings database</a>.</p>\n</li>\n<li>Silver hake is part of the small-mesh multispecies management unit, along with <a href=\"https://www.fishwatch.gov/profiles/red-hake\">red hake</a> and offshore hake, which are collectively referred to as whiting.\"</li>\n<li>The small-mesh multispecies fishery in the United States operates from Maine to Cape Hatteras, North Carolina.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Fishermen using small-mesh trawl gear to catch silver hake must comply with a number of specific requirements to reduce bycatch of larger groundfish species.</li>\n<li>In designated areas, fishermen are required to use raised-footrope trawls, which are designed to keep the net off the bottom to reduce bycatch of flatfish and other species.</li>\n<li>Discard rates for silver hake are relatively low (10% of the northern silver hake landings and 21% of the southern whiting and offshore hake landings).</li>\n<li>Approximately 60% of the <a href=\"https://www.fishwatch.gov/profiles/red-hake\">red hake</a> caught as bycatch in the whiting fishery are discarded.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational fishermen do not target silver hake but sometimes catch them incidentally while fishing for other groundfish.</li>\n<li>There are currently no restrictions on recreational possession, size, or gear type for silver hake caught recreationally in federal waters.</li>\n<li>There are no specific areas for the recreational fishery.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Silver hake is a good source of selenium, vitamin B, magnesium, and protein.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Silver hake are silvery in color, brownish on the back and whitish on the belly, with black speckles on the back and black inside the mouth.</li>\n<li>They have large heads for their size and sharp teeth.</li>\n<li>They have long pectoral fins and are fast swimmers.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.31 g",
    "Quote": "U.S. wild-caught silver hake is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#8195a5",
    "Research": "<ul>\n<li><a href=\"http://www.nature.com/ncomms/journal/v2/n8/full/ncomms1420.html\" rel=\"external\">Silver hake track changes in Northwest Atlantic circulation</a>.</li>\n</ul>\n",
