    "Location": "<ul>\n<li>Skipjack tuna are found in tropical and subtropical oceans around the world, including the waters around the U.S. Pacific Islands and the U.S. West Coast.&nbsp;</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Pacific Islands",
    "Population": "Assumed to be above target population levels in the Eastern Pacific. Near target levels and fishing rate promotes population growth in the Western and Central Pacific.",
    "Population Status": "<ul>\n<li>\n<p>There are two stocks of skipjack tuna: the Eastern Pacific stock and the Western and Central Pacific stock. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Eastern Pacific stock is not overfished and not subject to overfishing (2004 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10160\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Western and Central Pacific stock &nbsp;is not overfished and is not subject to overfishing (2019&nbsp;stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=11691\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n<li>Skipjack tuna is a notoriously difficult species to assess. Due to skipjacks high and variable productivity, its difficult to determine the effect of fishing on the population using standard fisheries data and stock assessment methods. Assessments are particularly difficult for the Eastern Tropical Pacific stock due to limited data.</li>\n</ul>\n",
    "Scientific Name": "Katsuwonus pelamis",
    "Species Aliases": "<a href=\"/species-aliases/ocean-bonito\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Ocean bonito</a>, <a href=\"/species-aliases/lesser-tuna\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Lesser tuna</a>, <a href=\"/species-aliases/aku\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Aku</a>, <a href=\"/species-aliases/katsuo\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Katsuo</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/atlantic_skipjack_tuna.png",
    "alt": "Pacific Skipjack Tuna illustration",
    "title": "Pacific Skipjack Tuna"
    },
    "Species Name": "Pacific Skipjack Tuna",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Like other tropical tunas, skipjack tuna grow fast, up to nearly 4 feet and more than 70 pounds.</li>\n<li>They have a short life span compared to other temperate tunas, around 8 to 12 years.</li>\n<li>In the Pacific, skipjack are able to reproduce when they reach about 1.3 feet in length.</li>\n<li>They spawn throughout the year in tropical waters and seasonally (spring to early fall) in subtropical waters.</li>\n<li>Depending on their size, females produce between 100,000 and 2 million eggs each time they spawn.</li>\n<li>Once fertilized, the eggs hatch in about 1 day, depending on temperature.</li>\n<li>Skipjack spawn more than once a season, and some spawn almost every day.</li>\n<li>They are opportunistic feeders, preying on a variety of fish, crustaceans, cephalopods, mollusks, and sometimes other skipjack tunas.</li>\n<li>Large pelagic fishes such as billfish, sharks, and other large tunas prey on skipjack tuna.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "103",
    "Carbohydrate": "0 g",
    "Cholesterol": "47 mg",
    "Color": "<p>When raw, good-quality skipjack tuna meat is deep red. Smaller fish are lighter red. Cooked skipjack becomes light gray.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
