    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>Most of the global harvest of Pacific skipjack tuna comes from the Western and Central Pacific&nbsp;Ocean (WCPO).</li>\n<li>U.S. fisheries typically account for less than 10 percent of harvest from the WCPO.</li>\n<li>In 2020, commercial landings of Pacific skipjack tuna totaled more than 700,000 pounds and were valued at more than $800,000, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>&nbsp;(these landings do not include skipjack&nbsp;tuna landed outside the U.S.).</li>\n<li>The majority of landings of non-purse seine caught skipjack tuna are in Hawaii, with some landings in American Samoa, the Commonwealth of the Northern Mariana Islands, and Guam.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Purse seines are primarily used to catch Pacific skipjack tuna.</li>\n<li>Skipjack tuna is also caught in longline, pole-and-line, and troll fisheries.</li>\n<li>Fishing gear used to catch Pacific skipjack tuna rarely contacts the seafloor so habitat impacts are minimal.</li>\n<li>Interactions with protected species such as sea turtles, marine mammals, and seabirds in these fisheries are rare and survival rates are estimated to be high for all gear types.</li>\n<li>Many purse seine fishermen use fish aggregating devices (FADs) to target tunas.</li>\n<li>FADs can be drifting, floating or submerged objects deployed and tracked by vessels, including through the use of radio or satellite buoys, for the purpose of aggregating target tuna species.</li>\n<li>FADs are also known to attract non-target and bycatch species, including juvenile tunas, sharks, other fish, and occasionally protected species.</li>\n<li>Use of FADS is prohibited in certain areas at certain times of the year to regulate fishing effort and reduce bycatch of juvenile tunas.</li>\n<li>Longline and purse seine fishermen are trained in safe handling and release techniques for sea turtles, seabirds, and marine mammals, and they carry and use specific equipment for handling and releasing these animals.</li>\n<li>Scientists and managers continue to monitor bycatch in these fisheries through logbooks and fishery observer programs.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational anglers fish for skipjack tuna with troll, rod-and-reel, and handline gear and sometimes by free-diving with spear guns.\n<ul>\n<li>Off California, anglers must be licensed and must follow daily bag limits. Recreational charter boats must keep logbooks of their catch.</li>\n<li>There are no federal regulations for recreational fishing off Hawaii and U.S. Pacific Island territories, but local rules may apply.</li>\n</ul>\n</li>\n<li>In 2019, recreational anglers landed more than 2.7 million pounds of skipjack tuna, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">recreational fishing landings database</a>. These figures may not match other agency sources of data due to confidential information.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Skipjack is an excellent source of low-fat protein.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Skipjack tuna do not have scales except on the corselet and the lateral line. The corselet is a band of large, thick scales forming a circle around the body behind the head and extending backward along the lateral line. The lateral line is a faint line running lengthwise down each side of the fish.</li>\n<li>Their back is dark purplish-blue, and their lower sides and belly are silvery with four to six conspicuous dark bands that run from behind the head to the tail, which may look like a series of dark blotches.</li>\n</ul>\n",
    "Production": null,
    "Protein": "22.00 g",
    "Quote": "U.S. wild-caught Pacific skipjack tuna is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#414551",
    "Research": "<ul>\n<li>Scientists at NOAAs <a href=\"https://swfsc.noaa.gov/\">Southwest</a> and <a href=\"https://www.pifsc.noaa.gov/\">Pacific Islands Fisheries Science Centers</a> contribute to research on Pacific tropical tunas including skipjack by monitoring U.S. fisheries, conducting stock assessments, and participating in international forums for the assessment of these species.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.328 g",
    "Selenium": "36.5  mcg",
    "Serving Weight": "100 g (raw)",
    "Servings": "1",
    "Sodium": "37 mg",
    "Source": "<p>U.S. wild-caught from Hawaii, California, U.S. Pacific Island territories, and the high seas.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Skipjack tuna has the most pronounced taste of all of the tropical tunas.</p>\n",
    "Texture": "<p>Firm and moist, with large flakes.</p>\n",
    "Path": "/profiles/pacific-skipjack-tuna",
    "last_update": "01/05/2022 - 17:27"
