    "Location": "<ul>\n<li>Swordfish are found around the world in tropical, temperate, and sometimes cold waters of the Atlantic, Indian, and Pacific Oceans.</li>\n<li>They are found in the Gulf Stream of the Western North Atlantic, extending north into the Grand Banks of Newfoundland.</li>\n<li>North Atlantic swordfish annually migrate thousands of miles along the eastern seaboard of the United States and Canada and also in the eastern Atlantic along Africa and Europe.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic, Southeast",
    "Population": "Above target population level.",
    "Population Status": "<ul>\n<li>According to the 2017 stock assessment, North Atlantic swordfish is not overfished and&nbsp;not subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10953\">Stock SMART</a>.</li>\n<li>The <a href=\"http://www.iccat.int/en/\" rel=\"external\">International Commission for the Conservation of Atlantic Tunas (ICCAT)</a>&nbsp;assesses the abundance of swordfish in the Atlantic.&nbsp;</li>\n</ul>\n",
    "Scientific Name": "Xiphias gladius",
    "Species Aliases": "<a href=\"/species-aliases/broadbilled-swordfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Broadbilled swordfish</a>, <a href=\"/species-aliases/broadbill\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Broadbill</a>, <a href=\"/species-aliases/espada\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Espada</a>, <a href=\"/species-aliases/emperado\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Emperado</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/north_atlantic_swordfish.png",
    "alt": "North Atlantic Swordfish illustration",
    "title": "North Atlantic Swordfish"
    },
    "Species Name": "North Atlantic Swordfish",
    "Animal Health": null,
    "Availability": "<p>Year-round, with peaks from June through October.&nbsp;</p>\n",
    "Biology": "<ul>\n<li>Atlantic swordfish are one of the fastest predators in the ocean. Their streamlined body allows them to swim at high speeds, up to 50 mph.&nbsp;</li>\n<li>They grow quickly and reach a maximum size of about 1,165 pounds. However, the average size caught in the fishery is 50 to 200 pounds.</li>\n<li>Swordfish live about 9 years.</li>\n<li>Females are able to reproduce between 4 and 5 years of age. Depending on their size, females can produce anywhere from 1 million to 29 million eggs.&nbsp;</li>\n<li>They spawn multiple times throughout the year in warm tropical and sub-tropical waters.</li>\n<li>In the western North Atlantic, they spawn south of the Sargasso Sea and in the upper Caribbean from December to March, and off the southeast coast of the United States from April through August.</li>\n<li>Swordfish feed on a variety of fish and invertebrates such as squid.</li>\n<li>They capture prey by slashing their bill back and forth, stunning or injuring prey in the process.</li>\n<li>They have developed unique characteristics, such as a special eye muscles and a heat exchange system that allows them to swim in deep, cold water in search of prey.&nbsp;</li>\n<li>Swordfish feed at the top of the food chain and are rarely preyed on by other animals. Sharks and larger predatory fishes may sometimes eat juvenile swordfish.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "121 ",
    "Carbohydrate": "0 g",
    "Cholesterol": "39 mg",
    "Color": "<p>Raw flesh ranges from white and ivory to pink and orange. Cooked meat is beige.</p>\n",
