    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of North Atlantic swordfish totaled 2.8 million pounds and were valued at $9.4 million, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:14517926993230::NO:::\">commercial fishing landings database</a>.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Fishermen mainly use pelagic longline gear to harvest swordfish. They may also use rod-and-reel, harpoon, and buoy gear.</li>\n<li>Pelagic longline gear and handgear have no impact on habitat because theyre used in the water column and dont come into contact with the ocean floor.</li>\n<li>Handgear used to catch swordfish is very selective and bycatch is minimal.</li>\n<li>NOAA Fisheries has taken the following additional measures to prevent bycatch and sustainably manage this fishery. Fishermen are:\n<ul>\n<li>Required to use large circle hooks and certain types of bait that limit gear interactions with sea turtles. Circle hooks are specifically designed to minimize the damage caused by hooking, giving animals that are captured and released a better chance at survival.</li>\n<li>Trained and certified to use special techniques to safely de-hook and release incidentally caught turtles.</li>\n<li>Required to stop fishing and move 1 nautical mile if they encounter a protected species.</li>\n<li>Required to protect pilot whales and Risso's dolphins when fishing in the Mid-Atlantic Bight by limiting the length of their lines to 20 nautical miles and posting <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/safe-handling-release-and-identification-workshops?_ga=2.224394971.1714109454.1598375662-1749182424.1566822859\">marine mammal handling/release guidelines</a> on their vessels. In addition, if fishing in the Cape Hatteras Special Research Area, pelagic longliners must contact NOAA Fisheries at least 48 hours prior to a trip and carry observers if requested.</li>\n<li>Required to use weak hooks in the Gulf of Mexico to reduce incidental catch of bluefin tuna, and are prohibited from using live bait to reduce bycatch of billfish.</li>\n<li>Restricted from areas in the Gulf of Mexico to reduce bycatch of all species.</li>\n<li>Required to carry <a href=\"https://www.fisheries.noaa.gov/topic/enforcement?_ga=2.210737749.1714109454.1598375662-1749182424.1566822859#vessel-monitoring\">vessel monitoring systems</a> onboard their boats to ensure compliance with regulations.</li>\n<li>Required to carry at-sea fisheries observers upon request. NOAA Fisheries reviews observer data to monitor protected species interactions and takes appropriate action as necessary.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational fishermen have fished for swordfish off the East Coast since the 1920s.</li>\n<li>Permits are required.</li>\n<li>Minimum size requirement and catch limits.&nbsp;</li>\n<li>Certain areas are closed to fishing during certain times of the year.</li>\n<li>Recreational fishermen must also report their swordfish catch by <a href=\"https://hmspermits.noaa.gov/catchReports\">web</a>, <a href=\"https://hmspermits.noaa.gov/mobileApp\">mobile app</a>, or by phone 888-872-8862. If they release the fish, they must release it without removing it from the water in a manner that ensures it has a good chance of survival.</li>\n<li><a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-highly-migratory-species-fishery-compliance-guides\">Compliance guides</a> are available for all recreational regulations across Atlantic highly migratory species fisheries.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Swordfish is an excellent source of selenium, niacin, vitamin B12, and zinc. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Swordfish have a long, flattened bill that looks like a sword, as their name implies.</li>\n<li>They have a stout, rounded body and large eyes.&nbsp;</li>\n<li>Their first dorsal (back) fin is tall and crescent-shaped. Their second dorsal fin is much smaller.</li>\n<li>Their anal fins (on their belly) are similar in shape to the dorsal fins but are smaller.</li>\n<li>They have a broad, crescent-shaped tail.</li>\n<li>Their color is darkest on top, generally black or brown, and fades to a lighter color below.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "19.8 g",
    "Quote": "U.S. wild-caught north Atlantic swordfish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#2d2e2a",
    "Research": "<ul>\n<li>NOAA Fisheries scientists maintain an Atlantic-wide <a href=\"https://www.sefsc.noaa.gov/species/fish/tagging.htm\">cooperative tagging program</a> and conduct electronic tagging of swordfish using pop-up satellite archival tags. These tags provide intimate details of the life of individual fish in their natural environment without requiring researchers or anglers to physically retrieve the tags from the fish or from the ocean.&nbsp;</li>\n</ul>\n",
