    "Location": "<ul>\n<li>Brown rock shrimp are found from Norfolk, Virginia, south through the Gulf of Mexico to Mexicos Yucatan Peninsula.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Southeast",
    "Population": "The population level is unknown.",
    "Population Status": "<ul>\n<li>\n<p>According to the 2018 stock assessment, brown rock shrimp is not subject to overfishing. There is currently not enough information to determine the population size, so it is unknown. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Brown%20rock%20shrimp%20-%20Southern%20Atlantic%20Coast&amp;stockid=10803\">Stock SMART</a>.</p>\n</li>\n<li>Brown rock shrimp are highly productive. Their population size varies naturally from year to year based on environmental conditions.</li>\n</ul>\n",
    "Scientific Name": "Sicyonia brevirostris",
    "Species Aliases": "<a href=\"/species-aliases/rock-shrimp\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Rock shrimp</a>, <a href=\"/species-aliases/florida-rock-shrimp\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Florida rock shrimp</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/rock_shrimp.png",
    "alt": "Illustration of a Brown Rock Shrimp",
    "title": "Brown Rock Shrimp"
    },
    "Species Name": "Brown Rock Shrimp",
    "Animal Health": null,
    "Availability": "<p>Year-round with peaks from July through October.</p>\n",
    "Biology": "<ul>\n<li>Brown rock shrimps growth and development depends on factors such as water temperature and salinity. They can grow up to 6 inches in length, but most brown rock shrimp found in shallow waters are less than 2 inches long.</li>\n<li>They are highly productive and have a short life span, between 20 and 22 months.</li>\n<li>Females are able to reproduce when they reach at least  to 1 inch in length. Males mature when they reach about  inch long.</li>\n<li>Brown rock shrimp spawn year-round in offshore waters, with peaks between November and January. Individual females can spawn three or more times in one season.</li>\n<li>Males and females mate, and the eggs are fertilized when the female simultaneously releases egg and sperm. Eggs hatch within 24 hours.</li>\n<li>Juvenile and adult brown rock shrimp feed on the ocean floor, mainly eating small bivalve mollusks and crustaceans.</li>\n<li>Sheepshead, minnows, water boatmen, and insect larvae eat postlarval brown rock shrimp.</li>\n<li>A wide variety of species prey on juvenile and adult brown rock shrimp.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "110",
    "Carbohydrate": "0 g",
    "Cholesterol": "140 mg",
    "Color": "<p>Transparent or clear white, with fine pinkish or purple lines.</p>\n",
