    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, landings of brown rock shrimp totaled more than 395,000 pounds and were valued at $788,000, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:4411414098274::NO:::\">commercial fishing landings database</a>.</li>\n<li>It is not uncommon for brown rock shrimp to experience considerable variations from year to year.</li>\n<li>Brown rock shrimp had limited marketability before 1969, when a machine was developed that could split the rock-hard shell and devein the shrimp. The first major harvest of brown rock shrimp (1,200 pounds) was recorded in 1970 and was valued at $642. Two years later, landings totaled 443,035 pounds and were valued at more than $258,000.</li>\n<li>Almost all the harvest is sold as meat because brown rock shrimp are difficult to peel.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen harvest shrimp with trawls towed near the ocean floor.</li>\n<li>Shrimpers using otter trawl gear in the South Atlantic and Gulf of Mexico are required to use sea <a href=\"https://www.fisheries.noaa.gov/southeast/bycatch/turtle-excluder-device-regulations\">turtle excluder devices (TEDs)</a>.</li>\n<li>Some shrimp trawlers must also install <a href=\"https://www.sefsc.noaa.gov/labs/mississippi/brd/\">bycatch reduction devices</a> behind the TED, to reduce finfish bycatch.</li>\n<li>Area closures if finfish bycatch exceeds certain thresholds.&nbsp;</li>\n<li>Fishermen do not trawl in areas with coral reefs and other known areas of high relief to avoid damage to their nets.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Under federal management, there is no recognized recreational fishery. Fishing in federal waters requires a permit.</li>\n<li>Recreational fishermen catch brown rock shrimp seasonally and almost always in state waters. State regulations vary from state to state.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Brown rock shrimp are a good source of selenium, vitamin B12, iron, niacin, and phosphorus.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Brown rock shrimp have a thick, rigid, stony shell.</li>\n<li>Their bodies are off-white to pinkish in color, with the back surface darker and blotched or barred with lighter shades.</li>\n<li>Their legs are red to reddish-purple and barred with white. The abdomen has deep transverse grooves and numerous nodules.</li>\n<li>Short hairs cover their body and appendages.</li>\n<li>Their eyes are large and deeply pigmented.</li>\n<li>Brown rock shrimp are the deep-water cousin of <a href=\"https://www.fishwatch.gov/profiles/white-shrimp\">white</a>, <a href=\"https://www.fishwatch.gov/profiles/pink-shrimp\">pink</a>, and <a href=\"https://www.fishwatch.gov/profiles/brown-shrimp\">brown</a> shrimp also found in the warm waters of the southeastern United States. They are the largest of six rock shrimp species found in this area.</li>\n</ul>\n",
    "Production": null,
    "Protein": "21 g",
    "Quote": "U.S. wild-caught brown rock shrimp is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#c49469",
