    "Location": "<ul>\n<li>Yellowtail flounder are found along the Atlantic coast of North America from Newfoundland to the Chesapeake Bay.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "The Cape Cod/Gulf of Maine stock is below target population levels. The Georges Bank and Southern New England/Mid-Atlantic stocks are significantly below target population levels.  Rebuilding plans are in place for all three stocks.",
    "Population Status": "<ul>\n<li>\n<p>There are three stocks of yellowtail flounder: Southern New England/Mid-Atlantic stock, Cape Cod/Gulf of Maine stock, and a Georges Bank stock. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Southern New England/Mid-Atlantic stock is overfished but not subject to overfishing (2019 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Yellowtail%20flounder%20-%20Southern%20New%20England%20/%20Mid-Atlantic&amp;stockid=10034\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Cape Cod/Gulf of Maine stock is not overfished but still rebuilding to the target level, and not subject to overfishing (2019 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Yellowtail%20flounder%20-%20Cape%20Cod%20/%20Gulf%20of%20Maine&amp;stockid=10035\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Georges Bank stock is overfished and subject to overfishing (2013 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Yellowtail%20flounder%20-%20Southern%20New%20England%20/%20Mid-Atlantic&amp;stockid=10034\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Limanda ferruginea",
    "Species Aliases": "<a href=\"/species-aliases/flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Flounder</a>, <a href=\"/species-aliases/rusty-dab\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Rusty dab</a>",
