    "Species Name": "Yellowtail Flounder",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Yellowtail flounder grow faster than most flatfish, up to 22 inches and 2.2 pounds.</li>\n<li>They can live up to 17 years, although most dont live past age 7.</li>\n<li>They also mature earlier than most flatfish.</li>\n<li>Almost all females are able to reproduce by the time they reach age 3.</li>\n<li>They spawn during the spring and summer.</li>\n<li>Females deposit their eggs on the ocean floor. After the eggs are fertilized, they float to the surface and the larvae drift in surface waters for about 2 months.</li>\n<li>When yellowtail flounder are first hatched, their eyes are symmetrical, with an eye on each side of their head. As the fish grows, it flattens out and the left eye slowly moves over to the right side of its head. After this metamorphosis, the juvenile settles to the ocean bottom.</li>\n<li>Juvenile yellowtail flounder mostly eat worms.</li>\n<li>Adults feed on crustaceans and worms.</li>\n<li>Spiny dogfish, skate, and a number of fish such as cod, hakes, flounder, and monkfish prey on yellowtail flounder.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
