    "Fishing Rate": "At recommended levels in Cape Cod/Gulf of Maine and Southern New England/Mid-Atlantic.  In Georges Bank the fishing rate is reduced to end overfishing.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of yellowtail flounder totaled more than 1.1 million pounds, and were valued at over $1.1 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:13669368926832::NO:RP::\">commercial fishing landings database</a>.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Yellowtail flounder are commonly harvested using trawl nets and, to a lesser extent, gillnets.</li>\n<li>Areas closures and gear restrictions reduce habitat impacts from trawl nets. &nbsp;Fishermen follow management measures to designed to reduce interactions with marine mammals, including gear modifications, seasonal closures, and use of marine mammal deterrents.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Yellowtail flounder are not commonly encountered by the recreational fishery.</li>\n<li><a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/recfishing/regs/index.html\">Regulations</a> include minimum fish sizes and possession limits.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Flounder is a good low-fat source of B vitamins and niacin.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Yellowtail flounder is a thin-bodied, right-eyed flounder.</li>\n<li>They are wide  nearly half as broad as they are long  with an oval body.</li>\n<li>They have a small mouth and an arched lateral line.</li>\n<li>Their upper side, including the fins, is brownish or olive, tinged with red and marked with large, irregular rusty red spots.</li>\n<li>True to their name, their tail fin and the edges of the two long fins are yellow.</li>\n<li>The underside is white, except for the caudal peduncle (the area between the body and the tail), which is yellowish.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.84 g",
    "Quote": "U.S. wild-caught yellowtail flounder is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#8b632a",
    "Research": "<ul>\n<li>Scientists at NOAA Fisheries <a href=\"https://www.nefsc.noaa.gov/\">Northeast Fisheries Science Center</a> conduct bottom trawl surveys every year during the fall and spring in inshore and offshore areas off the northeast coast to assess and monitor the abundance of yellowtail flounder and other species. Managers use this data along with information from Canadian, state agency, and university-run surveys to determine the status of the yellowtail flounder stocks.</li>\n<li>\n<p><a href=\"https://www.nefsc.noaa.gov/saw/sasi/sasi_report_options.php\">2019 Northeast Groundfish Operational Stock Assessments</a></p>\n</li>\n</ul>\n",
