    "Fishery Management": "<ul>\n<li>There are three stocks of winter flounder in U.S. waters, the Gulf of Maine, Georges Bank, and Southern New England/Mid-Atlantic stocks.</li>\n<li><a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/species/multispecies/\">NOAA Fisheries</a> and the <a href=\"https://www.nefmc.org/\">New England Fishery Management Council</a> manage the fishery in federal waters. The <a href=\"http://asmfc.org/\">Atlantic States Marine Fisheries Commission</a> coordinates management of winter flounder in state waters (0 to 3 miles offshore).</li>\n<li>Winter flounder, along with other groundfish in New England waters, are managed under the&nbsp;<a href=\"https://www.nefmc.org/management-plans/northeast-multispecies\">Northeast Multispecies Fishery Management Plan</a>, which includes:\n<ul>\n<li>Permitting requirements for commercial vessels.</li>\n<li>Separate management measures for recreational vessels.</li>\n<li>Time/Area Closures to protect spawning fish and habitat.</li>\n<li>Minimum fish sizes to prevent harvest of juvenile fish.</li>\n<li>Annual catch limits, based on best available science.</li>\n<li>An optional sector (<a href=\"https://www.fisheries.noaa.gov/national/laws-and-policies/catch-shares\">catch share</a>) program can be used for winter flounder and other groundfish species. The sector program allows fishermen to form harvesting cooperatives and work together to decide when, where, and how they harvest fish.</li>\n</ul>\n</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>Winter flounder get their name because of their migrations. In the winter, adults migrate from offshore areas where they feed to inshore bays and estuaries where they spawn.</li>\n<li>While inshore, they live on muddy sand, clean sand, clay, and pebbly or gravelly bottom habitats.</li>\n<li>They often bury their whole body, except for their eyes, in bottom sediments.</li>\n<li>Larvae and some 1-year-olds live in the estuaries where they were born.</li>\n<li>Juveniles prefer sand or sand-silt bottoms with a wide range of salinity and temperature.</li>\n</ul>\n",
    "Habitat Impacts": "Area closures and gear restrictions protect habitats that are affected by some kinds of trawl gear.",
