    "Location": "<ul>\n<li>Winter flounder are found in estuaries and on the continental shelf of the Northwest Atlantic, from the Gulf of St. Lawrence, Canada to North Carolina. They are most common north of Delaware Bay.</li>\n</ul>\n",
    "Management": null,
