    "Population": "Significantly below target population levels in Georges Bank and Southern New England/Mid-Atlantic. Rebuilding plans are in place for both stocks. The population level is unknown in the Gulf of Maine.",
    "Population Status": "<ul>\n<li>In U.S. waters, there are three stocks of winter flounder: Georges Bank, Southern New England/Mid-Atlantic, and Gulf of Maine. According to the most recent stock assessments:\n<ul>\n<li>\n<p>The Georges Bank stock is overfished, but is not subject to overfishing (2020 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10029\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Southern New England/Mid-Atlantic stock is still overfished, but not subject to overfishing (2020 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10030\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Gulf of Maine stock is not subject to overfishing and the overfished status is unknown because the population size could not be estimated due to highly uncertain assessment results (2020 stock assessment).&nbsp; Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10028\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Pseudopleuronectes americanus",
    "Species Aliases": "<a href=\"/species-aliases/flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Flounder</a>, <a href=\"/species-aliases/sole\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sole</a>, <a href=\"/species-aliases/lemon-sole\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Lemon sole</a>, <a href=\"/species-aliases/georges-bank-flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Georges Bank flounder</a>, <a href=\"/species-aliases/blackback-flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Blackback flounder</a>",
