    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Winter flounder live 15 to 18 years and grow to more than 2 feet in length.</li>\n<li>They spawn during the winter and spring in shallow inshore waters, often returning to the same areas where they were born to spawn.</li>\n<li>Females usually produce between 500,000 and 1.5 million eggs.</li>\n<li>They deposit their eggs on sandy bottoms and algal mats at night, usually about 40 times every spawning season.</li>\n<li>Newly hatched larvae have one eye on each side of the head.</li>\n<li>Five to six weeks after they hatch, larvae settle to the bottom to begin their transformation into juveniles.</li>\n<li>After weeks of adapting to living on the bottom, their left eye migrates to the right side of their body and their metamorphosis is complete.</li>\n<li>The growth and survival rates of larvae and juveniles depend on several factors, including temperature, salinity, water quality, and the availability of food.</li>\n<li>Winter flounder feed during the day because they depend on sight to locate prey.</li>\n<li>Their small mouths limit what they can eat.</li>\n<li>They feed on small invertebrates, shrimp, clams, and worms.</li>\n<li>Fish (mainly striped bass, bluefish, toadfish, and summer flounder), birds, invertebrates, winter skate, and marine mammals prey on larval and juvenile winter flounder.</li>\n<li>Atlantic cod, spiny dogfish, and monkfish prey on adults.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
