    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of winter flounder totaled approximately 1 million pounds, and were valued at $2.2 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:4400597929742::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Winter flounder are commonly harvested using trawl nets and to a lesser extent gillnets.</li>\n<li>Areas closures and gear restrictions reduce habitat impacts from trawl nets. &nbsp;Fishermen follow management measures to designed to reduce interactions with marine mammals, including gear modifications, seasonal closures, and use of marine mammal deterrents.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>There is a small recreational fishery for winter flounder. Anglers use bait to target flounder in nearshore waters.</li>\n<li><a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/recfishing/regs/index.html\">Regulations</a> include seasons, minimum fish sizes, and possession limits.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Flounder is a good, low-fat source of B vitamins and niacin.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Winter flounder have an oval shape and a thick body.</li>\n<li>Their eyes are on the right side of their body.</li>\n<li>They have a straight lateral line and dark coloring.</li>\n<li>Their coloring often varies with their habitat, ranging from muddy to slightly reddish brown, olive green, or dark slate, to an almost black upper side.</li>\n<li>Their underside is white, and their dorsal and anal fins are often tinged with pink, red, or yellow.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.84 g",
    "Quote": "U.S. wild-caught winter flounder is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#6b5a52",
    "Research": "<ul>\n<li>Scientists at <a href=\"https://www.nefsc.noaa.gov/\">NOAAs Northeast Fisheries Science Center</a> conduct bottom trawl surveys every year during the fall and spring in inshore and offshore areas off the northeast coast to assess and monitor the abundance of winter flounder and other species. Managers use this data, along with information from state agency and university-run surveys, to determine the status of the winter flounder population.<a href=\"https://www.nefsc.noaa.gov/saw/sasi/sasi_report_options.php\"> </a><a href=\"https://www.nefsc.noaa.gov/groundfish/operational-assessments-2017/\">Northeast Groundfish Operational Assessments website</a>.</li>\n<li>Research has indicated that winter flounder is an excellent candidate for stock enhancement, in which juvenile fish hatched from wild brood stock are raised in captivity and released into the wild. Scientists now know how to raise winter flounder and have learned how to release them in a way that maximizes their survival in the wild.</li>\n<li><a href=\"http://winterflounderenhancement.blogspot.com/\" rel=\"external\">Research</a>&nbsp;has also focused on understanding the impact of farm-raised winter flounder on the wild population. Fishermen, scientists, members of the aquaculture industry, and fisheries managers are all collaborating in this research to find ways to protect and enhance winter flounder and its fishery.</li>\n</ul>\n",
