    "Location": "<ul>\n<li>Coho salmon are found throughout the North Pacific Ocean and in most coastal streams and rivers from Alaska to central California.</li>\n<li>In North America, they're most abundant in coastal areas from southeast Alaska to central Oregon.</li>\n<li>Coho salmon have also been introduced in all the Great Lakes, as well as many other landlocked reservoirs throughout the United States.&nbsp;&nbsp;</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Alaska",
    "Population": "There are many stocks of coho salmon. Some stocks are above target population levels, while others are below.",
    "Population Status": "<ul>\n<li>Alaska:\n<ul>\n<li>There are more than 20 different stocks of coho salmon.</li>\n<li>Alaskan populations of coho salmon are near or above target population levels.</li>\n<li>The Alaska Coho Salmon Assemblage consists of coho salmon, sockeye salmon, pink salmon, and chum salmon throughout southeast Alaska. There are 3 indicator stocks of coho salmon that are used to determine the status of the assemblage; these indicator stocks are Auke Creek, Berners River, and Hugh Smith Lake.&nbsp; According to the 2017 stock assessments, these stocks are not overfished and not subject to overfishing.&nbsp; Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockname=coho\">Stock SMART</a>.</li>\n</ul>\n</li>\n<li>West Coast:\n<ul>\n<li>The status of coho populations in California and the Pacific Northwest varies.</li>\n<li>As of 2020&nbsp;many individual stocks are not overfished, but <a href=\"https://www.fisheries.noaa.gov/species/coho-salmon-protected\">one is listed as endangered, and three are considered threatened</a> under the <a href=\"https://www.fisheries.noaa.gov/topic/laws-policies#endangered-species-act\">Endangered Species Act (ESA)</a>.</li>\n<li>Summary 2018-2020 stock assessment information for all West Coast coho&nbsp;salmon stocks can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockname=coho\">Stock SMART</a>.</li>\n</ul>\n</li>\n<li>Populations are affected by:\n<ul>\n<li>Changes in ocean and climatic conditions.</li>\n<li>Habitat loss from dam construction and urban development.</li>\n<li>Degraded water quality from agricultural and logging practices.</li>\n</ul>\n</li>\n<li>Population conservation efforts include:\n<ul>\n<li>Removal and modification of dams that obstruct salmon migration.</li>\n<li>Restoration of degraded habitat.</li>\n<li>Acquisition of key habitat.</li>\n<li>Improvements to water quality and instream flow.</li>\n</ul>\n</li>\n<li>The <a href=\"https://www.westcoast.fisheries.noaa.gov/protected_species/salmon_steelhead/recovery_planning_and_implementation/pacific_coastal_salmon_recovery_fund.html\">Pacific Coastal Salmon Recovery Fund</a> supports the restoration of salmon species.</li>\n</ul>\n",
    "Scientific Name": "Oncorhynchus kisutch",
    "Species Aliases": "<a href=\"/species-aliases/salmon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Salmon</a>, <a href=\"/species-aliases/silver-salmon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Silver salmon</a>, <a href=\"/species-aliases/medium-red-salmon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Medium red salmon</a>, <a href=\"/species-aliases/hoopid-salmon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Hoopid salmon</a>, <a href=\"/species-aliases/white-salmon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">White salmon</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/coho_salmon.png",
    "alt": "Coho Salmon illustration",
    "title": "Coho Salmon"
    },
    "Species Name": "Coho Salmon",
    "Animal Health": null,
    "Availability": "<p>Fresh mainly in summer through late fall and frozen year-round.&nbsp;</p>\n",
    "Biology": "<ul>\n<li>Coho salmon are anadromousthey hatch in freshwater streams and&nbsp;spend a year in streams and rivers then migrate out to the saltwater environment of the ocean to feed and grow.</li>\n<li>Some stocks of coho salmon migrate more than 1,000 miles in the ocean, while other stocks remain in marine areas close to the streams where they were born.</li>\n<li>They spend about 1 years feeding in the ocean, then return to their natal streams or rivers to spawn, generally in fall or early winter.</li>\n<li>Adult coho salmon usually weigh 8 to 12 pounds and are 24 to 30 inches long.</li>\n<li>They typically spawn between the ages of 3 and 4.</li>\n<li>Female coho dig out gravel nests (redds) on stream bottoms where they lay their eggs.</li>\n<li>The eggs incubate for 6 to 7 weeks until they hatch.</li>\n<li>All coho salmon die after spawning.</li>\n<li>The newly hatched larvae remain in the gravel until the yolk sac is absorbed.</li>\n<li>While in fresh water, young coho salmon feed on plankton and insects.</li>\n<li>While in the ocean, they switch to a diet of small fishes such as herring, sandlance, anchovies, and sardines.</li>\n<li>Adults are also known to eat juveniles of other salmon species, especially pink and chum salmon, as well as juvenile sablefish.</li>\n<li>Otters, seals, and a variety of fish and birds prey on juvenile coho. Sharks, sea lions, seals, and orcas feed on adult coho.</li>\n<li>After salmon spawn and die, <a href=\"http://www.adfg.alaska.gov/index.cfm?adfg=wildlifenews.view_article&amp;articles_id=97&amp;issue_id=21\" rel=\"external\">salmon carcasses</a>&nbsp;are a valuable source of energy and nutrients to the river ecosystem. Carcasses have been shown to improve newly hatched salmon growth and survival by contributing nitrogen and phosphorous compounds to streams.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
