    "Fishing Rate": "Managers set fishing rates to avoid jeopardizing the survival and recovery of coho salmon stocks that are below their target levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>\n<p>In 2019, commercial landings of coho salmon totaled approximately 27 million pounds and were valued at more than $30 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:16450094704559::NO:::\">commercial fishing landings database</a>.</p>\n</li>\n<li>\n<p>Most of the U.S. coho salmon harvest comes from Alaska (approximately 25.7 million pounds) with a smaller amount from Washington (approximately 1.1 million pounds) and Oregon (approximately 134,000 pounds).</p>\n</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Coho salmon are mainly harvested in commercial troll fisheries in the Pacific Northwest and Alaska.</li>\n<li>Troll vessels catch salmon by \"trolling\" their lines with bait or lures through groups of feeding fish.</li>\n<li>To retrieve hooked fish, the lines are wound on spools by hand or hydraulically, and the fish are gaffed when alongside the vessel.</li>\n<li>The troll fishery produces low-volume, high-quality product.</li>\n<li>Coho are also harvested in commercial seine and gillnet fisheries (described <a href=\"http://www.adfg.alaska.gov/static/fishing/PDFs/commercial/whatkindofboat_cf.pdf\" rel=\"external\">here</a>), both in fisheries targeting stocks of coho and as bycatch in fisheries targeting other species of salmon.</li>\n<li>Fishing gear used to catch coho salmon rarely contacts the ocean floor and has little impact on habitat.</li>\n<li>Bycatch is low and usually consists of other salmon species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Salmon are a favorite catch of recreational fishermen.</li>\n<li>Coho are spectacular fighters and the most acrobatic of the Pacific salmon.</li>\n<li>Recreational fishermen use a variety of fishing gear to harvest coho salmon.</li>\n<li>To ensure recreational fisheries are sustainable, West Coast anglers are only allowed to keep a certain number of salmon per fishing trip.</li>\n<li>In Alaska, regulations vary by area and individual fisheries.</li>\n<li>Recreational fisheries in high-use areas (Cook Inlet, Southeast Alaska, Copper River) are regulated through management plans that allocate fish between competing commercial and recreational fishermen.</li>\n<li>In Washington, Oregon, and the Columbia River, hatchery fish are marked (by clipping the adipose fin), so wild coho can be released and have lower fishing mortality rates.</li>\n</ul>\n</li>\n<li>Subsistence fishery:\n<ul>\n<li>Salmon is an important source of spiritual and physical sustenance for Western Indian tribes and Alaska natives, and salmon are culturally important to many other residents of these areas.</li>\n<li>Subsistence fishermen use a variety of fishing gear to harvest coho salmon.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Coho salmon is low in sodium and a good source of omega-3 fatty acids, protein, niacin, vitamin B12, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Commonly called silver salmon, coho have dark metallic blue or greenish backs with silver sides and a light belly.</li>\n<li>While they are in the ocean, they have small black spots on their back and on the upper lobe of the tail.</li>\n<li>The gumline in the lower jaw has lighter pigment than on Chinook salmon.</li>\n<li>In fresh water, spawning coho are dark with reddish-maroon coloration on the sides.</li>\n<li>Spawning males develop a strongly hooked snout and large teeth.</li>\n<li>Before juvenile coho migrate to the sea, they lose their parr marks (a pattern of vertical bars and spots useful for camouflage) and gain the dark back and light belly coloration of coho living in the ocean.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "21.62 g",
    "Quote": "U.S. wild-caught coho salmon is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#4a5463",
    "Research": "<ul>\n<li><a href=\"https://www.afsc.noaa.gov/ABL/SOE/soe_default.php\">NOAA's Alaska Fisheries Science Center Salmon Research</a></li>\n<li><a href=\"https://www.nwfsc.noaa.gov/research/themes/species/index.cfm#salmon\">NOAAs Northwest Fisheries Science Center Salmon Research</a></li>\n<li><a href=\"https://swfsc.noaa.gov/textblock.aspx?id=1040&amp;ParentMenuId=110\">NOAAs Southwest Fisheries Science Center Salmon Research</a></li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "1.260 g",
