    "Source": "<p>U.S. wild-caught mainly from Alaska with smaller amounts from Washington and Oregon.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Smaller coho have a delicate flavor. Fillets from larger fish have a mild taste.</p>\n",
    "Texture": "<p>Coho has a high oil content and appears soft when raw, but becomes firm and flaky when cooked.</p>\n",
