    "Animal Health": null,
    "Availability": "<p>Fresh from late summer to spring and frozen and canned year-round.&nbsp;</p>\n",
    "Biology": "<ul>\n<li>Chum salmon are anadromousthey hatch in fresh water streams and rivers then migrate out to the saltwater environment of the ocean to feed and grow.</li>\n<li>Chum salmon do not reside in fresh water for an extended period (unlike coho, Chinook, and sockeye salmon).</li>\n<li>They can grow up to 3.6 feet and 30 to 35 pounds, but their average weight is 8 to 15 pounds.</li>\n<li>Young chum salmon (fry) typically migrate directly to estuarine and marine waters soon after they are born.</li>\n<li>As they grow larger, they migrate offshore across the North Pacific Ocean.</li>\n<li>As they approach sexual maturity, they migrate back into coastal waters and return to the fresh water area where they were born to spawn.</li>\n<li>They typically spawn between the ages of 3 and 6.</li>\n<li>They spawn from late summer to March, with peak spawning concentrated in early winter when the river flows are high.</li>\n<li>They usually nest in areas in the lowermost reaches of rivers and streams, within 60 miles of the ocean.</li>\n<li>They prefer to nest in areas with upwelling currents to provide oxygen for their developing embryos, and they cover their nests (redds) with gravel.</li>\n<li>In North America, female chum salmon typically have 2,000 to 4,000 eggs.</li>\n<li>All chum salmon die after they spawn.</li>\n<li>Young chum salmon feed on insects as they migrate downriver and on insects and marine invertebrates in estuaries and near-shore marine habitats.</li>\n<li>Adults eat copepods, fishes, mollusks, squid, and tunicates.</li>\n<li>Various fish and birds prey on juvenile chum salmon. Sharks, sea lions and seals, and orcas eat adult chum salmon.</li>\n<li>After salmon spawn and die, <a href=\"http://www.adfg.alaska.gov/index.cfm?adfg=wildlifenews.view_article&amp;articles_id=97&amp;issue_id=21\" rel=\"external\">salmon carcasses</a>&nbsp;are a valuable source of energy and nutrients to the river ecosystem. Carcasses have been shown to improve newly hatched salmon growth and survival by contributing nitrogen and phosphorous compounds to streams.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
