    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>\n<p>In 2019, commercial landings of chum salmon totaled more than 115.9 million pounds and were valued at more than $61.6 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:17165494918407::NO:::\">commercial fishing landings database</a>.</p>\n</li>\n<li>\n<p>Most of the harvest comes from Alaska (more than 113 million pounds).</p>\n</li>\n<li>\n<p>They are also harvested in Washington (more than 2.5 million pounds).</p>\n</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Purse seines and gillnets are used to catch chum salmon.</li>\n<li>Purse seiners catch salmon by encircling them with a long net and drawing the bottom closed to capture the fish.</li>\n<li>Gillnetters catch salmon by setting curtain-like nets perpendicular to the salmons trajectory as they migrate along the coast toward fresh water.</li>\n<li>Chum salmon are also caught in commercial troll fisheries for Chinook and coho salmon.</li>\n<li>Fishing gear used to catch chum salmon rarely contacts the ocean floor and has little impact on habitat.</li>\n<li>Bycatch is low and usually consists of other salmon species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Salmon are a favorite catch of recreational fishermen.</li>\n<li>Recreational fishermen use a variety of fishing gear to harvest chum salmon.</li>\n<li>To ensure recreational fisheries are sustainable, West Coast anglers are only allowed to keep a certain number of salmon per fishing trip.</li>\n<li>In Alaska, regulations vary by area and individual fisheries.</li>\n<li>Recreational fisheries in high-use areas (Cook Inlet, Southeast Alaska, Copper River) are regulated through management plans that allocate fish between competing commercial and recreational fishermen.</li>\n</ul>\n</li>\n<li>Subsistence fishery:\n<ul>\n<li>Salmon is an important source of spiritual and physical sustenance for Western Indian tribes and Alaska natives, and salmon are culturally important to many other residents of these areas.</li>\n<li>Subsistence fishermen use a variety of fishing gear to harvest chum salmon.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Chum salmon is low in sodium and a good source of omega-3 fatty acids, protein, niacin, vitamin B12, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Chum salmon is one of the largest species of Pacific salmon, second only to Chinook salmon in size.</li>\n<li>When in the ocean, chum salmon are metallic greenish-blue along the back with black speckles, similar to both sockeye and coho salmon.</li>\n<li>As they enter fresh water, their appearance changes dramatically.\n<ul>\n<li>Both sexes develop a tiger stripe pattern of bold red and black stripes.</li>\n<li>Males develop enormous canine-like fangs and their bodies have a striking calico pattern, with the front two-thirds of the flank marked by a bold, jagged, reddish line and the back third by a jagged black line.</li>\n<li>Spawning females are less flamboyantly colored and do not have fangs.</li>\n</ul>\n</li>\n<li>When juvenile chum salmon are about to migrate to sea, they lose their parr marks (vertical bars and spots useful for camouflage) and gain the dark back and light belly of fish living in open water.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.14 g",
    "Quote": "U.S. wild-caught chum salmon is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
