    "Species Name": "Brown Shrimp",
    "Animal Health": null,
    "Availability": "<p>Year-round, with peaks in the summer.</p>\n",
    "Biology": "<ul>\n<li>Brown shrimps growth depends on factors such as water temperature and salinity, and they can reach up to 7 inches in length.</li>\n<li>They have a short life span, usually less than two years.</li>\n<li>Brown shrimp are able to reproduce when they reach about 5  inches long.</li>\n<li>They spawn in relatively deep water.</li>\n<li>Females typically release about 500,000 to 1 million eggs near the ocean floor.</li>\n<li>Peak spawning is in spring and summer, with newly hatched shrimp entering estuaries in February and March to settle in their nursery habitat. &nbsp;&nbsp;</li>\n<li>Brown shrimp larvae feed on plankton (tiny floating plants and animals).</li>\n<li>Juvenile and adult shrimp feed on the bottom at night. They are omnivorous, and feed on worms, algae, microscopic animals, and various types of organic debris.</li>\n<li>Sheepshead minnows, water boatmen, and insect larvae eat postlarval shrimp.</li>\n<li>Grass shrimp, killifishes, and blue crabs prey on young shrimp.</li>\n<li>A wide variety of finfish feed heavily on juvenile and adult shrimp.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "106 ",
    "Carbohydrate": "0.91 g",
    "Cholesterol": "152 mg",
    "Color": "<p>Raw shrimp meat is translucent pink to gray. When cooked, their shells are pinkish-red and their meat is pearly white with pink and red shadings.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
