    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, landings of brown shrimp totaled 67 million pounds and were valued at more than $151 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:3431466813768::NO:::\">commercial fishing landings database</a>.</li>\n<li>The three species of penaeid shrimp (<a href=\"http://www.fishwatch.gov/profiles/white-shrimp\">white</a>, <a href=\"https://www.fishwatch.gov/profiles/pink-shrimp\">pink</a>, and brown) make up the vast majority&nbsp;of the shrimp harvested in the southeast. This fishery is one of the most valuable fisheries in the southeastern United States.</li>\n<li>Almost all of the brown shrimp harvested in the United States comes from the Gulf of Mexico, mainly from Texas and Louisiana.</li>\n<li>Annual harvests of brown shrimp vary considerably from year to year, primarily due to environmental conditions affecting population size.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen harvest shrimp with trawls towed near the ocean floor. The nets are wide in the front and taper toward the back.</li>\n<li>Shrimpers using otter trawl gear in the South Atlantic and Gulf of Mexico are required to use sea <a href=\"https://www.fisheries.noaa.gov/southeast/bycatch/turtle-excluder-device-regulations\">turtle excluder devices (TEDs)</a>.</li>\n<li>Some shrimp trawlers must also install <a href=\"https://www.sefsc.noaa.gov/labs/mississippi/brd/\">bycatch reduction devices</a> behind the TED, to reduce finfish bycatch.</li>\n<li>Area closures if&nbsp;fishing effort exceeds certain thresholds.</li>\n<li>Trawlers in the Gulf of Mexico must have a weak-link in the tickler chain, which hangs in front of the net and drags along the ocean floor to stir up shrimp from the bottom into the net. This weak-link allows the tickler chain to drop away if it gets hung up on natural bottom structures.</li>\n<li>Fishermen do not trawl in areas with coral reefs and other known areas of high-relief to avoid damage to their nets.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Under federal management, there is no recognized recreational fishery. Fishing in federal waters requires a permit.</li>\n<li>Recreational fishermen catch brown shrimp seasonally and almost always in state waters. State regulations vary from state to state.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Shrimp is low in saturated fat and is a very good source of protein, selenium, and vitamin B12.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Brown shrimp are crustaceans with 10 slender, relatively long walking legs and five pairs of swimming legs located on the front surface of the abdomen.</li>\n<li>They are grooved on the back surface of the shell and have a well-developed, toothed rostrum (part of their shell) that extends to or beyond the outer edge of the eyes.</li>\n<li>The tails of brown shrimp usually have a purple to reddish purple band and green or red pigmentation.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.31 g",
    "Quote": "U.S. wild-caught brown shrimp is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
