    "Location": "<ul>\n<li>Pacific mackerel are found from southeastern Alaska to Mexico but are most common south of Point Conception, California.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast",
    "Population": "Above target population levels.",
    "Population Status": "<ul>\n<li>Pacific mackerel is not overfished (2019 stock assessment), and&nbsp;not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Pacific%20chub%20mackerel%20-%20Pacific%20Coast&amp;stockid=10163\">Stock SMART</a>.</li>\n<li>Pacific mackerel naturally experience boom and bust cycles of abundance, which is typical of other small pelagic species that have relatively short life spans and high reproduction rates.</li>\n<li>The Pacific mackerel stock is well above its target population level. However, in historical terms, the population remains at a relatively low abundance level, due primarily to oceanographic conditions.</li>\n</ul>\n",
    "Scientific Name": "Scomber japonicus",
    "Species Aliases": "<a href=\"/species-aliases/chub-mackerel\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Chub mackerel</a>, <a href=\"/species-aliases/spanish-mackerel\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Spanish mackerel</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/pacific_mackeral.png",
    "alt": "Illustration of a Pacific Mackerel",
    "title": "Pacific Mackerel"
    },
    "Species Name": "Pacific Mackerel",
    "Animal Health": null,
    "Availability": "<p>Year-round, but primarily in summer.</p>\n",
    "Biology": "<ul>\n<li>Pacific mackerel grow fast, up to 25 inches and more than 6 pounds.</li>\n<li>They can live up to 18 years but are able to reproduce by age 4, and sometimes as early as age 1.</li>\n<li>They spawn at different times of the year, depending on where they live. Pacific mackerel spawn from late April to September off California, year-round off central Baja California peaking from June through October, and from late fall to early spring off Cabo San Lucas.</li>\n<li>They spawn several times a year, releasing batches of almost 70,000 eggs each time. The eggs usually hatch within 4 to 5 days.</li>\n<li>Pacific mackerel feed on plankton (tiny floating plants and animals) and the younger stages of all the pelagic species such as anchovies and sardines, as well as their own young.</li>\n<li>Various larger fish (such as sharks and tunas), marine mammals, and seabirds eat Pacific mackerel.</li>\n<li>Pacific mackerel school as a defense against predators. Often they will school with other pelagic species such as jack mackerel and sardines.</li>\n<li>As adults, they migrate north to Washington in the summer and south to Baja California in the winter. The northerly movement in summer is accentuated during El Nio events.</li>\n<li>They also travel inshore and offshore off Californiatheyre more abundant inshore from July to November and more abundant offshore from March to May.</li>\n</ul>\n",
    "Bycatch": "Bycatch is low because gear used is selective.",
