    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of Pacific mackerel totaled more than 1.5 million pounds and were valued at $272,000, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:3577333744020::NO:::\">commercial fishing landings database</a>.</li>\n<li>There is no directed fishery for mackerel in Oregon or Washington, but small&nbsp;amounts are taken incidentally by whiting trawlers and salmon trollers.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Round haul nets are used to catch Pacific mackerel.</li>\n<li>Habitat and bycatch impacts are minimal because the gear is used at the surface.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational fishermen catch Pacific mackerel in California but seldom target them.</li>\n<li>The statewide recreational harvest makes up a small fraction (less than 5 percent in weight) of the total landings.</li>\n<li>Californias recreational catch of Pacific mackerel is included within the fishery harvest guideline, but there are no other restrictions on this fishery.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Pacific mackerel is a good source of riboflavin and vitamin B6, and a very good source of protein, niacin, vitamin B12, selenium, and omega-3 fatty acids.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>The body of the Pacific mackerel tapers at both ends.</li>\n<li>They have a pointy head and a large mouth.</li>\n<li>The head is dark blue, the back is dark blue with about 30 dark wavy lines, and the undersides are silver green.</li>\n<li>Pacific mackerel can be distinguished from other mackerel by counting the finlets on their back; Pacific mackerel typically have four to six finlets.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.07 g",
    "Quote": "U.S. wild-caught Pacific mackerel is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#5f8794",
    "Research": "<ul>\n<li><a href=\"https://swfsc.noaa.gov/textblock.aspx?Division=FRD&amp;id=1119\" target=\"_blank\">NOAA Southwest Fisheries Science Center Mackerel Research</a></li>\n<li>Because environmental conditions greatly influence the abundance of coastal pelagic species such as Pacific mackerel, scientists are concerned about how climate change will affect the productivity of these species. NOAA and its partners track how oceanographic fluctuations affect marine resources, including Pacific mackerel.</li>\n</ul>\n",
