    "Location": "<ul>\n<li>Most of the U.S. catch of Pacific bluefin tuna is within about 100 nautical miles of the California coast.&nbsp;</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Pacific Islands",
    "Population": "Significantly below target population levels. Rebuilding measures are in place for U.S. fishermen.",
    "Population Status": "<ul>\n<li>According to the 2020&nbsp;stock assessment, Pacific bluefin tuna are overfished and subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=11687\">Stock SMART</a>.</li>\n<li>NOAA Fisheries first determined the Pacific bluefin tuna stock to be overfished in 2013. The 2020&nbsp;assessment completed by the <a href=\"http://isc.fra.go.jp/index.html\" rel=\"external\">International Scientific Committee for Tuna and Tuna-Like Species</a>&nbsp;in the North Pacific Ocean found the stock is still overfished.</li>\n</ul>\n",
    "Scientific Name": "Thunnus orientalis",
    "Species Aliases": "<a href=\"/species-aliases/northern-bluefin-tuna\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Northern bluefin tuna</a>, <a href=\"/species-aliases/tuna\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Tuna</a>, <a href=\"/species-aliases/bluefin-tuna\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Bluefin tuna</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/atlantic_bluefin_tuna.png",
    "alt": "Illustration of a Pacific Bluefin Tuna",
    "title": "Pacific Bluefin Tuna"
    },
    "Species Name": "Pacific Bluefin Tuna",
    "Animal Health": null,
    "Availability": "<p>Year-round, but most Pacific bluefin tuna are caught between May and October, and are sold to local restaurants.</p>\n",
    "Biology": "<ul>\n<li>Pacific bluefin tunas reach maturity at approximately 5 years of age and can live up to 26 years, although the average lifespan is about 15 years.</li>\n<li>Adults are approximately 1.5 meters (4 feet 11 inches) long and weigh about 60 kilograms (130 pounds).</li>\n<li>The maximum reported length and weight for Pacific bluefin tuna is 3 meters (9.8 feet) in length and 450 kilograms (990 pounds).</li>\n<li>Pacific bluefin tunas are predatory and mainly eat squids and fish, such as sardines and anchovies, saury, herring, pompanos, mackerel, hake, other tunas, and occasionally red crabs and krill.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "144",
    "Carbohydrate": "0 g",
    "Cholesterol": "38 mg",
    "Color": "<p>Deep red when uncooked. When cooked, the meat is an off-white or ivory color.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
