    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "Reduced to end overfishing.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>The average annual bluefin landings by U.S. commercial vessels fishing in the eastern Pacific Ocean represent only 2 percent of the average annual landings from all fleets fishing there.</li>\n<li>U.S.-caught Pacific bluefin tuna are commonly landed in California by fishermen who sell to local restaurants.</li>\n<li>In 2019, U.S. commercial landings of Pacific bluefin tuna totaled approximately 603,000 pounds and were valued at more than $730,000, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:1229232247924::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Purse seine, hook-and-line, and harpoon gear&nbsp;are used to catch Pacific bluefin tuna.</li>\n<li>Fishing gear used to catch bluefin tuna rarely contacts the seafloor so habitat impacts are minimal.</li>\n<li>These fishing methods are fairly selective and allow for the live release of unintentionally caught species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Pacific bluefin tuna are a highly valued species by recreational anglers.</li>\n<li>West Coast recreational fishing grounds primarily include offshore waters of southern California and northern Baja, and have historically included waters as far north as Monterey Bay.</li>\n<li>Commercial passenger fishing vessels and private boaters target Pacific bluefin tuna with recreational fishing gear using live bait (sardines or anchovy), casting jigs, and trolling jigs.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Bluefin tuna is a very good source of protein, thiamin, selenium, vitamin B6, and omega-3 fatty acids.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Pacific bluefin tuna have black or dark blue dorsal sides, with a grayish-green iridescence.</li>\n<li>Their bellies are dotted with silver or gray spots or bands.</li>\n<li>They have a series of small yellow fins, edged in black, running from the second dorsal fin to the tail.</li>\n<li>A distinguishing characteristic of Pacific bluefin tuna is that the tips of the pectoral fins do not reach the front of the second dorsal fin.</li>\n<li>They have relatively small eyes compared to other species of tuna.</li>\n</ul>\n",
    "Production": null,
    "Protein": "23.33 g",
    "Quote": "Although Pacific-wide populations are well below target levels, U.S. wild-caught Pacific bluefin tuna is a smart seafood choice because it is sustainably managed under rebuilding measures that limit harvest by U.S. fishermen.",
    "Quote Background Color": "#102b50",
    "Research": "<ul>\n<li><a href=\"https://www.fisheries.noaa.gov/news/some-good-news-pacific-bluefin-tuna\">Some Good News for Pacific Bluefin Tuna</a></li>\n<li><a href=\"http://www.sciencedirect.com/science/article/pii/S0165783612001579\" rel=\"external\">A Hybrid Stock Synthesis  Virtual Population Analysis Model of Pacific Bluefin Tuna</a></li>\n<li><a href=\"https://www.fisheries.noaa.gov/west-coast/science-data/fukushima-radiation-us-west-coast-tuna\" rel=\"external\">Radiocesium in Pacific Bluefin Tuna <em>Thunnus orientalis</em> in 2012 Validates New Tracer Technique</a></li>\n<li><a href=\"http://www.sciencedirect.com/science/article/pii/S0165783612000021\" rel=\"external\">Estimating Fishing and Natural Mortality Rates for Pacific Bluefin Tuna (<em>Thunnus orientalis</em>) Using Electronic Tagging Data</a></li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "1.257 g",
    "Selenium": "36.5 mcg",
    "Serving Weight": "100 g (raw)",
    "Servings": "1",
    "Sodium": "39 mg",
    "Source": "<p>U.S. wild-caught along the West Coast, primarily from California.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Bluefin has a distinctive flavor. With its high fat content, it is especially prized for sushi and sashimi. Cooking is generally not advised as it produces a strong fish taste and odor.</p>\n",
    "Texture": "<p>Bluefin tuna flesh is the darkest and fattiest of any tuna. A higher fat content in bluefin tuna is equated with a higher-quality product. The flesh has the firmness and appearance of beef steaks.</p>\n",
