    "Fishery Management": "<ul>\n<li><a href=\"https://www.fisheries.noaa.gov/region/pacific-islands\">NOAA Fisheries</a> and the <a href=\"http://wpcouncil.org/\">Western Pacific Fishery Management Council</a> manage this fishery in the Pacific Islands.</li>\n<li>Managed under the <a href=\"http://www.wpcouncil.org/fisheries/pacific-remote-islands/\">Fishery Ecosystem Plan for the Pelagic Fisheries of the Western Pacific</a>:\n<ul>\n<li>No management measures specifically apply to opah. However, general management measures apply to the fisheries that harvest opah.</li>\n<li>Fishermen are required to have permits and record their catch.</li>\n<li>Gear restrictions and operational requirements to minimize bycatch and potential gear conflicts among different fisheries.</li>\n<li>A limit on the number of permits for Hawaii and American Samoa longline fisheries controls participation in the fishery.</li>\n<li>Longline fishing is prohibited in some areas to protect endangered Hawaiian monk seals, reduce conflicts between fishermen, and prevent localized stock depletion (when a large quantity of fish are removed from an area).</li>\n<li>These zones are enforced through the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/topic/enforcement?_ga=2.86435227.2022413580.1600088945-1749182424.1566822859#vessel-monitoring\">vessel monitoring system program</a> (longline boats must be equipped with a satellite transponder that provides real-time position updates and tracks vessel movements).</li>\n<li>Hawaii- and American Samoabased longline vessels must carry onboard observers when requested by NOAA Fisheries, in part to record interactions with sea turtles, seabirds, and marine mammals.</li>\n<li>Annual training in safe handling and release techniques for protected species is required, and all vessels must carry and use specific equipment for handling and releasing these animals.</li>\n</ul>\n</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>Opah live in deep open ocean waters.</li>\n</ul>\n",
    "Habitat Impacts": "Fishing gear used to catch opah rarely contacts the ocean floor so habitat impacts are minimal.",
