    "Location": "<ul>\n<li>Opah are found in tropical and temperate waters around the world.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Pacific Islands",
    "Population": "The stock has never been assessed. The population level is unknown but presumed stable.",
    "Population Status": "<ul>\n<li>Opah has never been assessed, but there is no evidence that populations are in decline or that fishing rates are too high.</li>\n<li>Despite the opah's value to commercial and recreational fishermen, little research on the basic biology and ecology of opah has been conducted.</li>\n<li>To begin to fill some of the data gaps, NOAA's <a href=\"https://swfsc.noaa.gov/index.aspx?Division=FRD&amp;id=20067&amp;ParentMenuId=100\">Southwest Fisheries Science Center</a> began collecting biological samples from opah in 2009 and initiated an electronic tagging program in 2011.</li>\n<li>Scientists hope to continue tagging opah to learn about their movements and range. This research will provide the basic life history information necessary for future population assessments and management.</li>\n</ul>\n",
    "Scientific Name": "Lampris guttatus, Lampris spp.",
    "Species Aliases": "<a href=\"/species-aliases/moonfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Moonfish</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/opah.png",
    "alt": "Illustration of an Opah",
    "title": "Opah"
    },
    "Species Name": "Opah",
    "Animal Health": null,
    "Availability": "<p>Year-round, but landings seem to peak from April through August.</p>\n",
    "Biology": "<ul>\n<li>Because opah are not a major commercial seafood species and they live in the deep ocean, scientists know very little about their biology and ecology.</li>\n<li>Scientists assume opah share general characteristics with other Pacific Ocean pelagic fish.</li>\n<li>Scientists estimate that opah grow quickly.</li>\n<li>Although theyre not sure of opahs exact life span, scientists age opah by their fin rays, assuming fin ray marks are formed annually.</li>\n<li>Most opah caught in longline fisheries are estimated to be between 1 and 6 years old.</li>\n<li>They average about 100 pounds with a diameter of 3 feet.</li>\n<li>Opah spawn in warm surface waters throughout the year in the tropics and more seasonally in cooler waters.</li>\n<li>Opah seem to be very productive, potentially spawning many times throughout the spawning season.</li>\n<li>Opah are capable of traveling long distances, often in response to changing oceanic conditions such as temperature.</li>\n</ul>\n",
    "Bycatch": "There is no directed fishery. Regulations are in place to minimize bycatch in the tuna and swordfish fisheries, which incidentally catch opah.",
