    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial landings of opah in Hawaii totaled more than 3.2 million pounds and were valued at more than $6.2 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:7738934315666::NO:::\">commercial fishing landings database</a>. These figures may not match other agency sources of data due to confidential information.</li>\n<li>While there is no directed fishery for opah, they are harvested in small but significant quantities. U.S. fishermen catch them incidentally in tuna and swordfish fisheries around the U.S. Pacific Islands and off southern California.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>In Hawaii, opah are caught using longlines set deep below the surface to target bigeye tuna.</li>\n<li>Off California, they're taken incidentally in the California drift gillnet fishery targeting swordfish.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Although not commonly caught, opah are prized by deepwater recreational anglers for their unique colors and light flavor.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Opah is a rich source of omega-3s, protein, niacin, vitamins B6 and B12, phosphorus, and selenium. It is also low in sodium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Opah are an unusual looking fishthey have a round, flat body thats silvery gray in color.</li>\n<li>Toward the belly, the silver shades to a rose red, dotted with white spots.</li>\n<li>Their fins and mouth are red, and their large eyes are encircled with gold.</li>\n</ul>\n",
    "Production": null,
    "Protein": "24 g",
    "Quote": "U.S. wild-caught opah is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#af2316",
    "Research": "<ul>\n<li><a href=\"https://www.fisheries.noaa.gov/feature-story/seafood-industry-and-scientists-team-make-most-opah\">Seafood Industry and Scientists Team Up to Make the Most of Opah</a></li>\n<li><a href=\"https://www.fisheries.noaa.gov/feature-story/clues-fish-auction-reveal-several-new-species-opah\" rel=\"external\">Scientists identify five distinct species of Opah</a></li>\n<li><a href=\"https://www.fisheries.noaa.gov/feature-story/warm-blood-makes-opah-agile-predator\">Warm Blood Makes Opah an Agile Predator</a></li>\n<li><a href=\"https://swfsc.noaa.gov/index.aspx?Division=FRD&amp;id=20067&amp;ParentMenuId=100\">NOAA's Southwest Fisheries Science Center's Opah Page</a></li>\n<li><a href=\"http://oceanexplorer.noaa.gov/projects/02census/pelagics/pelagics.html\">NOAA Ocean Explorer Census of Marine Life Studying Pelagics: Discovering the long distance migration and deep diving behavior for large pelagics in the central North Pacific with pop-up archival transmitting tags</a></li>\n</ul>\n",
