    "Location": "<ul>\n<li>Atlantic herring are found on both sides of the North Atlantic. In the western North Atlantic, they are found from Labrador to Cape Hatteras, North Carolina.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "Significantly below target population level. A rebuilding plan is being developed for the Atlantic herring stock.",
    "Population Status": "<ul>\n<li>According to the 2020&nbsp;stock assessment, Atlantic herring are overfished, but not subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10572\">Stock SMART</a>.</li>\n<li>Herring populations are naturally highly variable.</li>\n</ul>\n",
    "Scientific Name": "Clupea harengus",
    "Species Aliases": "<a href=\"/species-aliases/herring\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Herring</a>, <a href=\"/species-aliases/sea-herring\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sea herring</a>, <a href=\"/species-aliases/sild\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sild</a>, <a href=\"/species-aliases/common-herring\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Common herring</a>, <a href=\"/species-aliases/labrador-herring\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Labrador herring</a>, <a href=\"/species-aliases/sardine\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sardine</a>, <a href=\"/species-aliases/sperling\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sperling</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/atlantic_herring.png",
    "alt": "Atlantic Herring illustration",
    "title": "Atlantic Herring"
    },
    "Species Name": "Atlantic Herring",
    "Animal Health": null,
    "Availability": "<p>Year-round. Most of the herring eaten in the United States is canned and either pickled or smoked.</p>\n",
    "Biology": "<ul>\n<li>Atlantic herring are one of nearly 200 herring species in the family <em>Clupeidae</em>.</li>\n<li>They grow quickly, up to 14 inches.</li>\n<li>They can live up to 15 years.</li>\n<li>They are able to reproduce when they reach age 4.</li>\n<li>Atlantic herring migrate in schools to areas where they feed, spawn, and spend the winter.</li>\n<li>They spawn as early as August in Nova Scotia and eastern Maine and from October through November in the southern Gulf of Maine, Georges Bank, and Nantucket Shoals.</li>\n<li>Female herring can produce 30,000 to 200,000 eggs.</li>\n<li>They deposit their eggs on rock, gravel, or sand ocean bottom.</li>\n<li>Schools of herring can produce so many eggs that they cover the ocean bottom in a dense carpet of eggs several centimeters thick.</li>\n<li>The eggs usually hatch in 7 to 10 days, depending on temperature.</li>\n<li>By late spring, larvae grow into juvenile herring, which form large schools in coastal waters during the summer.</li>\n<li>Atlantic herring is an important species in the food web of the northwest Atlantic Ocean.</li>\n<li>A variety of bottom-dwelling fishincluding winter flounder, cod, haddock, and red hakefeed on herring eggs.</li>\n<li>Juvenile herring are heavily preyed upon due to their abundance and small size. A number of fish, sharks, skates, marine mammals, and seabirds prey on herring.</li>\n<li>Atlantic herring feed on zooplankton (tiny floating animals), krill, and fish larvae.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
