    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of Atlantic herring totaled approximately 21 million pounds and were valued at $6.8 million, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:14533496384056::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>During the past decade, the United States has accounted for 78 percent of the total herring harvest, with Canada harvesting the remainder.</li>\n<li>The Atlantic herring fishery is extremely valuable to the economy in the Northeast United States. Herring are sold frozen, salted, or canned as sardines in both U.S. and international markets and provide affordable bait to fishermen targeting lobster, blue crab, and tuna.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Historically, Gulf of Maine herring were harvested along the coast in fixed-gear weirs (a fence of long stakes driven into the ground with nets arranged in a circle). Today, herring are primarily harvested by mid-water trawlers and purse seiners.</li>\n<li>Mid-water trawlers deploy and tow a net in the water column to catch schooling fish such as herring. The large front end of the net herds schooling fish toward the narrow back end, where they become trapped.</li>\n<li>Purse seiners catch schooling fish near the surface by encircling them with a net. When the net is around the school, fishermen lift up a wire that runs through the bottom of the net, closing the purse from below.</li>\n<li>Fisheries that harvest groundfish (such as cod and haddock) and herring operate in the same areas during the same seasons. Herring vessels may incidentally catch groundfish (mainly haddock). Managers set a cap on the amount of haddock that can be caught by the herring&nbsp;midwater trawl fishery.</li>\n<li>The Atlantic herring fishery can incidentally catch marine mammals. Currently, these takes are below limits that would require mitigation under the Marine Mammal Protection Act.</li>\n<li>Management measures to reduce marine mammal interactions include research, outreach to educate fisherman about actions they can take in the event of a marine mammal interaction, and efforts to communicate interaction hotspots to fishermen.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Atlantic herring are a great source of omega-3s, vitamin B12, and iron.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Atlantic herring are small schooling fish.</li>\n<li>They are silvery in color, with a bluish or greenish-blue back.</li>\n</ul>\n",
    "Production": null,
    "Protein": "17.96 g ",
    "Quote": "U.S. wild-caught Atlantic herring is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#748e94",
    "Research": "<ul>\n<li>NOAAs <a href=\"https://www.nefsc.noaa.gov/\">Northeast Fisheries Science Center</a> collects data on herring during its bottom trawl surveys and also conducts a dedicated herring acoustic survey, concentrated on the Georges Bank region. Members of the lobster fishing industry (who have an interest in the herring resource as a bait source), private research institutions, and the Science Center have conducted acoustic herring surveys using smaller industry vessels, with sampling largely concentrated on inshore Gulf of Maine herring stocks. Results from these efforts are not yet available.</li>\n</ul>\n",
