    "NOAA Fisheries Region": "Southeast",
    "Population": "Above target population levels in the South Atlantic and the Gulf of Mexico. ",
    "Population Status": "<ul>\n<li>\n<p>There are three stocks of vermilion snapper: South Atlantic, Gulf of Mexico, and Caribbean. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The South Atlantic stock is not overfished (2018), and is not subject to overfishing based on 2020&nbsp;catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10257\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Gulf of Mexico stock is not overfished (2020), and is not subject to overfishing based on 2020 catch data.&nbsp; &nbsp;Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10256\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>In the Caribbean, vermilion snapper is contained in the Snappers Complex. Scientists have not assessed the Snappers Complex, so the population status is unknown. The Snappers Complex is not subject to overfishing based on 2019 catch data.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Rhomboplites aurorubens",
    "Species Aliases": "<a href=\"/species-aliases/snapper\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Snapper</a>, <a href=\"/species-aliases/beeliner\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Beeliner</a>, <a href=\"/species-aliases/clubhead-snapper\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Clubhead snapper</a>, <a href=\"/species-aliases/night-snapper\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Night snapper</a>, <a href=\"/species-aliases/besugo\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Besugo</a>, <a href=\"/species-aliases/rubia\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Rubia</a>",
