    "Species Name": "Vermilion Snapper",
    "Animal Health": null,
    "Availability": "<p>Year-round, with peaks from August through November.</p>\n",
    "Biology": "<ul>\n<li>Vermilion snapper grow slowly, up to 2 feet long and 7 pounds.</li>\n<li>They can live to at least 15 years.</li>\n<li>Vermilion snapper are able to reproduce when theyre young, between 1 and 2 years old.</li>\n<li>They spawn multiple times from April to September, but most often from June to August.</li>\n<li>Vermilion snapper eat fish, shrimp, crabs, worms, and other bottom-dwelling invertebrates, as well as squids and plankton (tiny floating plants and animals).</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.  ",
