    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of vermilion snapper in the Gulf of Mexico and South Atlantic totaled 1.6 million pounds and were valued at more than $5.6 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:12220081225749::NO:::\">commercial fishing landings database</a>.&nbsp;&nbsp;More than a third of these landings were harvested off the west coast of Florida.</li>\n<li>Vermilion snapper is one of the most frequently caught snappers along the southeastern coast.</li>\n<li>Seasonal closure in federal waters of the U.S. Caribbean and in territorial waters of St. Thomas/St. John from October through December.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>In all regions, commercial fishermen primarily use vertical hook-and-line gear (handline and bandit gear) to catch vermilion snapper.</li>\n<li>Although it has minimal impacts on habitat, hook-and-line gear can incidentally catch other finfish.</li>\n<li>Fishermen in the Gulf of Mexico and South Atlantic are required to use circle hooks in certain areas and de-hooking devices to improve the chance of survival of any unintentionally caught fish.</li>\n<li>In the Gulf of Mexico, fishermen are encouraged to use <a href=\"https://www.flseagrant.org/fisheries/venting/\" rel=\"external\">venting tools</a> or <a href=\"https://www.flseagrant.org/fisheries/catch-and-release-fishing/descending-and-venting/\" rel=\"external\">fish descenders</a> when fish are caught showing signs of barotrauma. Barotrauma occurs when reef fish are quickly brought to the surface by hook-and-line gear and the gas in their swimbladders overexpands. <a href=\"https://www.flseagrant.org/fisheries/venting/\" rel=\"external\">Venting tools</a> help deflate the expanded abdominal cavity, preventing serious injury to the fish and easing their return to deep water. Fish descenders help fish return to deeper waters so air in the abdomen can recompress.</li>\n<li>Fish traps, gill nets, and bottom longlines can generate bycatch. There are restrictions limiting or prohibiting the use of these gears in the South Atlantic, Gulf of Mexico, and U.S. Caribbean to reduce bycatch.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Vermilion snapper are a popular catch for recreational fishermen.</li>\n<li>In 2020, recreational anglers landed approximately 2.5 million pounds of vermilion snapper, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:12220081225749::NO:::\">recreational fishing landings database</a>.&nbsp; &nbsp;</li>\n<li>Minimum size limits.</li>\n<li>Bag limits.</li>\n<li>Seasonal closure in federal waters of the U.S. Caribbean and in territorial waters of St. Thomas/St. John from October through December.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Vermilion snapper is low in saturated fat and sodium and is a very good source of protein.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Vermilion snapper have streamlined bodies.</li>\n<li>They are pale to silvery white below and vermilion (orange-red) above. They have narrow, yellow-gold streaks (some horizontal and others diagonal) below the lateral line.</li>\n<li>Their back (dorsal) fin is rose-colored with a yellow edge, and the tail (caudal) fin is red with a faint black edge.&nbsp;</li>\n</ul>\n",
