    "Species Name": "Atlantic Blacktip Shark",
    "Animal Health": null,
    "Availability": "<p>Availability varies because the fishery is open periodically throughout the year and closes when the quota is reached.</p>\n",
    "Biology": "<ul>\n<li>Atlantic blacktip sharks grow quickly, and can reach up to 6 feet in length. The oldest observed blacktip shark was 15.5 years old.</li>\n<li>They often form large groups, segregated into separate schools of males and females when they are not mating. They mate between March and June.</li>\n<li>Males mature at 4 to 5 years of age, while females mature later, at 6 to 7 years of age.</li>\n<li>Females have an 11- to 12-month gestation period and give birth to an average of three pups per litter in the Atlantic and four to five pups per litter in the Gulf of Mexico.</li>\n<li>Pups are born in shallow nursery grounds away from the adult population. After giving birth, the females leave the nursery area while juveniles remain.&nbsp;</li>\n<li>Blacktip sharks eat bony fishes, smaller sharks, squids, stingrays, shrimp, and crabs. They often follow fishing boats and are sometimes seen consuming discarded fish.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
