    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>In 2019, commercial landings of Atlantic and Gulf of Mexico blacktip sharks totaled 211,200&nbsp;pounds, and were valued at $121,700, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:13687024585429::NO:::\">commercial fishing landings database</a>.&nbsp;\n<ul>\n<li>To commercially harvest Atlantic sharks, vessel owners must obtain a valid Atlantic shark directed or incidental limited access permit or a smoothhound shark open access permit. &nbsp;More information regarding limited access permits can be found in the <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-highly-migratory-species-fishery-compliance-guides#commercial-fishing-compliance-guide\">Atlantic HMS commercial compliance guide</a>.</li>\n<li>Atlantic blacktip sharks belong to the large coastal shark complex. &nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Bottom longlines and gillnets are used to catch Atlantic blacktip sharks.</li>\n<li>Gear used to catch blacktip sharks may catch other species, including marine mammals and turtles.</li>\n<li>Fishermen using bottom longline or gillnet gear must complete a protected species safe handling, release, and identification <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-protected-species-safe-handling-release-and\">workshop</a>.</li>\n<li>Certain areas are closed to shark fishing to protect nursery areas, sensitive habitats, and populations.</li>\n<li><a href=\"https://www.fisheries.noaa.gov/topic/enforcement/vessel-monitoring\">Vessel monitoring systems</a> ensure fishermen are complying with area closures.</li>\n<li>To reduce bycatch of fish, including Atlantic blacktip sharks, shrimp trawlers are required to use <a href=\"https://www.sefsc.noaa.gov/labs/mississippi/brd/\">bycatch reduction devices</a>, which are designed to retain shrimp but allow fish to exit the net.</li>\n</ul>\n</li>\n<li>Recreational fishermen typically use rod-and-reel gear for blacktip sharks:\n<ul>\n<li>In 2019, recreational anglers landed more than 74,000 pounds of blacktip shark, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:16683200594774::NO:::\">recreational fishing landings database</a>.&nbsp;&nbsp;</li>\n<li>Recreational fishermen must have an Atlantic <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-highly-migratory-species-permits-and-reporting-forms\">HMS permit</a> to harvest Atlantic blacktip sharks in federal waters. As of January 1, 2018, all HMS recreational permit holders will need a shark endorsement to fish for, retain, possess, or land sharks.&nbsp;</li>\n<li>Fishermen fishing recreationally for sharks will be required to use circle hook in most places.&nbsp; For more information regarding these requirements, please refer to HMS regulations and the <a href=\"https://www.fisheries.noaa.gov/webdam/download/64522080\">Amendment 5b compliance guide</a>.</li>\n<li>Blacktip sharks are a popular recreational species because they are found near shore and often jump and spin out of the water.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Shark is a low-fat source of protein and is high in selenium and vitamins B6 and B12. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health</a> and seafood.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Atlantic blacktip sharks are gray to gray-brown, with white on the belly and a conspicuous wedge-shaped band or Z-shaped line on the sides.</li>\n<li>Their pectoral, dorsal, and tail fins have black tips, but the anal fin is white.&nbsp;</li>\n<li>Their bodies have a torpedo shape, which allows them to swim through the water with little effort.</li>\n<li>Atlantic blacktip sharks are often confused with spinner sharks due to their similar size, shape, coloration, and behavior. Both species are known for leaping and spinning out of the water while feeding on schools of fish. A distinguishing feature is that the anal fin on the blacktip shark is white whereas the anal fin of the spinner shark has a black tip.</li>\n</ul>\n",
    "Production": null,
    "Protein": "21 g",
    "Quote": "U.S. wild-caught Atlantic blacktip shark is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
