    "Population Status": "<ul>\n<li>\n<p>There are two stocks of haddock: Georges Bank and Gulf of Maine stocks. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Georges Bank stock is not overfished and is not subject to overfishing (2019 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10520\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Gulf of Maine stock is not overfished and is not subject to overfishing (2019 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10519\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
