    "Biology": "<ul>\n<li>Haddock are a fast-growing species that typically range between 1 and 3 feet long at maturity.</li>\n<li>They can live for 10 or more years, although NOAA Fisheries scientists typically catch haddock that are between 3 and 7 years old.</li>\n<li>They generally weigh between 2 and 7 pounds.</li>\n<li>Haddock begin to reproduce between the ages of 1 and 4 years old and at 10.5 to 11.7 inches long.</li>\n<li>They spawn between January and June on eastern Georges Bank, to the east of Nantucket Shoals and along the Maine coast over rock, gravel, sand, or mud bottoms.</li>\n<li>Haddock are very productive. Every year, an average-sized female produces around 850,000 eggs, and larger females can produce up to 3 million eggs.</li>\n<li>Females release their eggs in batches near the ocean floor, where a courting male fertilizes them.</li>\n<li>Once fertilized, eggs rise to the surface where they drift with ocean currents.</li>\n<li>Newly hatched haddock remain near the surface for several months before they settle to the bottom.</li>\n<li>Haddock feed on a variety of bottom-dwelling animals, including mollusks, worms, crustaceans, sea stars, sea urchins, sand dollars, brittle stars, and occasional fish eggs.</li>\n<li>Adults sometimes eat small fish, especially herring.</li>\n<li>Spiny dogfish, skates, and many groundfish species (cod, pollock, cusk, hake, monkfish, halibut, and sea raven) prey on juvenile haddock. Gray seals also prey on haddock.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
