    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial landings of haddock totaled more than 19.2 million pounds and were valued at $18.9 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:3445549958587::NO:::\">commercial fishing landings database</a>.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Haddock are commonly harvested using trawl nets, gillnets, bottom longlines, and rod and reel.</li>\n<li>Gillnets, longlines, and rod and reel used to harvest haddock have little to no impact on habitat.</li>\n<li>Areas closures and gear restrictions reduce habitat impacts from trawl nets.</li>\n<li>Fishermen follow management measures to designed to reduce interactions with marine mammals, including gear modifications, seasonal closures, and use of marine mammal deterrents.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Haddock are highly prized by recreational fishermen. Recreational vessels make up a significant proportion of the harvest in the Gulf of Maine.</li>\n<li>Haddock are commonly harvested by anglers fishing offshore waters with bait. Fishing occurs year-round.</li>\n<li>In 2019, recreational anglers landed more than 1.8 million pounds, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:3445549958587::NO:::\">recreational fishing landings database</a>.&nbsp;&nbsp;</li>\n<li><a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/recfishing/regs/index.html\">Regulations</a> include seasons, minimum fish sizes and possession limits.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Haddock is a great source of low-fat protein, magnesium, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Haddock are a member of the cod family, but they are smaller than Atlantic cod.</li>\n<li>They can be distinguished by a black thumbprint found on each side of their body.</li>\n<li>Their skin is also less mottled than cod.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.91 g",
    "Quote": "U.S. wild-caught haddock is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#433321",
    "Research": "<ul>\n<li>Scientists at NOAAs <a href=\"https://www.nefsc.noaa.gov/\">Northeast Fisheries Science Center</a> conduct bottom trawl surveys every year during the fall and spring in inshore and offshore areas off the northeast coast to monitor the abundance of haddock and other species. They use these data, along with data from surveys conducted by the state fisheries management agencies and fishery statistics, to determine the status of the haddock stocks.</li>\n<li><a href=\"https://www.nefsc.noaa.gov/saw/sasi/sasi_report_options.php\">2019&nbsp;Northeast Groundfish Operational Assessments website</a></li>\n<li>Through research conducted at the Atlantic Marine Aquaculture Center, University of New Hampshire, and NOAA Fisheries scientists found&nbsp;that haddock is a good candidate for open ocean aquaculture. The fish grew wellfrom 1/2 ounce to nearly 4 poundsand further research on feed formulation and maturation control will likely improve growth rates. Mortality was low, and there was no incidence of disease. None of the haddock escaped from the cage, and a rigorous monitoring program detected no impact on the surrounding environment.</li>\n</ul>\n",
