    "Location": "<ul>\n<li>In North America, Chinook salmon range from the Monterey Bay area of California to the Chukchi Sea area of Alaska.&nbsp;</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Alaska",
    "Population": "There are numerous stocks of Chinook salmon. Some are above target population levels, others below. Some are wild and some are hatchery-raised, but all are managed using strict regulations to balance the needs of people and other marine life.",
    "Population Status": "<ul>\n<li>Alaska:\n<ul>\n<li>In Alaska, the status of Chinook salmon stocks varies.</li>\n<li>Some stocks are in decline, while others are steady or increasing.</li>\n<li>None are listed under the <a href=\"https://www.fisheries.noaa.gov/topic/laws-policies#endangered-species-act\">Endangered Species Act (ESA)</a>.</li>\n<li>According to the 2017 stock assessment, the Eastern North Pacific Far North Migrating stock is not overfished and not subject to overfishing.&nbsp; Summary stock assessment information can be found on&nbsp;<a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Chinook%20salmon%20-%20Eastern%20North%20Pacific%20Far%20North%20Migrating&amp;stockid=10604\">Stock SMART.</a></li>\n</ul>\n</li>\n<li>West Coast:\n<ul>\n<li>The status of Chinook stocks in California and the Pacific Northwest varies.</li>\n<li>Some stocks are in decline, while others are steady or increasing.</li>\n<li>As of 2020, <a href=\"https://www.fisheries.noaa.gov/species/chinook-salmon-protected\">two populations</a>&nbsp;of Chinook salmon&nbsp;are listed as endangered, and seven are listed as threatened under the ESA.</li>\n<li>Summary 2020 stock assessment information for all West Coast Chinook salmon stocks can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockname=chinook\">Stock SMART</a>.</li>\n</ul>\n</li>\n<li>Populations are affected by:\n<ul>\n<li>Changes in ocean and climatic conditions.</li>\n<li>Habitat loss from dam construction and urban development.</li>\n<li>Degraded water quality from agricultural and logging practices.</li>\n</ul>\n</li>\n<li>Population conservation efforts include:\n<ul>\n<li>Captive-rearing in hatcheries.</li>\n<li>Removal and modification of dams that obstruct salmon migration.</li>\n<li>Restoration of degraded habitat.</li>\n<li>Acquisition of key habitat.</li>\n<li>Improvements to water quality and instream flow.</li>\n</ul>\n</li>\n<li>The <a href=\"https://www.westcoast.fisheries.noaa.gov/protected_species/salmon_steelhead/recovery_planning_and_implementation/pacific_coastal_salmon_recovery_fund.html\">Pacific Coastal Salmon Recovery Fund</a> supports the restoration of salmon species.</li>\n</ul>\n",
    "Scientific Name": "Oncorhynchus tshawytscha",
    "Species Aliases": "<a href=\"/species-aliases/king-salmon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">King salmon</a>, <a href=\"/species-aliases/spring-salmon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Spring salmon</a>, <a href=\"/species-aliases/tyee\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Tyee</a>, <a href=\"/species-aliases/winter\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Winter</a>, <a href=\"/species-aliases/quinnat\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Quinnat</a>, <a href=\"/species-aliases/blackmouth\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Blackmouth</a>",
