    "Animal Health": null,
    "Availability": "<p>Fresh mainly in the summer and early fall, though the timing varies by area and fishery. Frozen year-round.</p>\n",
    "Biology": "<ul>\n<li>Chinook salmon are anadromousthey hatch in freshwater streams and rivers then migrate out to the saltwater environment of the ocean to feed and grow.</li>\n<li>Chinook salmon are the largest of the Pacific salmon, hence the name king salmon.</li>\n<li>They can grow as long as 4.9 feet and up to 129 pounds, but typical length and weight of mature fish are about 3 feet and 30 pounds.</li>\n<li>They spend a few years feeding in the ocean, then return to their natal streams or rivers to spawn, generally in summer or early fall.</li>\n<li>Chinook salmon sexually mature between the ages of 2 and 7 but are typically 3 or 4 years old when they return to spawn.</li>\n<li>Chinook dig out gravel nests (redds) on stream bottoms where they lay their eggs.</li>\n<li>All Chinook salmon die after spawning.</li>\n<li>Young Chinook salmon feed on terrestrial and aquatic insects, amphipods, and other crustaceans.</li>\n<li>Older Chinook primarily feed on other fish.</li>\n<li>Fish (such as whiting and mackerel) and birds eat juvenile Chinook salmon.</li>\n<li>Marine mammals, such as orcas and sea lions, and sharks eat adult salmon.</li>\n<li>Salmon are also primary prey for Southern Resident killer whales, an endangered species.</li>\n<li>After salmon spawn and die, <a href=\"http://www.adfg.alaska.gov/index.cfm?adfg=wildlifenews.view_article&amp;articles_id=97&amp;issue_id=21\" rel=\"external\">salmon carcasses</a> are a valuable source of energy and nutrients to the river ecosystem. Carcasses have been shown to improve newly hatched salmon growth and survival by contributing nitrogen and phosphorous compounds to streams.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "179",
    "Carbohydrate": "0 g",
    "Cholesterol": "50 mg",
    "Color": "<p>The meat is almost always red, never pink, except for the rare <a href=\"http://www.adfg.alaska.gov/index.cfm?adfg=wildlifenews.view_article&amp;articles_id=244&amp;issue_id=43\" rel=\"external\">white-meat variety</a>.</p>\n",
