    "Fishing Rate": "Managers set fishing rates to avoid jeopardizing the survival and recovery of Chinook salmon stocks that are below their target levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, U.S. commercial landings of Chinook salmon totaled approximately 9.9 million pounds and were valued at&nbsp;$43 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:8058436371159::NO:::\">commercial fishing landings database</a>.</li>\n<li>Most of the Chinook in the U.S. market comes from U.S. and Canadian fisheries.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Chinook salmon are harvested using a variety of gear types.</li>\n<li>Troll vessels catch salmon by \"trolling\" their lines with bait or lures through groups of feeding fish.</li>\n<li>To retrieve hooked fish, the lines are wound on spools by hand or hydraulically, and the fish are pulled aboard when alongside the vessel.</li>\n<li>The troll fishery produces low-volume, high-quality product.</li>\n<li>Chinook are also harvested in commercial seine and gillnet fisheries (described <a href=\"http://www.adfg.alaska.gov/static/fishing/PDFs/commercial/whatkindofboat_cf.pdf\" rel=\"external\">here</a>), both in fisheries targeting stocks of Chinook and as bycatch in fisheries targeting other species of salmon.</li>\n<li>Fishing gear used to catch Chinook salmon rarely contacts the ocean floor and has little impact on habitat.</li>\n<li>Bycatch is low and usually consists of other salmon species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Chinook salmon are a favorite catch of recreational fishermen.</li>\n<li>Recreational fishermen use a variety of fishing gear to harvest Chinook salmon.</li>\n<li>To ensure recreational fisheries are sustainable, West Coast anglers are only allowed to keep a certain number of salmon per fishing trip.</li>\n<li>In Alaska, regulations vary by area and individual fisheries.</li>\n<li>Recreational fisheries in high-use areas (Cook Inlet, Southeast Alaska, Copper River) are regulated through management plans that allocate fish between competing commercial and recreational fishermen.</li>\n</ul>\n</li>\n<li>Subsistence fishery:\n<ul>\n<li>Salmon is an important source of spiritual and physical sustenance for Western Indian tribes and Alaska natives, and salmon are culturally important to many other residents of these areas.</li>\n<li>Subsistence fishermen use a variety of fishing gear to harvest Chinook salmon.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Chinook salmon is low in sodium and is a good source of omega-3 fatty acids, protein, niacin, vitamin B12, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>When theyre in the ocean, Chinook salmon are blue-green on the back and top of the head with silvery sides and white bellies.</li>\n<li>They have black spots on the upper half of the body and on both lobes of the tail fin.</li>\n<li>Chinook salmon also have a black pigment along the gum line, thus the nickname \"blackmouth.\"</li>\n<li>In fresh water, when they are about to spawn, Chinook change to olive brown, red, or purplish. This color change is particularly evident in males.</li>\n<li>Spawning adult males can be distinguished by their hooked upper jaw.</li>\n<li>Females can be distinguished by a torpedo-shaped body, robust mid-section, and blunt nose.</li>\n<li>Juveniles in fresh water (fry) have well-developed parr marks on their sides (the pattern of vertical bars and spots useful for camouflage).</li>\n<li>Before juveniles migrate to the sea, they lose their parr marks and gain the dark back and light belly characteristic of fish living in open water.</li>\n</ul>\n",
    "Production": null,
    "Protein": "19.93 g",
    "Quote": "Although some Chinook populations are below target levels, U.S. wild-caught Chinook salmon is a smart seafood choice because they are sustainably managed with careful consideration given to impacts on endangered and threatened species.",
    "Quote Background Color": "#6d9489",
    "Research": "<ul>\n<li><a href=\"https://www.afsc.noaa.gov/ABL/SOE/soe_default.php\">NOAA's Alaska Fisheries Science Center Salmon Research</a></li>\n<li><a href=\"https://www.nwfsc.noaa.gov/research/themes/species/index.cfm#salmon\">NOAAs Northwest Fisheries Science Center Salmon Research</a></li>\n<li><a href=\"https://swfsc.noaa.gov/textblock.aspx?id=1040&amp;ParentMenuId=110\">NOAAs Southwest Fisheries Science Center Salmon Research</a></li>\n</ul>\n",
