    "Servings": "1",
    "Sodium": "47 mg",
    "Source": "<p>U.S. wild-caught from Alaska to California.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Chinook salmon has a pronounced buttery, rich taste. They are the most highly prized salmon in the culinary world. &nbsp;White Chinook taste the same as the darker variety.</p>\n",
    "Texture": "<p>Oily, flaky, and meaty.</p>\n",
