    "Species Name": "Pacific Bigeye Tuna",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Bigeye tuna grow fast and can reach about 6.5 feet in length.</li>\n<li>They live 7 to 8 years and are able to reproduce when they are 3 years old.</li>\n<li>Bigeye tuna spawn throughout the year in tropical waters and seasonally in cooler waters.</li>\n<li>Theyre able to spawn almost daily, releasing millions of eggs each time.</li>\n<li>Eggs are found in the top layer of the ocean, buoyed at the surface by a single oil droplet until they hatch.</li>\n<li>Bigeye tuna feed near the top of the food chain, preying on fish, crustaceans, and squid.</li>\n<li>They are prey for many top predators, including larger tunas and billfish.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "130",
    "Carbohydrate": "0 g",
    "Cholesterol": "45 mg",
    "Color": "<p>Bigeye tuna meat has a reddish-pink color.</p>\n",
