    "Fishing Rate": "At recommended levels in the Western and Central Pacific and the Eastern Pacific.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>The majority of U.S.-caught bigeye tuna comes from Hawaii, although a substantial amount is also harvested by U.S. purse seine vessels and landed in American Samoa or other countries for canning.</li>\n<li>\n<p>In 2020, commercial landings of Pacific bigeye tuna totaled 16.7 million pounds and were valued at $56 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:28374356332900::NO:::\">commercial fishing landings database</a>.&nbsp;</p>\n</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Fishermen based in Hawaii, American Samoa, and the U.S. Pacific Islands target Pacific bigeye tuna with hook-and-line, pelagic longline, or troll fishing gear</li>\n<li>U.S. commercial purse seine fishermen in the western and central Pacific also harvest bigeye tuna.</li>\n<li>The U.S. coastal purse seine fleet operating in the eastern Pacific Ocean typically fish off California for small pelagic species such as sardine and anchovy and sometimes catch bigeye tuna when warm water from the south brings bigeye tuna within their range.</li>\n<li>Fishing gear used to catch Pacific bigeye tuna rarely contacts the seafloor so habitat impacts are minimal.</li>\n<li>Restrictions on the type of fishing gear that can be used, and prohibitions on fishing in certain areas, minimize impacts on protected species.</li>\n<li>Interactions with protected species such as sea turtles, marine mammals, and seabirds in these fisheries are rare and survival rates are estimated to be high for all gear types.</li>\n<li>Longline fishermen are trained in safe handling and release techniques for sea turtles, seabirds, and marine mammals, and they carry and use specific equipment for handling and releasing these animals.</li>\n<li>Scientists and managers continue to monitor bycatch in these fisheries through logbooks and fishery observer programs.</li>\n<li>Management measures are in place to minimize bycatch of juvenile Pacific bigeye tuna.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Off California, anglers must be licensed and daily bag limits are in place. Recreational charter boats must keep logbooks of their catch.</li>\n<li>There are no federal regulations for recreational fishing off Hawaii and U.S. Pacific Island territories, but local rules may apply.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Bigeye tuna is low in saturated fat and sodium and is rich in niacin, vitamins B6 and B12, selenium, and phosphorous.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Bigeye tuna are dark metallic blue on the back and upper sides and white on the lower sides and belly.</li>\n<li>The first fin on their back is deep yellow, the second dorsal and anal fins are pale yellow, and the finlets are bright yellow with black edges.</li>\n<li>Bigeye and <a href=\"https://www.fishwatch.gov/profiles/pacific-yellowfin-tuna\">yellowfin</a>&nbsp;tuna look fairly similar. In fact, its hard to distinguish the two species without experience.</li>\n<li>Among other characteristics, the bigeyes eyes are larger than the yellowfins and their finlets have black edges.</li>\n</ul>\n",
    "Production": null,
    "Protein": "27 g",
    "Quote": "U.S. wild-caught Pacific bigeye tuna is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#102b50",
    "Research": "<ul>\n<li><a href=\"https://www.fisheries.noaa.gov/feature-story/20-year-migration-hawaiian-fishery-bigeye-tuna\">The 20-year Migration of the Hawaiian Fishery for Bigeye Tuna</a></li>\n<li>Scientists at NOAAs <a href=\"https://www.pifsc.noaa.gov/\">Pacific Islands</a> and <a href=\"https://swfsc.noaa.gov/\">Southwest Fisheries Science Centers</a>&nbsp;monitor tuna fisheries and conduct research on tunas and other animals caught as bycatch in those fisheries.</li>\n<li><a href=\"https://swfsc.noaa.gov/textblock.aspx?Division=FRD&amp;ParentMenuID=189&amp;id=1123\">NOAAs Southwest Fisheries Science Center Pacific Ocean Tropical Tuna Research</a></li>\n</ul>\n",
