    "Taste": "<p>Bigeye tuna has a mild, meaty flavor, with a higher fat content than <a href=\"https://www.fishwatch.gov/profiles/pacific-yellowfin-tuna\">yellowfin</a>&nbsp;and is preferred by sashimi lovers.</p>\n",
