    "Species Name": "Atlantic Shortfin Mako Shark",
    "Animal Health": null,
    "Availability": "<p>Year-round, with peaks from April through October.</p>\n",
    "Biology": "<ul>\n<li>Shortfin mako sharks grow slowly, reach up to 13&nbsp;feet long, and can live to be over 30 years old.</li>\n<li>They are not able to reproduce until about 8 years old (~6 feet) for males and 19 years old (~9 feet) for females. They have a 3-year reproductive cycle and a gestation period of approximately 18 months.</li>\n<li>Mating occurs from summer to fall. Eggs are fertilized internally and develop inside the mother.</li>\n<li>Females bear live pups, which are approximately 2 feet long when born. This large size at birth helps reduce the number of potential predators and enhances the pups chance of survival.</li>\n<li>Mean litter size is&nbsp;12, and up to 30 pups have been reported, though scientists have only examined a handful of litters.</li>\n<li>Shortfin mako sharks are aggressive predators that feed near the top of the food web on marine fishes such as bluefish, swordfish, tuna, marine mammals, and other sharks.</li>\n<li>They have few predators, mainly larger sharks that may prey on smaller shortfin mako sharks.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
