    "Fishing Rate": "Reduced to end overfishing.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of Atlantic shortfin mako shark totaled 42,500 pounds and were valued at $57,000, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:15966980466526::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Shortfin mako sharks&nbsp;are primarily caught incidentally in longline fisheries targeting swordfish and tuna.</li>\n<li>Commercial retention of shortfin mako sharks is&nbsp;restricted to longline (pelagic and bottom) and gillnet gear.</li>\n<li>Gear used to catch shortfin mako does not impact habitat, because&nbsp;it does not contact the ocean floor.</li>\n<li>NOAA Fisheries has taken the following additional measures to prevent bycatch and sustainably manage this fishery:\n<ul>\n<li>Fishermen are required to use circle hooks on pelagic longlines to reduce bycatch of sea turtles. All pelagic longline fishing vessels must also carry safe handling and release gear to help in returning any unintentionally caught protected species to the sea safely.</li>\n<li>Fishermen using longline or gillnet gear must complete a protected species safe handling, release, and identification <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-shark-identification-workshops\">workshop</a>.</li>\n<li>Certain areas are closed to fishing to protect nursery areas, sensitive habitats, and populations.</li>\n<li><a href=\"https://www.fisheries.noaa.gov/topic/enforcement/vessel-monitoring\">Vessels monitoring systems</a> ensure fishermen are complying with area closures.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>In 2020, recreational anglers landed more than 1.1 million pounds of Atlantic shortfin mako shark, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:1107541287724::NO:RP::\">recreational fishing landings database</a>.&nbsp;&nbsp;</li>\n<li>Fishermen mainly use rod-and-reel gear. Shortfin mako sharks show low levels of post release mortality on rod and reel gear.</li>\n<li>Fishermen must have an Atlantic <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-highly-migratory-species-permits-and-reporting-forms\">HMS permit</a> to harvest Atlantic shortfin mako sharks in federal waters.</li>\n<li>Fishermen with an HMS recreational permit with a shark endorsement are required to use non-offset, non-stainless steel circle hooks when fishing for sharks recreationally, except when fishing with flies or artificial lures, in federal waters.</li>\n<li>There are trip bag limits.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Mako sharks are&nbsp;high in protein, low in fat, and a good source of niacin, vitamins B6 and B12, phosphorus, and selenium. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Shortfin mako sharks have very pointed snouts and long gill slits.&nbsp;</li>\n<li>They have dark blue/gray backs, light metallic blue sides, and white undersides.</li>\n<li>Shortfin mako sharks are easily confused with longfin makos. Longfin makos have much longer pectoral fins and larger eyes, and the area on their snout is darker.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.98 g",
    "Quote": "U.S. wild-caught Atlantic shortfin mako shark is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#51627b",
    "Research": "<ul>\n<li>Scientists from <a href=\"https://www.nefsc.noaa.gov/nefsc/Narragansett/sharks/age.html\">NOAAs Northeast Fisheries Science Center</a> are working with scientists from the Southeast Fisheries Science Center, Canadian Department of Fisheries and Oceans, and the Portuguese Institute for the Ocean and Atmosphere, on updating the life history information on shortfin mako sharks in the Atlantic Ocean, such as age parameters and reproductive information.</li>\n<li>NOAA Fisheries also runs the <a href=\"https://www.nefsc.noaa.gov/nefsc/Narragansett/sharks/tagging.html\">Cooperative Shark Tagging Program</a> to study the life history of Atlantic sharks. Started in 1962, the program involves thousands of recreational and commercial fishermen, scientists, and fisheries observers. Participants tag large coastal and pelagic sharks and record information about the shark (date and location where caught, gear used, and the size and <a href=\"https://www.nefsc.noaa.gov/nefsc/Narragansett/sharks/sexdet.html\">sex of the shark</a>).</li>\n</ul>\n",
