    "Species Name": "Atlantic Common Thresher Shark",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Common thresher sharks live a long time (19 to 50 years), reproduce late in life, and have only a few young at a time.</li>\n<li>They grow slowly, but can reach up to 20 feet long.</li>\n<li>Males sexually mature when theyre 8 to 11 feet long and 3 to 6 years old. Females are able to reproduce when theyre 8 to 9 feet long and 4 to 5 years of age.&nbsp;</li>\n<li>Common thresher sharks mate in late summer. Eggs are fertilized internally and develop within the mother. Females bear live, fully developed young after a long gestation period (9 months), and only have a few pups.</li>\n<li>Common thresher sharks are aggressive predators that feed near the top of the food chain on schooling fish such as herring and mackerel and occasionally on squid and seabirds.&nbsp;</li>\n<li>They are named for their long, scythe-like tail, which is used to stun fish before preying on them.</li>\n<li>Adult common thresher sharks have few predators, but younger, smaller ones may fall prey to larger sharks.</li>\n</ul>\n",
    "Bycatch": "Bycatch is low because Atlantic common thresher sharks are primarily incidental catch in other fisheries.",
