    "Harvest": "<ul>\n<li>In 2020, commercial landings of Atlantic common thresher shark totaled 80,000 pounds and were valued at $45,000, according&nbsp;to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:15385506747448::NO:::\">commercial fishing landings database</a>. These figures may not match other agency sources of data due to confidential information.\n<ul>\n<li>To commercially harvest Atlantic sharks, vessel owners must obtain a valid Atlantic shark directed or incidental limited access permit or a smoothhound shark open access permit. &nbsp;More information regarding limited access permits can be found in the <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-highly-migratory-species-fishery-compliance-guides\">Atlantic HMS fishery compliance guides</a>.</li>\n<li>Most of this catch comes from New Jersey and North Carolina.&nbsp;</li>\n<li>Common thresher sharks are part of the pelagic shark complex.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Atlantic common thresher sharks are primarily caught incidentally in longline fisheries targeting swordfish and tuna.&nbsp;</li>\n<li>Gear used to catch common thresher sharks does not impact habitat since it does not contact the ocean floor.</li>\n<li>Fishermen are required to use circle hooks on pelagic longlines to reduce bycatch of sea turtles. All pelagic longline fishing vessels must also carry safe handling and release gear to help in returning any unintentionally caught protected species to the sea safely.</li>\n<li>Fishermen using longline or gillnet gear must complete a protected species safe handling, release, and identification <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-protected-species-safe-handling-release-and\">workshop</a>.</li>\n<li>Certain areas are closed to fishing to protect nursery areas, sensitive habitats, and populations.</li>\n<li><a href=\"https://www.fisheries.noaa.gov/national/enforcement/regional-vessel-monitoring-information#atlantic-highly-migratory-species\">Vessel&nbsp;monitoring systems</a> ensure fishermen are complying with area closures.</li>\n</ul>\n</li>\n</ul>\n<ul>\n<li>Recreational fishermen fish for common thresher sharks, mainly with rod-and-reel gear.\n<ul>\n<li>Recreational fishermen must have an Atlantic <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-highly-migratory-species-permits\">HMS permit</a> to harvest Atlantic common thresher sharks in federal waters.&nbsp; As of January 1, 2018, all HMS recreational permit holders will need a shark endorsement to fish for, retain, possess, or land sharks.&nbsp;</li>\n<li>In 2020, recreational anglers landed 500,000 pounds of common thresher shark, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:11446134604799::NO:RP::\">recreational fishing landings database</a>.</li>\n<li>In addition to the shark endorsement, fishermen fishing recreationally for sharks will be required to use circle hook in most places. <a href=\"https://www.fisheries.noaa.gov/podcast/hooked-sharks\">Circle hooks</a> are less likely to be caught in the thresher sharks long tail, increasing the survival of sharks that are caught and released.&nbsp; For more information regarding these requirements, please refer to HMS regulations and the <a href=\"https://www.fisheries.noaa.gov/atlantic-highly-migratory-species/atlantic-highly-migratory-species-fishery-compliance-guides\">Amendment 5b compliance guide</a>.</li>\n<li>Sharks must be a minimum size to be caught, and there is a limit on how many sharks can be caught per fishing trip.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Shark is high in protein and low in fat. It is a good source of niacin, vitamins B6 and B12, phosphorus, and selenium. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health</a>&nbsp;and seafood.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Thresher sharks are brown, gray, blue-gray, or blackish on the back and underside of their snout.</li>\n<li>They are lighter on the sides, and fully white below.</li>\n<li>Their pectoral, pelvic, and dorsal fins are blackish, and there are sometimes white dots on the tips of the pectoral, pelvic, and tail fins.</li>\n<li>Their tail fin is sickle-shaped, and the upper part is extremely long, about half the length of their body.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.98 g",
    "Quote": "U.S. wild-caught Atlantic common thresher shark is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#4a5d6c",
    "Research": "<ul>\n<li>Scientists at <a href=\"https://www.fisheries.noaa.gov/about/northeast-fisheries-science-center\">NOAAs Northeast Fisheries Science Center</a> and the University of Rhode Island are conducting <a href=\"https://www.nefsc.noaa.gov/nefsc/Narragansett/sharks/age.html\">age and growth</a> studies on thresher sharks. Scientists are also studying the life history of thresher sharks, including reproductive and food habits.</li>\n<li>NOAA Fisheries runs the <a href=\"https://www.nefsc.noaa.gov/nefsc/Narragansett/sharks/tagging.html\">Cooperative Shark Tagging Program</a> to study the life history of Atlantic sharks. Started in 1962, the program involves thousands of recreational and commercial fishermen, scientists, and fisheries observers. Participants tag large coastal and pelagic sharks and record information about the shark (date and location where caught, gear used, and the size and <a href=\"https://www.nefsc.noaa.gov/nefsc/Narragansett/sharks/sexdet.html\">sex of the shark</a>).\n<ul>\n<li>Data from this program provide valuable information on shark migrations, help increase our understanding of shark biology, and improve management of the resource.</li>\n</ul>\n</li>\n</ul>\n",
