    "Location": "<ul>\n<li>In the United States, yellowfin sole are found in the North Pacific Ocean from British Columbia up to the Chukchi Sea (north of the Bering Sea).</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Alaska",
    "Population": "Above target population levels.",
    "Population Status": "<ul>\n<li>There are two stocks of yellowfin sole: one in the Bering Sea/Aleutian Islands and one in the Gulf of Alaska. According to the most recent stock assessments:\n<ul>\n<li>The Bering Sea/Aleutian Islands stock is not overfished (2020 stock assessment) and not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Yellowfin%20sole%20-%20Bering%20Sea%20/%20Aleutian%20Islands&amp;stockid=10031\">Stock SMART</a>.</li>\n<li>The Gulf of Alaska Shallow Water Flatfish Complex, which includes yellowfin sole, is not overfished, based on the stock assessments of Northern rock sole and rock sole.&nbsp; This complex is not subject to overfishing based on 2020 catch data.&nbsp; Summary stock assessment information for Northern rock sole can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Northern%20rock%20sole%20-%20Gulf%20of%20Alaska&amp;stockid=10836\">Stock SMART</a> and for rock sole on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Rock%20sole%20-%20Gulf%20of%20Alaska&amp;stockid=10040\">Stock SMART</a>.</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Limanda aspera",
    "Species Aliases": "<a href=\"/species-aliases/sole\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sole</a>, <a href=\"/species-aliases/flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Flounder</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/yellowfin_sole.png",
    "alt": "Yellowfin Sole",
    "title": "Yellowfin Sole"
    },
    "Species Name": "Yellowfin Sole",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Yellowfin sole grow up to more than 1 feet long and can live up to 39 years.</li>\n<li>Most females are able to reproduce when they reach 10 years old, or when theyre about 1 foot long.</li>\n<li>They spawn in the spring and summer in shallow waters on the inner continental shelf. Females produce between 1 and 3 million eggs.</li>\n<li>Larvae and early juveniles eat plankton and algae. Late juveniles and adults eat bivalves, worms, amphipods, mollusks, krill, shrimp, brittle stars, sculpins, and other crustaceans.&nbsp;</li>\n<li>Pacific cod and halibut prey on juvenile yellowfin sole.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
