    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>In 2020, commercial landings of yellowfin sole totaled 290.5 million pounds, and were valued at $60 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:17991831086588::NO:RP::\">commercial fishing landings database</a>.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Bottom trawls are used to catch yellowfin sole.</li>\n<li>Trawls that are used to harvest yellowfin sole can contact the ocean floor and impact habitats, depending on the makeup of the ocean bottom and the size of the gear.</li>\n<li>Bottom trawls cause minimal damage to habitat when targeting yellowfin sole over soft, sandy, or muddy ocean bottoms in Alaska.</li>\n<li>NOAA Fisheries and the North Pacific Fishery Management Council have implemented large <a href=\"https://www.npfmc.org/habitat-protections/\">closed areas</a> to protect sensitive habitats from bottom trawls.</li>\n<li><a href=\"https://alaskafisheries.noaa.gov/fisheries/cwm\">Vessel monitoring systems</a> allow enforcement staff and fishery managers to monitor GPS locations of fishing activities to ensure vessels are complying with closed areas.</li>\n<li>There are limits on the amount of halibut and crab that groundfish fisheries can incidentally catch. If the limit is reached, managers close the fishery for the remainder of the season to minimize bycatch.&nbsp;</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Yellowfin sole is an excellent source of low-fat protein and calcium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Yellowfin sole are a flatfish with a small mouth and moderately large eyes that are both on one side of their body.</li>\n<li>Their body shape is generally round with rounded edges on the tail fin.</li>\n<li>Their upper side is olive to dark brown with dark mottling, and their underside is pale.&nbsp;</li>\n<li>Yellowfin sole are named for their yellowish fins. Their fins also have faint dark bars and a narrow dark line at their base.</li>\n<li>Their anal spine is thin, sharp, and exposed.&nbsp;</li>\n<li>They have rough scales on both sides of the body.</li>\n</ul>\n",
