    "Location": "<ul>\n<li>Yellowfin tuna are found in tropical and subtropical oceans around the world, including the waters around the U.S. Pacific Islands and off southern California.&nbsp;</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Pacific Islands",
    "Population": "Above target population levels.",
    "Population Status": "<ul>\n<li>\n<p>There are two stocks of Pacific yellowfin tuna: The Eastern Pacific stock and the Central and Western Pacific stock. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Eastern Pacific stock is not overfished and not&nbsp;subject to overfishing (2020&nbsp;stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10168\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Central and Western Pacific stock is not overfished and is not subject to overfishing (2020 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=11694\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Thunnus albacares",
    "Species Aliases": "<a href=\"/species-aliases/ahi\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Ahi</a>, <a href=\"/species-aliases/kihada\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Kihada</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/atlantic_yellowfin_tuna.png",
    "alt": "Pacific Yellowfin Tuna",
    "title": "Pacific Yellowfin Tuna"
    },
    "Species Name": "Pacific Yellowfin Tuna",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Yellowfin tuna grow fast, up to 6 feet long and 400 pounds, and have a somewhat short life span of 6 to 7 years.</li>\n<li>Most yellowfin tuna are able to reproduce when they reach age 2.</li>\n<li>They spawn throughout the year in tropical waters and seasonally at higher latitudes.</li>\n<li>Their peak spawning periods are in spring and fall.</li>\n<li>Yellowfin are very productive. Females can spawn almost daily and release millions of eggs each time they spawn.</li>\n<li>Adult yellowfin tuna feed near the top of the food chain on fish, squid, and crustaceans.</li>\n<li>Fish, seabirds, dolphins, and other animals prey on larval and juvenile tuna.</li>\n<li>Marine mammals, billfish, and sharks feed on adult tuna.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
