    "Fishing Rate": "At recommended levels in the western and central Pacific Ocean and in the eastern Pacific Ocean.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>U.S. harvest of yellowfin tuna in the Pacific Ocean makes up only a small percentage of the yellowfin tuna harvested worldwide.</li>\n<li>In 2020, commercial landings of Pacific yellowfin tuna totaled 8&nbsp;million pounds and were valued at more than $16&nbsp;million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Fishermen based in Hawaii, American Samoa, and the U.S. Pacific Islands&nbsp;target Pacific yellowfin tuna with hook-and-line, pelagic longline, or troll fishing gear.&nbsp;</li>\n<li>U.S. commercial purse seine fishermen in the western and central Pacific Ocean and the eastern Pacific Ocean&nbsp;also harvest yellowfin tuna.</li>\n<li>There is a small U.S. purse seine fleet operating in the eastern Pacific Ocean that sometimes targets Pacific yellowfin tuna when warm water from the south brings the species within their range.</li>\n<li>Fishing gear used to catch Pacific yellowfin tuna rarely contacts the seafloor so habitat impacts are minimal.</li>\n<li>Restrictions on the type of fishing gear that can be used and prohibitions on fishing in certain areas minimize impacts on protected species.</li>\n<li>Interactions with protected species such as sea turtles, marine mammals, and seabirds in these fisheries are rare and survival rates are estimated to be high for all gear types.</li>\n<li>Longline fishermen are trained in safe handling and release techniques for sea turtles, seabirds, and marine mammals, and they carry and use specific equipment for handling and releasing these animals.</li>\n<li>Scientists and managers continue to monitor bycatch in these fisheries through logbooks and fishery observer programs.</li>\n<li>Management measures are in place to minimize bycatch of juvenile Pacific yellowfin tuna.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>In 2020, recreational anglers landed approximately 4.5 million pounds of Pacific yellowfin tuna, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">recreational fishing landings database</a>.&nbsp;</li>\n<li>Recreational anglers fish for yellowfin tuna with troll, rod-and-reel, and handline gear and sometimes by free-diving with spear guns.\n<ul>\n<li>Off California, anglers must be licensed and daily bag limits are in place. Recreational charter boats must keep logbooks of their catch.</li>\n<li>There are no federal regulations for recreational fishing off Hawaii and U.S. Pacific Island territories, but local rules may apply.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Yellowfin tuna is low in saturated fat and sodium and is a very good source of protein, thiamin, selenium, vitamin B6, and omega-3s.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Yellowfin tuna are torpedo-shaped.</li>\n<li>They are metallic dark blue on the back and upper sides and change from yellow to silver on the belly.</li>\n<li>True to their name, their dorsal and anal fins and finlets are bright yellow.</li>\n<li>An adult yellowfin tuna can be distinguished from other tunas by its long, bright-yellow dorsal fin and a yellow stripe down its side.</li>\n</ul>\n",
    "Production": null,
    "Protein": "23.38 g",
    "Quote": "U.S. wild-caught Pacific yellowfin tuna is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#dab316",
    "Research": "<ul>\n<li>Scientists at NOAAs <a href=\"https://swfsc.noaa.gov/\">Southwest</a> and <a href=\"https://www.pifsc.noaa.gov/\">Pacific Islands Fisheries Science Centers</a> contribute to <a href=\"https://swfsc.noaa.gov/textblock.aspx?Division=FRD&amp;ParentMenuID=189&amp;id=1123\">research on Pacific tropical tunas</a> such as yellowfin tuna by monitoring U.S. fisheries, conducting stock assessments, and participating in international forums for the assessment of these species.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.235 g",
    "Selenium": "36.5 mcg",
    "Serving Weight": "100 g (raw)",
    "Servings": "1",
    "Sodium": "37 mg",
    "Source": "<p>U.S. wild-caught from Hawaii, California, U.S. Pacific Island territories, and the high seas.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Yellowfin tuna has a mild, meaty flavor. Its more flavorful than albacore, but leaner than bluefin.</p>\n",
    "Texture": "<p>The meat is firm and moist, with large flakes.</p>\n",
