    "Species Name": "Pacific Whiting",
    "Animal Health": null,
    "Availability": "<p>Frozen whiting is available year-round.</p>\n",
    "Biology": "<ul>\n<li>Pacific whiting grow fast, up to 3 feet in length, and can live to more than 15 years old.</li>\n<li>Relatively little is known about their spawning season and locations. They are known to spawn in large numbers from January through March off south-central California, and were traditionally thought to migrate seasonally. However, recent studies have also shown that they may spawn as far north as Canada.</li>\n<li>In the spring, they travel nearshore and to the north to feed along the continental shelf and slope from northern California to Vancouver Island.</li>\n<li>In the summer, they form large schools along the continental shelf break. In years with warmer water temperatures, whiting tend to move farther north during the summer. Older whiting tend to migrate farther than younger fish.</li>\n<li>Female whiting are able to reproduce when they reach 2 to 4 years old (13 to 16 inches long). Males mature by 3 years of age (11 inches long).</li>\n<li>Females release their eggs, which males then fertilize externally. Eggs hatch in 5 to 6 days.</li>\n<li>They feed on shrimp, krill, and pelagic schooling fish, such as eulachon and Pacific herring. As whiting grow larger, fish make up a greater part of their diet.</li>\n<li>Many fish-eating species, such as <a href=\"https://www.fishwatch.gov/profiles/lingcod\">lingcod</a> and Humboldt squid, prey on Pacific whiting. <a href=\"https://www.fishwatch.gov/profiles/sablefish\">Sablefish</a>, <a href=\"https://www.fishwatch.gov/profiles/pacific-albacore-tuna\">albacore</a>, <a href=\"https://www.fishwatch.gov/profiles/alaska-pollock\">pollock</a>, <a href=\"https://www.fishwatch.gov/profiles/pacific-cod\">Pacific cod</a>, rockfish, sharks, and marine mammals also feed on Pacific whiting.</li>\n</ul>\n",
    "Bycatch": "Bycatch is low because mid-water trawls target schools of whiting.",
