    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>In 2020, commercial landings of Pacific whiting totaled 546 million pounds and were valued at $36.8 million, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:3802353482674::NO:::\">commercial fishing landings database</a>.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Mid-water trawls are primarily used to catch Pacific whiting.</li>\n<li>Mid-water trawling has minimal impact on habitat and low incidental catch of other species.</li>\n<li>Fishermen follow a number of regulations to reduce potential bycatch in the fishery. &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;\n<ul>\n<li>Mesh on the narrow, back end (codend) of their nets must be at least 3 inches to prevent bycatch of small fish.</li>\n<li>Regulations restrict where fishermen may harvest Pacific whiting to reduce bycatch of Chinook salmon.</li>\n<li>Each sectors catch is restricted by limits on bycatch of Chinook salmon and depleted rockfish species.</li>\n<li>There is 100 percent observer monitoring on at-sea processors and catcher vessels.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>Recreational fishermen do not target Pacific whiting but sometimes catch them incidentally while fishing for other groundfish and salmon.&nbsp;</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Whiting is a good source of selenium, vitamin B, magnesium, and protein.&nbsp;</p>\n",
