    "Quote": "U.S. wild-caught Pacific whiting is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#76868e",
    "Research": "<ul>\n<li>Scientists have identified a number of research areas that could improve stock assessments and management of Pacific whiting, including refining the estimates of abundance from the <a href=\"https://www.nwfsc.noaa.gov/research/divisions/fram/groundfish/acoustic.cfm\">acoustic surveys</a> and researching the links between oceanographic conditions and the distribution of the coastal stock.</li>\n<li>The <a href=\"http://www.psmfc.org/\" rel=\"external\">Pacific States Marine Fisheries Commission</a> conducts research on <a href=\"https://www.fisheries.noaa.gov/west-coast/bycatch/lights-camera-bycatch-leds-light-way-chinook-salmon\">how the use of artificial light helps Chinook salmon escape from Pacific hake midwater trawl nets</a>.</li>\n</ul>\n",
