    "Fishery Management": "<ul>\n<li><a href=\"http://sero.nmfs.noaa.gov/sustainable_fisheries/gulf_sa/cmp/index.html\">NOAA Fisheries</a> and the <a href=\"http://www.safmc.net/\">South Atlantic</a> and <a href=\"http://www.gulfcouncil.org/\">Gulf of Mexico</a> Fishery Management Councils manage the Spanish mackerel fishery.</li>\n<li>Managed under the <a href=\"http://safmc.net/fishery-management-plans-amendments/coastal-migratory-pelagics/\">Fishery Management Plan for the Coastal Migratory Pelagic Resources in the Gulf of Mexico and South Atlantic Region</a>:\n<ul>\n<li>Commercial fishermen must have a permit to harvest Spanish mackerel in federal waters.</li>\n<li>Annual catch limits are divided between the commercial and recreational fisheries for the Atlantic and Gulf of Mexico stocks. The commercial allocation is 55 percent in the Atlantic and 57 percent in the Gulf of Mexico.&nbsp;</li>\n<li>Seasonal and per-fishing-trip limits.</li>\n<li>Minimum size limit to allow fish time to mature and spawn.</li>\n<li>Spanish mackerel must be landed with heads and fins intact in both the commercial and recreational fisheries.</li>\n<li>Seasons for Atlantic and Gulf of Mexico stocks. Seasons can close early if quotas are reached.</li>\n<li>Prohibition on purse seine and drift gillnet fishing gear.&nbsp;</li>\n<li>Extends management area for Spanish mackerel through the <a href=\"http://www.mafmc.org/\">Mid-Atlantic Fishery Management Council's</a> jurisdiction (North Carolina to New York).</li>\n<li>The <a href=\"http://www.asmfc.org/species/spanish-mackerel\" rel=\"external\">Atlantic States Marine Fisheries Commission</a> works with the South Atlantic Fishery Management Council to coordinate management of Spanish mackerel fisheries in state waters to ensure theyre managed similarly to the fisheries in adjacent federal waters.</li>\n</ul>\n</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>Spanish mackerel mostly live in open water but are sometimes found over deep grass beds and reefs, as well as in shallow estuaries.</li>\n<li>They prefer water temperatures above 68 F.</li>\n</ul>\n",
    "Habitat Impacts": " Fishing gears used to harvest Spanish mackerel have minimal impacts on habitat.",
