    "Species Name": "Spanish Mackerel",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Spanish mackerel grow fast, up to 13 pounds, and can live up to 12 years.</li>\n<li>They are able to reproduce by age 2.</li>\n<li>There are two distinct populations, one in the Gulf of Mexico and one in the Atlantic.</li>\n<li>They spawn from April to September off the North Carolina and Virginia coasts in the Atlantic Ocean and in shallow coastal waters in the eastern Gulf of Mexico.</li>\n<li>Spanish mackerel release their eggs in batches throughout the spawning season. Females can have 500,000 to 1.5 million eggs over the spawning season.&nbsp;</li>\n<li>Spanish mackerel swim in large, fast-moving schools.</li>\n</ul>\n<ul>\n<li>They migrate as the seasons and water temperatures change.</li>\n<li>Along the Atlantic coast, Spanish mackerel spend the winter off Florida and move northward to North Carolina in early April and to New York in June. As waters cool later in the year, they return south to Florida waters.</li>\n<li>In the eastern Gulf of Mexico, they migrate to the west of Cape San Blas, Florida. They remain in the northern Gulf of Mexico until September and migrate south along the coast in the fall.</li>\n<li>Spanish mackerel prey primarily on herring, menhaden, sardines, mullet, needlefish, and anchovy and, to a lesser degree, shrimp, crabs, and squid. They are often seen forcing schools of small fish into tight bundles and nearly pushing them out of the water when feeding.</li>\n<li>Dolphins and sharks prey on Spanish mackerel.&nbsp;</li>\n</ul>\n",
    "Bycatch": " Bycatch is low because hook-and-line, cast nets, and gillnet gear are selective.  ",
    "Calories": "139",
    "Carbohydrate": "0 g",
    "Cholesterol": "76 mg",
    "Color": "<p>Raw mackerel is grayish and oily. When cooked, mackerel is off-white to beige in color.</p>\n",
