    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of Spanish mackerel totaled 3.7 million pounds and were valued at $4.3 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:9678559156933::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>Commercial landings of Spanish mackerel have generally been increasing in the Atlantic over the past decade.&nbsp;&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen use cast nets, gillnets, and hook-and-line gear to harvest Spanish mackerel. Cast nets account for the majority of landings.</li>\n<li>Spanish mackerel are caught in coastal waters at or near the surface, so fishing gear has minimal impacts on habitat.</li>\n<li>Fishermen throw cast nets and set gillnets directly on schools of Spanish mackerel, so they rarely catch other species.</li>\n<li>Hook-and-line fisheries for mackerel are selective and have little bycatch.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>In 2019, recreational harvest of Spanish mackerel totaled more than 15.4&nbsp;million pounds.</li>\n<li>Recreational catches of Spanish mackerel in the South Atlantic have remained stable&nbsp;while those in the Gulf of Mexico have decreased in recent years.</li>\n<li>Spanish mackerel is an important species for recreational fishermen, who often use them as bait for big game fishing.</li>\n<li>Recreational management measures include:\n<ul>\n<li>Minimum size limits.</li>\n<li>Limits on the number of mackerel fishermen can catch.&nbsp;</li>\n<li>Spanish mackerel must be landed with their heads and fins intact.</li>\n<li>Charter vessel/headboat operators must have a vessel permit for coastal migratory fish and must comply with possession limits.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Spanish mackerel is an excellent source of selenium, niacin, and vitamins B6 and B12. It is also rich in omega-3 fatty acids.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Spanish mackerel have a greenish back with silver sides and belly.</li>\n<li>They are covered with very tiny scales.</li>\n<li>They have yellow or olive green oval spots all over. These spots distinguish Spanish mackerel from cero mackerel, which have yellow-gold streaks along their midline.</li>\n<li>Spanish mackerel can be distinguished from king mackerel by their smaller size and the absence of the lateral line that drops abruptly below the second dorsal fin.</li>\n</ul>\n",
    "Production": null,
    "Protein": "19.29 g",
    "Quote": "U.S. wild-caught Spanish mackerel is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
