    "Location": "<ul>\n<li>Northern rock sole are found from Puget Sound through the Bering Sea and Aleutian Islands to the Kuril Islands (north of Japan).</li>\n<li>Southern rock sole are found from the southeast Bering Sea to Baja California.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Alaska",
    "Population": "Above target population levels in Alaska.",
    "Population Status": "<ul>\n<li>There are two species of rock sole: Northern rock sole and rock sole. According to the most recent stock assessments:\n<ul>\n<li>The Bering Sea/Aleutian Islands Rock Sole Complex is not overfished (2020 stock assessment) and not subject to overfishing based on 2020 catch data. Northern rock sole is assessed and is the primary species in the complex. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Northern%20rock%20sole%20-%20Bering%20Sea%20/%20Aleutian%20Islands&amp;stockid=11050\">Stock SMART</a>.</li>\n<li>The Gulf of Alaska Shallow Water Flatfish Complex is not overfished (2017 stock assessments) and not subject to overfishing based on 2020 catch data. Northern rock sole and rock sole are assessed and are the primary species in the complex. Summary stock assessment information for Northern rock sole can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Northern%20rock%20sole%20-%20Gulf%20of%20Alaska&amp;stockid=10836\">Stock SMART</a> and for rock sole on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Rock%20sole%20-%20Gulf%20of%20Alaska&amp;stockid=10040\">Stock SMART</a>.</li>\n<li>Rock sole is managed as part of the Other Flatfish Complex and has not been assessed so the population status is unknown. This complex is not subject to overfishing based on 2019 catch data.</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Lepidopsetta billineta (southern rock sole) and Lepidopsetta polyxystra (northern rock sole)",
    "Species Aliases": "<a href=\"/species-aliases/sole\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sole</a>, <a href=\"/species-aliases/flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Flounder</a>, <a href=\"/species-aliases/rock-flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Rock flounder</a>, <a href=\"/species-aliases/two-lined-flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Two-lined flounder</a>, <a href=\"/species-aliases/white-bellied-flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">White-bellied flounder</a>",
