    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Rock sole grow up to 2 feet long and can live for more than 20 years.</li>\n<li>They are able to reproduce when they reach 4 to 7 years old.&nbsp;</li>\n<li>Northern rock sole spawn in midwinter and spring, and southern rock sole spawn in the summer. &nbsp;&nbsp;</li>\n<li>Females lay eggs near the ocean bottom, and the eggs stick wherever they land. Eggs hatch between 6 and 25 days later, depending on water temperature.</li>\n<li>Larval rock sole eat plankton and algae.</li>\n<li>Early juveniles eat zooplankton, and late juvenile and adults prey on bivalves, worms, amphipods, mollusks, and crustaceans.&nbsp;</li>\n<li>Larger fishes, including rock sole, feed on larval and juvenile rock sole.</li>\n<li>Sharks, marine mammals, and larger fishes prey on adults.</li>\n<li>Rock soles coloring and movements on the sea floor often confuse predators.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "91",
    "Carbohydrate": "0 g",
    "Cholesterol": "48 mg",
    "Color": "<p>Creamy white flesh.&nbsp;</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
