    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>In 2020, commercial landings of rock sole totaled 61.3 million pounds and were valued at $12.5 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:17991831086588::NO:RP::\">commercial fishing landings database</a>.\n<ul>\n<li>Almost all commercial harvest of rock sole comes from Alaska, mainly the Bering Sea.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Bottom trawls are primarily used to harvest rock sole.</li>\n<li>Trawls can contact the ocean floor and impact habitats, depending on the characteristics of the ocean bottom and the size of the gear.</li>\n<li>Bering Sea/Aleutian Islands and Gulf of Alaska fishermen use a <a href=\"https://www.afsc.noaa.gov/Quarterly/jfm2010/jfm10featurelead.htm\">modified trawl gear</a> which reduces the impact of trawling on animals living on the sea floor, including crabs.</li>\n<li>Halibut, salmon, and crab are sometimes incidentally caught in the groundfish fishery in Alaska.&nbsp;</li>\n<li>There is a limit on how much halibut, herring, and crab can be incidentally caught. If this limit is reached, an area or the entire fishery is closed for the remainder of the season.&nbsp;</li>\n<li>NOAA Fisheries and the regional fishery management council have implemented large <a href=\"https://www.npfmc.org/habitat-protections/\">closed areas</a> to protect sensitive rocky, cold-water coral and sponge habitats from bottom trawls.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Rock sole is an excellent source of low-fat protein, calcium, and other nutrients.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Rock sole are a flatfish with both eyes located on the right side of their head.</li>\n<li>Northern rock soles underside is creamy white, whereas southern rock soles underside is white with glossy highlights.</li>\n<li>Rock sole are sometimes called roughback because of the rough scales on their backs.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.84 g",
    "Quote": "U.S. wild-caught rock sole is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
