    "Research": "<ul>\n<li><a href=\"https://www.afsc.noaa.gov/race/behavioral/rocksole_fbe.htm\">NOAA's Alaska Fisheries Science Center conducts research on rock sole</a></li>\n<li><a href=\"https://www.afsc.noaa.gov/Science_blog/EBS_6.htm\">Studying bottom-dwelling fishes and crabs of the Eastern Bering Sea Shelf</a></li>\n</ul>\n",
