    "Species Name": "Pacific Wahoo",
    "Animal Health": null,
    "Availability": "<p>Year-round, with peaks during summer and fall.</p>\n",
    "Biology": "<ul>\n<li>Wahoo grow fast, up to 8 feet and 158 pounds, though they are commonly between 3.3 and 5.4 feet long.</li>\n<li>Males are able to reproduce when they reach 2.8 feet in length, and females when they reach 3.3 feet. Theyre usually about 1 year old at this stage.</li>\n<li>Wahoo spawn year-round in tropical waters and during the summer in higher latitudes, including Hawaii.&nbsp;</li>\n<li>Individual wahoo spawn multiple times throughout the spawning season. Females release millions of eggs per year to compensate for eggs that might not survive to adulthood.</li>\n<li>Wahoo mainly feed on fish, including frigate mackerel, butterfish, porcupine fish, and round herring. They compete with tuna for the same kind of food.</li>\n<li>Scientists have theorized that a wahoo is able to eat fish larger than itself by using its sharp teeth to render large prey into bite-size pieces.&nbsp;</li>\n<li>A number of predators feed on juvenile wahoo.</li>\n</ul>\n",
    "Bycatch": "There is no directed fishery for Pacific wahoo.  Regulations are in place to minimize bycatch in fisheries that incidentally catch this species.",
    "Calories": "167",
    "Carbohydrate": "0 g",
    "Cholesterol": "64 mg",
    "Color": "<p>Raw meat is pale pink. It turns white when cooked.</p>\n",
