    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>U.S. commercial fisheries in the western and central Pacific harvest the majority of U.S.-caught wahoo.</li>\n<li>In 2020, commercial landings of Pacific wahoo from the Pacific Islands totaled 817,000 pounds and were valued at &nbsp;$1.8 million, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:10109603266213::NO:::\">commercial fishing landings database</a>. The majority of the catch comes from Hawaii.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>There is no directed fishery for Pacific wahoo, but they may be incidentally harvested in troll and longline fisheries.&nbsp;</li>\n<li>Troll and longlines do not contact the ocean floor, so there are no impacts to habitat.&nbsp;</li>\n<li>U.S. pelagic longline fishermen who may incidentally catch wahoo are required to use specific tools and handling techniques to mitigate bycatch of turtles and marine mammals.</li>\n<li>Time-area closures also limit and prevent interactions between pelagic longline gear and non-target species.</li>\n<li>Onboard observers are required in some fisheries to record any interactions with sea turtles, seabirds, and marine mammals.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>In 2020, recreational landings of Pacific wahoo totaled 1.9 million pounds, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:14060249524701::NO:RP::\">recreational fishing landings database</a>.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Wahoo is an excellent source of low-fat protein.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Wahoo are steel blue above and pale blue below.</li>\n<li>Theyre covered with small scales and have a series of 25 to 30 irregular blackish-blue vertical bars on their sides.&nbsp;</li>\n<li>Wahoo have large mouths with strong, triangular, compressed, and finely serrated teeth.&nbsp;</li>\n<li>Their snouts are about as long as the rest of their heads.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "19.32 g",
    "Quote": "U.S. wild-caught Pacific wahoo is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
