    "Location": "<ul>\n<li>King mackerel are found in the Atlantic Ocean from Massachusetts to Brazil, including the Caribbean and Gulf of Mexico.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Southeast",
    "Population": "Above target population levels in the South Atlantic and the Gulf of Mexico.",
    "Population Status": "<ul>\n<li>There are two stocks of king mackerel: the South Atlantic stock and the Gulf of Mexico stock. According to the most recent stock assessments:\n<ul>\n<li>The South Atlantic stock is not overfished and not subject to overfishing (2020 stock assessment).&nbsp;Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10172\">Stock SMART</a>.</li>\n<li>The Gulf of Mexico stock is not overfished and is not subject to overfishing (2020 stock assessment).&nbsp;Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10171\">Stock SMART</a>.</li>\n</ul>\n</li>\n<li>Prior to the 1980s, king mackerel fisheries were essentially unregulated and became depleted. Regulations were implemented in 1983 to control harvest and rebuild declining stocks of king mackerel, and today these stocks have been restored to target population levels.&nbsp;</li>\n</ul>\n",
    "Scientific Name": "Scomberomorus cavalla ",
    "Species Aliases": "<a href=\"/species-aliases/cavalla\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Cavalla</a>, <a href=\"/species-aliases/sierra\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sierra</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/king_mackerel.png",
    "alt": "Illustration of King Mackarel",
    "title": "King Mackarel"
    },
    "Species Name": "King Mackerel",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>King mackerel grow fast, up to 5  feet and 100 pounds.</li>\n<li>They can live more than 20 years.</li>\n<li>They are able to reproduce when they reach 2 years of age.</li>\n<li>There are two distinct populations, one in the Gulf of Mexico and one in the Atlantic.</li>\n<li>They spawn on the outer continental shelf from May through October. Females release eggs in the open water, where they are fertilized.</li>\n<li>Females grow much larger than males, an evolutionary strategy that maximizes the amount of eggs that a female can produce. Females can produce 50,000 to several million eggs.</li>\n<li>King mackerel are carnivores, feeding on fish, squid, and shrimp. Theyre voracious feeders and have been observed leaping out of the water in pursuit of prey.</li>\n<li>Juvenile and larger pelagic fish feed on smaller king mackerel. Bottlenose dolphins and large fish, such as sharks and tunas, feed on adult king mackerel.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Bycatch is low because hook-and-line and gillnet gear is selective.",
    "Calories": "105",
    "Carbohydrate": "0 g",
    "Cholesterol": "53 mg",
    "Color": "<p>Raw mackerel is grayish and oily. When cooked, mackerel is off-white to beige in color.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
    "Farming Methods": null,
    "Farming Methods_": null,
    "Fat, Total": "2 g",
    "Feeds_": null,
    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended levels in the South Atlantic and the Gulf of Mexico.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of king mackerel totaled more than 5&nbsp;million pounds and were valued at&nbsp;$12&nbsp;million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>.</li>\n<li>A large fraction of the commercial catch is harvested from the winter mixing zone in South Florida, which includes Gulf of Mexico and South Atlantic waters.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Most commercial fishermen use hook-and-line gear such as handlines, rod-and-reel, and troll gear. Gillnets are also allowed in limited areas.</li>\n<li>Hook-and-line and gillnet gear have minimal impact on habitat because they do not contact the ocean floor.</li>\n<li>Fishing gear used to harvest king mackerel is very selective, so the fishery has little bycatch.&nbsp;&nbsp;</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>In 2020, recreational harvest of king mackerel totaled more than 9&nbsp;million pounds, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">recreational fishing landings database</a>.&nbsp;</li>\n<li>King mackerel is a large, aggressive fish that is popular with recreational fishermen and makes up a large part of the total harvest.</li>\n<li>King mackerel support a valuable tournament fishery.</li>\n<li>Recreational management measures include:\n<ul>\n<li>Minimum size limits.</li>\n<li>Bag limits.</li>\n<li>King mackerel must be landed with their heads and fins intact.</li>\n<li>Charter vessel/headboat operators must have a vessel permit for coastal migratory fish and must comply with possession limits.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>King mackerel is rich in omega-3 fatty acids, low in fat, and a very good source of protein, riboflavin, niacin, vitamin B12, and selenium. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>King mackerel are iron-gray on the back and silvery on their sides and belly.</li>\n<li>They have pale to dusky fins.</li>\n<li>Small king mackerel sometimes have spots like Spanish mackerel, but king mackerel can be distinguished by their sharply dipping lateral line and gray anterior (near the front) dorsal (the upper side) fin.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.28 g",
    "Quote": "U.S. wild-caught king mackerel is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#a1afc4",
