    "Location": "<ul>\n<li>Market squid are found from the tip of Baja California to southeastern Alaska, but are most abundant between Punta Eugenia in Baja California and Monterey Bay, California.&nbsp;</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast",
    "Population": "The population level is unknown. The entire population replaces itself annually.",
    "Population Status": "<ul>\n<li>Short- and long-term changes in the market squid population are poorly understood, so there are no reliable estimates of population.</li>\n<li>Squid have a short life span (6 to 9 months) and fishermen target spawning squid because they die shortly after they reproduce.\n<ul>\n<li>Even without fishing, the entire population replaces itself annually. As a result, market squid populations can handle a relatively high amount of fishing pressure.</li>\n<li>Ensuring that fishermen capture squid that have already spawned is key to the production of the next generation and future health of the population.</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Doryteuthis (Loligo) opalescens",
    "Species Aliases": "<a href=\"/species-aliases/squid\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Squid</a>, <a href=\"/species-aliases/pacific-loligo-squid\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Pacific loligo squid</a>, <a href=\"/species-aliases/opalescent-inshore-squid\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Opalescent inshore squid</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/california_market_squid.png",
    "alt": "Illustration of a California Market Squid",
    "title": "California Market Squid"
    },
    "Species Name": "California Market Squid",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Market squid are fast-growing animals with a short natural life span. They reach up to 1 foot in total length, including their arms.</li>\n<li>They reproduce right before they die, around the age of 1 year.</li>\n<li>They spawn year-round. Spawning occurs April through October in central California and October through the end of April or May in southern California.</li>\n<li>Spawning squid congregate in large schools near their spawning grounds, usually over sandy habitats.&nbsp;</li>\n<li>Males deposit spermatophores into females, and the eggs are fertilized as females release them. Females produce about 20 egg cases, with each case containing about 200 individual eggs.</li>\n<li>Females deposit eggs on sandy habitats, building large mounds of egg cases. Eggs take several days to months to hatch, depending on temperature.</li>\n<li>Newly hatched eggs are called paralarvae and resemble miniature adults.</li>\n<li>Juvenile market squid feed on small crustaceans. As they grow, they feed on krill, small crustaceans, small fish, and other squid.&nbsp;</li>\n<li>Market squid are a critical food source for a variety of fish (salmon, <a href=\"https://www.fishwatch.gov/profiles/lingcod\">lingcod</a>,<a>&nbsp;</a>and rockfish), seabirds, and marine mammals.</li>\n</ul>\n",
    "Bycatch": "Bycatch is low because pelagic roundhaul gear is selective.",
    "Calories": "92",
    "Carbohydrate": "3.08 g",
    "Cholesterol": "233 mg",
    "Color": "<p>Raw squid should be moist, shiny, and ivory colored. Pink, yellow, or purple flesh indicates deterioration. Cooked squid is opaque white.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
    "Farming Methods": null,
    "Farming Methods_": null,
