    "Fishing Rate": "The fishing rate is unknown. Timing of fishing can promote annual replacement of the population.",
    "Harvest": "<ul>\n<li>In 2019, commercial landings of market squid totaled more than 32 million pounds and were valued at approximately $16.4 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>. These figures may not match other agency sources of data due to confidential information.</li>\n<li><a href=\"https://www.fisheries.noaa.gov/west-coast/sustainable-fisheries/west-coast-highly-migratory-species-fisheries-and-gear\">Purse seines</a> and scoop nets are used to harvest market squid.</li>\n<li>Habitat and bycatch impacts are minimal because the gear is used at the surface around dense schools of fish, which usually contain only one species.</li>\n<li>Fishermen usually fish for market squid at night directly above the spawning grounds where females lay their eggs.\n<ul>\n<li>Squid seiners typically work with light boatssmaller vessels with several high-powered lights pointed from various angles. The lights attract groups of spawning squid to surface waters.</li>\n<li>Once a group of squid comes to the surface, the light boat signals the seiner to deploy its net, encircling the light boat, in order to catch the squid located under the lights.</li>\n</ul>\n</li>\n<li>California market squid are an important source of bait for the states recreational fishing industry.&nbsp;</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Squid are an excellent source of selenium, riboflavin, and vitamin B12.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Market squid are members of the mollusk family known as cephalopods, which means foot-on-head.</li>\n<li>They have eight arms and two tentacles that extend from the ends of their bodies where their mouths are located.</li>\n<li>They have a mixed, iridescent coloration of milky white and purple, but their coloring can change in response to environmental conditions.</li>\n</ul>\n",
    "Production": null,
    "Protein": "15.58 g",
    "Quote": "U.S. wild-caught California market squid is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
