    "Research": "<ul>\n<li>NOAAs Southwest Fisheries Science Center <a href=\"https://www.fisheries.noaa.gov/about/fisheries-resources-division-southwest-fisheries-science-center\">conducts research on market squid</a>.</li>\n</ul>\n",
