    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of Atlantic wahoo totaled 59,000 pounds and were valued at $200,000, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:9556848019797::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>The majority of commercial landings came from Florida and North Carolina.</li>\n</ul>\n</li>\n<li>Most (more than&nbsp;95&nbsp;percent) of the U.S. harvest of wahoo comes from the <a href=\"https://www.fishwatch.gov/profiles/pacific-wahoo\">Pacific</a>, mainly Hawaii.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>The authorized gear types in the fisheries for wahoo in the Atlantic include the automatic reel, bandit gear, handline, pelagic longline, rod and reel, and spearfishing gear (including powerheads).</li>\n<li>Hook-and-line gear has minimal impact on bottom habitat because it doesnt contact the ocean floor.</li>\n<li>Longlines can incidentally catch sea turtles, marine mammals, and other species.</li>\n<li>Longline fishermen must use specific hooks and bait and must follow safe handling and release guidelines to prevent this bycatch.</li>\n<li>Fishing is prohibited in certain areas to protect species such as billfish.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Atlantic wahoo is a popular fish for recreational fishermen.</li>\n<li>In 2020, recreational landings of Atlantic wahoo totaled more than 1.3 million pounds, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:14060249524701::NO:RP::\">recreational fishing landings database</a>.</li>\n<li>Daily bag limits.</li>\n<li>Prohibition on the sale of recreational catch without approriate permits.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Atlantic wahoo is an excellent source of low-fat protein.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Atlantic wahoo are steel blue above and pale blue below.</li>\n<li>They are covered with small scales and have a series of 25 to 30 irregular blackish-blue vertical bars on their sides that fade rapidly after death.</li>\n<li>They have large mouths with strong, triangular, compressed, and finely serrated teeth.</li>\n<li>Their snouts are about as long as the rest of their heads.</li>\n</ul>\n",
    "Production": null,
