    "Species Name": "Gag Grouper",
    "Animal Health": null,
    "Availability": "<p>Fresh year-round in the Gulf of Mexico, and from May through December in the South Atlantic.</p>\n",
    "Biology": "<ul>\n<li>Gag grow slowly, can reach more than 3 feet in length, and weigh up to 50 pounds.</li>\n<li>They can live as long as 30 years.</li>\n<li>They are protogynous hermaphrodites  they begin life as females and sexually mature around age 4. As they grow older, they change to males, around age 8.</li>\n<li>They spawn from mid-January to early May in the South Atlantic and from late January to mid-April in the Gulf of Mexico. Gag spawn in large groups along the continental shelf. Females spawn multiple times per season, releasing between 60,000 and 1.7 million eggs each time they spawn.&nbsp;</li>\n<li>They eat a variety of fish, crabs, shrimp, and squid.</li>\n<li>Adult gag and large fish prey on juvenile gag.</li>\n<li>Sharks and other large fish prey on adult gag.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch. ",
