    "Fishing Rate": "Reduced to end overfishing in the South Atlantic and the Gulf of Mexico.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of gag totaled 863,000 pounds valued at $4.5 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:21068746012673::NO:::\">commercial fishing landings database</a>.&nbsp; Most commercial harvest comes from the west coast of Florida.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen mainly use vertical hook-and-line gear to harvest gag, and some also use longlines and spears.</li>\n<li>Trawl gear, fish traps, and bottom longlines are prohibited in some areas to reduce bycatch. Several areas are closed to all fishing to protect snappers and groupers, including gag.</li>\n<li>Sea turtles and other reef fishes, such as snappers and groupers, can be incidentally caught while fishing for gag.</li>\n<li>In certain areas, fishermen are required to use circle hooks to improve the chance of survival of any unintentionally caught fish and to reduce turtle hookings.</li>\n<li>Commercial and charterboat/headboat reef fish fishermen must use appropriate release gear and follow handling protocols to increase the chance of survival for any incidentally caught <a href=\"https://www.fisheries.noaa.gov/southeast/endangered-species-conservation/sea-turtle-and-smalltooth-sawfish-release-gear-protocols\">sea turtles</a>.</li>\n<li>Fishermen are encouraged to use <a href=\"https://www.flseagrant.org/fisheries/venting/\" rel=\"external\">venting tools</a>&nbsp;or <a href=\"https://www.flseagrant.org/fisheries/catch-and-release-fishing/descending-and-venting/\" rel=\"external\">fish descenders</a> when fish are caught showing signs of barotrauma. Barotrauma occurs when reef fish are quickly brought to the surface by hook-and-line and the gas in their swimbladders over expands. <a href=\"https://www.flseagrant.org/fisheries/venting/\" rel=\"external\">Venting tools</a> help deflate the expanded abdominal cavity, preventing serious injury to the fish and making it easier for them to return to deep water.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Gag makes up a large part of the recreational harvest in the South Atlantic and Gulf of Mexico.&nbsp; In 2020, recreational harvest totaled approximately $3.3 million pounds, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:21068746012673::NO:::\">recreational fishing landings database</a>.</li>\n<li>In the South Atlantic:\n<ul>\n<li>Annual catch limits&nbsp;and accountability measures.</li>\n<li>Bag and size limits.</li>\n</ul>\n</li>\n<li>In the Gulf of Mexico:\n<ul>\n<li>Annual catch limits&nbsp;and accountability measures.</li>\n<li>Bag and size limits.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Grouper is low in saturated fat. It is also a good source of vitamins B6 and B12, phosphorus, potassium, protein, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Gag have long, compressed bodies.</li>\n<li>Their coloring varies with their size. Large gag are dark brownish-gray above and paler below, with traces of dark wavy markings on the sides. Smaller fish are much lighter and have dark brown or charcoal kiss-like marks along their sides.</li>\n<li>Gags scientific species name, <em>microlepis</em>, is derived from the Greek words micro for small and lepis meaning scale, in reference to the small scales of this fish.</li>\n</ul>\n",
    "Production": null,
    "Protein": "19.38 g",
    "Quote": "U.S. wild-caught gag grouper is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#8c7f63",
