    "Animal Health": null,
    "Availability": "<p>Year-round.&nbsp;</p>\n",
    "Biology": "<ul>\n<li>Flathead sole grow up to 1.8 feet and can live at least 34 years.</li>\n<li>They are able to reproduce at 2 to 3 years old in the southern part of their range, but not until 6 years old in the northern part.</li>\n<li>Flathead sole spawn from February through April in deeper waters on the continental shelf.</li>\n<li>Depending on their size, females release 72,000 to 600,000 eggs. Eggs are large and are fertilized externally.</li>\n<li>Eggs hatch in 9 to 20 days, depending on water temperature.</li>\n<li>Flathead sole generally feed on bottom-dwelling invertebrates such as crustaceans, mollusks, and brittle stars, as well as fish and squid.</li>\n<li><span><a href=\"https://www.fishwatch.gov/profiles/pacific-cod\">Pacific cod</a>, <a href=\"https://www.fishwatch.gov/profiles/pacific-halibut\">halibut</a>, <a href=\"https://www.fishwatch.gov/profiles/alaska-pollock\">Alaska pollock</a>, and&nbsp;</span><a href=\"https://www.fishwatch.gov/profiles/arrowtooth-flounder\">arrowtooth flounder</a>&nbsp;prey on flathead sole.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch. ",
