    "Harvest": "<ul>\n<li>In 2019, commercial landings of flathead sole totaled 38.4 million pounds and were valued at approximately $9 million dollars, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:8902444321281::NO:::\">commercial fishing landings database</a>.\n<ul>\n<li>Almost all commercial harvest of flathead sole comes from Alaska, mainly the Bering Sea and Aleutian Islands.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Flathead sole are primarily caught with bottom trawls, and some are caught with pelagic trawls.</li>\n<li>Gulf of Alaska&nbsp;and Bering Sea/Aleutian Islands fishermen use <a href=\"https://www.afsc.noaa.gov/Quarterly/jfm2010/jfm10featurelead.htm\">modified trawl gear</a> that reduces the impact of trawling on animals living on the sea floor, including crabs.</li>\n<li>Halibut, salmon, and crab are incidentally caught in the groundfish fishery in Alaska.</li>\n<li>There are limits on how much halibut, herring, and crab can be caught incidentally. If this limit is reached, an area or the entire fishery is closed for the remainder of the season.</li>\n<li>In Alaska and on the West Coast, NOAA Fisheries and the regional fishery management councils have implemented large closed areas to protect sensitive rocky, cold-water coral and sponge habitats from bottom trawls.</li>\n</ul>\n</li>\n<li>Recreational fishermen may fish for flathead sole:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;\n<ul>\n<li>Only hook-and-line and spear gear is allowed.</li>\n<li>Bag limits on the number of fish that can be caught vary by state.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Flathead sole is an excellent source of low-fat protein as well as calcium and vitamins.&nbsp;</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Flathead sole have an oval-shaped, compressed body.</li>\n<li>They are flatfish, with both of their eyes located on the right side of their head.</li>\n<li>Their upper side is dark olive brown to reddish gray-brown, sometimes with dusky blotches, and their underside is white.&nbsp;</li>\n<li>Their dorsal and anal fins have dusky blotches.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.84 g ",
    "Quote": "U.S. wild-caught flathead sole is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#603b3a",
    "Research": "<ul>\n<li><a href=\"https://www.afsc.noaa.gov/Science_blog/EBS_6.htm\">Studying bottom-dwelling fishes and crabs of the Eastern Bering Sea Shelf</a></li>\n<li>Scientists continue to research the principal drivers of recruitment for flathead sole in the Bering Sea. Recruitment refers to young fish surviving, growing, and recruiting to the adult population. Although recruitment has important implications for management of the stock, current evidence for the relative importance of population density and environmental effects on recruitment remain unclear.</li>\n</ul>\n",
