    "Location": "<ul>\n<li>Summer flounder are found in the Atlantic Ocean from Nova Scotia to the east coast of Florida.</li>\n<li>In U.S. waters, summer flounder are most common in the mid-Atlantic region from Cape Cod, Massachusetts, to Cape Fear, North Carolina.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "Near target levels but fishing rate promotes population growth.",
    "Population Status": "<ul>\n<li>According to the 2021&nbsp;stock assessment, summer flounder are not overfished and are not subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10191\">Stock SMART</a>.</li>\n<li>Scientists at NOAAs <a href=\"https://www.nefsc.noaa.gov/\">Northeast Fisheries Science Center</a> estimate the abundance of summer flounder using data collected during their annual bottom trawl surveys, along with data from state- and university-run surveys.</li>\n<li>The summer flounder stock declined to record lows in the late 1980s and early 1990s. With improved reproduction and survival rates and sustainable management, spawning stock biomass (a measure of the amount of summer flounder able to reproduce) has increased substantially.</li>\n</ul>\n",
    "Scientific Name": "Paralichthys dentatus",
    "Species Aliases": "<a href=\"/species-aliases/flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Flounder</a>, <a href=\"/species-aliases/fluke\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Fluke</a>, <a href=\"/species-aliases/northern-fluke\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Northern fluke</a>, <a href=\"/species-aliases/hirame\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Hirame</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/summer_flounder.png",
    "alt": "Summer Flounder",
    "title": "Summer Flounder"
    },
    "Species Name": "Summer Flounder",
    "Animal Health": null,
    "Availability": "<p>Year-round. Summer flounder is sold whole and in fillets and is available fresh or frozen.</p>\n",
    "Biology": "<ul>\n<li>Summer flounder grow fast and have a relatively short life, about 12 to 14 years.</li>\n<li>Males grow to more than 2 feet in length and females grow up to 3 feet.</li>\n<li>They are able to reproduce when they reach age 2 or 3.</li>\n<li>Summer flounder spawn in the fall and early winter when they migrate offshore.</li>\n<li>They spawn several times throughout the spawning season.</li>\n<li>Spawning peaks in October and November when water temperatures change and autumn plankton is most productive. The combination of these elements improves the chance of survival for larval summer flounder.</li>\n<li>Depending on their size, females have between 460,000 and more than 4 million eggs. They release the eggs into the water column and the eggs hatch in waters of the continental shelf.</li>\n<li>Newly hatched larvae move with the currents toward coastal areas, where they develop into juveniles.</li>\n<li>Summer flounder eat a mixed diet of fish and invertebrates throughout their life.</li>\n<li>Larval and post-larval flounder feed on zooplankton (tiny floating animals) and small crustaceans.</li>\n<li>Juveniles eat crustaceans and fish.</li>\n<li>Adults are opportunistic feeders, eating whatever food is convenient at the time, and feed mostly on fish and crustaceans.</li>\n<li>Summer flounder lay on the ocean floor concealed, partly by sand and partly by their coloration, and wait for their prey to swim by. When suitable prey appears, flounder ambush them.</li>\n<li>Larval and juvenile summer flounder are preyed upon until they grow large enough to fend for themselves. Predators include spiny dogfish, monkfish, cod, hakes, sea raven, longhorn sculpin, and fourspot flounder. Large sharks, rays, and monkfish prey on adult summer flounder.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
