    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of summer flounder totaled more than 7.3 million pounds and were valued at approximately $22 million, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:7304793136905::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen mainly use bottom trawls to harvest summer flounder, fishing offshore in the winter and inshore in the summer.</li>\n<li>Bottom trawls contact the bottom and can impact bottom habitats. However, summer flounder are most common on sandy ocean bottoms, which are more resilient than other habitat types to the impacts of fishing gear.</li>\n<li>Bottom trawls can unintentionally catch sea turtles. Since 1992, all vessels fishing for summer flounder with bottom trawls off Virginia and North Carolina have been required to use NOAA Fisheriesapproved <a href=\"https://www.fisheries.noaa.gov/southeast/bycatch/turtle-excluder-devices\">turtle excluder devices (TEDs)</a> in their nets. A TED is a sorting grid that is sewn into a trawl net to allow fish to pass through to the back of the net while preventing larger animals, such as turtles and sharks, from passing through by allowing them to escape through flaps above or below the TED.</li>\n<li>Management measures also require the mesh on fishing nets to be a certain size to prevent bycatch of juvenile summer flounder and other species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Summer flounder are one of the most popular recreational fish on the Atlantic coast.</li>\n<li>In 2020, recreational landings were more than 10 million pounds, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:6228678179267::NO:::\">recreational fishing landings database</a>.</li>\n<li>Anglers fish for summer flounder from the shore, piers, and boats with hook-and-line gear.</li>\n<li>The recreational harvest limits for each state are based on the recreational catch in 1998.</li>\n<li>Regulations for the recreational fishery are typically adjusted annually. They include an annual harvest limit, closed seasons, a minimum size for landed fish, and possession limits.</li>\n<li>Recreational landings varied widely over the years until harvest limits were put in place in 1993.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Flounder is a good low-fat source of B vitamins and niacin.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Summer flounder have flat bodies.</li>\n<li>They are white below and some shade of brown, gray, or drab above.</li>\n<li>Theyre nicknamed chameleons of the sea because theyre able to change their coloring to blend in with the texture and color of the bottom where they live.</li>\n<li>They also have spots on their back and can be distinguished because at least five of these dark spots are arranged in an \"X\" pattern.</li>\n<li>Summer flounder is a left-eyed flatfish (both eyes are on the left side of its body when viewed from above with the dorsal fin facing up). When larvae develop into juveniles, their right eye moves across the top of the head to the left side.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.84 g",
    "Quote": "U.S. wild-caught summer flounder is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#847852",
    "Research": "<ul>\n<li>Scientists from <a href=\"https://www.nefsc.noaa.gov/\">NOAAs Northeast Fisheries Science Center</a> conduct annual spring and fall bottom trawl surveys in waters from just south of Cape Hatteras to Canadian waters. These survey results, along with data from surveys run by states and universities and catch data from the commercial and recreational fisheries, help scientists estimate the abundance of summer flounder and other species in the Mid-Atlantic and New England.</li>\n<li>The Northeast Fisheries Science Centers <a href=\"https://www.nefsc.noaa.gov/fsb/program.html\">Fishery Observer Program</a> has collected information on landings and discards in the commercial summer flounder fishery for more than 20 years.</li>\n<li><a href=\"https://www.nefsc.noaa.gov/press_release/pr2017/news/nr1711/\">Cooperative Research Targets Summer Flounder</a></li>\n</ul>\n",
