    "Location": "<ul>\n<li>Rex sole are found from central Baja California to the Bering Sea.&nbsp;&nbsp;</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Alaska",
    "Population": "Above target population levels in the Gulf of Alaska and on the West Coast. ",
    "Population Status": "<ul>\n<li>According to the <a href=\"https://www.fisheries.noaa.gov/resource/data/2018-assessment-rex-sole-stock-gulf-alaska\">2018&nbsp;stock assessment</a>, rex sole in the Gulf of Alaska are not overfished and are not subject to overfishing.</li>\n<li>In the Bering Sea/Aleutian Islands rex sole are managed under the other flatfish complex:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;\n<ul>\n<li>This complex was last <a href=\"https://www.fisheries.noaa.gov/resource/data/assessment-other-flatfish-stock-complex-bering-sea-and-aleutian-islands\">assessed in 2018</a>, but data were insufficient to determine whether the complex is overfished.</li>\n<li>An overfishing level is set for the complex and, as long as this level is not exceeded, the complex is not subject to overfishing. The complex is not subject to overfishing.</li>\n</ul>\n</li>\n<li>According to the <a href=\"https://swfsc.noaa.gov/publications/CR/2013/2013Cope.pdf\">2013 stock assessment</a>, rex sole on the West Coast are not overfished</li>\n<li>Rex sole are part of the other flatfish complex on the West Coast,and are not subject to overfishing based on <a href=\"https://www.nwfsc.noaa.gov/research/divisions/fram/observation/data_products/species_management.cfm\">2018 catch data</a>.&nbsp;</li>\n</ul>\n",
    "Scientific Name": "Glyptocephalus zachirus",
