    "Availability": "<p>Year-round, but highest catch is from February through May.&nbsp;</p>\n",
    "Biology": "<ul>\n<li>Rex sole are moderately long-lived (they can live for nearly 30 years), and can grow up to 23 inches.</li>\n<li>Female rex sole grow faster and live longer than males.&nbsp;</li>\n<li>Depending on location, rex sole may reach maturity between 3.5 and 13 years.</li>\n<li>Rex sole spawn from October through May. They are serial spawners, and will deposit eggs on more than one occasion throughout the spawning season. The spawning season may be longer in the Gulf of Alaska.</li>\n<li>Eggs are fertilized near the sea bed, move into the water column, and require a few weeks to hatch. Once hatched, larvae spend about a year in the water column before settling to the bottom as juveniles.</li>\n<li>Rex sole feed on shrimp, amphipods, and worms.</li>\n<li>Sharks, skates, rays, <a href=\"https://www.fishwatch.gov/profiles/lingcod\">lingcod</a>, <a href=\"https://www.fishwatch.gov/profiles/arrowtooth-flounder\">arrowtooth flounder</a>, and some species of rockfish prey on rex sole.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
