    "Harvest": "<ul>\n<li>In 2018, landings of rex sole totaled more than 5.3 million pounds and were valued at more than $1.86 million.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Bottom trawls are used to catch rex sole.</li>\n<li>Trawls that are used to harvest rex sole can contact the ocean floor and impact habitats, depending on the characteristics of the ocean bottom and the size of the gear.</li>\n<li>Bottom trawls cause minimal damage to habitat when targeting rex sole over soft, sandy, or muddy ocean bottoms on the West Coast and in Alaska.</li>\n<li>In Alaska and on the West Coast, NOAA Fisheries and the regional fishery management councils have implemented large closed areas to protect sensitive rocky, cold-water coral and sponge habitats from bottom trawls.</li>\n<li><a href=\"https://www.westcoast.fisheries.noaa.gov/fisheries/management/vms.html\">Vessel monitoring systems</a> allow enforcement staff and fishery managers to monitor GPS locations of fishing activities to ensure vessels are complying with closed areas.</li>\n<li>In Alaska, there are limits on the amount of halibut and crab that groundfish fisheries can incidentally catch. If the limit is reached, managers close the fishery for the remainder of the season.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Rex sole is an excellent source of low-fat protein.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Rex sole is a flatfish with both eyes located on the right side of its head.</li>\n<li>They have small mouths with a blunt, rounded snout. Their body is oval, but elongated, and very slender.</li>\n<li>The upper side of their body is light brown to gray in color and their underside is white.&nbsp;</li>\n</ul>\n",
