    "Location": "<ul>\n<li>Red grouper are found in the western Atlantic Ocean from Massachusetts through the Gulf of Mexico and south to Brazil.&nbsp;</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Southeast",
    "Population": "Above target population level in the Gulf of Mexico. Significantly below target population level in the South Atlantic. A rebuilding plan is in place for the South Atlantic stock.",
    "Population Status": "<ul>\n<li>\n<p>There are two stocks of red grouper: the Gulf of Mexico stock and the South Atlantic stock. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Gulf of Mexico stock is not overfished (2019 stock assessment), and is not subject to overfishing based on 2020&nbsp;catch data.&nbsp; Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10331\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The South Atlantic stock is overfished (2017 stock assessment), but is not subject to overfishing based on 2020&nbsp;catch data.&nbsp; Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10332\">Stock SMART</a>. A rebuilding plan has been in place since 2012.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Epinephelus morio",
    "Species Aliases": "<a href=\"/species-aliases/grouper\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Grouper</a>, <a href=\"/species-aliases/cherna-americana\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Cherna americana</a>, <a href=\"/species-aliases/negre\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Negre</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/red_grouper.png",
    "alt": "Red Grouper",
    "title": "Red Grouper"
    },
    "Species Name": "Red Grouper",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Red grouper grow slowly, up to almost 50 inches long and more than 50 pounds.</li>\n<li>The oldest recorded red grouper in the South Atlantic was 26 years old and the oldest recorded in the Gulf of Mexico was 29 years old.</li>\n<li>They are protogynous hermaphrodites  they begin life as females and sexually mature when they reach 4 to 6 years of age. Some later transform into males, most often between the ages of 7 and 15. The proportion of males in the population increases with age.</li>\n<li>They spawn frequently, close to 26 times a year, in shallow waters from February through June.</li>\n<li>Red grouper feed on a wide variety of fish, octopus, and crustaceans, including shrimp, lobsters, and mantis shrimp.</li>\n<li>They are among the top predators in reef community food webs and may control some aspects of community balance in reef systems. Red grouper are unspecialized and opportunistic feeders  they eat any convenient prey. They engulf prey whole by opening their large mouths, dilating their gill covers, rapidly drawing in a current of water, and inhaling the food.</li>\n<li>Smaller grouper are preyed on by the same predators that eat snappers, including jacks, other groupers, sharks, barracudas, and morays. Large sharks and carnivorous marine mammals prey on adult red grouper.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
