    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended levels in the Gulf of Mexico and the South Atlantic.",
    "Harvest": "<ul>\n<li>Commercial fishery:&nbsp;\n<ul>\n<li>In 2020, commercial landings of red grouper totaled 2.8 million pounds and were valued at approximately $12 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:14440981298202::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen mainly use hook-and-line gear, including longlines and handlines, to harvest red grouper.&nbsp;</li>\n<li>Trawl gear, fish traps, and bottom longlines are prohibited in some areas to reduce bycatch. Several areas are closed to all fishing to protect snappers and groupers, including red grouper.</li>\n<li>Sea turtles and other reef fishes, such as snappers and groupers, can be incidentally caught while fishing for red grouper.&nbsp;</li>\n<li>In certain areas, fishermen are required to use circle hooks to improve the chance of survival of any unintentionally caught fish and to reduce turtle hookings.&nbsp;</li>\n<li>Commercial and charterboat/headboat reef fish fishermen must use appropriate release gear and follow handling protocols to increase the chance of survival for any incidentally caught <a href=\"http://sero.nmfs.noaa.gov/sustainable_fisheries/gulf_sa/turtle_sawfish_release/index.html\">sea turtles</a>.&nbsp;</li>\n<li>Fishermen are encouraged to use <a href=\"https://www.flseagrant.org/fisheries/venting/\" rel=\"external\">venting tools</a>&nbsp;or <a href=\"https://www.flseagrant.org/fisheries/catch-and-release-fishing/descending-and-venting/\" rel=\"external\">fish descenders</a>&nbsp;when fish are caught showing signs of barotrauma. Barotrauma occurs when reef fish are quickly brought to the surface by hook-and-line and the gas in their swimbladders expands. <a href=\"https://www.flseagrant.org/fisheries/venting/\" rel=\"external\">Venting tools</a>&nbsp;help deflate the expanded abdominal cavity, potentially reduce injury to the fish, and make it easier to return to deep water.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Red grouper is a popular fish among recreational fishermen in the South Atlantic and Gulf of Mexico.</li>\n<li>In 2020, recreational anglers landed 2.9 million pounds of red grouper, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:14440981298202::NO:::\">recreational fishing landings database</a>.&nbsp;&nbsp;</li>\n<li>In the South Atlantic:\n<ul>\n<li>Annual catch limits&nbsp;and accountability measures.</li>\n<li>Bag and size limits.</li>\n<li>The fishery is closed during the spawning season (from January through April).</li>\n</ul>\n</li>\n<li>In the Gulf of Mexico:\n<ul>\n<li>Annual catch limits&nbsp;and accountability measures.</li>\n<li>Bag and size limits.</li>\n<li>The fishery is closed during the spawning season (from February 1 through March 31) in deep water to protect spawning aggregations.</li>\n</ul>\n</li>\n<li>In the U.S. Caribbean:\n<ul>\n<li>Annual catch limits.</li>\n<li>Seasonal closure from February 1 through April 30.</li>\n<li>Bag and vessel limits.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Red grouper is low in saturated fat, and a good source of vitamins B6 and B12, phosphorus, potassium, protein, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Red grouper have robust bodies with small scales.</li>\n<li>Their head and body are dark reddish brown, shading pink or reddish below with occasional white spots on the sides and black spots on the cheeks.</li>\n<li>They have large mouths with a lower jaw that often projects slightly beyond their upper jaw, with bands of slender, sharp teeth, and usually a few stout, fixed canines.</li>\n<li>Their large mouths allow them to eat their prey whole.&nbsp;</li>\n</ul>\n",
