    "Location": "<ul>\n<li>English sole are found off the west coast of North America from the Bering Sea and Aleutian Islands to central Baja California.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Alaska",
    "Population": "Above target population level on the West Coast.",
    "Population Status": "<ul>\n<li>There are three stocks of English sole: Pacific coast, one stock contained in a stock complex in the Gulf of Alaska, and one stock contained in a stock complex in the Bering Sea/Aleutian Islands.&nbsp; According to the most recent stock assessments:\n<ul>\n<li>The Pacific coast stock is not overfished (2013 stock assessment) and not subject to overfishing based on 2019&nbsp;catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=English%20sole%20-%20Pacific%20Coast&amp;stockid=10046\">Stock SMART</a>.</li>\n<li>The Gulf of Alaska Shallow Water Flatfish Complex is not overfished (2017&nbsp;stock assessments) and not subject to overfishing based on 2020 catch data. Northern rock sole and rock sole are assessed and English sole is a secondary species in the complex. Summary stock assessment information for Northern rock sole can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Northern%20rock%20sole%20-%20Gulf%20of%20Alaska&amp;stockid=10836\">Stock SMART</a> and summary stock assessment information for rock sole can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Rock%20sole%20-%20Gulf%20of%20Alaska&amp;stockid=10040\">Stock SMART</a>.</li>\n<li>The Bering Sea/Aleutian Islands stock is managed as part of the Other Flatfish Complex and has not been assessed so the population status is unknown. This complex is not subject to overfishing based on 2020 catch data.</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Parophrys vetulus",
    "Species Aliases": "<a href=\"/species-aliases/sole\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sole</a>, <a href=\"/species-aliases/lemon-sole\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Lemon sole</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/english_sole.png",
    "alt": "English sole",
    "title": "English sole"
    },
    "Species Name": "English Sole",
    "Animal Health": null,
    "Availability": "<p>Year-round.&nbsp;</p>\n",
    "Biology": "<ul>\n<li>Female English sole grow twice as large as males, up to about 2 feet.&nbsp;</li>\n<li>Females can live for more than 20 years, 4 years longer than males.</li>\n<li>Males are able to reproduce when they reach 2 years old. Females mature starting at 3 years old.</li>\n<li>They spawn from winter to early spring over soft muddy ocean floors in water 165 to 230 feet deep.</li>\n<li>Depending on their size, females release between 150,000 and 2 million eggs. Eggs sink to the bottom a few days after spawning.&nbsp;</li>\n<li>Larvae stay near the surface for about 2 to 3 months before they are transported by wind and tidal streams to nearshore and estuarine nursery areas  an uncommon characteristic for a flatfish species in this region.&nbsp;</li>\n<li>Juveniles spend 1 to 2 years developing in nursery areas before migrating out to deeper waters, typically in late May.</li>\n<li>Larvae feed on plankton (tiny floating plants and animals).</li>\n<li>Juvenile and adult English sole feed on crustaceans, worms, small bivalves, clam siphons, and other bottom-dwelling invertebrates.</li>\n<li>English sole feed during the day using sight and smell, and sometimes dig for their prey.</li>\n<li>Seabirds, larger fishes, and marine mammals prey on juveniles. Marine mammals, sharks, and other large fish prey on adults.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
