    "Harvest": "<ul>\n<li>In 2020, commercial landings of English sole totaled 1.1 million&nbsp;pounds and were valued at $142,000, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200::::::\">commercial fishing landings database</a>.\n<ul>\n<li>The majority of the catch comes from Oregon and Alaska.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Bottom trawls are used to catch English sole.</li>\n<li>Trawls that are used to harvest English sole can contact the ocean floor and impact habitats, depending on the characteristics of the ocean bottom and the size of the gear.</li>\n<li>Bottom trawls cause minimal damage to habitat when targeting English sole over soft, sandy, or muddy ocean bottoms on the West Coast and in Alaska.</li>\n<li>In Alaska and on the West Coast, NOAA Fisheries and the regional fishery management councils have implemented large closed areas to protect sensitive rocky, cold-water coral and sponge habitats from bottom trawls.</li>\n<li><a href=\"https://www.westcoast.fisheries.noaa.gov/fisheries/management/vms.html\">Vessel monitoring systems</a> allow enforcement staff and fishery managers to monitor GPS locations of fishing activities to ensure vessels are complying with closed areas.</li>\n<li>In Alaska, fishery managers limit the amount of halibut, herring, and crab that groundfish fisheries can incidentally catch. If the limit is reached, managers close the fishery for the remainder of the season.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Excellent source of low-fat protein as well as selenium and vitamins.&nbsp;</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>English sole are flatfish, with both of their eyes located on the right side of their head.</li>\n<li>They have a pointed snout and their upper eye is visible on their underside.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.84 g ",
    "Quote": "U.S. wild-caught English sole is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#733b26",
    "Research": "<ul>\n<li><a href=\"https://www.afsc.noaa.gov/Science_blog/EBS_6.htm\">Studying bottom-dwelling fishes and crabs of the Eastern Bering Sea Shelf</a></li>\n<li>The coastal states and treaty tribes conduct port-side monitoring programs that provide valuable biological data to support stock assessment science and aid in proper management decisions.</li>\n<li>Scientists have identified a number of areas where additional research would substantially improve their ability to reliably and precisely model trends in the abundance of English sole, including extending the stock assessment to include Canadian waters, improving historical and current catch data, and collecting more data on English soles sexual maturity.</li>\n</ul>\n",
