    "Location": "<ul>\n<li>Dover sole is found in the Pacific Ocean from the Bering Sea and western Aleutian Islands to southern Baja California.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Alaska",
    "Population": "Above target population levels on the West Coast and in the Gulf of Alaska.",
    "Population Status": "<ul>\n<li>There are three stocks of Dover sole: Pacific coast, one stock contained in a stock complex in the Gulf of Alaska, and one stock contained in a stock complex in the Bering Sea/Aleutian Islands.&nbsp; According to the most recent stock assessments:\n<ul>\n<li>The Pacific coast stock is not overfished (2011 stock assessment) and not subject to overfishing based on 2018 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Dover%20sole%20-%20Pacific%20Coast&amp;stockid=10022\">Stock SMART</a>.</li>\n<li>The Gulf of Alaska Deepwater Flatfish Complex is not overfished (2018 stock assessment) and not subject to overfishing based on 2020 catch data. Dover sole is assessed and is the primary species in the complex. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Dover%20sole%20-%20Gulf%20of%20Alaska&amp;stockid=10021\">Stock SMART</a>.</li>\n<li>The Bering Sea/Aleutian Islands stock is managed as part of the Other Flatfish Complex and has not been assessed so the population status is unknown. This complex is not subject to overfishing based on 2020 catch data.</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Microstomus pacificus",
    "Species Aliases": "<a href=\"/species-aliases/sole\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sole</a>, <a href=\"/species-aliases/slippery-sole\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Slippery sole</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/dover_sole_1.png",
    "alt": "Dover sole illustration.",
    "title": ""
    },
    "Species Name": "Dover Sole",
    "Animal Health": null,
    "Availability": "<p>Year-round.&nbsp;</p>\n",
    "Biology": "<ul>\n<li>Females grow faster and larger than males, up to 30 inches long, about 2 to 4 inches longer than males.</li>\n<li>Males can live up to 58 years, about 5 years longer than females.</li>\n<li>Dover sole spawn near the ocean floor in waters deeper than 1,440 feet. Spawning seasons vary by location:\n<ul>\n<li>In the Gulf of Alaska from January to August.</li>\n<li>Off Oregon from November to April.</li>\n<li>In Puget Sound from January to March.</li>\n</ul>\n</li>\n<li>Females produce between 52,000 to 266,000 eggs, which can be as large as 0.1 inches.</li>\n<li>Dover sole eggs are fertilized externally and are found in the upper part of the water column.</li>\n<li>Once hatched, larvae settle into deeper water, then travel inshore to find suitable nursery areas.</li>\n<li>Dover sole larvae usually settle to the bottom after a year of living in the upper water column, but some remain larvae for as long as 2 years.\n<ul>\n<li>Scientists believe these holdover larvae delay settling to the bottom due to unfavorable environmental conditions.</li>\n</ul>\n</li>\n</ul>\n<ul>\n<li>Dover sole feed during the daytime by sight and smell.</li>\n<li>Their mouths are well adapted for feeding on small invertebrates that live within ocean floor sediments.</li>\n<li>Larval Dover sole feed on small crustaceans, eggs, and larval crustaceans as well as plankton.</li>\n<li>Juveniles and adults feed on worms, bivalves, brittle stars, and small bottom-dwelling crustaceans.</li>\n<li>Seabirds and fish that live near the surface prey on larval Dover sole.</li>\n<li>Sharks, bottom-feeding marine mammals, <a href=\"https://www.fishwatch.gov/profiles/pacific-cod\">Pacific cod</a>,&nbsp;<a href=\"https://www.fishwatch.gov/profiles/arrowtooth-flounder\">arrowtooth flounder</a>,&nbsp;<span>and sometimes <a href=\"https://www.fishwatch.gov/profiles/sablefish\">sablefish</a>&nbsp;</span>feed on juveniles and adults.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
