    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>In 2019, commercial landings of Dover sole totaled more than 14.28 million pounds and were valued at more than $6.2 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215\">commercial fishing landings database</a>.\n<ul>\n<li>Most of the landings come from Oregon, California, and Washington State.&nbsp;&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Bottom trawls are used to catch Dover sole.</li>\n<li>Trawls that are used to harvest Dover sole can contact the ocean floor and impact habitats, depending on the characteristics of the ocean bottom and the size of the gear.</li>\n<li>Bottom trawls cause minimal damage to habitat when targeting Dover sole over soft, sandy, or muddy ocean bottoms off the West Coast and Alaska.</li>\n<li>In Alaska and on the West Coast, NOAA Fisheries and the regional fishery management councils have implemented large closed areas to protect sensitive rocky, cold-water coral and sponge habitats from bottom trawls.</li>\n<li><a href=\"https://www.westcoast.fisheries.noaa.gov/fisheries/management/vms.html\">Vessel monitoring systems</a> allow enforcement staff and fishery managers to monitor GPS locations of fishing activities to ensure vessels are complying with closed areas.</li>\n<li>In Alaska, there are limits on the amount of halibut that can be incidentally caught in groundfish fisheries. If the limit is reached, managers close the fishery for the remainder of the season.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Dover sole is an excellent source of low-fat protein and calcium.&nbsp;</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>\n<p>Dover sole are flatfish, with both of their eyes located on the right side of their head.</p>\n</li>\n<li>\n<p>Their mouths are small relative to their size.</p>\n</li>\n<li>\n<p>Their drab coloring helps them camouflage themselves on the ocean floor.</p>\n</li>\n<li>In some areas, Dover sole are referred to as slippery sole because they excrete mucous onto their skin/scales, making them incredibly slippery and hard to pick up.&nbsp;&nbsp;</li>\n</ul>\n",
