    "Fishery Management": "<ul>\n<li><a href=\"https://sero.nmfs.noaa.gov/\">NOAA Fisheries</a>, the <a href=\"https://myfwc.com/\" rel=\"external\">Florida Fish and Wildlife Conservation Commission</a>, the governments of Puerto Rico and the U.S. Virgin Islands, and the <a href=\"http://www.safmc.net/\">South Atlantic</a>, <a href=\"http://www.gulfcouncil.org/\">Gulf of Mexico</a>, and <a href=\"https://caribbeanfmc.com/\">Caribbean</a> Fishery Management Councils manage the Caribbean spiny lobster fishery.</li>\n<li>In the South Atlantic and Gulf of Mexico, managed under the <a href=\"http://gulfcouncil.org/fishery-management/implemented-plans/spiny-lobster/\">Fishery Managment Plan for the Spiny Lobster in the Gulf of Mexico and South Atlantic</a> and the <a href=\"https://myfwc.com/fishing/saltwater/commercial/spiny-lobster/\" rel=\"external\">Florida Fish and Wildlife Conservation Commission Commercial Regulations for Spiny Lobster</a>:\n<ul>\n<li>Streamlined management measures that extend regulations in Florida state waters into federal waters.</li>\n<li>Fishermen must have a permit to harvest spiny lobster.</li>\n<li>Annual catch limits in place.</li>\n<li>Bag limits for commercial and recreational fishermen, depending on where they are harvested.</li>\n<li>Fishing season closed from April 1 through August 5 off Florida and the Gulf states to protect spiny lobsters during peak spawning season.</li>\n<li>Prohibition on harvesting and importing egg-bearing females or females that have been stripped of eggs.</li>\n<li>Minimum size limits on harvested and imported lobsters to allow them to spawn before they are harvested.</li>\n<li>Prohibition on spears, hooks, piercing devices, explosives, or poisons to harvest spiny lobster. Non-wooden traps must have biodegradable escape panels.</li>\n<li>Harvest is prohibited at all times in several marine protected areas in southern Florida.</li>\n<li>Closed areas in the Florida Keys to protect threatened species of coral that can be damaged by fishing gear.</li>\n<li>Prohibition on imports of spiny lobster tail meat that is not in whole-tail form with the exoskeleton (shell) attached.</li>\n<li>Prohibition on the use of traps for the recreational harvest of spiny lobster in federal waters off North Carolina, South Carolina, Georgia, and Florida.</li>\n</ul>\n</li>\n<li>In Puerto Rico and the U.S. Virgin Islands, spiny lobster in federal waters are managed under the <a href=\"https://caribbeanfmc.com/fishery-management/fishery-management-plans\">Fishery Managment Plan for the Spiny Lobster Fishery of Puerto Rico and the U.S. Virgin Islands</a>, and under territorial regulations in each of Puerto Rico and the U.S. Virgin Islands:\n<ul>\n<li>In federal waters only, annual catch limit allocated among the three island management areas (Puerto Rico, St. Croix, and St. Thomas/St. John).</li>\n<li>Minimum size limits that allow them to spawn before they are harvested.</li>\n<li>Bag limits for recreational harvest in federal waters of the U.S. Caribbean.</li>\n<li>Prohibition on spears, hooks, piercing devices, explosives, or poisons to harvest spiny lobster.</li>\n<li>Prohibition on the use of gillnets and trammel nets in federal waters to harvest spiny lobster.</li>\n<li>Traps, pots, buoys, and boats should be identified and marked and all&nbsp;traps must have biodegradable escape panels.</li>\n<li>Fishermen may not bring egg-bearing female lobsters aboard a vessel (they may be kept in pots or traps until the eggs are shed).</li>\n<li>Spiny lobsters must be whole when they are brought to port.</li>\n<li>Similar restrictions on importing spiny lobsters.</li>\n</ul>\n</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>Caribbean spiny lobster live in shallow waters to depths of 295 feet, and occasionally deeper.</li>\n<li>Larval spiny lobster float in the water column.</li>\n<li>As they grow, they swim to nearshore habitats and settle in dense vegetation, especially among macroalgae. They live there until they reach about 0.6 to 0.8 inches, then find shelter in crevices provided by large sponges, octocorals (soft corals), and solution holes until they are about 1 inches long.</li>\n<li>When they reach 2 to 3.15 inches, they travel from their nearshore nursery habitat to shallow banks in nearshore waters.</li>\n<li>As mature adults, they inhabit bays, estuarine areas, coral reefs and other offshore habitats, but are found on softbottom and seagrass during migratory periods.</li>\n<li>Adults move along shore and offshore seasonally, migrating in single-file lines to deeper water to escape cold and turbid waters.</li>\n</ul>\n",
    "Habitat Impacts": "Lobster trap closures are in place in some areas to prevent damage to ocean bottom habitat. ",
