    "Location": "<ul>\n<li>Caribbean spiny lobster are found along the continental shelf of the southeastern United States from North Carolina to Texas, in Bermuda, throughout the Caribbean Sea, and south to Brazil.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Southeast",
    "Population": "Above target population level in St. Thomas/St. John, St. Croix, and Puerto Rico.  The population levels are unknown in the South Atlantic and the Gulf of Mexico, but presumed stable.",
    "Population Status": "<ul>\n<li>\n<p>There are four stocks of Caribbean spiny lobster: the South Atlantic/Gulf of Mexico stock, and three stocks in the Caribbeanthe Puerto Rico, St. Thomas/St. John, and St. Croix stocks. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The South Atlantic/Gulf of Mexico stock is not subject to overfishing and the population status is unknown (2005 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=11031\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Puerto Rico stock is not subject to overfishing and not overfished (2020 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=12532\">Stock SMART</a>.<br />\n\t\t\t\t\tThe St. John/St. Thomas stock is not subject to overfishing and not overfished (2020 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=12533\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The St. Croix stock is not subject to overfishing and not overfished (2020 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=12534\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Panulirus argus",
    "Species Aliases": "<a href=\"/species-aliases/crawfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Crawfish</a>, <a href=\"/species-aliases/rock-lobster\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Rock lobster</a>, <a href=\"/species-aliases/bug\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Bug</a>, <a href=\"/species-aliases/florida-lobster\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Florida lobster</a>, <a href=\"/species-aliases/langosta-espinosa\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Langosta espinosa</a>",
