    "Animal Health": null,
    "Availability": "<p>Fresh from August through March, but fishing drops sharply after the first of the year. Frozen year-round.</p>\n",
    "Biology": "<ul>\n<li>Caribbean spiny lobsters grow by molting  they vacate their old shells while simultaneously absorbing water, expanding their body size. They molt about 25 times in their first 5 to 7 years of life, and once per year when theyre older. It takes them about 2 years to grow to the 3-inch carapace (shell) legal-harvesting size.</li>\n<li>Spiny lobsters may live 15 years or more, but there is uncertainty regarding this estimate due to the difficulty in ageing this species.</li>\n<li>They can grow up to 15 pounds and 18 inches.</li>\n<li>Females can reproduce when they reach 2.75 to 3 inches carapace length in the southeastern United States, and 3.6 inches in the Caribbean.</li>\n<li>They spawn from March through August in the Florida Keys on offshore reefs, and spawn throughout the year in the Caribbean.</li>\n<li>The male spiny lobster deposits sperm packets on the underside of the female. She scratches them to release sperm as she releases her eggs (between 230,000 to 2.6 million eggs each time they spawn, depending on her size). The female carries the fertilized eggs beneath her tail, and is referred to as berried. The eggs hatch in about 3 weeks.</li>\n<li>Most of the spiny lobster larvae found in U.S. waters are from upstream sources in the Caribbean.</li>\n<li>Young spiny lobsters feed on soft-bodied plankton (tiny floating plants and animals).</li>\n<li>Juveniles and adults are carnivores, preying on snails, crabs, and clams. They are nocturnal, inhabiting coral reefs, burrows, and dens during the day and foraging for food at night.</li>\n<li>Many predators feed on juvenile and adult spiny lobster, including groupers, snappers, sharks, skates, turtles, and octopuses.</li>\n</ul>\n",
