    "Fishing Rate": "At recommended levels in the South Atlantic, Gulf of Mexico, St. Thomas/St. John, St. Croix, and Puerto Rico.",
    "Harvest": "<ul>\n<li>In 2019, landings of Caribbean spiny lobster in the Gulf of Mexico and South Atlantic totaled more than 4.1 million pounds and were valued at more than $32 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>Gear types, habitat, and bycatch:\n<ul>\n<li>Commercial fishermen harvest spiny lobsters either by diving, bullynetting, or using wooden, plastic, or metal traps.</li>\n<li>Bullynetting is conducted in shallow waters at night when lobster are foraging for food or migrating offshore. A vessel uses a spotlight to find the lobster and then uses a bullynet to harvest the lobster.</li>\n<li>Buoys are attached to each trap, or if stringing a trap line, at each end of the trap line. Commercial traps are weighted with cement and must have a degradable escape panel to prevent ghost fishing (when a lost or abandoned trap continues to capture lobsters or other species).</li>\n<li>The State of Florida runs <a href=\"https://myfwc.com/fishing/saltwater/trap-debris/\" rel=\"external\">two programs</a>&nbsp;dedicated to removing lost and abandoned traps from state waters and has the authority to expand those programs into federal waters.</li>\n<li>Fishermen generally use undersized lobster (known as shorts) as bait to attract legal-sized lobsters into their traps. Mortality of these undersized lobsters is around 10 percent.</li>\n<li>Traps sometimes incidentally catch reef fish and ornamental fish, but most are released alive. Threatened or endangered species, such as sea turtles and corals, occasionally become entangled in trap lines.</li>\n</ul>\n</li>\n<li>Recreational anglers harvest spiny lobsters primarily by snorkeling or scuba diving. Undersized lobsters must be released unharmed immediately without removal from water.&nbsp;</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Spiny lobster is low in saturated fat and is a very good source of protein and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Caribbean spiny lobsters get their name from forward-pointing spines that cover their bodies to help protect them from predators.</li>\n<li>They have a reddish brown shell, marked with occasional dark spots and two large, cream-colored spots on the top of the second segment of the tail.</li>\n<li>They have long, horn-like antennae over their eyes that they wave to scare off predators, and smaller antennae-like antennules that sense movement and detect chemicals in the water.</li>\n<li>Unlike the American lobster, they lack large front claws.</li>\n<li>They are harvested for their tail meat. Items marketed as lobster tail are usually from spiny lobster.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.60  g",
    "Quote": "U.S. wild-caught Caribbean spiny lobster is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#784821",
    "Research": "<ul>\n<li>Lobsters spend a long time in the larval stage traveling with the currents. This leads scientists to suspect that young lobsters that survive to adulthood (recruits) in the United States come from many other areas. Recent genetic studies have shown almost all recruits in U.S. waters are from the Caribbean. This means that the spawning biomass in the Caribbean is more relevant to the population status than the spawning biomass in Florida.</li>\n<li>There is considerable interest in growing spiny lobster in aquaculture operations, and studies have indicated that the tropical species of spiny lobster (including Caribbean) have the most potential. These species grow relatively quickly and are highly marketable. <a href=\"https://thefishsite.com/articles/candidate-species-for-aquaculture-caribbean-spiny-lobster-ipanulirus-argus-i\">Research is ongoing worldwide</a>.</li>\n<li><a href=\"ftp://ftp.nodc.noaa.gov/pub/data.nodc/coris/library/NOAA/CRCP/project/10007/CRCP_FWRI_Year2_Report_text.pdf\">Scientists Study Lobsters in Marine Zones at Florida Keys National Marine Sanctuary</a>&nbsp;(PDF)</li>\n</ul>\n",
