    "Harvest": "<ul>\n<li>\n<p>In 2020, commercial landings of Pacific albacore tuna &nbsp;totaled approximately 16 million pounds and were valued at more than $25 million, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:10541705334568::NO:::\">commercial fishing landings database</a>.&nbsp;The majority of Pacific albacore tuna is landed in Washington and Oregon. Hawaii albacore is mostly sold fresh.</p>\n</li>\n<li>There are also significant landings in American Samoa, averaging approximately 3 million pounds per year, most of which is canned locally.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>On the U.S. West Coast, commercial fishermen target the North Pacific stock primarily using troll and pole-and-line (bait boat) gear.</li>\n<li>In Hawaii, longline vessels harvest North Pacific albacore while targeting swordfish and other tunas.</li>\n<li>In American Samoa, longline vessels target the South Pacific stock.</li>\n<li>Bycatch in U.S. albacore fisheries is low. Based on limited observer data, troll and pole-and-line (bait boat) gears have insignificant levels of bycatch.</li>\n<li>Interactions with sea turtles can occur in longline fisheries, but extensive observer coverage in U.S. fisheries indicates these interactions are rare. U.S. longline fishermen are required to use specific tools and handling techniques to reduce the effects of bycatch.</li>\n<li>Several other management measures are in place, such as gear modifications and time-area closures, to limit and prevent interactions between longline gear and other species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>North Pacific albacore are a popular and important recreational species.</li>\n<li>Off the West Coast, commercial passenger fishing vessels are required to keep logbooks to document their catch.</li>\n<li>There are bag limits in U.S. federal and state waters.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Albacore is a heart-healthy protein choice that is low in fat and cholesterol. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Albacore tuna have torpedo-shaped bodies, smooth skin, and streamlined fins.</li>\n<li>They are metallic, dark blue on the back with dusky to silvery white coloration along the sides of the belly.</li>\n<li>They have exceptionally long pectoral fins, which are at least half the length of their bodies.</li>\n<li>The edge of the tail fin is white.</li>\n</ul>\n",
    "Production": null,
    "Protein": "31 g",
    "Quote": "U.S. wild-caught Pacific albacore tuna is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#79b5dc",
    "Research": "<ul>\n<li><a href=\"https://swfsc.noaa.gov/textblock.aspx?Division=FRD&amp;ParentMenuId=139&amp;id=1194\">Southwest Fisheries Science Center</a> scientists are working with the American Fishermens Research Foundation on monitoring programs and other research efforts to improve knowledge of the biology and migration of North Pacific albacore in the waters off the U.S. Pacific coast.</li>\n<li><a href=\"https://swfsc.noaa.gov/textblock.aspx?Division=FRD&amp;id=1168&amp;ParentMenuId=139\">NOAAs Southwest Fisheries Science Center Albacore Research</a>.</li>\n</ul>\n",
