    "Species Name": "Wreckfish",
    "Animal Health": null,
    "Availability": "<p>Fresh from mid-April through mid-January, but availability is sporadic because only a few fishermen harvest wreckfish.</p>\n",
    "Biology": "<ul>\n<li>Wreckfish are typically 40 to 60 pounds and 2 to 4 feet long, but can reach a maximum of 220 pounds and 6 feet.</li>\n<li>They have a long life span, with some living more than 70 years.</li>\n<li>They are able to reproduce at 8 years of age.</li>\n<li>From January through mid-April, wreckfish spawn multiple times in the Charleston Bump area. After eggs are fertilized externally, eggs and larvae are transported across the Atlantic Ocean to the coast of Europe, and return as adults to the southeastern coast of the United States.</li>\n<li>Wreckfish in the South Atlantic are large predators that lurk in caves and under overhangs on the <a href=\"http://oceanexplorer.noaa.gov/explorations/islands01/log/sep27/media/foodchain.html\">Charleston Bump</a> and come out to feed on fish and squid migrating during the day. They have no known predators.</li>\n</ul>\n",
    "Bycatch": "Bycatch is low because fishermen use a very selective methodbottom hook-and-line gear with hydraulic reelsto catch wreckfish.",
    "Calories": "97 ",
    "Carbohydrate": "0 g",
    "Cholesterol": "41 mg",
    "Color": "<p>White when raw and cooked.</p>\n",
