    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>The wreckfish fishery is small, with fewer than 10 vessels currently operating in the commercial sector. Commercial landings are confidential due to the limited number of participants.</li>\n<li>The total annual catch limit has remained over 400,000 pounds since 2015.</li>\n<li>Wreckfish fishermen are based in Charleston, South Carolina, northeast Florida, and the Florida Keys. They supply most of the wreckfish in the domestic market.</li>\n<li>Primary fishing grounds are the <a href=\"http://oceanexplorer.noaa.gov/explorations/islands01/log/sep27/media/foodchain.html\">Charleston Bump</a>, a deep-water bank located 80 to 100 miles southeast of Charleston, South Carolina.</li>\n<li>Commercial fishermen use heavy-duty hydraulic reels with 1/8-inch cable to harvest wreckfish. They attach heavy weights and multiple circle hooks baited with squid to the cable and fish just above the bottom.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Wreckfish is a low-fat, high-protein source of B vitamins and minerals, including selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Wreckfish are bluish gray on the back and paler with a silvery sheen on the belly. Fins are blackish brown. Juveniles have black blotches on their head and body.</li>\n<li>They have a big head with a big mouth and a rough, bony ridge across the upper part of the gill cover.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.43 g ",
    "Quote": "U.S. wild-caught wreckfish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#94867b",
    "Research": "<ul>\n<li>Researchers have conducted surveys of the Charleston Bump, to determine wreckfish abundance and observe the geological features of their deep-water habitat.</li>\n<li>Submersibles and special sampling gear are used to conduct surveys because the rugged bottom topography and currents of the Charleston Bump make traditional sampling impossible.</li>\n<li>Additional research:\n<ul>\n<li><a href=\"http://www.bioone.org/doi/abs/10.1086/679974\">Whatever Happened to the Wreckfish Fishery? An Evaluation of the Oldest Finfish ITQ Program in the United States</a></li>\n<li><a href=\"https://www.sefsc.noaa.gov/labs/panama/mp/sadchapc.htm\">NOAA Southeast Fisheries Science Center: South Atlantic Deep-Sea Coral Habitat Area of Particular Concern</a></li>\n<li><a href=\"http://oceanexplorer.noaa.gov/explorations/04etta/background/fisheries/fisheries.html\">NOAA Ocean Explorer  Expanding Fisheries and Fishery Potential on the Blake Plateau</a>, <a href=\"http://oceanexplorer.noaa.gov/explorations/islands01/background/islands/sup11_bump.html\">A Profile of the Charleston Bump</a></li>\n<li><a href=\"http://oceanexplorer.noaa.gov/explorations/islands01/log/sep27/sep27.html\">The Charleston Bump: A Deep Reef 'Island' in the Gulf Stream</a></li>\n</ul>\n</li>\n</ul>\n",
