    "Location": "<ul>\n<li>Queen conch are found throughout the Caribbean and in the Gulf of Mexico, south Florida, the Bahamas, and Bermuda.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Southeast",
    "Population": "Significantly below target population levels. Rebuilding plan is in place.",
    "Population Status": "<ul>\n<li>According to the 2009 <a href=\"http://sedarweb.org/sedar-pw-03-caribbean-data-evaluation-workshop-final-report\">stock assessment</a> queen conch are overfished, but are not subject to overfishing based on 2017&nbsp;catch data.</li>\n</ul>\n",
    "Scientific Name": "Strombus gigas ",
    "Species Aliases": "<a href=\"/species-aliases/conch\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Conch</a>, <a href=\"/species-aliases/pink-conch\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Pink conch</a>, <a href=\"/species-aliases/carrucho\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Carrucho</a>, <a href=\"/species-aliases/caracol-reina\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Caracol Reina</a>, <a href=\"/species-aliases/lobatus-gigas\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Lobatus gigas</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/queen_conch_0.png",
    "alt": "Queen conch",
    "title": "Queen conch"
    },
    "Species Name": "Queen Conch",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Queen conch can live a long time, up to 30 years.</li>\n<li>They grow up to 12 inches in length and can weigh up to 5 pounds.</li>\n<li>They are able to reproduce when they reach 3 to 4 years old, when they develop their flared lip.</li>\n<li>Queen conch spawns from spring through the summer, in shallower water areas with clean coral sand. Both males and females mate with multiple individuals, and eggs are fertilized internally.</li>\n<li>Females lay long egg masses containing hundreds of thousands of eggs on patches of bare sand or occasionally seagrass. Eggs hatch after about 72 hours.</li>\n<li>Larval conch feed on plankton before settling to the ocean bottom. Adults feed on algae, incidentally ingesting bits of seagrass, macroalgae, sediment, and small bottom-dwelling animals in the process.</li>\n<li>Crabs, turtles, sharks, and rays feed on queen conch.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Primarily harvested by hand, so the fishery is very selective and there is little, if any, bycatch.",
    "Calories": "130",
    "Carbohydrate": "1.7 g",
    "Cholesterol": "65 mg",
    "Color": "<p>Depending on the conchs size, the meat ranges in color from snow white to a pale, golden orange. The larger the animal, the darker the meat.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
    "Farming Methods": null,
    "Farming Methods_": null,
    "Fat, Total": "1.2 g",
    "Feeds_": null,
    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>Commercial landings of queen conch meat from Puerto Rico (territorial waters) and the U.S. Virgin Islands&nbsp;(federal and territorial waters) in 2017 was 234,700&nbsp;pounds.</li>\n<li>Queen conch are primarily caught by hand, so there is minimal impact on habitat and little bycatch.</li>\n<li>The queen conch fishery has a long tradition in the Caribbean region. The meat is sold either fresh or dried and the shells are used in pottery and jewelry.</li>\n<li>The United States is a major importer of queen conch, due to the limited harvest allowed in federal waters and U.S. Virgin Islands territorial waters.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Queen conch is a good low-fat source of protein. It is high in vitamins E and B12, magnesium, selenium, and folate, but is also high in cholesterol.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Queen conch have a large, conical shell that typically ranges in size from 6 to 9 inches, but can reach a maximum size of 12 inches.</li>\n<li>The shell has blunt spikes and is typically orange, but often looks gray because its covered in algae and debris. The inside of the lip ranges from bright orange to pink.</li>\n<li>Queen conch have long eye stalks and a large, tube-like mouth (proboscis) that it can pull into its shell if threatened.</li>\n<li>As older juveniles mature, they develop a large lip on their shell that continues to thicken as the animal ages.</li>\n</ul>\n",
    "Production": null,
    "Protein": "26.3 g",
    "Quote": "U.S. wild-caught queen conch is a smart seafood choice because it is sustainably managed under a rebuilding plan that allows limited harvest by U.S. fishermen.",
    "Quote Background Color": "#d99d57",
    "Research": "<ul>\n<li>Researchers and entrepreneurs have been raising queen conch in aquaculture operations since the 1970s. Efforts have focused on hatcheries to enhance wild stocks as well as commercial aquaculture, growing queen conch to market size to offset fishing pressure on wild stocks.</li>\n<li>Researchers continue to look for ways to improve <a href=\"https://wkrec.ca.uky.edu/files/queenconch.pdf\" rel=\"external\">queen conch aquaculture</a>.&nbsp;</li>\n<li>Additional research:\n<ul>\n<li><a href=\"http://flowergarden.noaa.gov/science/conch_burnside.html\">Flower Garden Banks National Marine Sanctuary Queen Conch Research</a></li>\n<li><a href=\"https://www.fws.gov/international/pdf/factsheet-import-queen-conch-2013.pdf\">U.S. Fish and Wildlife Service: Importing Queen Conch - What You Need to Know</a></li>\n</ul>\n</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.37 g",
    "Selenium": "40.3 mcg",
    "Serving Weight": "100 g",
    "Servings": "1",
    "Sodium": "153 mg",
    "Source": "<p>U.S. wild-caught from Puerto Rico and the U.S. Virgin Islands, but U.S. harvest is very limited.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Conch has a sweet, slightly smoky flavor, similar to abalone or clam, and an almost crunchy texture. Fresh, farmed conch is sweeter and more tender than frozen, wild conch.</p>\n",
    "Texture": "<p>Young thin-lipped conchs have more tender meat than larger, thick-lipped ones.</p>\n",
    "Path": "/profiles/queen-conch",
    "last_update": "01/05/2022 - 14:26"
