    "Species Name": "Red King Crab",
    "Animal Health": null,
    "Availability": "<p>Year-round, but generally harvested from October to January.</p>\n",
    "Biology": "<ul>\n<li>Red king crabs can grow to be very large, up to 24 pounds with a leg span of 5 feet. Males grow faster and larger than females.</li>\n<li>&nbsp;Female red king crabs reproduce once a year and release between 50,000 and 500,000 eggs.</li>\n<li>Larvae hatch from eggs looking like tiny shrimp.</li>\n<li>The larvae feed on phytoplankton and zooplankton for 2 to 3 months before metamorphosing into tiny crabs and settling on the ocean bottom.</li>\n<li>Red king crabs can only grow by molting (shedding their old shell and growing a new one).&nbsp;</li>\n<li>After molting they are soft and vulnerable to predators until their new shell hardens.</li>\n<li>Red king crabs eat almost anything they can find and crush with their claws.</li>\n<li>Smaller crabs eat algae, small worms, small clams, and other small animals.</li>\n<li>Larger crabs eat a much wider range of items including worms, clams, mussels, barnacles, crabs, fish, sea stars, sand dollars, and brittle stars.</li>\n<li>Smaller crabs are eaten by a variety of groundfish, octopi, sea otters, and crabs, including other red king crabs.</li>\n<li>Large red king crabs have few predators except right after molting.&nbsp;</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
