    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "The Bristol Bay and Norton Sound stocks are at recommended levels. The Pribilof Islands and Western Aleutian Islands stocks are closed to fishing for red king crab.",
    "Harvest": "<ul>\n<li>In 2019, commercial landings of all king crab in Alaska totaled approximately 10.8 million pounds and were valued at more than $67.5 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:15293554151240::NO:::\">commercial fishing landings database</a>.</li>\n<li>Red king crab are mainly harvested in Bristol Bay. Some catch also comes from fisheries in Norton Sound.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Mesh-covered pots that are 7 to 8 square feet are used to catch red king crab.</li>\n<li>Only male crabs can legally be caught and sold.</li>\n<li>Crab pots can unintentionally catch female crabs (which may not be harvested), males under the commercial size, and non-targeted crab species as well as a small number of other species including octopus, Pacific cod, Pacific halibut, other flatfish, sponges, coral, and sea stars.</li>\n<li>Regulations require fishermen to install escape panels and rings on their pots to prevent ghost fishing (when lost pots continue to capture and kill species) and to reduce bycatch.</li>\n<li>Habitat impacts from the red king crab fishery are minor because fishing occurs in areas of soft sediment such as silt and mud. Soft sediments are unlikely to be damaged by fishing gear. Crab pots are less damaging than mobile gear because they are stationary and contact a much smaller area of the seafloor.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>King crab is low in saturated fat and is a great source of protein, vitamin B12, phosphorus, zinc, copper, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Red king crabs are the largest of the commercially harvested crabs.</li>\n<li>They range in color from brownish to bluish red and are covered in sharp spines.</li>\n<li>They have three pairs of walking legs and one pair of claws.</li>\n<li>Their claws are different shapes. One is a large, heavy-duty claw that is used for crushing prey, and the other smaller claw is used for more delicate handling of food items.</li>\n<li>Determining the sex of red king crabs is easy. Males have a triangular abdominal flap and females have a rounded one.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.29 g",
    "Quote": "U.S. wild-caught red king crab is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#b03a2e",
