    "Location": "<ul>\n<li>Pacific spiny dogfish are found from the Bering Sea to Baja California.</li>\n<li>They are more common off the U.S. West Coast and British Columbia than in the Gulf of Alaska or the Bering Sea and Aleutian Islands region.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Alaska",
    "Population": "The Pacific Coast stock is above target population levels. ",
    "Population Status": "<ul>\n<li>\n<p>There are three stocks of Pacific spiny dogfish: Pacific coast, Bering Sea/Aleutian Islands, and Gulf of Alaska. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Pacific coast stock is not overfished (2011 stock assessment), and not subject to overfishing based on 2019 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Spiny%20dogfish%20-%20Pacific%20Coast&amp;stockid=10702\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Pacific spiny dogfish stock is managed as part of the Bering Sea / Aleutian Islands Shark Complex and has been assessed, but there is not enough information to determine the population size so the overfished&nbsp;status is unknown (2016 stock assessment). This complex is not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Bering%20Sea%20/%20Aleutian%20Islands%20Shark%20Complex&amp;stockid=11403\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Pacific spiny dogfish stock is managed as part of the Gulf of Alaska Complex and has been assessed, but there is not enough information to determine the population size so the overfished&nbsp;status is unknown (2020 stock assessment). This complex is not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Gulf%20of%20Alaska%20Shark%20Complex&amp;stockid=11450\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Squalus suckleyi",
    "Species Aliases": "<a href=\"/species-aliases/dogfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Dogfish</a>, <a href=\"/species-aliases/spring-dogfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Spring dogfish</a>, <a href=\"/species-aliases/spiked-dogfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Spiked dogfish</a>, <a href=\"/species-aliases/grayfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Grayfish</a>, <a href=\"/species-aliases/spur-dog\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Spur dog</a>, <a href=\"/species-aliases/piked-dogfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Piked dogfish</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/atlantic_spiny_dogfish_shark_0.png",
    "alt": "Illustration of a Pacific Spiny Dogfish.",
    "title": "Pacific Spiny Dogfish"
    },
    "Species Name": "Pacific Spiny Dogfish",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Spiny dogfish live a long time, sometimes more than 80 years.</li>\n<li>They grow slowly, up to more than 4 feet and 22 pounds, although adults are generally 2 to 3 feet long.</li>\n<li>Spiny dogfish arent able to reproduce until theyre older  females mature at an average age of 35, males mature at an average age of 19.</li>\n<li>Female spiny dogfish are internally fertilized, and pups are retained in utero for 18 to 22 months. Depending on their size, female spiny dogfish can have up to 22 pups each reproductive cycle.</li>\n<li>Females generally release their young during the fall in shallow bays.</li>\n<li>The newborn pups range in length from 8 to 12 inches.</li>\n<li>Spiny dogfish are opportunistic feeders, eating whatever prey is available. They mainly eat small, schooling pelagic fish such as herring, and small invertebrates such as shrimp, crab, and squid.</li>\n<li>They are preyed upon by larger species of shark, including larger spiny dogfish, and by larger fishes (such as cod and hake), seals, and killer whales.</li>\n</ul>\n",
    "Bycatch": "Fishermen generally do not directly target spiny dogfish in waters off the Pacific Coast and Alaska. As a result, there are no bycatch impacts from a directed fishery.",
    "Calories": "130",
    "Carbohydrate": "0 g",
    "Cholesterol": "51 ng",
    "Color": "<p>Raw meat is white. The outer flesh can have a reddish color, which turns brown when cooked. The rest of the meat is white when cooked.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
