    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>In 2020, commercial landings of Pacific spiny dogfish totaled approximately 650,000 million pounds and were valued at more than $13,000, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:14065380104249::NO:::\">commercial fishing landings database</a>.</li>\n<li>Spiny dogfish is a low-value species and is mostly taken as bycatch in Pacific coastbased fisheries targeting other commercially important species.</li>\n<li>There are currently no directed commercial fisheries for shark species in federally or state-managed waters of Alaska. Spiny dogfish are caught incidentally in fisheries for halibut and groundfish. Nearly all shark catch is discarded at sea, but some spiny dogfish are retained as incidental catch in state-managed fisheries.</li>\n<li>Spiny dogfish are generally not targeted by anglers off the Pacific coast or Alaska. However, due to their aggressive feeding nature, spiny dogfish tend to bite baited hooks and are incidentally caught in many recreational fisheries.</li>\n<li>Off the Pacific coast, spiny dogfish are considered a bottomfish so they are restricted by the recreational coastwide aggregate limit (although most are released because theyre not highly valued) as well as all other applicable bottomfish recreational restrictions (depth, season, etc.).</li>\n<li>In Alaska, anglers have annual bag limits for sharks of any kind.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Shark is a low-fat source of protein that is high in selenium and vitamins B6 and B12. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Spiny dogfish are slim with a narrow, pointed snout and distinctive white spots.</li>\n<li>Their bodies are gray above and white below.</li>\n<li>True to their name, they have sharp spines in front of each of their two dorsal fins.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.98 g",
    "Quote": "U.S. wild-caught Pacific spiny dogfish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#8d7170",
    "Research": "<ul>\n<li>Spiny dogfish have been the subject of electromagnetic field research due to their unique Ampullae of Lorenzini organs, found in other elasmobranchs (sharks, skates, and rays). Ampullae of Lorenzini are organs around the mouth that are filled with fluids that help neurons detect changes in electromagnetic fields generated by movements of prey. Some researchers have also theorized that the bottom-dwelling elasmobranchs use electromagnetic fields generated from changes in sea floor substrate to assist with migration patterns.</li>\n<li>Because spiny dogfish have skeletons made of cartilage rather than bone, they have a different mechanism for producing what are referred to as B (bone marrow) cells in bony vertebrates. In elasmobranchs, these cells are generated outside the skeleton, instead of in the bone marrow. Studying this phenomenon, as well as dogfish renal glands (which produce certain compounds shown to have some success in combating HIV virus particles and cancer cells), may have value in the development of future biomedical treatment therapies for humans.</li>\n<li><a href=\"https://www.afsc.noaa.gov/ABL/MESA/mesa_sa_sharks.php\">NOAA Alaska Fisheries Science Center Shark Research</a></li>\n</ul>\n",
