    "Fishery Management": "<ul>\n<li>Since 1923, the United States and Canada have coordinated Pacific halibut management through a bilateral commission known as the <a href=\"https://www.iphc.int/\" rel=\"external\">International Pacific Halibut Commission (IPHC)</a>. NOAA Fisheries and the <a href=\"https://www.npfmc.org/\">North Pacific</a> and <a href=\"https://www.pcouncil.org/\">Pacific Fishery Management Councils</a> are responsible for allocating allowable catch among harvesters in the U.S. fisheries.</li>\n<li>IPHC:\n<ul>\n<li>Using the latest scientific information on the abundance and potential yield of the Pacific halibut stock, establishes catch limits annually for fisheries in U.S. and Canadian waters.</li>\n<li>Sets the catch limits at a level that will ensure the long-term welfare of the Pacific halibut stock.</li>\n<li>Sets the dates for the fishing season, which usually spans from March to November and is closed the rest of the year when Pacific halibut spawn.</li>\n<li>The commercial fishery has a minimum size requirement to protect juvenile Pacific halibut.</li>\n</ul>\n</li>\n<li>In Alaska, the North Pacific Fishery Management Council is responsible for allocating the catch limits among users and user groups fishing off Alaska and developing regulations for the fishery, in line with IPHC recommendations. <a href=\"https://www.fisheries.noaa.gov/region/alaska\">NOAA Fisheries</a> is responsible for implementing and enforcing these regulations:\n<ul>\n<li><a href=\"https://www.fisheries.noaa.gov/alaska/commercial-fishing/pacific-halibut-and-sablefish-individual-fishing-quota-ifq-program\">Individual fishing quota program</a>, which allocates the total allowable catch among fishing vessels and individual fishermen. With their catch set, fishermen have the flexibility to harvest their quota anytime, creating a safer, more efficient, more valuable, and environmentally responsible fishery.</li>\n<li><a href=\"https://www.npfmc.org/community-development-program/\">Community development quota program</a>, which allocates a percentage of the total allowable catch to eligible western Alaska villages to allow them to participate and invest in fisheries in the Bering Sea and Aleutian Islands and to support sustainable economic and community development in western Alaska.</li>\n</ul>\n</li>\n<li>For waters off the U.S. West Coast, the Pacific Fishery Management Council is responsible for allocating the catch limits among users and user groups fishing off the West Coast and developing regulations for the fishery, in line with Commission recommendations. <a href=\"http://www.westcoast.fisheries.noaa.gov/\">NOAA Fisheries</a> is responsible for implementing and enforcing these regulations:\n<ul>\n<li>Establish regulations for Pacific halibut fisheries in U.S. waters off Washington, Oregon, and California (known as IPHC Regulatory Area 2A).</li>\n<li>The IPHC sets the catch limit for Pacific halibut in this regulatory area, and the Pacific Council allocates the catch among the following user groups: non-tribal commercial (incidental salmon troll fishery, directed longline Pacific halibut fishery, and incidental longline sablefish fishery), sport, and treaty Indian commercial and ceremonial-and-subsistence.</li>\n</ul>\n</li>\n<li>The Pacific Council describes the division of Pacific halibut catch each year in the Pacific Halibut Catch Sharing Plan for Area 2A.</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>Juveniles (1 inch and larger) live in shallow, near-shore waters off Alaska and British Columbia.</li>\n<li>Pacific halibut move to deeper water as they age.</li>\n<li>Adults migrate seasonally from shallow summer feeding grounds to deeper winter spawning grounds.</li>\n</ul>\n",
    "Habitat Impacts": "Fishing gears used to harvest Pacific halibut have minimal impacts on habitat.",
