    "Location": "<ul>\n<li>Pacific halibut are found in coastal waters from Santa Barbara, California, to Nome, Alaska.</li>\n<li>They are most common in the central Gulf of Alaska, particularly near Kodiak Island.&nbsp;</li>\n</ul>\n",
