    "Population Status": "<ul>\n<li>According to the 2020&nbsp;stock assessment, Pacific halibut is not overfished and is fished at the recommended level that is set by the International Pacific Halibut Commission. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Pacific%20halibut%20-%20Pacific%20Coast%20/%20Alaska&amp;stockid=11082\">Stock SMART</a>.</li>\n</ul>\n",
