    "Availability": "<p>Available fresh from March to November and frozen year-round.</p>\n",
    "Biology": "<ul>\n<li>Pacific halibut are one of the largest flatfish  they can weigh up to about 500 pounds and grow to more than 8 feet long.</li>\n<li>Males tend to be smaller than females.</li>\n<li>Males sexually mature when they are 8 years old, and females are able to reproduce by the age of 12.</li>\n<li>They spawn during the winter in deep water along the continental slope, mainly in the Bering Sea, Aleutian Islands, Gulf of Alaska, and south to British Columbia.</li>\n<li>Depending on their size, females can have between 500,000 and 4 million eggs.</li>\n<li>Scientists believe females release their eggs in batches over several days during the spawning season. Eggs hatch after 12 to 20 days,&nbsp;dependent on water temperature.</li>\n<li>The larvae slowly float close to the surface, where they remain for about 6 months until they reach their adult form and settle to the bottom in shallow water.</li>\n<li>Larval Pacific halibut feed on zooplankton (tiny floating organisms).</li>\n<li>Juveniles eat small crustaceans and other organisms that live on the seafloor.</li>\n<li>Halibut live to be relatively old  the oldest halibut on record was 55 years old, but halibut over age 25 are rare.</li>\n<li>Adults aggressively prey on a variety of groundfish, sculpins, sand lance, herring, octopus, crabs, clams, and occasionally smaller Pacific halibut.</li>\n<li>Marine mammals and sharks sometimes eat Pacific halibut but, due to their large size, adult Pacific halibut are rarely preyed upon by other fish.</li>\n</ul>\n",
