    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial landings of Pacific halibut totaled more than 24.4 million pounds and were valued at more than $97.6 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:12261558388582::NO:::\">commercial fishing landings database</a>. Approximately 95 percent of this harvest was landed in Alaska.</li>\n<li>Pacific halibut is one of the most valuable commercial and recreational fishery resources in the North Pacific Ocean.</li>\n<li>About 2 percent of the halibut population that can be fished is found off Oregon and Washington, about 15 percent off British Columbia, and the remainder off Alaska.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Historically, only hook-and-line gear was allowed to target Pacific halibut.&nbsp;In recent years, vessels fishing with pot gear in certain areas or fisheries may retain Pacific halibut although this has been at very low levels.</li>\n<li>Commercial fishermen predominantly use bottom longlines (setlines), which minimally impact habitat.</li>\n<li>Setlines can incidentally catch seabirds, but widespread use of seabird avoidance devices (called streamers) in the fishery has reduced seabird bycatch by up to 90 percent per vessel.</li>\n<li>In general, the commercial Pacific halibut fishery is fairly selective in the fish it catches because of the size of hook needed to harvest such a large fish. Using a large hook generally reduces bycatch of smaller fish.</li>\n<li>Fishermen use circle hooks to increase catch rates, and these hooks also improve the survival of any undersized Pacific halibut caught and released.</li>\n<li>Pacific halibut are also caught in commercial fisheries targeting other species. Regulations, such as gear and fishery restrictions, are in place to reduce bycatch of Pacific halibut in those fisheries.</li>\n</ul>\n</li>\n<li>Sport fishery:\n<ul>\n<li>Pacific halibut is a popular target for sport fishermen.</li>\n<li>In 2019, approximately 500,000 fish were caught by recreational fishermen in Alaska, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:12261558388582::NO:::\">recreational fishing landings database</a>.</li>\n<li>The <a href=\"https://www.fisheries.noaa.gov/alaska/recreational-fishing/sport-fishing-alaska\">sport fishery in Alaska</a> is managed under daily bag and possession limits.</li>\n<li>Sport fishermen often fish for Pacific halibut from charter (party) boats, which accounts for 55 percent of the total recreational harvest in Alaska.</li>\n<li>The charter fishery has more restrictive regulations to keep its harvest within the levels set by managers, including a permit system that limits the number of charter boats that can fish for Pacific halibut.</li>\n<li>Oregon, Washington, and California have catch limits for sport Pacific halibut fishing. The demand for Pacific halibut sport fishing is so high that closed seasons, bag limits, and possession limits are all used to control the recreational fishery and extend the season as long as possible.</li>\n</ul>\n</li>\n<li>Tribal fisheries:\n<ul>\n<li>Pacific halibut is an important source of spiritual and physical sustenance for Western Indian tribes and Alaska natives, and is caught in ceremonial and subsistence fisheries.</li>\n<li>Tribal commercial fisheries for Pacific halibut use similar gear types to non-tribal commercial fisheries, although allocations between tribes and fisheries and fishing season details are set both by individual tribes and through cooperative management programs bridging multiple tribes.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Pacific halibut is low in saturated fat and sodium and is a very good source of protein, niacin, phosphorus, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Pacific halibut have flat, diamond-shaped bodies.</li>\n<li>They swim sideways, and the upper side is typically mottled gray to dark brown, which helps them blend in with sandy or muddy bottoms. Their underside is typically white.</li>\n<li>Both of their eyes are on the upper side of their body.</li>\n<li>Their scales are small and buried in the skin, giving them a smooth appearance.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.81 g",
    "Quote": "U.S. wild-caught Pacific halibut is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#713d2b",
    "Research": "<ul>\n<li>The IPHC conducts a comprehensive&nbsp;<a href=\"https://iphc.int/management/science-and-research/fishery-independent-setline-survey-fiss\" rel=\"external\">fishery&nbsp;independent setline survey</a> over the geographic range of Pacific halibut in U.S. and Canadian waters. For 2017, this survey added more sampling sites in IPHC Regulatory Area 4B and off of northern California, improving estimates of Pacific halibut abundance at the southern end of its range.</li>\n<li><a href=\"https://www.nwfsc.noaa.gov/news/features/pacific_halibut/index.cfm\">Scientists report declines in Pacific halibut mortality</a></li>\n</ul>\n",
