    "Location": "<ul>\n<li>Spiny dogfish are found in the North Atlantic and North Pacific Oceans, mostly in the temperate and subarctic areas. In the Northwest Atlantic, they are found from Labrador to Florida and are most abundant between Nova Scotia and Cape Hatteras.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "Below target population levels.",
    "Population Status": "<ul>\n<li>According to the 2018&nbsp;stock assessment, Atlantic spiny dogfish are not overfished and are not subject to overfishing.&nbsp;Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10701\">Stock SMART</a>.</li>\n<li>Scientists project that the number of mature females may decline somewhat in the coming years due to the low number of pups born during the 1990s when spiny dogfish were heavily fished. This potential decline is not expected to result in the stock becoming overfished.</li>\n</ul>\n",
    "Scientific Name": "Squalus acanthias",
    "Species Aliases": "<a href=\"/species-aliases/dogfish-shark\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Dogfish shark</a>, <a href=\"/species-aliases/cape-dogfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Cape dogfish</a>, <a href=\"/species-aliases/cape-shark\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Cape shark</a>, <a href=\"/species-aliases/spring-dogfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Spring dogfish</a>, <a href=\"/species-aliases/spiked-dogfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Spiked dogfish</a>, <a href=\"/species-aliases/grayfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Grayfish</a>, <a href=\"/species-aliases/spur-dog\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Spur dog</a>, <a href=\"/species-aliases/piked-dogfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Piked dogfish</a>",
