    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Like all sharks, dogfish grow slowly, mature late in life, and live a long time (35 to 40 years).</li>\n<li>Females grow larger and mature later than malestheyre first able to reproduce at age 12 compared to males at age 6.</li>\n<li>They spawn in winter in offshore waters.</li>\n<li>Females have between two and 12 eggs per spawning season. The eggs are fertilized internally and, after a gestation period of 18 to 24 months, female dogfish bear live young (an average of six pups).</li>\n<li>They are opportunistic feeders, preying on whatever is most available.</li>\n<li>Smaller spiny dogfish tend to feed primarily on crustaceans, while larger dogfish like to eat jellyfish, squid, and schooling fish.</li>\n<li>Dogfish are preyed upon by cod, red hake, goosefish, other spiny dogfish, larger sharks, seals, and orcas.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "130",
    "Carbohydrate": "0 g",
    "Cholesterol": "51 mg",
    "Color": "<p>The raw meat is white. The outer flesh can have a reddish hue, which turns brown when cooked. The rest of the meat remains white when cooked.</p>\n",
