    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>In 2020, commercial landings of spiny dogfish in the Atlantic totaled more than 16.7&nbsp;million pounds, and were valued at more than&nbsp;$3.6&nbsp;million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:15966980466526::NO:::\">commercial fishing landings database</a>. These figures may not match other agency sources of data due to confidential information. The majority of the harvest was landed in Massachusetts and Virginia.</li>\n<li>The U.S. commercial fishery supplies European markets that use dogfish for fish and chips in England, and as a popular beer garden snack called shillerlocken in Germany.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Atlantic spiny dogfish are targeted by gillnet and longline vessels, mainly in state waters.</li>\n<li>They are often caught incidentally in trawl, gillnet, and longline fisheries for groundfish species.</li>\n<li>Trawls can impact habitat, depending on where they are used.</li>\n<li>Managers have implemented a variety of measures for groundfish trawl fisheries to protect habitat, including closing some areas to fishing and modifying fishing gear to reduce contact with habitat.</li>\n<li>Trawl and gillnet fisheries for groundfish can incidentally catch other fish and marine mammals (such as large whales, harbor porpoises, bottlenose dolphins) and sea turtles. Restrictions on gear and fishing effort in these fisheries reduce these interactions.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Shark is a low-fat source of protein and is high in selenium and vitamins B6 and B12. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Spiny dogfish are slim, with a narrow, pointed snout and characteristic white spots.</li>\n<li>They are gray above and white below.</li>\n<li>They have two dorsal fins with ungrooved large spines.</li>\n<li>Males grow up to 3.3 feet, and females grow up to 4 feet.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.98 g ",
    "Quote": "U.S. wild-caught Atlantic spiny dogfish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#8d7170",
    "Research": "<ul>\n<li>In January 2011, <a href=\"http://www.nefsc.noaa.gov/\">NOAA's Northeast Fisheries Science Center</a> launched a cooperative initiative to tag spiny dogfish in the Gulf of Maine, Southern New England, and Georges Bank. This project is designed to answer longstanding questions about stock structure, movement patterns, and life history to update and improve dogfish stock assessments.</li>\n<li>There is a small scientific fishery in Maine, which uses spiny dogfish to study several of the species unique biological characteristics. For example, studies of dogfish rectal glands help scientists better understand the function of human kidneys. Dogfish also secrete a compound called squalamine, which has strong antibiotic characteristics and shows promise as an anti-cancer agent.</li>\n</ul>\n",
