    "Species Name": "Arrowtooth Flounder",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Arrowtooth flounder grow slowly and can live up to 27 years.</li>\n<li>Males can reach 2 feet in length, and females grow a bit larger, up to almost 3 feet.</li>\n<li>Males sexually mature when they reach 3 to 7 years old, and females are able to reproduce when they reach 4 to 8 years old.</li>\n<li>Spawning season varies by location:\n<ul>\n<li>Off the West Coast from late fall through early spring.</li>\n<li>In the Gulf of Alaska during spring and summer.</li>\n<li>Off the coast of Alaska during fall and winter.</li>\n</ul>\n</li>\n<li>They spawn multiple times during a spawning season, releasing eggs that are then fertilized externally.</li>\n<li>Arrowtooth flounder eggs hatch in deep water (below 400 meters) and rise up the water column as they develop, then settle to the ocean bottom during the summer and fall.</li>\n<li>Larvae eat copepods, a type of small crustacean.</li>\n<li>Juveniles and adults feed on crustaceans (mainly pink shrimp and krill) and fish (mainly cod, herring, and pollock).</li>\n<li>A variety of fish and marine mammals prey on arrowtooth flounder, including skates, sharks, shortspine thornyhead, halibut, orcas, other toothed whales, and harbor seals.</li>\n<li>In the Gulf of Alaska, arrowtooth flounder are an important part of the diet of Steller sea lions.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
