    "Harvest": "<ul>\n<li>In 2019, commercial landings of arrowtooth flounder totaled more than 72.1 million pounds and were valued at over $7.2 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:4273842543681::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>Although arrowtooth flounder are a low-value fish, fishermen have been retaining more of the arrowtooth they catch  up to about 80 percent in Alaska.</li>\n<li>Catches have been higher because arrowtooth flounder are more abundant, resulting in higher incidental catch in other fisheries, in addition to increased marketing efforts for arrowtooth fish meal and surimi.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Bottom trawl gear is used to catch arrowtooth flounder.</li>\n<li>In general, arrowtooth flounder live on sandy or sand/gravel habitats. These soft bottom habitats are usually more resilient than other habitats to trawling impacts.</li>\n<li>In Alaska, NOAA Fisheries scientists and the flatfish fishing industry collaborated to develop changes to fishing gear that would reduce effects of flatfish trawling on seafloor habitats of the central Gulf of Alaska and the eastern Bering Sea shelf. The modified gear they developed  <a href=\"https://www.afsc.noaa.gov/Quarterly/jfm2010/jfm10featurelead.htm\">Bering Sea flatfish gear</a>  not only reduced impacts to sea floor habitat and the animals living there but also reduced the fisherys impacts on crabs. In the central Gulf of Alaska and Bering Sea areas, flatfish fishermen are now required to use this modified fishing gear.</li>\n<li>Halibut are sometimes incidentally caught, but there is a limit on the number of halibut that can be incidentally caught in the fishery. When this limit is reached, the directed fishery is closed.</li>\n<li>Some rockfish are still unintentionally caught, but management caps the amount of rockfish that can be incidentally caught in the fishery.</li>\n<li>To protect sensitive fish habitat off the West Coast, gear restrictions <a href=\"https://www.westcoast.fisheries.noaa.gov/fisheries/groundfish/groundfish_closed_areas.html\">limit where flatfish fishermen can fish</a>.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Flounder is a good, low-fat source of B vitamins and niacin.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Arrowtooth flounder are a relatively large, brownish colored flatfish with a large mouth.</li>\n<li>Theyre members of the family <em>Pleuronectidae</em>, the right-eyed flounders, which have both eyes on the right side and lie on the ocean floor on their left side.</li>\n</ul>\n",
