    "Population Status": "<ul>\n<li>According to the 2021 stock assessment, Alaska snow crabs are overfished, but not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Snow%20crab%20-%20Bering%20Sea&amp;stockid=10820\">Stock SMART</a>.</li>\n</ul>\n",
