    "Species Name": "Alaska Snow Crab",
    "Animal Health": null,
    "Availability": "<p>Generally harvested from January to April in the Eastern Bering Sea, but available year-round.</p>\n",
    "Biology": "<ul>\n<li>Male Alaska snow crab can reach 6 inches in shell width but females seldom grow larger than 3 inches.</li>\n<li>Scientists estimate that snow crabs may live for up to 20 years.</li>\n<li>Females can carry up to nearly 100,000 eggs, depending on their size. They hatch their larvae in the spring when there is plenty of food in the water column.</li>\n<li>When the eggs hatch, the larvae look like tiny shrimp.</li>\n<li>The larvae feed on phytoplankton and zooplankton before metamorphosing into tiny crabs and settling on the ocean bottom.</li>\n<li>Alaska snow crabs can only grow by molting (shedding their old shell and growing another).&nbsp;</li>\n<li>After molting snow crabs are soft and vulnerable to predators until their new shell hardens.</li>\n<li>When they have reached sexual maturity, both females and males undergo a terminal molt, after which they never molt again.</li>\n<li>Snow crabs will eat almost anything they can catch and break open with their claws, including fish, shrimp, crabs, worms, clams, brittle stars, snails, algae, and sponges. They will also scavenge on anything dead they find.</li>\n<li>Seals, sea otters, octopi, other crabs, and a wide variety of fish prey on Alaska snow crabs.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
