    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>In 2020, commercial landings of Alaska snow crab totaled more than 36.6 million pounds and were valued at more than $101.7 million,&nbsp;according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:15293554151240::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>The fishing fleet is made up of a variety of vessels, from small inshore vessels to large-scale super crabbers that fish in the Bering Sea.</li>\n<li>Commercial fishermen use crab pots to harvest snow crab.</li>\n<li>They bait the pots with chopped herring, mackerel, or squid and lower the pots over silt and mud ocean bottoms. After a couple days, fishermen haul the pots back on board, empty them, and sort the catch  then start all over again.</li>\n<li>Crab pots can unintentionally catch female crabs (which may not be harvested), males under the commercial size, and non-targeted crab species as well as a small number of other species including octopus, Pacific cod, Pacific halibut, other flatfish, sponges, coral, and sea stars.</li>\n<li>Management requires fishermen to install escape panels and rings on their pots to prevent ghost fishing (when lost pots continue to capture and kill species) and to reduce bycatch.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Alaska snow crab is a low-fat source of protein.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Alaska snow crabs have a hard rounded shell, four pairs of walking legs, and one pair of claws.</li>\n<li>On top they are brownish in color and underneath they are lighter. Their eyes are green or greenish blue.</li>\n<li>Males and females can be distinguished by the shape of their abdominal flaps. On males this flap is triangular, and on females it is broadly rounded.</li>\n</ul>\n",
