    "Location": "<ul>\n<li>Ocean quahogs are found in the western Atlantic as far south as Cape Hatteras, North Carolina.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "Above target population level.",
    "Population Status": "<ul>\n<li>According to the 2020&nbsp;stock assessment the ocean quahog stock is not overfished and is not subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Ocean%20quahog%20-%20Atlantic%20Coast&amp;stockid=10788\">Stock SMART</a>.</li>\n<li>Ocean quahogs are relatively unproductive and can only support low levels of fishing.</li>\n<li>Population levels are declining despite relatively low fishing rates, but remain above target levels.</li>\n</ul>\n",
    "Scientific Name": "Arctica islandica",
    "Species Aliases": "<a href=\"/species-aliases/clam\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Clam</a>, <a href=\"/species-aliases/quahog\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Quahog</a>, <a href=\"/species-aliases/black-clam\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Black clam</a>, <a href=\"/species-aliases/mahogany-quahog\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Mahogany quahog</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/hard_clam_ocean_quahog.png",
    "alt": "Hard clam/ocean quahog",
    "title": "Hard clam/ocean quahog"
    },
    "Species Name": "Ocean Quahog",
    "Animal Health": null,
    "Availability": "<p>Year-round. Sold processed (minced, chopped, or cut into strips) or in chowders, bisques, and sauces. Smaller ocean quahogs from Maine waters are marketed as mahogany clams and are sold on the half-shell market or for steaming.</p>\n",
    "Biology": "<ul>\n<li>Ocean quahogs spawn by releasing eggs and sperm into the water column where the eggs are fertilized.</li>\n<li>Larvae drift with the currents for at least 30 days until they develop into juveniles and settle to the bottom.</li>\n<li>Ocean quahogs spawn once a year, either in the summer or fall. The spawning season is sometimes extended over a number of months as quahogs release eggs and sperm a little at a time.</li>\n<li>Ocean quahogs are among the longest-lived marine organisms in the world. Off the U.S. East Coast, where the fishery takes place, ocean quahogs can live for at least 200 years.</li>\n<li>They grow very slowly and do not start to reproduce until around age 6, and do not reach a commercially harvestable size until about age 20.</li>\n<li>Ocean quahogs are filter feeders. They bury themselves in the ocean floor and pump oxygen-filled water and food particles in through their siphons, which extend above the surface of the ocean floor.</li>\n<li>They feed on microscopic algae.</li>\n<li>Many animals prey on juvenile ocean quahogs, including invertebrates such as rock crabs, sea stars, and other crustaceans, and fish such as longhorn sculpin, ocean pout, haddock, and cod.</li>\n<li>Once ocean quahogs have reached a certain size, they have a very low predation rate. They burrow in the sandy ocean floor and their thick shells close completely, providing substantial protection from potential predators.</li>\n</ul>\n",
    "Bycatch": "Fishing gear used to harvest quahogs is designed to minimize bycatch.",
    "Calories": "74",
    "Carbohydrate": "2.57 g",
    "Cholesterol": "34 mg",
    "Color": "<p><span>Meat is pinkish.</span></p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
    "Farming Methods": null,
    "Farming Methods_": null,
    "Fat, Total": "0.97 g",
    "Feeds_": null,
    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>In 2019, the ocean quahog fishery produced more than 11.3 million pounds of meats, valued at more than $9.1 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:12965743518037::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>Commercial fishermen harvest ocean quahogs with hydraulic clam dredges, which use jets of water to dislodge ocean quahogs from the sandy ocean floor.</li>\n<li>A smaller dry dredge (without hydraulic jets) is used in Maine waters.</li>\n<li>Ocean quahogs burrow into sandy bottoms on the continental shelf, an environment that is thought to recover quickly after a hydraulic clam dredge passes over it.</li>\n<li>The bars on commercial clam dredges are spaced several inches apart so they do not collect anything but the targeted quahogs.</li>\n<li>The quahog fishery is managed under an individual transferable quota program that slows the pace of the fishery and increases its efficiency, significantly reducing bycatch.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Quahogs provide low-fat, high-quality protein and are an excellent source of selenium, iron, and vitamin B12.&nbsp;&nbsp;</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Ocean quahogs are bivalve mollusksthey have two hinged shells that enclose their body.</li>\n<li>Shells are thick and oval-shaped.</li>\n<li>Outside is a dull gray with growth rings that can be used to determine its age.</li>\n<li>Interior is white with a purple border.</li>\n<li>Most quahogs in U.S. waters are 2.8 to 4.3 inches in shell length.</li>\n</ul>\n",
    "Production": null,
    "Protein": "12.77 g",
    "Quote": "U.S. wild-caught ocean quahog is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#555555",
    "Research": "<ul>\n<li>Scientists from NOAAs <a href=\"http://www.nefsc.noaa.gov/\">Northeast Fisheries Science Center</a>, along with industry and academic partners, survey clam populations every 3 years and usually follow these surveys with a full stock assessment.</li>\n<li>A few species of algae cause Harmful Algal Blooms (HABs), which can have negative impacts on fish, marine animals, birds, and humans. Species of Alexandrium cause HABs in New England that are commonly referred to as red tides. Alexandrium toxin becomes concentrated in shellfish such as quahogs. The shellfish themselves are not affected by the toxin, but eating shellfish tainted with this toxin can lead to paralytic shellfish poisoning (PSP), a potentially fatal human illness. There are large-scale monitoring programs all along the coast to assess toxin levels in shellfish; if toxins are detected these areas are immediately closed to harvesting. Contamination from PSP has had a huge impact on fisheries for ocean quahogs. Georges Bank, where ocean quahogs are abundant, has been closed to fishing since 1990 due to the presence of PSP toxins in quahog tissue. In January 2013, a portion of Georges Bank was reopened to the harvest of ocean quahogs and surfclams by vessels using a new PSP testing protocol that was developed through the collaborative efforts of industry, scientists, and managers. This will allow safe harvest of these abundant Georges Bank resources.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.094 g",
    "Selenium": "24.3 mcg",
    "Serving Weight": "100 g",
    "Servings": "1",
    "Sodium": "56 mg",
    "Source": "<p>U.S. wild-caught from Maine to Virginia.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>A somewhat stronger taste than other clams.</p>\n",
    "Texture": "<p>Firmer than other clams.</p>\n",
