    "Location": "<ul>\n<li>Gray triggerfish are found in the western Atlantic Ocean from Nova Scotia south to Argentina, including Bermuda and the Gulf of Mexico.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Southeast",
    "Population": "In the Gulf of Mexico, below target population level but fishing rate promotes population growth. A rebuilding plan is in place. In the South Atlantic, the population level is unknown.",
    "Population Status": "<ul>\n<li>\n<p>There are two stocks of gray triggerfish: the Gulf of Mexico stock and the South Atlantic stock. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Gulf of Mexico stock is not overfished but still rebuilding to target levels (2015 stock assessment), and not subject to overfishing based on 2020&nbsp;catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10062\">Stock SMART</a>. The stock is rebuilding and a rebuilding plan is in place.</p>\n</li>\n<li>\n<p>Scientists conducted an exploratory assessment of South Atlantic gray triggerfish in 2001 and determined that the stock was not subject to overfishing, but overfished status is unknown.&nbsp; A formal stock assessment has not been conducted.</p>\n</li>\n</ul>\n</li>\n<li>Scientists from <a href=\"https://www.sefsc.noaa.gov/\">NOAAs Southeast Fisheries Science Center</a> monitor the abundance of these populations. Scientists, managers, and stakeholders assess the status of these stocks through the <a href=\"http://sedarweb.org/\">Southeast Data, Assessment, and Review (SEDAR)</a> process.</li>\n</ul>\n",
    "Scientific Name": "Balistes capriscus",
    "Species Aliases": "<a href=\"/species-aliases/grey-triggerfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Grey triggerfish</a>, <a href=\"/species-aliases/leatherjacket\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Leatherjacket</a>, <a href=\"/species-aliases/leatherneck\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Leatherneck</a>, <a href=\"/species-aliases/taly\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Taly</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/gray_triggerfish.png",
    "alt": "Gray triggerfish",
    "title": "Gray triggerfish"
    },
    "Species Name": "Gray Triggerfish",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Triggerfish can grow to be up to 13 pounds, 28 inches fork length (length from the tip of the snout to the center of the fork of the tail), and 16 years old.</li>\n<li>Males are larger than females.</li>\n<li>Triggerfish become sexually mature at approximately 2 years old.</li>\n<li>Spawning occurs from April to August. Male gray triggerfish establish territories, build nests in the sand, and entice females into the nest to spawn. The male and female tightly circle one another in the nest, rapidly changing color, and the female deposits eggs in the nest shortly afterward.</li>\n<li>Females deposit an average of 772,415 eggs. After fertilization females aerate the eggs by fanning and blowing on them.</li>\n<li>One male can defend up to three active nests on one reef. An active nest is defined as one female on the nest guarding and aerating the eggs. Females also guard the nest from predators, such as wrasses, groupers, and red snappers.</li>\n<li>Eggs hatch within 24 to 48 hours and the larvae travel to the surface, where they often live among mats of planktonic (open ocean) <em>Sargassum, </em>a floating brown algae.</li>\n<li>Larvae and juveniles spend 4 to 7 months in the planktonic environment before they move to the ocean bottom.</li>\n<li>Adults primarily eat benthic invertebrates including crabs, sea urchins, shrimp, sand dollars, lobsters, and mollusks. They have a small mouth with a strong jaw and specialized teeth used to crush and chisel holes in their hard-shelled prey. Gray triggerfish sometimes direct a stream of water over sandy ocean bottom habitat to expose sand dollars to eat. Juvenile gray triggerfish feed on hydroids, barnacles, and polychaetes.</li>\n<li>Amberjack, grouper, and sharks prey upon adult gray triggerfish; tuna, dolphinfish, marlin, sailfish, and sharks prey upon juvenile gray triggerfish.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch of sea turtles and smalltooth sawfish that may interact with fishing gear used to harvest gray triggerfish.",
