    "Fishing Rate": "At recommended levels in the South Atlantic and the Gulf of Mexico.",
    "Harvest": "<ul>\n<li>In 2020, commercial landings of gray triggerfish totaled 273,000 pounds and were valued at approximately $760,000, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:13005227800943::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>Hand lines and electric or hydraulic reels are primarily used to catch gray triggerfish commercially.</li>\n<li>Recreational harvest is primarily by hook-and-line.</li>\n<li>In 2020, recreational fishermen harvested 2.4&nbsp;million pounds of gray triggerfish in the Atlantic and the Gulf of Mexico, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:32939222333165::NO:::\">recreational fishing landings database</a>.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": null,
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Adults are primarily olive-gray, have blue spots and lines on the upper body and dorsal (back) fin, and the upper rims of their eyes are blue.</li>\n<li>Adults have the ability to change color, particularly during the spawning season (April to August). Males turn a dark charcoal gray, while nesting females display a contrasting white and black color pattern.</li>\n<li>Juveniles are yellowish with small violet dots and can have large, irregular dark patches on the body and fins.</li>\n<li>Juveniles also have saddle markings and light spots on their dorsal (back) and anal fins.</li>\n<li>Triggerfish get their name from spines on the dorsal (back) fins that can be used as a predator defense and for anchoring. The first spine is large, and when erect it remains so until the fish deflexes the smaller second spine, triggering the first. When a triggerfish is threatened it will dive into a tight crevice and anchor itself into place by locking its erect spine.</li>\n</ul>\n",
    "Production": null,
    "Protein": "21 g",
    "Quote": "U.S. wild-caught gray triggerfish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#91a4ad",
    "Research": "<ul>\n<li>A <a href=\"http://sedarweb.org/s31rd33-site-fidelity-and-movement-reef-fishes-tagged-unreported-artificial-reef-sites-nw-florida\">study</a> by scientists indicates that gray triggerfish have high site fidelity, tending to stay in a certain area, at artificial reef sites in the Gulf of Mexico.</li>\n</ul>\n",
