    "Sugars, Total": "0 g",
    "Taste": "<p>The meat of gray triggerfish is uniquely sweet. It tastes more like crab than fish. The meat is of excellent quality and is good fresh, smoked, and dried/salted.</p>\n",
    "Texture": "<p>Firm.</p>\n",
