    "Species Name": "Bluefish",
    "Animal Health": null,
    "Availability": "<p>Fresh year-round, but varies by area. Not available frozen. Buy in season, and plan to cook within a day of purchase.</p>\n",
    "Biology": "<ul>\n<li>Bluefish live up to 12&nbsp;years.</li>\n<li>They grow fast, up to 31 pounds and 39 inches.</li>\n<li>They are able to reproduce at age 2, when theyre 15 to 20 inches in length.</li>\n<li>Depending on their size, females can have between 400,000 and 2 million eggs.</li>\n<li>Bluefish spawn multiple times in spring and summer.</li>\n<li>They exhibit feeding behavior called the bluefish blitz, where large schools of big fish attack bait fish near the surface, churning the water like a washing machine. They feed voraciously on their prey, eating almost anything they can catch and swallow.</li>\n<li>Bluefish have razor-sharp teeth and shearing jaws that allow them to ingest large parts, increasing the maximum size of the prey they can eat.</li>\n<li>They eat squid and fish, particularly menhaden and smaller fish such as silversides.</li>\n<li>Sharks, tunas, and billfishes are typically the only fish predators large and fast enough to prey on adult bluefish.</li>\n<li>Bluefish make up a major part of the diet of <a href=\"https://www.fishwatch.gov/profiles/atlantic-shortfin-mako-shark\">shortfin mako sharks</a>&nbsp;and are also very important in the diets of <a href=\"https://www.fishwatch.gov/profiles/north-atlantic-swordfish\">swordfish</a>.</li>\n<li>Oceanic birds prey on juvenile bluefish.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
