    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>In 2020, commercial landings of bluefish totaled more than 2.4 million pounds and were valued at approximately $2 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:3577333744020::NO:::\">commercial fishing landings database</a>.</li>\n<li>In 2020, recreational anglers landed more than 13.6 million pounds of bluefish, according to the NOAA Fisheries<a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:3577333744020::NO:::\"> recreational fishing landings database</a>.</li>\n<li>Florida, North Carolina, and New Jersey account for the largest percentage of the recreational bluefish harvest.</li>\n<li>Peak recreational harvest occurs from May through October with over 70 percent of the catch in July and August.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Recreational fishermen use hook-and-line gear that has minimal impacts on habitat.</li>\n<li>Commercial fishermen use a variety of gears including trawls, gillnets, haul seines, and pound nets, and the impacts vary by gear type.</li>\n<li>Gillnets can occasionally entangle marine mammals. However, the bluefish fishery uses nets with small mesh and sinking gillnets to reduce bycatch.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Bluefish are a good source of selenium, niacin, vitamin B12, magnesium, and potassium. As apex predators, bluefish can accumulate comparatively high levels of mercury and PCB contaminants. Limited consumption has been recommended in some states.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Bluefish are blue-green on the back and silvery on the sides and belly.</li>\n<li>They have a prominent jaw, with sharp, compressed teeth.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.04 g",
    "Quote": "U.S. wild-caught bluefish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#89aec3",
    "Research": "<ul>\n<li>Several key scientific gaps need to be addressed to better inform management. For example, more research is needed on the relationship between the age and length of bluefish, how much bluefish is caught and discarded in the commercial fisheries for bluefish and other species, the resulting impacts, and general population trends. There is also uncertainty in the methods used by scientists to monitor bluefish abundance and in estimating recreational catch along the Atlantic coast. In 2012, managers established a coast-wide sampling program designed to improve the quantity and quality of information used in future bluefish stock assessments.</li>\n<li>The coast-wide&nbsp;sampling program resulted in a significant increase in the amount of age-length keys and catch-at-age data that was used in the 2015 benchmark assessment.</li>\n<li>The bluefish management plan allows managers to set aside a small percentage of the annual catch limit for research. Proceeds from the sale of this set-aside catch are used to fund research on the bluefish resource and fishery.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.915 g",
    "Selenium": "36.5 mcg",
    "Serving Weight": "100 g",
    "Servings": "1",
    "Sodium": "60 mcg",
    "Source": "<p>U.S. wild-caught from Massachusetts to Florida.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Rich, full flavor. The larger the fish, the more pronounced the taste. A strong-flavored, dark strip of meat on the fillet may be removed before cooking.</p>\n",
    "Texture": "<p>Coarse, moist meat with edible skin.</p>\n",
