    "Species Name": "Black Sea Bass",
    "Animal Health": null,
    "Availability": "<p>Generally year-round, but varies by state.</p>\n",
    "Biology": "<ul>\n<li>Black sea bass grow slowly, up to 2 feet and 9 pounds.</li>\n<li>They are able to reproduce when they reach 1 to 3 years old.</li>\n<li>They are protogynous hermaphroditesmost black sea bass start out as females, and as they mature and grow they become males. Researchers arent sure why this happens, but one hypothesis suggests the relative scarcity of males in a spawning group may be the stimulus for a female to switch sex.</li>\n<li>Black sea bass spawn in coastal areas from January through July.</li>\n<li>Males gather a group of females to mate with and aggressively defend their territory.</li>\n<li>Depending on their size, females can produce between 30,000 and 500,000 eggs in a spawning season.</li>\n<li>Females can live up to 8 years; males live up to 12.</li>\n<li>Black sea bass eat whatever prey is available, but they especially like crabs, shrimp, worms, small fish, and clams.</li>\n<li>Little skate, spiny dogfish, monkfish, spotted hake, and summer flounder all feed on black sea bass.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "97",
    "Carbohydrate": "0 g",
    "Cholesterol": "41 mg",
    "Color": "<p>Uncooked meat should be sparkling white and translucent. The meat is snow white when cooked.</p>\n",
