    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:Gear types, habitat impacts, and bycatch:\n<ul>\n<li>In 2020, commercial landings of black sea bass totaled 4.3&nbsp;million pounds, and were valued at $10.4&nbsp;million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:1541783939502::NO:::\">commercial fishing landings database</a>.</li>\n<li>In the Mid-Atlantic, commercial fishermen mainly use hook-and-line gear, pots, and otter trawls to catch black sea bass. Fisheries for black sea bass change seasonally with the species migrations.\n<ul>\n<li>Of the gear utilized within the snapper grouper fishery, only the black sea bass pot is considered to pose an entanglement risk to marine mammals.</li>\n<li>When the fish are inshore, commercial fishermen catch them primarily with fish pots (both baited and unbaited) and handlines.</li>\n<li>Once the fish move offshore in the winter, theyre caught in the trawl fishery that primarily targets summer flounder, scup, and longfin squid.</li>\n</ul>\n</li>\n<li>In the South Atlantic, fishermen use pots and sometimes handlines.\n<ul>\n<li>Fish bycatch in the pot fishery is minor because the gear is often not baited (black sea bass are drawn to the structure and enter the pots without bait), and the pot mesh size is regulated to select fish at or above the minimum size limit.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>Recreational fishery&nbsp;\n<ul>\n<li>Recreational fishermen use handlines to catch black sea bass.</li>\n<li>In 2020, recreational anglers landed more than 9.5&nbsp;million pounds of black sea bass, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:1541783939502::NO:::\">recreational fishing landings database</a>.&nbsp;&nbsp;</li>\n<li>In the Mid-Atlantic:\n<ul>\n<li>There are size and bag limits, and seasonal measures, established for federal waters as well as by each state, to ensure that the recreational harvest limit is not exceeded.</li>\n<li>The recreational fishery is evaluated after the season, and management measures (bag, size, and/or season) are adjusted as needed in the following year.</li>\n</ul>\n</li>\n<li>In the South Atlantic:\n<ul>\n<li>There are size limits, fishing season, and daily and yearly catch limits, established for federal waters as well as by each state.</li>\n<li>There is a limit on annual recreational harvest, and recreational harvest is prohibited when the limit is met. If annual catch exceeds the limit, the overage is taken off the following years limit.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Black sea bass is a good low-fat source of protein and magnesium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Black sea bass are usually black, but smaller ones are more of a dusky brown.</li>\n<li>The belly is slightly paler than the sides.</li>\n<li>Fins are dark with dusky spots.</li>\n<li>The dorsal fin is marked with a series of white spots and bands.</li>\n<li>During spawning, dominant males turn bright blue and have a blue hump on their heads.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.43 g",
    "Quote": "U.S. wild-caught black sea bass is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#4c4e4d",
    "Research": "<ul>\n<li>NOAA Fisheries and state fishery agencies have collaborated with commercial and recreational fishermen to examine the population size, exploitation rate, and seasonal movements of northern Atlantic coast black sea bass by participating in the <a href=\"https://www.nefsc.noaa.gov/read/popdy/TagReporting/recapture_form.pl?species=bsb\">Cooperative Black Sea Bass Tagging Project</a>. Reporting tagged fish to the project helps contribute to our understanding of this species.</li>\n<li>Using data from the Southeast Reef Fish Survey, NMFS researchers and partners recently identified the spawning season and several spawning locations for black sea bass along the southeastern United States. This research is described in the open access journal <a href=\"http://journals.plos.org/plosone/article?id=10.1371/journal.pone.0172968\">PLoS ONE</a>, and was also adapted for schools in <a href=\"http://www.sciencejournalforkids.org/science-articles/how-can-we-protect-fish-better\">The Science Journal for Kids</a>.</li>\n<li>Demand for this tasty fish in seafood and sushi markets has grown much faster than the supply from wild fisheries. In an attempt to meet this high demand, NOAA researchers evaluated the potential of black sea bass for commercial aquaculture and found <a href=\"https://www.nefsc.noaa.gov/press_release/2008/SciSpot/ss0803/\">promising results</a>black sea bass can be grown from larvae to adults in recirculating aquaculture systems, eating a diet of commercial pellet feeds. They recommend that future aquaculture research efforts focus on understanding the factors that control growthinvestigating optimal culture temperatures, lighting conditions, reproductive physiology, and the species nutritional requirements at various life stages.</li>\n</ul>\n",
