    "Location": "<ul>\n<li>Petrale sole are found from Alaska to Coronado Island, Baja California.</li>\n<li>They are rare north and west of southeast Alaska and in the interior waters of British Columbia.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Alaska",
    "Population": "Above target population levels on the Pacific Coast and in the Gulf of Alaska. ",
    "Population Status": "<ul>\n<li>There are three stocks of petrale sole: Pacific coast, one stock contained in a stock complex in the Gulf of Alaska, and one stock contained in a stock complex in the Bering Sea/Aleutian Islands.&nbsp; According to the most recent stock assessments:\n<ul>\n<li>The Pacific coast stock is not overfished (2015 stock assessment) and not subject to overfishing based on 2018 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Petrale%20sole%20-%20Pacific%20Coast&amp;stockid=10011\">Stock SMART</a>.</li>\n<li>The Gulf of Alaska stock is managed as part of the Shallow Water Flatfish Complex, which is not overfished (2020 stock assessment) and not subject to overfishing based on 2020 catch data. Northern rock sole is assessed and petrale sole is a secondary species in the complex. Summary stock assessment information for Northern rock sole can be found on&nbsp;<a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Northern%20rock%20sole%20-%20Bering%20Sea%20/%20Aleutian%20Islands&amp;stockid=11050\">Stock SMART</a>.</li>\n<li>The Bering Sea/Aleutian Islands stock is managed as part of the Other Flatfish Complex and has not been assessed so the population status is unknown. This complex is not subject to overfishing based on 2020 catch data.</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Eopsetta jordani",
    "Species Aliases": "<a href=\"/species-aliases/sole\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sole</a>, <a href=\"/species-aliases/flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Flounder</a>, <a href=\"/species-aliases/california-sole\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">California sole</a>, <a href=\"/species-aliases/brill\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Brill</a>, <a href=\"/species-aliases/petral\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Petral</a>, <a href=\"/species-aliases/jordan%E2%80%99s-flounder\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Jordans flounder</a>, <a href=\"/species-aliases/round-nosed-sole\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Round-nosed sole</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/petrale_sole.png",
    "alt": "Illustration of a petrale sole",
    "title": ""
    },
    "Species Name": "Petrale Sole",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Petrale sole grow fast when theyre young, and females tend to grow faster than males.</li>\n<li>Females can reach up to 24 inches long, while males may reach up to 18 inches long.</li>\n<li>They can live up to 35 years, but recent data suggests few live longer than 17 years.</li>\n<li>Petrale sole can reproduce when they are 3 to 8 years old, or when theyre about 1 foot long. Females can produce 400,000 to 1.5 million eggs.</li>\n<li>They spawn from November to April in British Columbia, Washington, and Oregon. Spawning begins slightly earlier in California.</li>\n<li>Petrale sole is a broadcast spawner. Males and females release their sperm and eggs into the water column and eggs are fertilized externally.</li>\n<li>Eggs hatch in 6 to 13.5 days, depending on water temperatures.</li>\n<li>Petrale sole larvae spend their first 5 to 6 months up in the water column before they transform to their adult form and settle to the bottom.</li>\n<li>Petrale sole larvae eat plankton. Small juveniles eat mysids, sculpins, and other juvenile flatfish. Large juveniles and adults eat shrimp and other crustaceans, as well as krill, pelagic fishes, brittle stars, and juvenile petrale sole.</li>\n<li>Plankton-eating invertebrates and pelagic fishes eat petrale sole eggs. Adult petrale sole and other large flatfishes prey on juvenile petrale sole.</li>\n<li>Sharks, bottom-feeding marine mammals, larger flatfishes, and pelagic fishes feed on adults.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
