    "Harvest": "<ul>\n<li>In 2019, commercial landings of petrale sole totaled more than 5.7 million pounds and were valued at $6.9 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:3523155919183::NO:::\">commercial fishing landings database</a>.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Bottom trawls are used to catch petrale sole.</li>\n<li>Trawls that are used to harvest petrale sole can contact the ocean floor and impact habitats, depending on the characteristics of the ocean bottom and the size of the gear.</li>\n<li>Bottom trawls cause minimal damage to habitat when targeting petrale sole over soft, sandy, or muddy ocean bottoms on the West Coast and Alaska.</li>\n<li>On the West Coast, NOAA Fisheries and the North Pacific Fishery Management Council have implemented large <a href=\"https://www.westcoast.fisheries.noaa.gov/fisheries/management/groundfish_closures/groundfish_closed_areas.html\">closed areas</a> to minimize bycatch and protect habitat.</li>\n<li><a href=\"https://www.westcoast.fisheries.noaa.gov/fisheries/management/vms.html\">Vessel monitoring systems</a> allow enforcement staff and fishery managers to monitor GPS locations of fishing activities to ensure vessels are complying with closed areas.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Petrale sole is an excellent source of low-fat protein and calcium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Petrale sole is a right-eyed flounder (both eyes are on its right side), with an oval to round body.</li>\n<li>Its upper side is uniform light to dark brown, and its underside is white, sometimes with pink traces.</li>\n<li>Petrale sole have large mouths, with two rows of small, arrow-shaped teeth on their upper jaw and one row of teeth on the lower jaw.</li>\n</ul>\n",
