    "Location": "<ul>\n<li>Greater amberjack are found in the Atlantic and Pacific Oceans.</li>\n<li>In the western Atlantic, they are found from Nova Scotia to Brazil, including the Gulf of Mexico and the Caribbean.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Southeast",
    "Population": "Above target population level in the South Atlantic, but significantly below target population level in the Gulf of Mexico, where a rebuilding plan is in place. The population level in the Caribbean is unknown.",
    "Population Status": "<ul>\n<li>\n<p>There are three stocks of greater amberjack: Gulf of Mexico, South Atlantic, and Caribbean. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Gulf of Mexico stock is overfished and subject to overfishing (2020&nbsp;stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10207\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The South Atlantic stock is not overfished&nbsp;and not subject to overfishing (2020 stock assessment).&nbsp;Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10208\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The population status of the U.S. Caribbean Jacks Complex, which includes greater amberjack, is unknown. The complex has not been assessed, but according to 2019&nbsp;catch data, the complex is not subject to overfishing.</p>\n</li>\n</ul>\n</li>\n<li>During the period of high landings in the 1980s and 1990s, greater amberjack became an important alternative for red drum, which experienced decreases in commercial landings. In the Gulf of Mexico, fishing rates were too high and the stock was declared overfished in 2001. The stock has been in a rebuilding plan since 2003.</li>\n</ul>\n",
    "Scientific Name": "Seriola dumerili",
    "Species Aliases": "<a href=\"/species-aliases/amberjack\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Amberjack</a>, <a href=\"/species-aliases/medregal\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Medregal</a>, <a href=\"/species-aliases/coronado\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Coronado</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/greater_amberjack.png",
    "alt": "Illustration of a Greater Amberjack.",
    "title": "Greater Amberjack"
    },
    "Species Name": "Greater Amberjack",
    "Animal Health": null,
    "Availability": "<p>Fresh (except in April when fishery is closed) and frozen year-round.</p>\n",
    "Biology": "<ul>\n<li>Greater amberjack can grow up to 6 feet long and live to be 17 years old.</li>\n<li>Adults can weigh up to 200 pounds, but are most commonly found to be up to 40 pounds.</li>\n<li>Females grow larger in size and live longer than males.</li>\n<li>Greater amberjack mature at about 3 to 4 years of age and spawn primarily from March to June on reefs and shipwrecks. A female can release between 18 and 59 million eggs during a single spawning season.</li>\n<li>Adults eat mostly crab, squid, and other fishes found on reefs.</li>\n<li>Juveniles feed on plankton, including crustacean larvae and other small invertebrates.</li>\n<li>Predators include seabirds and larger fishes, such as yellowfin tuna.</li>\n</ul>\n",
    "Bycatch": "Bycatch is low because the gears used to catch greater amberjack are selective.",
