    "Harvest": "<ul>\n<li>In 2019, commercial landings of greater amberjack totaled more than 811,000 pounds and were valued at more than $1.4 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:514195378169::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>Electric and hydraulic reels and hand lines are used to catch most of the commercial harvest of greater amberjack. Recreational harvest is by hook-and-line.</li>\n<li>Greater amberjack are large trophy fish prized by anglers.</li>\n<li>In 2019, recreational anglers landed more than 2.2 million pounds of greater amberjack, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:32939222333165::NO:::\">recreational fishing landings database</a>.&nbsp;&nbsp;</li>\n</ul>\n",
