    "Human Health": null,
    "Physical Description": "<ul>\n<li>Greater amberjack have a dark amber strip on their head, extending from their nose to their first dorsal (back) fin, which becomes more defined when the fish is excited or feeding.</li>\n<li>They have a brownish or bluish-grey back, a silvery-white belly, and an amber horizontal strip along the middle of their body.</li>\n<li>Juveniles have a yellow color and five or six dark vertical bars along the sides.</li>\n</ul>\n",
