    "Location": "<ul>\n<li>Thresher sharks are found in the Eastern Pacific Ocean from Goose Bay, British Columbia, south to Baja California. Theyre also found off Panama and Chile.</li>\n<li>They migrate seasonally between Oregon/Washington and southern California/Baja Peninsula, Mexico.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast",
    "Population": "Above target population level.",
    "Population Status": "<ul>\n<li>According to the 2018 stock assessment, Pacific common thresher shark is&nbsp;not overfished and not subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Thresher%20shark%20-%20North%20Pacific&amp;stockid=11714\">Stock SMART</a>.</li>\n</ul>\n",
    "Scientific Name": "Alopias vulpinus",
    "Species Aliases": "<a href=\"/species-aliases/thresher-shark\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Thresher shark</a>, <a href=\"/species-aliases/fox-shark\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Fox shark</a>, <a href=\"/species-aliases/sea-fox\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sea fox</a>, <a href=\"/species-aliases/swingletail\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Swingletail</a>, <a href=\"/species-aliases/whiptail-shark\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Whiptail shark</a>, <a href=\"/species-aliases/thintail-shark\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Thintail shark</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/atlantic_common_thresher_shark.png",
    "alt": "Illustration of a Pacific Common Thresher Shark",
    "title": "Pacific Common Thresher Shark"
    },
    "Species Name": "Pacific Common Thresher Shark",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Thresher sharks grow slowly, reaching lengths up to 18 feet.</li>\n<li>They live a long time, between 19 and 50 years.</li>\n<li>They mature when they reach about 5 years old and 5 feet in length.</li>\n<li>Thresher sharks mate in midsummer. Eggs are fertilized internally and develop inside the female.</li>\n<li>After a gestation period of about 9 months, females bear two to four live pups in the spring.</li>\n<li>Thresher sharks feed on small pelagic fishincluding anchovies, sardines, hake, mackerel, and squid.</li>\n<li>They are named for their long, scythe-like tail, which they use to swat and stun fish before preying on them.</li>\n<li>Top-level predators, such as killer whales and larger sharks, prey on common threshers.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch. ",
