    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>In 2020, commercial landings of thresher shark on the West Coast totaled 135,000 pounds and were valued at more than $92,000, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:16961841144095::NO:::\">commercial fishing landings database</a>.\n<ul>\n<li>Most&nbsp;thresher shark is landed&nbsp;in&nbsp;California.</li>\n</ul>\n</li>\n<li>Gear types and bycatch:\n<ul>\n<li>Drift gillnets are used to catch common thresher sharks.</li>\n<li>Drift gillnets can incidentally catch other species, such as ocean sunfish and blue sharks.</li>\n<li>Protected species, such as sperm whales and sea turtles, may be caught as bycatch in drift gillnet fisheries.</li>\n<li>Managers limit where and when drift gillnet fishermen can fish to help prevent bycatch. Logbooks and observer programs help monitor bycatch.</li>\n<li>The California thresher shark gillnet fishery is a <a href=\"https://www.fisheries.noaa.gov/national/marine-mammal-protection/marine-mammal-protection-act-list-fisheries\">Category I fishery</a>, meaning it has frequent incidental mortality or serious injury of marine mammals.\n<ul>\n<li>NOAA Fisheries works with the federally appointed <a href=\"https://www.fisheries.noaa.gov/national/marine-mammal-protection/marine-mammal-take-reduction-plans-and-teams\">Pacific Offshore Cetacean Take Reduction Team</a> to develop measures that reduce the impacts of this fishery on certain marine mammals.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>On the West Coast, recreational catch varies but has averaged roughly 20 metric tons in recent years. Recreational fishermen have daily bag limits for thresher sharks.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>High in protein, low in fat, and a good source of niacin, vitamin B6, phosphorous, and selenium. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Thresher sharks are brown, gray, blue-gray, or blackish on the back and underside of their snout.</li>\n<li>They are lighter on the sides, and fully white below.</li>\n<li>Fins are blackish, and some have white dots on the tips.</li>\n<li>Their tail fin is sickle-shaped, and the upper part is very long, about half the length of the body.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.98 g",
    "Quote": "U.S. wild-caught Pacific common thresher shark is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#4e6172",
    "Research": "<ul>\n<li><a href=\"https://swfsc.noaa.gov/textblock.aspx?Division=FRD&amp;ParentMenuId=87&amp;id=961\">NOAAs Southwest Fisheries Science Center</a> and <a href=\"https://www.fisheries.noaa.gov/region/pacific-islands\">Pacific Islands Fisheries Science Center</a> study Pacific sharks to learn more about their biology, distribution, movements, stock structure, and status, as well as their potential vulnerability to fishing pressure.\n<ul>\n<li>This information is shared with international, national, and regional fisheries conservation and management bodies charged with the conservation and sustainable management of pelagic sharks and other highly migratory species.</li>\n</ul>\n</li>\n<li>A <a href=\"https://swfsc.noaa.gov/news.aspx?id=18261\">study analyzing the stomach contents</a> of shortfin mako, blue, and common thresher sharks in the California Current provided new information that should help researchers characterize differences among the species as well as their roles in the ecosystem.</li>\n<li>To better understand the impact of recreational fishing on thresher sharks, NOAA has worked with external partners and anglers to study fishing practices to determine how many thresher sharks live or die after release, and to find ways to decrease the number of thresher sharks caught by their tails.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.925 g",
    "Selenium": "36.5 mcg",
    "Serving Weight": "100 g (raw)",
    "Servings": "1",
    "Sodium": "79 mg",
    "Source": "<p>U.S. wild-caught from California and the Pacific Islands.&nbsp;</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Mild flavor.</p>\n",
    "Texture": "<p>Firm, dense, and meat-like.</p>\n",
