    "Species Name": "Pacific Cod",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Pacific cod live for 20 years or less.</li>\n<li>They can grow up to 6 feet in length.</li>\n<li>Females are able to reproduce when theyre 4 or 5 years old, when they are between 1.6 and 1.9 feet long.</li>\n<li>Pacific cod spawn from January to May on the continental shelf edge and upper slope in waters 330 to 820 feet deep.</li>\n<li>Females can produce more than 1 million eggs when they spawn.</li>\n<li>Pacific cod school together and move seasonally from deep outer and upper continental shelf areas (where they spawn) to shallow middle-upper continental shelf feeding grounds.</li>\n<li>They feed on clams, worms, crabs, shrimp, and juvenile fish.</li>\n</ul>\n",
    "Bycatch": "Measures restricting the type of gear fishermen may use and when and where they may fish reduce bycatch of other species in the U.S. Pacific cod fisheries.",
    "Calories": "82",
    "Carbohydrate": "0 g",
    "Cholesterol": "37 mg",
    "Color": "<p>Raw Pacific cod is opaque and creamy white. Cooked cod is white.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
