    "Fishing Rate": "At recommended levels. ",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>Pacific cod is the second largest commercial groundfish catch off Alaska and virtually all of the United States.</li>\n<li>In 2019, commercial harvest of Pacific cod totaled more than 463.9 million pounds, and was worth more than $118.7 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:14165933430294::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>Most Pacific cod comes from the Bering and Barents Seas and the Gulf of Alaska and is harvested by the United States, Canada, Russia, and Korea.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Pacific cod are typically harvested along with several different groundfish species with longlines (hook-and-line) and bottom trawl gear.</li>\n<li>Pots (or traps) and jig gear are also used to catch Pacific cod.</li>\n<li>In the Gulf of Alaska the dominant gear has been pots, in the Aleutian Islands trawl gear is predominantly used, and in the Bering Sea longline gear is used most frequently.</li>\n<li>Bottom trawl vessels cause minimal damage when targeting Pacific cod over soft ocean bottoms. Trawls can have negative impacts in areas where Pacific cod are associated with living structural habitats, such as corals and sea whips.</li>\n<li>Some areas are closed to certain gear types to protect sensitive habitat and organisms.</li>\n<li>In Alaska, measures restricting the type of gear fishermen may use and when and where they may fish reduce bycatch of other species in the Alaska Pacific cod fisheries.</li>\n<li>There are limits on the amount of Pacific halibut that can be incidentally caught in trawl and hook-and-line fisheries. Longlines are known to catch seabirds incidentally, but current <a href=\"https://www.fisheries.noaa.gov/alaska/bycatch/seabird-bycatch-alaska\">measures</a> are reducing seabird bycatch.</li>\n<li>On the West Coast, area closures, reduced trip limits, non-retention rules, gear restrictions, and variable catch limits are used to help minimize impacts to overfished rockfish and prevent bycatch.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational fishing for Pacific cod in Alaska is minor compared to commercial fishing and mainly takes place in state waters (within 3 miles of shore). Recreational fishermen follow state regulations.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Pacific cod is a good source of low-fat protein, phosphorus, niacin, and vitamin B12.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Pacific cod are also known as gray cod because of their colortheyre brown or grayish with dark spots or patterns on the sides and a paler belly.</li>\n<li>They have a long chin barbell (a whisker-like organ near the mouth, like on a catfish) and dusky fins with white edges.</li>\n</ul>\n",
    "Production": null,
    "Protein": "17.9 g",
    "Quote": "U.S. wild-caught Pacific cod is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#7d6b4c",
    "Research": "<ul>\n<li>Changes in climate may be affecting the abundance of Pacific cod. Scientists at NOAAs <a href=\"https://www.fisheries.noaa.gov/alaska/fisheries-observers/pacific-cod-research-alaska\">Alaska Fisheries Science Center</a> and Oregon State University are working together to determine how climate change could impact growth and development of young Pacific cod in the Bering Sea. They will examine how temperature differences influence the timing and size of plankton blooms in the Bering Sea, which help determine the quality of habitat for Pacific cod.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.081 g",
    "Selenium": "36.5 mcg",
