    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>In 2019, commercial landings of northern anchovy totaled more than 22.4 million pounds and were valued at more than $1.1 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:12064433848579::NO:::\">commercial fishing landings database</a>. These figures may not match other agency sources of data due to confidential information.&nbsp;</li>\n<li>Northern anchovies are generally harvested in the United States with round haul gear.</li>\n<li>Northern anchovies have been fished off the West Coast since at least 1916. The fishery was small until the Pacific sardine fishery collapsed in the 1940s and 1950s. Processors began canning anchovies instead of sardines, and fishermen started harvesting more anchovies.</li>\n<li>Consumer demand for anchovies decreased after the sardine population recovered, and the commercial fishery for northern anchovies gradually declined.</li>\n<li>Today, northern anchovies are used mainly for bait in other fisheries and sometimes processed into fish meal.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Anchovies are high in calcium, iron, niacin, phosphorus, and selenium but are also high in cholesterol.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Northern anchovies are small, compressed fish with long snouts that overhang a large mouth.</li>\n<li>They are bluish-green above and silvery below, and adults have a faint silver stripe on the side.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.35 g",
    "Quote": "U.S. wild-caught northern anchovies are a smart seafood choice because they are sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#b5c4ca",
    "Research": "<ul>\n<li>Scientists at <a href=\"https://swfsc.noaa.gov/textblock.aspx?Division=FRD&amp;ParentMenuId=185&amp;id=1118\">NOAAs Southwest Fisheries Science Center</a> have researched northern anchovies for years to inform and improve management of the fishery. They also monitor the stock to ensure it is stable.&nbsp;</li>\n</ul>\n",
