    "Location": "<ul>\n<li>Lingcod are found from Kodiak Island in the Gulf of Alaska down to Baja California, but theyre most abundant near British Columbia and Washington.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "West Coast, Alaska",
    "Population": "Above target population levels along the Northern Pacific coast. Near target levels and fishing rate promotes population growth along the Southern Pacific coast.",
    "Population Status": "<ul>\n<li>There are two stocks of lingcod: Northern Pacific coast and Southern Pacific coast. According to the most recent stock assessments:\n<ul>\n<li>The Northern Pacific stock is not overfished (2021&nbsp;stock assessment), and not subject to overfishing based on 2019 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Lingcod%20-%20Northern%20Pacific%20Coast&amp;stockid=12194\">Stock SMART</a>.</li>\n<li>The Southern Pacific stock is not overfished (2021&nbsp;stock assessment), and not subject to overfishing based on 2019 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Lingcod%20-%20Southern%20Pacific%20Coast&amp;stockid=12195\">Stock SMART</a>.</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Ophiodon elongatus",
    "Species Aliases": "<a href=\"/species-aliases/cultus-cod\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Cultus cod</a>, <a href=\"/species-aliases/blue-cod\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Blue cod</a>, <a href=\"/species-aliases/bluefish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Bluefish</a>, <a href=\"/species-aliases/green-cod\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Green cod</a>, <a href=\"/species-aliases/buffalo-cod\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Buffalo cod</a>, <a href=\"/species-aliases/greenling\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Greenling</a>, <a href=\"/species-aliases/white-cod\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">White cod</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/lingcod.png",
    "alt": "Illustration of a lingcod",
    "title": "Illustration of Lingcod"
    },
    "Species Name": "Lingcod",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Lingcod grow quickly, up to 5 feet and 80 pounds, and can live more than 20 years.</li>\n<li>Males sexually mature when they are about 2 years old and almost 20 inches long.</li>\n<li>Females are able to reproduce when they are 3 years old and 30 inches long.</li>\n<li>In late fall, male lingcod gather and become territorial over areas suitable for spawning, usually shallow, rocky habitats.</li>\n<li>Mature females are rarely seen at these spawning grounds. Scientists believe that the females briefly visit these spawning areas during winter and spring and only stay long enough to deposit their eggs in crevices and under ledges.\n<ul>\n<li>Males guard the nests for 8 to 10 weeks until the eggs hatch. The presence of a male to guard the nest from predators appears essential for successful spawning. If something happens to the male, an unguarded nest can be decimated within 48 hours by feeding rockfish, starfish, sculpins, kelp greenling, and cod.</li>\n</ul>\n</li>\n<li>Larvae feed on zooplankton (tiny floating animals), including krill and larval crustaceans such as lobster and crab. Juveniles feed on small fish.</li>\n<li>Adults are aggressive predators and feed primarily on bottom-dwelling fish (including smaller lingcod), squid, octopi, and crab.</li>\n<li>Marine mammals, sharks, and larger lingcod prey on juvenile and adult lingcod.</li>\n</ul>\n",
    "Bycatch": "Regulations prohibit fishing in certain areas to protect sensitive fish populations.",
    "Calories": "85",
    "Carbohydrate": "0 g",
    "Cholesterol": "52 mg",
    "Color": "<p>Blue-green tint when raw, but is snow white once cooked.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
