    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of lingcod totaled more than 1.7 million pounds and were valued at more than $2.5 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:7781965190859::NO:::\">commercial fishing landings database</a>.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>On the West Coast, lingcod are typically harvested with other groundfish in the trawl fishery.\n<ul>\n<li>Lingcod are also harvested incidentally in bottom longline and salmon troll fisheries.</li>\n<li>Trawls that are used to harvest lingcod can contact the ocean floor and impact habitats, depending on the makeup of the ocean bottom and the size of the gear.</li>\n<li>Trawls cause minimal damage when targeting lingcod over soft, sandy, or muddy ocean bottoms.</li>\n<li>Some areas are closed to certain gear types to protect sensitive habitat and species.</li>\n<li>Area closures, reduced trip limits, non-retention rules, gear restrictions, and variable catch limits are used to help minimize impact to overfished rockfish species and prevent bycatch.</li>\n<li>Charterboat fishermen in Washington have successfully used alternate bait (large flatfish) to reduce yelloweye rockfish bycatch when targeting lingcod.</li>\n</ul>\n</li>\n<li>In Alaska, lingcod have been harvested for centuries by the indigenous coastal populations of Southeast, Southcentral, and Western Alaska.\n<ul>\n<li>Lingcod were traditionally caught for subsistence use with hooks made of wood or bone.</li>\n<li>Today, lingcod are still fished for subsistence use but are also harvested in commercial fisheries in southeast Alaska with longlines, trolls, and jigs.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational fishing for lingcod occurs on the West Coast and Alaska.</li>\n<li>In 2020, recreational anglers landed more than 1.1 million pounds of lingcod, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:7781965190859::NO:::\">recreational fishing landings database</a>.</li>\n<li>Due to high levels of yelloweye rockfish bycatch in West Coast hook-and-line recreational fisheries, recreational harvest needs to be carefully managed.</li>\n<li>Fish must be a certain size to be retained, and there are limits on the number of fish that sport fishermen can keep each day.</li>\n<li>In Alaska recreational fishing for lingcod is closed during spawning and nesting seasons.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Lingcod is a good source of low-fat protein and is high in vitamin B12 and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Lingcod are dark gray, brown, or greenish on the back with some copper-colored mottling or spotting along the upper back.</li>\n<li>Nicknamed buckethead, the lingcod has a large head and mouth, and 18 large, sharp teeth.</li>\n<li>Lingcod have long bodies that narrow toward the tail.&nbsp;</li>\n</ul>\n",
    "Production": null,
    "Protein": "17.66 g",
    "Quote": "U.S. wild-caught lingcod is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#a87455",
    "Research": "<ul>\n<li>Alaskas Department of Fish and Game has been conducting research on lingcod to investigate movement and exploitation rates in southeast Alaska. <a href=\"http://www.adfg.alaska.gov/index.cfm?adfg=lingcod.research\" rel=\"external\">Visit the Alaska Department of Fish and Game website for more information</a>.</li>\n</ul>\n",
