    "Location": "<ul>\n<li>Striped bass live along the East Coast from the St. Lawrence River in Canada to St. Johns River in Florida, and in the Gulf of Mexico from Florida to Louisiana.</li>\n<li>They were introduced to inland lakes and reservoirs and to the West Coast, where theyre now found from Mexico to British Columbia.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "Significantly below target levels and fishing rate promotes population growth.",
    "Population Status": "<ul>\n<li>According to the <a href=\"https://repository.library.noaa.gov/view/noaa/23031\">2018&nbsp;stock assessment</a>,&nbsp;Atlantic striped bass are overfished and subject to overfishing.</li>\n<li><a href=\"http://www.asmfc.org/uploads/file/5cc9ba4eAtlStripedBassStockAssessmentOverview.pdf\" rel=\"external\">ASMFCs Stock Assessment Overview</a> provides a summary of the 2018 benchmark stock assessment for Atlantic striped bass, including an overview of management, the types of data used, and how the data were analyzed.</li>\n</ul>\n",
    "Scientific Name": "Morone saxatilis",
    "Species Aliases": "<a href=\"/species-aliases/bass\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Bass</a>, <a href=\"/species-aliases/rockfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Rockfish</a>, <a href=\"/species-aliases/striper\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Striper</a>, <a href=\"/species-aliases/linesider\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Linesider</a>",
