    "Fishing Rate": "Reduced to end overfishing. Managers are exploring alternative coast-wide measures to address overfishing. There is a moratorium in federal waters.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial landings of striped bass (from state waters) totaled more than 4.08 million pounds and were valued at more than $15 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:6491901997953::NO:::\">commercial fishing landings database</a>. These figures may not match other agency sources of data due to confidential information.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen use gear types that have minimal impact on habitatgill nets, hook-and-line, pound nets, seines, and trawls.</li>\n<li>Most striped bass are caught in recreational fisheries, using mainly hook-and-line gear with little or no impact on habitat.</li>\n<li>Gillnets can incidentally capture protected species, such as sea turtles, large whales (right, humpback, and fin whales), harbor porpoise, dolphins, and Atlantic sturgeon.</li>\n<li>Federal regulations are in place to prevent bycatch of protected species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>The recreational harvest of striped bass regularly exceeds the commercial harvest.</li>\n<li>In 2019, recreational harvest of striped bass totaled more than 23.7&nbsp;million pounds.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Striped bass is a good source of low-fat protein and selenium. <a href=\"https://www.epa.gov/fish-tech\">Consumption advisories</a>&nbsp;for striped bass vary from state to state.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Striped bass have stout bodies with seven to eight continuous horizontal stripes on each side, from their gills to their tail.</li>\n<li>They are light green, olive, steel blue, black, or brown on top, with a white or silver iridescent underside.</li>\n</ul>\n",
    "Production": null,
    "Protein": "17.73 g",
    "Quote": "U.S. wild-caught Atlantic striped bass is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#817b62",
    "Research": "<ul>\n<li>NOAA Fisheries supports scientific research on striped bass to inform management decisions by state management agencies and the Atlantic States Marine Fisheries Commission.</li>\n<li>Scientists conduct research to address concerns about the species health. Striped bass seen in the Chesapeake Bay have had lesions associated with the disease <a href=\"https://www.vims.edu/research/departments/eaah/programs/projects/myco/index.php\">mycobacteriosis</a>, a slowly progressing bacterial infection that produces various external and internal symptoms. Researchers are exploring how some striped bass become infected while others do not.</li>\n<li>Striped bass are important predators in coastal and marine ecosystems. Scientists are developing a multispecies model that incorporates predatorprey and competitor interactions among striped bass, Atlantic menhaden, bluefish, and weakfish to help determine species abundance trends and understand the impacts of each fishery on the ecosystem.</li>\n</ul>\n",
