    "Species Name": "Atlantic Surfclam",
    "Animal Health": null,
    "Availability": "<p>Year-round. Sold processed, rather than live, in fresh, frozen, and canned products such as breaded clam strips, minced clams, stuffed clams, chowders, and broth.</p>\n",
    "Biology": "<ul>\n<li>Surflclams can live up to 35 years.</li>\n<li>On average, surfclams living in open water live longer than those living inshore.</li>\n<li>Surfclams grow fast, reaching a harvestable size of about 5 inches in 5 to 7 years.</li>\n<li>Growth rates depend on water temperaturesouthern surfclam populations in warmer water grow more slowly than the more northern populations.</li>\n<li>Some are able to reproduce by age 1, but most spawn by the end of their second year.</li>\n<li>Surfclams spawn from late spring through early fall.</li>\n<li>They shed their eggs and sperm directly into the water column.</li>\n<li>Larvae spend about 3 weeks in the water column as plankton before settling to the bottom to live.</li>\n<li>Surfclams are planktivorous filter feeders, straining tiny plants out of the water to eat.</li>\n<li>Larval surfclams eat algal cells.</li>\n<li>Adults primarily feed on diatoms, green algae, and naked flagellates.</li>\n<li>Snails, crabs, shrimp, and fish, including haddock and cod, feed on surfclams.</li>\n</ul>\n",
    "Bycatch": "Fishing gear used to harvest surfclams is designed to minimize bycatch.",
    "Calories": "74",
    "Carbohydrate": "2.57 g",
    "Cholesterol": "34 mg",
    "Color": "<p>The raw meat is whitish-orange. Cooked meat ranges from ivory to golden yellow, with some dark areas.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
    "Farming Methods": null,
    "Farming Methods_": null,
    "Fat, Total": "0.97 g",
    "Feeds_": null,
    "Feeds": null,
    "Fiber, Total Dietary": "0",
    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>In 2019, commercial landings of surfclam totaled more than 34.3 million pounds and were valued at $29.3 million, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:13731897478863::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n<li>Surfclams are the most important commercial clam species harvested in the United States.</li>\n<li>Commercial fishermen harvest surfclams with hydraulic clam dredgesessentially large, heavy sleds pulled along the sea floor. High-pressure jets blast water into the sediment, which temporarily liquefies it and allows a steel blade to pass through the first few inches of substrate and scoop the clams onto the dredge, where they are captured in a cage made of steel bars.</li>\n<li>Atlantic surfclams burrow into sandy bottoms on the continental shelf, an environment that is thought to recover quickly after a hydraulic clam dredge passes over it.</li>\n<li>The bars on commercial clam dredges are spaced several inches apart so they do not collect anything but the targeted surfclams.</li>\n<li>The surfclam fishery is managed under an individual transferable quota program that provides fishermen with more flexibility on when to fish, slows the pace of the fishery, and increases its efficiency, significantly reducing bycatch.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Surfclams provide low-fat, high-quality protein and are an excellent source of selenium and niacin.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Surfclams are the largest bivalves found in the western North Atlantic.</li>\n<li>They grow up to 8.9 inches, although clams larger than 7.9 inches are rare.</li>\n<li>Their shells are thick, triangular, and yellowish-white with rounded edges and concentric ridges.</li>\n<li>Shells do not close fully and gape slightly.</li>\n</ul>\n",
    "Production": null,
    "Protein": "12.77 g",
    "Quote": "U.S. wild-caught Atlantic surfclam is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#B7B2AC",
    "Research": "<ul>\n<li>Scientists from NOAAs <a href=\"https://www.nefsc.noaa.gov/\">Northeast Fisheries Science Center</a>, along with industry and academic partners, survey clam populations every 3 years and usually follow these surveys with a full stock assessment.</li>\n<li>A few species of algae cause Harmful Algal Blooms (HABs), which can have negative impacts on fish, marine animals, birds, and humans. Species of <em>Alexandrium</em> cause HABs in New England that are commonly referred to as red tides. <em>Alexandrium</em> toxin becomes concentrated in shellfish such as surfclams. The shellfish themselves are not affected by the toxin, but eating shellfish tainted with this toxin can lead to paralytic shellfish poisoning (PSP), a potentially fatal human illness. There are large-scale monitoring programs all along the coast to assess toxin levels in shellfish; if toxins are detected these areas are immediately closed to harvesting. Contamination from PSP has had a huge impact on fisheries for surfclams. Georges Bank, where almost a third of the surfclam stock is found, has been closed to fishing since 1990 due to the presence of PSP toxins in surfclam meats. In January 2013, a portion of Georges Bank was reopened to the harvest of surfclams and ocean quahogs by vessels using a new PSP testing protocol that was developed through the collaborative efforts of industry, scientists, and managers. This will allow safe harvest of these abundant Georges Bank resources.</li>\n</ul>\n",
