    "Source": "<p>U.S. wild-caught from New England to North Carolina. The United States is the only source of Atlantic surfclams.</p>\n",
    "Sugars, Total": "0",
    "Taste": "<p>When cooked, the white meat is mild and sweet.</p>\n",
    "Texture": "<p>Chewy.</p>\n",
