    "Location": "<ul>\n<li>In the eastern Pacific, theyre found from the Columbia River to Chile.&nbsp;</li>\n<li>Off the West Coast, theyre most common off California.&nbsp;</li>\n<li>In the Indo-Pacific, they are found from East Africa and the Red Sea to Hawaii.&nbsp;</li>\n</ul>\n",
