    "Animal Health": null,
    "Availability": "<p>Fresh from August to January.</p>\n",
    "Biology": "<ul>\n<li>Shortfin mako sharks grow slowly and can grow up to 12 feet, although average size is 6 to 7 feet.</li>\n<li>They have a long lifespan, and can live up to 30 years.&nbsp;</li>\n<li>They do not reproduce until late in life, when males are about 8 years old and females are around 20 years old.&nbsp;</li>\n<li>They have a 3-year reproductive cycle, including a 15- to 18-month gestation period.&nbsp;</li>\n<li>Eggs are fertilized internally, and develop inside the mother. Pups are born alive, and are fairly large when born.</li>\n<li>Theyre aggressive predators and feed near the top of the food chain on squid and pelagic fish (including swordfish, tuna, and other sharks).&nbsp;</li>\n<li>They have very few predators. Larger sharks and killer whales sometimes prey on younger, smaller shortfin makos.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "130",
    "Carbohydrate": "0 g",
    "Cholesterol": "51 mg",
    "Color": "<p>Fresh, raw meat is an ivory-pink or muddy, reddish color. When cooked it is ivory white.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
    "Farming Methods": null,
