    "Feeds": null,
    "Fiber, Total Dietary": "0 g",
    "Fishing Rate": "At recommended level.",
    "Harvest": "<ul>\n<li>In 2020, commercial landings of Pacific shortfin mako totaled 37,000 pounds and were valued at $23,000, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:16961841144095::NO:::\">commercial fishing landings database</a>.\n<ul>\n<li>Most shortfin mako harvested come from the <a href=\"https://www.fishwatch.gov/profiles/atlantic-shortfin-mako-shark\">Atlantic and Gulf of Mexico</a>.</li>\n</ul>\n</li>\n<li>Gear types and bycatch:\n<ul>\n<li>Harvested with drift gillnets off the West Coast and occasionally caught incidentally in longline fisheries for swordfish off Hawaii.&nbsp;</li>\n<li>Drift gillnets can incidentally catch other species, such as ocean sunfish and blue sharks.</li>\n<li>Protected species, such as sperm whales and sea turtles, may be caught as bycatch in drift gillnet fisheries.</li>\n<li>Managers limit where and when drift gillnet fishermen can fish to help prevent bycatch. Logbooks and observer programs help monitor bycatch.&nbsp;</li>\n<li>The California thresher shark gillnet fishery is a <a href=\"https://www.fisheries.noaa.gov/national/marine-mammal-protection/marine-mammal-protection-act-list-fisheries\">Category I fishery</a>, meaning it has frequent incidental mortality or serious injury of marine mammals. Mako sharks may be caught in this fishery.\n<ul>\n<li>NOAA Fisheries works with the federally appointed <a href=\"https://www.fisheries.noaa.gov/national/marine-mammal-protection/marine-mammal-take-reduction-plans-and-teams\">Pacific Offshore Cetacean Take Reduction Team</a> to develop measures that reduce the impacts of this fishery on certain marine mammals.</li>\n</ul>\n</li>\n<li>Fishermen must use acoustic devices that emit high-pitched noises to deter marine mammals, and net extenders to increase minimum fishing depth on drift gillnet gear, to protect marine mammals.</li>\n<li>Off Hawaii, gear restrictions and area closures help minimize impacts to sharks, marlins, and protected species that can be caught in longline gear.&nbsp;</li>\n<li>Safe handling techniques help reduce impacts on sea turtles and seabirds caught in the longline fishery in Hawaii.&nbsp;</li>\n<li>Scientists and managers continue to monitor bycatch in these fisheries through logbooks and vessel observer programs. &nbsp;</li>\n</ul>\n</li>\n<li>Shortfin mako are considered one of the great game fish of the world and are a popular catch in shark tournaments.&nbsp;</li>\n<li>Recreational charter vessel operators must keep logbooks documenting their catch, and there is a limit on the number of mako sharks recreational fishermen can catch.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>High in protein and low in fat, and a good source of niacin, vitamins B6 and B12, phosphorous, and selenium. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Shortfin mako sharks have pointed snouts and long gill slits.&nbsp;</li>\n<li>They have dark blue-gray backs, light metallic blue sides, and white undersides. &nbsp;</li>\n<li>Their teeth are conical and pointy and protrude forward from the jaw, making them visible even when their mouth is closed.&nbsp;</li>\n<li>They can be easily confused with the longfin mako shark (<em>Isurus paucus</em>).</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.98 g",
    "Quote": "U.S. wild-caught Pacific shortfin mako shark is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#7b9abf",
    "Research": "<ul>\n<li>Researchers at NOAAs <a href=\"https://swfsc.noaa.gov/textblock.aspx?Division=FRD&amp;ParentMenuId=87&amp;id=961\">Southwest Fisheries Science Center</a> and <a href=\"https://www.pifsc.noaa.gov/\">Pacific Islands Fisheries Science Center</a> study Pacific sharks to learn more about their biology, distribution, movements, stock structure, and status as well as their vulnerability to fishing pressure. &nbsp;</li>\n<li>In 2012, scientists released the results of <a href=\"https://swfsc.noaa.gov/news.aspx?id=18261\">a study analyzing the stomach contents</a> of shortfin mako, blue, and common thresher sharks in the California Current. This information should help researchers characterize differences among the species as well as their roles in the ecosystem.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.925 g",
    "Selenium": "36.5 mcg",
    "Serving Weight": "100 g (raw)",
    "Servings": "1",
    "Sodium": "79 mg",
    "Source": "<p>U.S. wild-caught from California and Hawaii.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Sweet with a full-bodied, meaty taste.</p>\n",
    "Texture": "<p>Soft when raw, firm once cooked.</p>\n",
