    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>In 2020, commercial landings of Pacific ocean perch totaled approximately 137 million pounds and were valued at more than $20 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:7201933262679::NO:::\">commercial fishing landings database</a>.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Bottom trawls are primarily used to catch Pacific ocean perch, although pelagic trawls are also used.</li>\n<li>Bottom trawls can contact the ocean floor and impact habitats, depending on the type and sensitivity of the habitat and the size of the gear.</li>\n<li>Trawls cause minimal damage when targeting species over soft, sandy, or muddy ocean bottoms.</li>\n<li>For Alaska and the West Coast, NOAA Fisheries and the regional fishery management councils have implemented large closed areas to protect sensitive rocky, cold-water coral and sponge habitats from bottom trawls.</li>\n<li>In Alaska, trawl fishermen targeting Pacific ocean perch might incidentally catch Pacific cod, arrowtooth flounder, rockfish, and sablefish. Halibut, salmon, and crab may also be caught as bycatch.\n<ul>\n<li>Pacific cod and sablefish are generally retained due to their high commercial value.</li>\n<li><a href=\"https://www.npfmc.org/wp-content/PDFdocuments/bycatch/Bycatchflyer913.pdf\">Bycatch limits</a> prevent too much bycatch of other species from being caught. If a target groundfish fishery exceeds a bycatch limit, the fishery may close for the remainder of the season.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Low in saturated fat and very high in selenium, phosphorous, and vitamin B12.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Pacific ocean perch are light red with several diffuse, olive-green patches on their upper backs where the body begins to narrow towards the tail fin. They also possess a prominent, cone-shaped knob on their lower jaw.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.62 g",
    "Quote": "U.S. wild-caught Pacific ocean perch is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#bd5c61",
    "Research": "<p>Scientists at the NOAA <a href=\"https://www.fisheries.noaa.gov/region/alaska#science\">Alaska Fisheries Science Center</a> are studying the <a href=\"https://www.afsc.noaa.gov/Quarterly/jas2011/divrptsRACE6.htm\">reproductive biology</a> and <a href=\"https://www.afsc.noaa.gov/Quarterly/JAS2014/divrptsREFM6.htm\">growth rates</a> of Pacific ocean perch.</p>\n",
