    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>U.S. commercial fisheries in the western and central Pacific harvest the majority of U.S. mahimahi.</li>\n<li>In 2020, commercial landings of Pacific mahimahi harvested from the U.S. Pacific Islands and the West Coast totaled approximately 600,000 pounds and were valued at $2 million, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:11201376722973::NO:::\">commercial fishing landings database</a>. The majority of the catch comes from Hawaii.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Most mahimahi are harvested using troll and handlines. Mahimahi may also be caught incidentally in pelagic longline fisheries for tuna and swordfish.</li>\n<li>The amount of bycatch associated with the mahimahi fishery varies. U.S. pelagic longline fishermen, who target tuna and swordfish and who may incidentally catch mahimahi, are required to use specific tools and handling techniques to mitigate bycatch of turtles and marine mammals.</li>\n<li>Fishing gear used to catch mahimahi rarely contacts the seafloor so habitat impacts are minimal.</li>\n<li>Time-area closures also limit and prevent interactions between pelagic longline gear and non-target species.</li>\n<li>Onboard observers are required in some fisheries to record any interactions with sea turtles, seabirds, and marine mammals.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Pacific mahimahi are a popular recreational fish.</li>\n<li>In 2020, recreational fishermen harvested approximately 2 million pounds of mahimahi in the U.S. Pacific Islands, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:13484637794078::NO:RP::\">recreational fishing landings database</a>.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Mahimahi is low in saturated fat and is a good source of vitamin B12, phosphorus, potassium, protein, niacin, vitamin B6, and selenium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Brightly colored back is an electric greenish blue, lower body is gold or sparkling silver, and sides have a mixture of dark and lights spots.</li>\n<li>Bright pattern fades almost immediately after mahimahi is harvested.</li>\n<li>Distinguished from the pompano dolphin by the number of dorsal fin rays and a very wide, square tooth patch on the tongue.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.5 g",
    "Quote": "U.S. wild-caught mahimahi is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#83a54b",
    "Research": "<ul>\n<li><a href=\"http://www.pifsc.noaa.gov/\">NOAA Pacific Islands Fisheries Science Center&nbsp;</a></li>\n</ul>\n",
