    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>In 2019, commercial landings of widow rockfish totaled more than 20.8 million pounds and were valued at more than $5.4 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:13614091446703::NO:::\">commercial fishing landings database</a>.</li>\n<li>The majority of the catch comes from Oregon and Washington and the remainer&nbsp;comes from California and Alaska.</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Primarily harvested with midwater trawl gear, which has minimal impacts on ocean bottom habitats. To a lesser extent, harvested with bottom trawl gear.</li>\n<li>Midwater and bottom trawls may sometimes catch other species of fish, including overfished and protected species.\n<ul>\n<li>Gear restrictions, closed areas, and catch share programs limit when, where, and how much trawl fishermen can harvest to reduce bycatch of other species.</li>\n<li><a href=\"https://www.westcoast.fisheries.noaa.gov/fisheries/management/groundfish_closures/rockfish_areas.html\">Rockfish conservation areas</a> eliminate fishing in areas on the West Coast where overfished rockfish species co-occur with target stocks, like widow rockfish. These closed areas help prevent bycatch of overfished rockfish.</li>\n</ul>\n</li>\n<li>Widow rockfish are often caught incidentally in the Pacific whiting fishery\n<ul>\n<li>Managers are working to reduce incidental catch through the use of annual catch limits and catch shares.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>Recreational anglers fish for widow rockfish, but they comprise only a minor part of recreational groundfish fisheries.</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": null,
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Widow rockfish are dusky-brown with traces of light yellow and red.</li>\n<li>They have black fin membranes and a strongly slanted anal fin.</li>\n<li>They have weak or reduced (short) head spines and a mouth that is relatively small when compared to other rockfish.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.8 g",
    "Quote": "U.S. wild-caught widow rockfish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations. ",
    "Quote Background Color": "#554b3d",
    "Research": "<ul>\n<li>NOAAs <a href=\"https://www.nwfsc.noaa.gov/\">Northwest</a> and <a href=\"https://www.fisheries.noaa.gov/region/alaska\">Alaska</a> Fisheries Science Centers survey the abundance of widow rockfish off the West Coast and Alaska.</li>\n<li>Scientists do not typically assess widow rockfish as part of a single-species abundance survey. Widow rockfish are more commonly assessed along with other groundfish.</li>\n</ul>\n",
