    "Location": "<ul>\n<li>In the western Atlantic, bluefin tuna are found from Newfoundland to the Gulf of Mexico.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic, Southeast",
    "Population": "The population level is unknown for bluefin tuna in the western Atlantic.",
    "Population Status": "<ul>\n<li>According to the 2020 stock assessment western Atlantic bluefin tuna stock is not subject to overfishing, but the overfished status is unknown. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10165\">Stock SMART</a>.</li>\n<li>For 2021-2023, the stock is being managed under <a href=\"https://www.iccat.int/Documents/Recs/compendiopdf-e/2020-06-e.pdf\">ICCAT Recommendation 20-06</a> that is responsive to scientific advice.&nbsp;</li>\n<li>Regulations do not allow targeted fishing of bluefin tuna in the Gulf of Mexico, an important spawning area for the species.</li>\n</ul>\n",
    "Scientific Name": "Thunnus thynnus",
    "Species Aliases": "<a href=\"/species-aliases/tuna\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Tuna</a>, <a href=\"/species-aliases/bluefin-tuna\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Bluefin tuna</a>, <a href=\"/species-aliases/toro\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Toro</a>, <a href=\"/species-aliases/maguro\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Maguro</a>, <a href=\"/species-aliases/giant-bluefin\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Giant bluefin</a>, <a href=\"/species-aliases/northern-bluefin-tuna\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Northern bluefin tuna</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/atlantic_bluefin_tuna_0.png",
    "alt": "",
    "title": ""
    },
    "Species Name": "Western Atlantic Bluefin Tuna",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Bluefin tuna grow more slowly than other tuna.</li>\n<li>They have a long lifespan, up to 20 years or more and generally dont spawn until they are about 8 years old.&nbsp;</li>\n<li>They spawn from mid-April to June, mainly in the Gulf of Mexico.</li>\n<li>Females can produce up to 10 million eggs a year.</li>\n<li>The eggs are fertilized in the water column and hatch in about 2 days.</li>\n<li>Bluefin tuna are top predators.</li>\n<li>Juveniles eat fish, squid, and crustaceans, and adults feed mainly on baitfish such as herring, bluefish, and mackerel.</li>\n<li>Sharks, marine mammals (including killer whales and pilot whales), and large fish feed on bluefin tuna. <a href=\"https://www.fishwatch.gov/profiles/bluefish\">Bluefish</a> and seabirds also prey upon juvenile bluefin tuna.</li>\n</ul>\n",
    "Bycatch": "Fishing gear used by U.S. fishermen to target schools of bluefin tuna is fairly selective, and allows for the live release of any unintentionally caught species.",
    "Calories": "144",
    "Carbohydrate": "0 g",
    "Cholesterol": "38 mg",
    "Color": "<p>Deep red when uncooked. When cooked, the meat is an off-white or ivory color.</p>\n",
    "Disease Treatment and Prevention": null,
    "Diseases in Salmon": null,
    "Displayed Seafood Profile Illustration": null,
    "Ecosystem Services": null,
    "Environmental Considerations": null,
    "Environmental Effects": null,
    "Farming Methods": null,
    "Farming Methods_": null,
    "Fat, Total": "4.9 g",
