    "Location": "<ul>\n<li>Tilefish are found along the outer continental shelf and upper continental slope of the entire U.S. East Coast and in the Gulf of Mexico.</li>\n<li>They are most abundant from Nantucket Island, Massachusetts, south to Cape May, New Jersey.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic, Southeast",
    "Population": "Above target population level in the Gulf of Mexico. Near target population levels but fishing rate promotes growth in the Mid-Atlantic and South Atlantic.",
    "Population Status": "<ul>\n<li>\n<p>There are three stocks of tilefish: Mid-Atlantic, South Atlantic, and Gulf of Mexico stocks. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Mid-Atlantic stock is not overfished and is not subject to overfishing (2021&nbsp;stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10385\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The South Atlantic stock is not overfished and not subject to overfishing (2021 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10384\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The Gulf of Mexico stock is not overfished and not subject to overfishing (2011 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Tilefish%20-%20Gulf%20of%20Mexico&amp;stockid=10383\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Lopholatilus chamaeleonticeps",
    "Species Aliases": "<a href=\"/species-aliases/golden-tilefish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Golden tilefish</a>, <a href=\"/species-aliases/golden-bass\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Golden bass</a>, <a href=\"/species-aliases/golden-snapper\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Golden snapper</a>, <a href=\"/species-aliases/great-northern-tilefish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Great northern tilefish</a>, <a href=\"/species-aliases/rainbow-tilefish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Rainbow tilefish</a>",
