    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of tilefish totaled 2 million pounds and were valued at $6.9 million,&nbsp;according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">commercial fishing landings database</a>.&nbsp;The majority of the commercial harvest was landed in New York, Florida, and New Jersey.</li>\n<li>Most of the commercial harvest of tilefish comes from the Mid-Atlantic/Southern New England stock, the largest of the three U.S. tilefish stocks.</li>\n</ul>\n</li>\n<li>Gear types:\n<ul>\n<li>Bottom longline gear catches the majority of the commercial harvest.</li>\n<li>A small amount of tilefish are caught with otter trawls in the Mid-Atlantic and with&nbsp;handlines in the South Atlantic and Gulf of Mexico.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>U.S. recreational fisheries for tilefish are smaller than the commercial fisheries, but have been increasing in recent years.</li>\n<li>In 2020, recreational anglers landed 290,000 pounds of tilefish,&nbsp;according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200::::::\">recreational fishing landings database</a>.&nbsp;The majority of the recreational harvest was landed in Florida.</li>\n<li>Regional recreational management measures include:\n<ul>\n<li>Recreational fishermen can keep a limited number of tilefish per fishing trip in the Mid-Atlantic/Southern New England area.</li>\n<li>There is a limit on how many tilefish recreational fishermen can keep and a limit on the total amount that can be harvested during the year in the South Atlantic.</li>\n<li>The South Atlantic recreational fishery is closed when the annual catch limit is projected to be met.</li>\n<li>In the Gulf of Mexico, tilefish are included in the reef fish aggregate bag limit, along with several other reef fish species, and the fishery is closed when the annual catch limit is projected to be met.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Tilefish are low in sodium. They are a good source of niacin and phosphorus, and a very good source of protein, vitamin B12, and selenium. More information on <a href=\"https://www.fishwatch.gov/eating-seafood/health\">health and seafood</a>.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Tilefish, sometimes known as the clown of the sea, are colorful.</li>\n<li>They are iridescent blue-green on the back, with numerous spots of bright yellow and gold. Their bellies are white, and their heads are rosy with blue under the eyes.</li>\n<li>Their pectoral fins are sepia-colored, and the edge of their anal fins is purplish-blue.</li>\n<li>Golden tilefish are easily distinguishable from other members of the tilefish family by the large crest on their head.</li>\n</ul>\n",
    "Production": null,
    "Protein": "17.5 g",
    "Quote": "U.S. wild-caught tilefish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#8e8135",
    "Research": "<ul>\n<li>The Mid-Atlantic/Southern New England tilefish stock is monitored and assessed by scientists from NOAAs <a href=\"https://www.fisheries.noaa.gov/about/northeast-fisheries-science-center\" target=\"_blank\">Northeast Fisheries Science Center</a> through the <a href=\"https://www.fisheries.noaa.gov/new-england-mid-atlantic/population-assessments/fishery-stock-assessments-new-england-and-mid-atlantic\">stock assessment process</a>.</li>\n<li>The Gulf of Mexico and Southeast Atlantic stocks are monitored and assessed by scientists from NOAAs <a href=\"https://www.fisheries.noaa.gov/about/southeast-fisheries-science-center\" target=\"_blank\">Southeast Fisheries Science Center</a> through the <a href=\"http://sedarweb.org/\">Southeast Data, Assessment, and Review (SEDAR)</a> process.</li>\n</ul>\n",
