    "Population": "Below target level in the Gulf of Mexico but fishing rate promotes population growth. Significantly below target population levels in the South Atlantic. Rebuilding plans are in place for both stocks.",
    "Population Status": "<ul>\n<li>\n<p>There are two stocks of red snapper: The Gulf of Mexico stock and the South Atlantic stock. According to the most recent stock assessments:</p>\n<ul>\n<li>\n<p>The Gulf of Mexico stock is not overfished but still rebuilding to target levels (2018 stock assessment), and not subject to overfishing based on 2020 catch data.&nbsp; Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Red%20snapper%20-%20Gulf%20of%20Mexico&amp;stockid=10230\">Stock SMART</a>.</p>\n</li>\n<li>\n<p>The South Atlantic stock is overfished and subject to overfishing (2021 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Red%20snapper%20-%20Southern%20Atlantic%20Coast&amp;stockid=10231\">Stock SMART</a>.</p>\n</li>\n</ul>\n</li>\n</ul>\n",
