    "Fishing Rate": "At recommended level in the Gulf of Mexico. Reduced to end overfishing in the South Atlantic.",
    "Harvest": "<ul>\n<li>Commercial sector:\n<ul>\n<li>In 2020, commercial landings of red snapper totaled approximately 7.7 million pounds and were valued at $31.5 million,&nbsp;according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:14838376182170::NO:RP::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Commercial fishermen mainly use hook-and-line gear (handlines and electric reels) to harvest red snapper, and sometimes use longlines (in the Gulf of Mexico) and spears.</li>\n<li>Commercial fishermen using hook and line gear attach multiple hooks to a vertical line and weight it at the bottom.</li>\n<li>Recreational anglers primarily use hook and line gear to harvest red snapper.</li>\n<li>Fishermen are encouraged to use <a href=\"https://www.flseagrant.org/fisheries/venting/\" rel=\"external\">venting tools</a> and <a href=\"https://videos.fisheries.noaa.gov/detail/video/3619674964001/recompression-devices:-helping-anglers-fish-smarter?autoStart=true&amp;q=recompression%20devices\" rel=\"external\">recompression devices</a> when releasing fish suffering from barotrauma. Barotrauma occurs when the swim bladder of a fish expands as it is quickly brought to the surface. Venting tools help deflate the swim bladder and recompression devices help the fish return to the depth at which it was caught to recompress the air in the swim bladder, preventing serious injury to the fish.</li>\n<li>In the Gulf of Mexico:\n<ul>\n<li>Regulations prohibit fishing in certain areas of the Gulf of Mexico to protect sensitive fish populations and habitats.</li>\n<li>Measures are in place to reduce sea turtle bycatch by longline gear and include limiting times or areas where fishermen can fish, gear restrictions, and a limit on the number of vessels that can participate in the reef fish fishery. &nbsp;In addition, all commercial fishermen must follow special sea turtle release protocols and have specialized gear to improve the chances of incidentally caught sea turtles to survive.</li>\n</ul>\n</li>\n<li>In the South Atlantic:\n<ul>\n<li>Circle hooks are required when fishing for snapper and grouper species north of latitude 28 N. De-hooking devices are also required in the South Atlantic for snapper-grouper species.</li>\n<li>Measures are in place to reduce sea turtle bycatch by fishing gear and include gear restrictions and handling requirements, and a limit on the number of vessels that can participate in the snapper-grouper fishery.</li>\n</ul>\n</li>\n</ul>\n</li>\n<li>\n<p>Recreational sector:</p>\n<ul>\n<li>\n<p>Recreational anglers primarily use hook and line gear to harvest red snapper.</p>\n</li>\n<li>\n<p>In 2020, recreational anglers landed approximately 14.9 million pounds of red snapper, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:13795838889086::NO:RP::\">recreational fishing landings database</a>.&nbsp;</p>\n</li>\n<li>\n<p>In the Gulf of Mexico:</p>\n<ul>\n<li>\n<p>Red snapper must be a minimum size to be caught, and there is a limit on how many red snapper anglers can keep per day.</p>\n</li>\n<li>\n<p>Charter vessels and headboats must have a permit to fish in federal waters.</p>\n</li>\n<li>\n<p>For-hire vessels must use specialized gear and follow certain sea turtle release protocols.</p>\n</li>\n</ul>\n</li>\n<li>In the South Atlantic:\n<ul>\n<li>In 2012, 2013, and 2014 the red snapper season was limited.</li>\n<li>In 20102011 and 20152016, the red snapper fishery was closed.</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Red snapper is low in saturated fat and sodium and is a very good source of protein.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Red snapper in deeper waters tend to be redder than those caught in shallower waters.</li>\n<li>They have a long triangular face with the upper part sloping more strongly than the lower.</li>\n<li>Their jaws are equal, with the lower one sometimes slightly projecting.</li>\n<li>They have enlarged canine teeth, which is why they are called snappers.</li>\n</ul>\n",
    "Production": null,
    "Protein": "20.51 g",
    "Quote": "U.S. wild-caught red snapper is a smart seafood choice because it is sustainably managed under rebuilding plans that allow limited harvest by U.S. fishermen.",
    "Quote Background Color": "#ab4946",
    "Research": "<ul>\n<li>Scientists from <span><a href=\"https://www.sefsc.noaa.gov/\">NOAA Fisheries Southeast Fisheries Science Center</a>&nbsp;</span>monitor the abundance of red snapper stocks. Scientists, managers, and stakeholders assess the status of these stocks through the <a href=\"http://sedarweb.org/\" target=\"_blank\">Southeast Data, Assessment, and Review</a> (SEDAR) process.</li>\n<li>Using data from the Southeast Reef Fish Survey, NMFS researchers and partners recently identified the spawning season and several spawning locations for red snapper along the southeastern United States.&nbsp; This research is described in the open access journal <a href=\"http://journals.plos.org/plosone/article?id=10.1371/journal.pone.0172968\">PLoS ONE</a>, and was also adapted for schools in <a href=\"http://www.sciencejournalforkids.org/science-articles/how-can-we-protect-fish-better\">The Science Journal for Kids</a>.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.285 g",
    "Selenium": "38.2 mcg",
