    "NOAA Fisheries Region": "West Coast, Alaska",
    "Population": "There are hundreds of sockeye salmon stocks in Alaska and several Pacific stocks. Some stocks are above target population levels, while others are below.",
    "Population Status": "<ul>\n<li>Alaska:\n<ul>\n<li>There are hundreds of stocks of sockeye salmon in Alaska.</li>\n<li>Some stocks are in decline, while others are steady or increasing.</li>\n<li>None are listed under the <a href=\"https://www.fisheries.noaa.gov/topic/laws-policies#endangered-species-act\" target=\"_blank\">Endangered Species Act (ESA)</a>.</li>\n<li>The Alaska Coho Salmon Assemblage consists of coho salmon, sockeye salmon, pink salmon, and chum salmon throughout southeast Alaska. There are 3 indicator stocks of coho salmon that are used to determine the status of the assemblage; these indicator stocks are Auke Creek, Berners River, and Hugh Smith Lake.&nbsp; According to the 2017 stock assessments, these stocks are not overfished and not subject to overfishing.&nbsp; Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockname=coho\">Stock SMART</a>.</li>\n</ul>\n</li>\n<li>West Coast:\n<ul>\n<li>As of 2019, <a href=\"https://www.fisheries.noaa.gov/species/sockeye-salmon-protected\" rel=\"external\">one population of sockeye salmon is listed as endangered and one is listed as threatened</a> under the ESA.</li>\n</ul>\n</li>\n<li>Populations are affected by:\n<ul>\n<li>Changes in ocean and climatic conditions.</li>\n<li>Habitat loss from dam construction and urban development.</li>\n<li>Degraded water quality from agricultural and logging practices.</li>\n</ul>\n</li>\n<li>Population conservation efforts include:\n<ul>\n<li>Captive-rearing in hatcheries.</li>\n<li>Removal and modification of dams that obstruct salmon migration.</li>\n<li>Restoration of degraded habitat.</li>\n<li>Acquisition of key habitat.</li>\n<li>Improvements to water quality and instream flow.</li>\n</ul>\n</li>\n<li>The <a href=\"https://www.westcoast.fisheries.noaa.gov/protected_species/salmon_steelhead/recovery_planning_and_implementation/pacific_coastal_salmon_recovery_fund.html\" target=\"_blank\">Pacific Coastal Salmon Recovery Fund</a> supports the restoration of salmon species.</li>\n</ul>\n",
    "Scientific Name": "Oncorhynchus nerka",
    "Species Aliases": "<a href=\"/species-aliases/red-salmon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Red salmon</a>, <a href=\"/species-aliases/blueback-salmon\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Blueback salmon</a>, <a href=\"/species-aliases/redfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Redfish</a>, <a href=\"/species-aliases/summer-sockeye\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Summer Sockeye</a>",
