    "Species Name": "Sockeye Salmon",
    "Animal Health": null,
    "Availability": "<p>Fresh summer and early fall. Frozen, canned, and smoked year-round.</p>\n",
    "Biology": "<ul>\n<li>Most sockeye salmon are anadromousthey hatch in freshwater streams and rivers or lakes and generally rear in freshwater lakes for 1 to 3 years, after which they reach the smolt stage and migrate to the ocean to feed and grow.</li>\n<li>They typically mature and return to fresh water after 2 to 3 years at sea, but some return earlier or stay at sea longer, between 4 and 5 years.</li>\n<li>Sockeye salmon that return earlier are almost always males and are called \"jacks.\"</li>\n<li>They typically spawn in the summer or fall.</li>\n<li>Females select spawning sites, dig nests (redds) with their tails, and deposit eggs (between 2,000 and 4,500) in the redds.</li>\n<li>Males swim over the redds and fertilize the eggs.</li>\n<li>Females cover their eggs with gravel using their tails.</li>\n<li>The eggs hatch during the winter, and the newly hatched salmon (alevins) remain in the gravel, living off the material stored in their yolk sacs until early spring.</li>\n<li>They then emerge as fry and spend 1 to 3 years in fresh water before reaching the smolt stage and migrating out to the ocean, usually in the spring.</li>\n<li>All sockeye salmon die within a few weeks after spawning.</li>\n<li>They sexually mature around the age of 5, which means their lifespan is about 5 years, although some live longer. Age 4 is more common in the Pacific Northwest.</li>\n<li>While in fresh water, juvenile sockeye salmon feed mainly on zooplankton (tiny floating animals), amphipods (small, shrimp-like crustaceans), and insects.</li>\n<li>In the ocean, sockeye salmon continue to feed on zooplankton but also eat larval and small adult fishes and occasionally squid.</li>\n<li>Fish (including other salmon) and birds feed on juvenile salmon.</li>\n<li>Sharks, lampreys, and marine mammals prey on adult salmon in the ocean. Bears, eagles, and occasionally wolves feed on sockeye salmon in fresh water.</li>\n<li>After salmon spawn and die, <a href=\"http://www.adfg.alaska.gov/index.cfm?adfg=wildlifenews.view_article&amp;articles_id=97&amp;issue_id=21\" rel=\"external\">salmon carcasses</a> are a valuable source of energy and nutrients to the river ecosystem. Carcasses have been shown to improve newly hatched salmon growth and survival by contributing nitrogen and phosphorous compounds to streams.</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
