    "Fishing Rate": "Managers set fishing rates to avoid jeopardizing the survival and recovery of sockeye salmon stocks that are below their target levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial landings of sockeye salmon totaled&nbsp;290 million pounds and were valued at approximately $466.5 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:17165494918407::NO:::\">commercial fishing landings database</a>.</li>\n<li>Almost all the sockeye salmon harvested in the United States comes from Alaska fisheries. Sockeye salmon are also harvested off the West Coast, mainly Washington, with a small amount harvested in Oregon.</li>\n<li>Sockeye salmon remain the preferred species for canning due to the rich orange-red color of their flesh.</li>\n<li>More than half of the U.S. sockeye salmon catch is sold fresh or frozen rather than canned.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Primarily harvested commercially in <a href=\"http://www.adfg.alaska.gov/static/fishing/PDFs/commercial/whatkindofboat_cf.pdf\" rel=\"external\" target=\"_blank\">net fisheries</a>, including gillnet purse seines and, more rarely, reef nets.</li>\n<li>Gillnetters catch salmon by setting curtain-like nets perpendicular to the sockeyes trajectory as they migrate along the coast toward fresh water. The mesh openings on the nets are just large enough to allow males (which are usually larger) to get stuck, or gilled, in the mesh.</li>\n<li>Purse seiners catch salmon by encircling them with a long net and drawing the bottom closed to capture the fish.</li>\n<li>Sockeye salmon are also caught in commercial troll fisheries for Chinook and coho salmon.</li>\n<li>Fishing gear used to catch sockeye salmon rarely contacts the ocean floor and has little impact on habitat.</li>\n<li>Bycatch is low and usually consists of other salmon species.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Sockeye salmon are a favorite catch of recreational fishermen.</li>\n<li>To ensure recreational fisheries are sustainable, West Coast anglers are only allowed to keep a certain amount of salmon per fishing trip.</li>\n<li>In Alaska, regulations vary by area and individual fishery.</li>\n<li>Recreational fisheries in high-use areas (Cook Inlet, Southeast Alaska, Copper River) are regulated through management plans that allocate fish between commercial and recreational fishermen.</li>\n</ul>\n</li>\n<li>Subsistence fishery:\n<ul>\n<li>Salmon is an important source of spiritual and physical sustenance for Western Indian tribes and Alaska natives, and salmon are culturally important to many other residents of these areas.</li>\n<li>Subsistence fishermen use a variety of fishing gear to harvest sockeye salmon.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Sockeye salmon is low in sodium and is a good source of omega-3 fatty acids.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Sockeye is one of the smaller species of Pacific salmon, measuring 1.5 to 2.5 feet in length and weighing 4 to 15 pounds.</li>\n<li>Kokanee (non-anadromous sockeye) rarely exceed 1.2 feet in length.</li>\n<li>Sea-going sockeye salmon have iridescent silver flanks, a white belly, and a metallic green-blue top, giving them their \"blueback\" name.</li>\n<li>Some fine black speckling may occur on the back, but the large spots typical of other Pacific salmon are absent. There are no spots on the fins, including the tail.</li>\n<li>As sockeye salmon return to their freshwater spawning grounds, their heads turn green and their bodies turn bright red, hence their other common name in Alaska is red salmon.</li>\n<li>Spawning males develop a humped back and hooked jaws filled with tiny, visible teeth.</li>\n</ul>\n",
    "Production": null,
    "Protein": "21.3 g",
    "Quote": "U.S. wild-caught sockeye salmon is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#676e81",
    "Research": "<ul>\n<li><a href=\"https://www.afsc.noaa.gov/ABL/SOE/soe_default.php\" target=\"_blank\">NOAA's Alaska Fisheries Science Center Salmon Research</a></li>\n<li><a href=\"https://www.nwfsc.noaa.gov/research/themes/species/index.cfm#salmon\" target=\"_blank\">NOAAs Northwest Fisheries Science Center Salmon Research</a></li>\n<li><a href=\"https://swfsc.noaa.gov/textblock.aspx?id=1040&amp;ParentMenuId=110\">NOAAs Southwest Fisheries Science Center Salmon Research</a></li>\n</ul>\n",
