    "NOAA Fisheries Region": "West Coast, Alaska",
    "Population": "Alaska sablefish are above target population levels. Pacific Coast sablefish are near target population levels, but fishing rates promote population growth.",
    "Population Status": "<p>There are two stocks of sablefish: Eastern Bering Sea/Aleutian Islands/Gulf of Alaska and Pacific coast. According to the most recent stock assessments:</p>\n<ul>\n<li>The Eastern Bering Sea/Aleutian Islands/Gulf of Alaska stock is not overfished (2020 stock assessment) and not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Sablefish%20-%20Eastern%20Bering%20Sea%20/%20Aleutian%20Islands%20/%20Gulf%20of%20Alaska&amp;stockid=10956\">Stock SMART</a>.</li>\n<li>The Pacific coast stock is not overfished (2019&nbsp;stock assessment) and not subject to overfishing based on 2018 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Sablefish%20-%20Pacific%20Coast&amp;stockid=10466\">Stock SMART</a>.</li>\n</ul>\n",
    "Scientific Name": "Anoplopoma fimbria",
    "Species Aliases": "<a href=\"/species-aliases/black-cod\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Black cod</a>, <a href=\"/species-aliases/butterfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Butterfish</a>, <a href=\"/species-aliases/skil\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Skil</a>, <a href=\"/species-aliases/beshow\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Beshow</a>, <a href=\"/species-aliases/coalfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Coalfish</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/sablefish.png",
    "alt": "",
    "title": ""
    },
    "Species Name": "Sablefish",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Females can grow more than 3 feet in length.</li>\n<li>Females are able to reproduce at 6  years old and more than 2 feet in length.</li>\n<li>Males are able to reproduce at age 5 and 1.9 feet in length.</li>\n<li>Sablefish spawn in deeper water along the continental slope from January to April in Alaska waters, and from January to March between California and British Columbia.</li>\n<li>Eggs develop in deep water for about 2 weeks until they hatch, then rise to the surface.</li>\n<li>Hatched larvae are moved by surface currents.</li>\n<li>Off southeast Alaska and British Columbia, juveniles appear in nearshore waters by fall.</li>\n<li>Juveniles have been found to migrate more than 2,000 miles in 6 or 7 years.</li>\n<li>Sablefish can live to be more than 90 years old.</li>\n</ul>\n",
    "Bycatch": "Regulations limit the amount of incidentally caught and discarded fish in the Alaska fishery. The catch shares program on the West Coast creates incentives to reduce bycatch.",
