    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2019, commercial landings of sablefish totaled more than 40.8 million pounds and were valued at more than $89 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:3:0\">commercial fishing landings database</a>.</li>\n<li>Sablefish are the highest valued finfish per pound in Alaska and West Coast commercial fisheries because of their rich oil content.</li>\n</ul>\n</li>\n<li>Gear, habitat impacts, and bycatch:\n<ul>\n<li>Longlines are used to harvest the majority of sablefish in Alaska.</li>\n<li>Increased catch efficiency, because of <a href=\"https://www.fisheries.noaa.gov/alaska/commercial-fishing/pacific-halibut-and-sablefish-individual-fishing-quota-ifq-program\" target=\"_blank\">individual fishing quotas</a>, reduces the number of hooks deployed and effects on bottom habitat.</li>\n<li>Individual fishing quotas reduce bycatch by allowing fishermen to operate at a slower pace and providing incentives to fish efficiently.</li>\n<li>Pot fishing has increased in the Bering Sea and Aleutian Islands, and the Gulf of Alaska, in response to killer whale interactions with fishing.</li>\n<li>West Coast fishermen harvest sablefish with trawls, longlines, and pots.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Sablefish are occasionally caught in Alaska recreational fisheries during their summer migrations onto the continental shelf.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Very high in long-chain omega-3 fatty acids, EPA, and DHA.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Sablefish look much like cod. They are often referred to as black cod, even though they are not actually part of the cod family.</li>\n</ul>\n",
    "Production": null,
    "Protein": "13.41 g",
    "Quote": "U.S. wild-caught sablefish is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#686963",
    "Research": "<ul>\n<li>NOAA Fisheries has been&nbsp;tagging and releasing sablefish in Alaska waters&nbsp;since 1972.</li>\n<li>Scientists use data from this program to study sablefish movements.</li>\n<li>Results show that sablefish are highly migratory for at least part of their lives, and that their migration can affect the amount of fish available for harvest in an area.</li>\n<li>Although the results of the longline survey are the primary data used to determine sablefish quotas, tag data provide complementary information that enhances survey data.</li>\n<li><a href=\"https://www.fisheries.noaa.gov/alaska/commercial-fishing/sablefish-research-alaska\" target=\"_blank\">NOAAs Alaska Fisheries Science Center</a> assesses the abundance of sablefish through annual&nbsp;longline surveys and trawl surveys every 1 to 3 years.</li>\n<li><a href=\"https://www.nwfsc.noaa.gov/\" target=\"_blank\">Northwest Fisheries Science Center</a> conducts bottom trawl surveys&nbsp;to assess the abundance of sablefish and other groundfish off the West Coast.</li>\n<li>Fishery data are collected by fishery observers and through required and voluntary logbook programs.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "3.201 g",
    "Selenium": "36.5 mcg",
    "Serving Weight": "100 g (raw)",
    "Servings": "1",
    "Sodium": "56 mg",
    "Source": "<p>Wild-caught off Alaska, Washington, Oregon, and California.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Sablefish have high oil content, making them exceptionally flavorful. They are often called butterfish because of their melt-in-your-mouth, oil-rich meat. Sablefish has a short shelf life and must be handled carefully.</p>\n",
    "Texture": "<p>Sablefish have soft, velvety texture. Their meat has large, white flakes.</p>\n",
