    "Fishery Management": "<ul>\n<li><a href=\"https://www.fisheries.noaa.gov/region/alaska\">NOAA Fisheries</a> and the <a href=\"https://www.npfmc.org/\">North Pacific Fishery Management Council</a> manage the Alaska pollock fishery.</li>\n<li>Managed under the Groundfish Fishery Management Plans for the <a href=\"https://www.fisheries.noaa.gov/management-plan/groundfish-gulf-alaska-management-plan\">Gulf of Alaska</a> and the <a href=\"https://www.fisheries.noaa.gov/management-plan/groundfish-bering-sea-and-aleutian-islands-management-plan\">Bering Sea/Aleutian Islands</a>:&nbsp;\n<ul>\n<li>The Alaska pollock fishery is a great example of how <a href=\"https://www.afsc.noaa.gov/refm/cbs/convention_description.htm\">science-based management</a> and monitoring can help ensure the long-term sustainability of the resource.</li>\n<li>The Bering Sea fishery&nbsp;is one of the first U.S. fisheries to be managed with <a href=\"https://www.fisheries.noaa.gov/national/laws-and-policies/catch-shares\">catch shares</a> and is often considered one of the best-managed fisheries in the world.</li>\n</ul>\n</li>\n</ul>\n",
    "Habitat": "<ul>\n<li>Younger pollock live in the mid-water region of the ocean; older pollock (age 5 and up) typically dwell near the ocean floor.</li>\n<li>Pollock swim in large schools in waters between 330 and 985 feet deep but are sometimes found as deep as 3,300 feet. &nbsp;</li>\n</ul>\n",
    "Habitat Impacts": "The Alaska pollock fishery uses midwater trawl nets that, although sometimes making contact with the bottom, have minimal impact on habitat.",
