    "Population": "Above target population levels for the Aleutian Islands, Eastern Bering Sea, and Western/Central/West Yakutat Gulf of Alaska stocks. The population levels are unknown for Bogoslof and Southeast Gulf of Alaska.",
    "Population Status": "<ul>\n<li>There are five stocks of walleye pollock: Aleutian Islands, Eastern Bering Sea, Western/Central/West Yakutat Gulf of Alaska, Bogoslof, and Southeast Gulf of Alaska. According to the most recent stock assessments:\n<ul>\n<li>The Aleutian Islands stock is not overfished (2020 stock assessment), and not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Walleye%20pollock%20-%20Aleutian%20Islands&amp;stockid=10511\">Stock SMART</a>.</li>\n<li>The Eastern Bering Sea stock is not overfished (2020 stock assessment), and not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Walleye%20pollock%20-%20Eastern%20Bering%20Sea&amp;stockid=10510\">Stock SMART</a>.</li>\n<li>The Western/Central/West Yakutat Gulf of Alaska stock is not overfished (2020 stock assessment), and not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Walleye%20pollock%20-%20Western%20/%20Central%20/%20West%20Yakutat%20Gulf%20of%20Alaska&amp;stockid=10513\">Stock SMART</a>.</li>\n<li>The Bogoslof stock has been assessed, but there is not enough information to determine the population size so the overfished&nbsp;status is unknown (2020 stock assessment). This stock is not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Walleye%20pollock%20-%20Bogoslof&amp;stockid=10512\">Stock SMART</a>.</li>\n<li>The Southeast Gulf of Alaska stock has been assessed, but there is not enough information to determine the population size so the overfished&nbsp;status is unknown (2020 stock assessment). This stock is not subject to overfishing based on 2020 catch data. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Walleye%20pollock%20-%20Southeast%20Gulf%20of%20Alaska&amp;stockid=10514\">Stock SMART</a>.</li>\n</ul>\n</li>\n</ul>\n<p>&nbsp;</p>\n",
    "Scientific Name": "Gadus chalcogrammus",
    "Species Aliases": "<a href=\"/species-aliases/pollock\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Pollock</a>, <a href=\"/species-aliases/walleye-pollock\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Walleye pollock</a>, <a href=\"/species-aliases/pacific-pollock\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Pacific pollock</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/alaska_pollock.png",
    "alt": "",
    "title": ""
    },
    "Species Name": "Alaska Pollock",
    "Animal Health": null,
    "Availability": "<p>Fresh from January to April and June to October. Frozen year-round.</p>\n",
    "Biology": "<ul>\n<li>Alaska pollock grow fast and have a relatively short life span of about 12 years.</li>\n<li>As a result, they are generally more productive compared to slower growing, longer living species.</li>\n<li>Some pollock begin to reproduce by the age of 3 or 4 and are extremely fertile, so each generation replaces aging or harvested fish in just a few years.</li>\n<li>In the spring, pollock migrate inshore to shallow water to breed and feed.</li>\n<li>They move back to warmer, deeper waters in the winter months.</li>\n<li>The survival of young pollock depends on several factors, such as the availability of food, environmental conditions, and predation.</li>\n<li>Their survival rate is highly variable, which can potentially cause large fluctuations in the abundance of pollock in a matter of a few years.</li>\n<li>Juvenile pollock eat zooplankton (tiny floating animals) and small fish.</li>\n<li>Older pollock feed on other fish, including juvenile pollock.</li>\n<li>Many other speciesincluding Steller sea lions and other marine mammals, fish, and seabirdsfeed on pollock and rely on them for survival.</li>\n</ul>\n",
    "Bycatch": "The Alaska pollock fishery is one of the cleanest in terms of incidental catch of other species (less than 1 percent).",
