    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>The Alaska pollock fishery is one of the most valuable in the world.</li>\n<li>In 2020, commercial landings of Alaska pollock from the Bering Sea and Gulf of Alaska totaled more than 3.23 billion pounds and were valued at approximately $420 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:1974086272918::NO:::\">commercial fishing landings database</a>.</li>\n<li>A quarter of pollock products are surimi (imitation crab), almost one-fifth is roe (eggs), and close to half are fillets.</li>\n<li>The majority of the U.S. catch of pollock comes from the Bering Sea.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>In the United States, pollock are caught by trawlers that tow a large cone-shaped net through the mid-water.</li>\n<li>Less than 1 percent of the total catch in the Alaska pollock fishery&nbsp;is made up of other species.</li>\n<li>Bycatch of Pacific salmon is a particular concern because of its importance to commercial and subsistence fisheries. The relative impact of the pollock fishery on critical salmon runs has been estimated to be relatively low, especially since 2007.</li>\n<li>100 percent of pollock fishing boats&nbsp;in the Bering Sea carry scientifically trained observers. &nbsp;They carefully monitor and count all Pacific salmon caught incidentally in the pollock nets. These salmon have never been allowed to be landed or sold by the pollock fishery but, when feasible, they are donated to local Alaska food banks.</li>\n<li>The <a href=\"https://www.npfmc.org/\">North Pacific Fishery Management Council</a> implemented measures in 2011 to increase incentives for fishermen to further reduce Chinook salmon bycatch.</li>\n<li>The pollock industry has developed several innovative approaches to meet these new requirements, including using NOAA Fisheries Observer program data to close salmon bycatch hotspots to fishing on a weekly basis and testing a new salmon excluder device for trawl nets.</li>\n<li>The Council improved the management of Chinook and chum salmon bycatch in the Bering Sea by creating a comprehensive salmon bycatch avoidance program in 2016, and continues to examine additional measures to minimize salmon bycatch.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Alaska pollock is a good source of omega-3 fatty acids, high in protein, and low in carbohydrates and fat.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Pollock is a member of the cod family.</li>\n<li>They can grow as long as 3 feet but typically reach lengths between 12 and 20 inches and weigh between 1 and 3 pounds.</li>\n<li>They have speckled coloring that helps them blend in with the seafloor to avoid predators.</li>\n</ul>\n",
    "Production": null,
    "Protein": "17.18 g",
    "Quote": "U.S. wild-caught Alaska pollock is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#746867",
    "Research": "<ul>\n<li><a href=\"https://www.afsc.noaa.gov/News/BeringSea_warming.htm\">Young pollock survival better than expected during most recent Bering Sea warm phase</a>.</li>\n<li>Scientists at the <a href=\"https://www.fisheries.noaa.gov/alaska/ecosystems/alaska-fish-research-surveys\">Alaska Fisheries Science Center</a> conduct bottom trawl surveys every year to assess the abundance of crab and groundfish (like pollock) in the Eastern Bering Sea.</li>\n<li>Scientists also conduct acoustic trawl surveys in this region every 2 years to estimate the abundance of pollock living off the bottom.</li>\n<li>In the Gulf of Alaska, scientists conduct acoustic trawl surveys on spawning grounds every winter and <a href=\"https://www.afsc.noaa.gov/RACE/midwater/default_mw.php\">bottom trawl surveys&nbsp;and acoustic trawl surveys</a> every other summer.</li>\n<li>The Alaska Department of Fish and Game also surveys nearshore bottom areas using trawls in the Gulf of Alaska (primarily to monitor crab stocks).&nbsp; Data from these surveys also contribute to estimating pollock abundance.</li>\n<li>Managers use the data from these surveys, along with information about the fishery and species, to determine how much pollock can be harvested every year and to make other management decisions.</li>\n</ul>\n",
    "Saturated Fatty Acids, Total": "0.164 g",
    "Selenium": "21.9 mcg",
    "Serving Weight": "100 g (raw)",
    "Servings": "1",
    "Sodium": "99 mg",
    "Source": "<p>U.S. wild-caught, mainly in the Bering Sea and Gulf of Alaska.</p>\n",
    "Sugars, Total": "0 g",
    "Taste": "<p>Pollock has mild-tasting flesh and is similar to other white fish like cod or haddock.</p>\n",
    "Texture": "<p>Pollock has a relatively fine texture and is well suited for a variety of preparations.</p>\n",
