    "Location": "<ul>\n<li>Monkfish are found in the Northwest Atlantic Ocean from the Grand Banks and northern Gulf of St. Lawrence south to Cape Hatteras, North Carolina.</li>\n<li>They can tolerate a wide range of temperatures and depths, from inshore waters down to nearly 3,000 feet.</li>\n<li>Monkfish migrate seasonally to spawn and feed.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic, Southeast",
    "Population": "Above target population levels.",
    "Population Status": "<ul>\n<li>There are two stocks of monkfish: Gulf of Maine / Northern Georges Bank and Southern Georges Bank / Mid-Atlantic. According to the most recent stock assessments:\n<ul>\n<li>The Gulf of Maine / Northern Georges Bank stock is not overfished and not subject to overfishing (2013 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Goosefish%20-%20Gulf%20of%20Maine%20/%20Northern%20Georges%20Bank&amp;stockid=10500\">Stock SMART</a>.</li>\n<li>The Southern Georges Bank / Mid-Atlantic stock is not overfished and not subject to overfishing (2013 stock assessment). Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Goosefish%20-%20Southern%20Georges%20Bank%20/%20Mid-Atlantic&amp;stockid=10501\">Stock SMART</a>.</li>\n</ul>\n</li>\n</ul>\n",
    "Scientific Name": "Lophius americanus",
