    "Animal Health": null,
    "Availability": "<p>Year-round, with peaks in the late fall and spring.</p>\n",
    "Biology": "<ul>\n<li>Female monkfish grow larger and live longer than male monkfish. Females live to at least 13 years and grow to more than 4 feet long, while males only live about 7 years and grow to almost 3 feet long.</li>\n<li>Males and females are able to reproduce when they reach about 14 inches and 16 inches long, respectively.</li>\n<li>Monkfish spawn from February to October.</li>\n<li>Females release large egg veils that can contain more than 1 million eggs.</li>\n<li>These egg veils float near the surface along with the prevailing currents for 1 to 3 weeks (depending on temperature) until the veil disintegrates and the larvae hatch.</li>\n<li>Monkfish migrate seasonally to spawn and feed.</li>\n<li>They travel by slowly swimming or by using the sturdy base of their pectoral fins to walk.</li>\n<li>Scientists speculate that their wing-like pectoral fins may be used to ride currents.</li>\n<li>Monkfish are opportunistic feeders, eating whatever prey is most available at the time.</li>\n<li>Larvae feed on zooplankton (tiny floating animals). Juveniles mostly eat small fish, shrimp, and squid.</li>\n<li>Adults mainly eat fish, including other monkfish, but also feed on crustaceans, mollusks, seabirds, and diving ducks.</li>\n<li>Monkfish ambush their preythey use a modified spine on their head as a fishing pole and bait to lure small fish toward their mouths.</li>\n<li>When the prey comes near, the monkfish takes a large gulp, which sucks the prey into its mouth and traps it behind rows of back-pointing teeth.</li>\n<li>Large monkfish have few predators.</li>\n<li>Predacious fish such as swordfish, sharks, and thorny skate prey on small monkfish.</li>\n</ul>\n",
    "Bycatch": "Regulations limit possession of bycatch species and require modified fishing gear to reduce bycatch.",
