    "Species Name": "Atlantic Mahi Mahi",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Atlantic mahi mahi grow up to almost 7 feet and 88 pounds.</li>\n<li>They live up to 5 years.</li>\n<li>They are capable of reproducing at 4 to 5 months old.</li>\n<li>Believed to spawn every 2 to 3 days during the spawning season, releasing between 33,000 and 66,000 eggs each time.</li>\n<li>In the Atlantic, spawn under patches of floating brown algae called&nbsp;<em>Sargassum</em>.</li>\n<li>Mahi mahi are top predators that feed in surface water during the day.</li>\n<li>They eat a wide variety of species, including small pelagic fish, juvenile tuna, invertebrates, billfish, jacks, pompano, and pelagic larvae of nearshore, bottom-living species.</li>\n<li>Predators include large tuna, marine mammals, marlin, sailfish, and swordfish.</li>\n<li>NOAA Fisheries</li>\n</ul>\n",
    "Bycatch": "Regulations are in place to minimize bycatch.",
    "Calories": "85",
    "Carbohydrate": "0 g",
    "Cholesterol": "73 mg",
    "Color": "<p>The raw flesh is pinkish to grayish-white, although the flesh along the lateral line is dark. When cooked, the meat is off-white.</p>\n",
