    "Fishing Rate": "At recommended levels.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial fishermen harvested more than 275,000 pounds of mahi mahi in the Atlantic and Gulf of Mexico (primarily from South Carolina, North Carolina, and Florida) valued at $812,000, according to the NOAA Fisheries&nbsp;<a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:12529454701066::NO:::\">commercial fishing landings database</a>.&nbsp;</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Hook-and-line gear (including handlines and longlines) is used for commercial harvest.</li>\n<li>Hook-and-line gear has minimal impact on habitat because it does not contact the ocean floor.</li>\n<li>Longlines can incidentally catch sea turtles, marine mammals, and other species.</li>\n<li>Longline fishermen follow measures to prevent bycatch and protect other species. These include using specific gear and safe handling techniques to reduce impacts on sea turtles, as well as not fishing in certain areas to protect species such as billfish.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>The mahi mahi fishery in the Atlantic, Caribbean, and Gulf of Mexico has historically been recreational.</li>\n<li>In 2020, recreational fishermen harvested more than 6.3 million pounds of mahi mahi in the Atlantic and the Gulf of Mexico, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:12529454701066::NO:::\">recreational fishing landings database</a>.</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Low in saturated fat and a good source of vitamin B12, phosphorus, and potassium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Brightly colored back is an electric greenish blue, lower body is gold or sparkling silver, and sides have a mixture of dark and light spots.</li>\n<li>Bright pattern fades almost immediately after mahi mahi is harvested.</li>\n<li>Adult males have a square head.</li>\n<li>Females have a rounded head.</li>\n<li>Distinguished from the pompano dolphin by its 55 to 66 dorsal fin rays and a very wide, square tooth patch on the tongue.</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.5 g",
    "Quote": "U.S. wild-caught mahi mahi is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#83a54b",
    "Research": "<ul>\n<li>An ongoing&nbsp;<a href=\"http://www.dolphintagging.com/\" rel=\"external\">research program</a> using anglers to tag and track mahi mahi has documented movement, behavior, and habitat utilization patterns.</li>\n</ul>\n",
