    "Location": "<ul>\n<li>In U.S. waters, cobia are most abundant from Virginia south through the Gulf of Mexico.</li>\n<li>Cobia migrate seasonally in the Atlantic and Gulf of Mexico.</li>\n<li>Along the Atlantic coast, they move south and offshore toward warmer waters during the late fall and winter.</li>\n<li>Cobia found in the northeastern Gulf of Mexico during the summer move to south Florida waters in the winter, possibly spending the winter near the Florida Keys.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic, Southeast",
    "Population": "Below target level but fishing rate promotes population growth.",
    "Population Status": "<ul>\n<li>\n<p>According to the 2020 stock assessment, the Gulf of Mexico stock is not overfished but is subject to overfishing. Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?app=browse_by_stock&amp;stockid=10390\">Stock SMART</a>.</p>\n</li>\n<li>Scientists from NOAA's Southeast Fisheries Science Center monitor the abundance of the population, and scientists, managers, and stakeholders assess the status of the stock through the&nbsp;<a href=\"http://sedarweb.org\">Southeast Data, Assessment, and Review (SEDAR) process</a>.</li>\n</ul>\n",
    "Scientific Name": "Rachycentron canadum",
    "Species Aliases": "<a href=\"/species-aliases/crabeater\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Crabeater</a>, <a href=\"/species-aliases/sergeantfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Sergeantfish</a>, <a href=\"/species-aliases/ling\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Ling</a>, <a href=\"/species-aliases/cabio\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Cabio</a>, <a href=\"/species-aliases/cubby-yew\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Cubby yew</a>, <a href=\"/species-aliases/lemonfish\" typeof=\"skos:Concept\" property=\"rdfs:label skos:prefLabel\" datatype=\"\">Lemonfish</a>",
    "Species Illustration Photo": {
    "src": "https://www.fishwatch.gov/sites/default/files/cobia.png",
    "alt": "",
    "title": ""
    },
    "Species Name": "Cobia",
    "Animal Health": null,
    "Availability": "<p>Year-round.</p>\n",
    "Biology": "<ul>\n<li>Cobia are the only member of the family Rachycentridae in North America.</li>\n<li>They grow up to 6 feet and 100 pounds and live up to 12 years.</li>\n<li>They are able to reproduce when they are youngfemales mature at age 3 and males mature at age 2.</li>\n<li>Cobia spawn in coastal bays and estuaries several times throughout their spawning season, which lasts from late June to mid-August in the Southeast and from late summer to early fall in the Gulf of Mexico. Females release between 375,000 and 2 million eggs each time they spawn.</li>\n<li>They are strong, aggressive predators, mainly feeding on crustaceans but also fish and squid. Larger pelagic fish prey on young cobia.</li>\n</ul>\n",
    "Bycatch": "Minimal, as commercial fishermen rarely target cobia.",
