    "Fishing Rate": "Reduced to end overfishing.",
    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>\n<p>In 2020, commercial landings of cobia totaled 131,000 pounds and were valued at $430,000, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:3739194198033::NO:::\">commercial fishing landings database</a>.</p>\n</li>\n<li>\n<p>Commercial fishermen do not directly target cobia and usually catch it while trawling for shrimp or fishing for other species such as mackerel.</p>\n</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Recreational fishermen like cobia because it is a large, powerful fish that puts up a good fight and provides a tasty meal.</li>\n<li>In 2020, recreational anglers landed more than 4.6&nbsp;million pounds of cobia, according to the NOAA Fisheries <a href=\"https://foss.nmfs.noaa.gov/apexfoss/f?p=215:200:3739194198033::NO:::\">recreational fishing landings database</a>.&nbsp; &nbsp;</li>\n</ul>\n</li>\n</ul>\n",
    "Harvest Type": "Wild",
    "Health Benefits": "<p>Cobia is a good source of low-fat protein. It is high in riboflavin, niacin, vitamin B6, magnesium, and potassium.</p>\n",
    "Human_Health_": null,
    "Human Health": null,
    "Physical Description": "<ul>\n<li>Cobia are dark brown with a single dorsal fin.</li>\n<li>Young cobia have distinct coloring, with alternating black and white horizontal stripes and splotches of bronze, orange, and green.</li>\n<li>Cobia are often mistaken for sharks or remoras. In fact, their closest living relative is the remora (shark sucker).</li>\n</ul>\n",
    "Production": null,
    "Protein": "18.99 g",
    "Quote": "U.S. wild-caught cobia is a smart seafood choice because it is sustainably managed and responsibly harvested under U.S. regulations.",
    "Quote Background Color": "#452f21",
    "Research": "<ul>\n<li>Most of the cobia you will find in the market comes from <a href=\"https://www.fishwatch.gov/aquaculture\">aquaculture</a> operations, mainly from China. For information about cobia aquaculture, visit the <a href=\"http://www.fao.org/fishery/culturedspecies/Rachycentron_canadum/en\" rel=\"external\" target=\"_blank\">Food and Agriculture Organization of the United Nations Cultured Aquatic Species Information on Cobia website&nbsp;</a></li>\n<li>There is a successful aquaculture operation in the Caribbean Sea off the coast of Panama - <a href=\"https://www.openblue.com/\">Open Blue</a>.</li>\n</ul>\n<p>For more information, visit the links below:</p>\n<ul>\n<li><a href=\"http://www.vims.edu/research/departments/fisheries/programs/tagging_research/cobia/index.php\" rel=\"external\" target=\"_blank\">Virginia Institute of Marine Science  Cobia Tagging</a></li>\n<li><a href=\"http://gcrl.usm.edu/research/cobia_aquaculture.php\" rel=\"external\" target=\"_blank\">The University of Southern Mississippi Gulf Coast Research Laboratory  Aquaculture of Cobia <em>Rachycentron canadum</em> in Closed Systems </a></li>\n</ul>\n",
