    "Location": "<ul>\n<li>In the Northwest Atlantic, cod range from Greenland to Cape Hatteras, North Carolina.</li>\n<li>In U.S. waters, cod is most common on Georges Bank and in the western Gulf of Maine.</li>\n</ul>\n",
    "Management": null,
    "NOAA Fisheries Region": "Greater Atlantic",
    "Population": "Significantly below target population levels. Rebuilding plans are in place.",
    "Population Status": "<ul>\n<li>Gulf of Maine stock:\n<ul>\n<li>According to the 2021 stock assessment, the Gulf of Maine stock is overfished and below the target biomass level. &nbsp;Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Atlantic%20cod%20-%20Gulf%20of%20Maine&amp;stockid=10508\">Stock SMART</a>.</li>\n<li>A 10-year rebuilding plan was implemented for this stock in 2014 and the stock is targeted to rebuild by 2024.&nbsp; This is the second rebuilding plan for this stock.</li>\n</ul>\n</li>\n<li>Georges Bank stock:\n<ul>\n<li>According to the 2013 stock assessment the Georges Bank stock is overfished and subject to overfishing based on the 2021 stock assessment.&nbsp; Summary stock assessment information can be found on <a href=\"https://www.st.nmfs.noaa.gov/stocksmart?stockname=Atlantic%20cod%20-%20Georges%20Bank&amp;stockid=10509\">Stock SMART</a>.</li>\n</ul>\n</li>\n<li>Both stocks are subject to overfishing. Fishing is still allowed, but at reduced levels.</li>\n<li>Historically, cod was so abundant off New England that early explorers named Cape Cod for the fish. Furthermore, Gloucester was established by a colonial charter issued to profit from cod fishing, and a painted sacred cod carved from pine has hung in the Massachusetts state house since 1784 as a symbol of prosperity.</li>\n<li>Due to high fishing pressure throughout the latter part of the 20th century, there are fewer fish in the U.S. stocks of Atlantic cod than the average for the past four decades.</li>\n<li>A primary source of rebuilding potential is the number of young fish coming into the population (recruitment). Over the past 20 years, recruitment has varied for the Gulf of Maine stock, and has been well below average for the Georges Bank stock.</li>\n</ul>\n",
    "Scientific Name": "Gadus morhua",
