    "Harvest": "<ul>\n<li>Commercial fishery:\n<ul>\n<li>In 2020, commercial landings of Atlantic cod totaled 1.6 million pounds and were valued at more than $3.5 million, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:10164932807040::NO:RP::\">commercial fishing landings database</a>.</li>\n</ul>\n</li>\n<li>Gear types, habitat impacts, and bycatch:\n<ul>\n<li>Cod are commonly harvested using trawl nets, gillnets, bottom longlines, and rod and reel.</li>\n<li>Gillnets, longlines, and rod and reel used to harvest cod have little to no impact on habitat.</li>\n<li>Closed areas and gear restrictions reduce habitat impacts from trawl nets.</li>\n<li>Fishermen follow management measures designed to reduce interactions with marine mammals, including gear modifications, seasonal closures, and use of marine mammal deterrents.</li>\n</ul>\n</li>\n<li>Recreational fishery:\n<ul>\n<li>Cod are highly prized by recreational fishermen. They are commonly harvested by anglers using lures or bait. Fishing occurs year-round.</li>\n<li>\n<p>In 2020, recreational anglers landed 386,000 pounds of Atlantic cod, according to the NOAA Fisheries <a href=\"https://www.fisheries.noaa.gov/foss/f?p=215:200:10164932807040::NO:RP::\">recreational fishing landings database</a>.&nbsp;&nbsp;</p>\n</li>\n<li><a href=\"https://www.greateratlantic.fisheries.noaa.gov/sustainable/recfishing/regs/index.html\">Regulations</a> include minimum fish sizes, possession limits, and closed seasons.</li>\n</ul>\n</li>\n</ul>\n",
