    "Human Health": null,
    "Physical Description": "<ul>\n<li>Atlantic cod are heavy-bodied with a large head, blunt snout, and a distinct barbel (a whisker-like organ, like on a catfish) under the lower jaw.</li>\n<li>Their coloring varies, ranging from light yellowish-green to red and olive, usually with darker speckles on the head, fins, tail, and body. The belly is light colored and usually spotless. Individuals can change color readily.</li>\n<li>Cod have an obvious lateral line (the faint line that runs lengthwise down each side of the fish).</li>\n</ul>\n",
    "Production": null,
    "Protein": "17.81 g",
    "Quote": "Although populations are well below target levels, U.S. wild-caught Atlantic cod is still a smart seafood choice because it is sustainably managed under a rebuilding plan that allows limited harvest by U.S. fishermen.",
    "Quote Background Color": "#847852",
    "Research": "<ul>\n<li>Scientists at NOAAs <a href=\"http://www.nefsc.noaa.gov/\" target=\"_blank\">Northeast Fisheries Science Center</a> conduct research bottom trawl surveys throughout the Northeast continental shelf every year during the fall and spring. These surveys collect data on the environment as well as biological samples from fish caught during research trawling. The data from these and other sources are used by scientists in stock assessments to estimate population size and fishing pressure.</li>\n<li><a href=\"https://www.nefsc.noaa.gov/groundfish/operational-assessments-2017/\">2019&nbsp;Northeast Groundfish Oper</a><a href=\"https://www.nefsc.noaa.gov/saw/sasi/sasi_report_options.php\">ational Assessments website</a></li>\n<li>Around the world, several universities and research institutions have conducted a number of <a href=\"https://umaine.edu/cooperative-aquaculture/cod/\" rel=\"external\">research projects</a> with industry partners to advance commercial cod production.</li>\n</ul>\n",
